import firebaseConf from "../../../database/firebase";

export const getValueStateActivity = (codeActividad, callback) => {
  firebaseConf
    .database()
    .ref("actividades_online/listado/" + codeActividad)
    .on("value", (data) => {
      try {
        callback({
          pantallaEspera: data.val().isPantallaEspera,
          estadoActividad: data.val().isStart,
          userId: data.val().user,
        });
      } catch (error) {
        callback({
          pantallaEspera: undefined,
          estadoActividad: undefined,
          userId: undefined,
        });
      }
    });
};

export const getDataInformativaPizarra = (code, user, idClase, callback) => {
  let ruta = firebaseConf
    .database()
    .ref("actividades_online/users")
    .child(user)
    .child(code);

  let dataReturn = {};

  ruta.once("value", (snapshot) => {
    let datosActividad = snapshot.val().datosActividad;
    let datosCurso = snapshot.val().datosCurso;

    ruta
      .child("dataClases")
      .child("clases")
      .child(idClase)
      .once("value", (dataClase) => {
        dataReturn = {
          datosActividad: datosActividad,
          datosCurso: datosCurso,
          datosClase: dataClase.val().infoClase,
        };
        callback(dataReturn);
      });
  });
};

export const getContenidoPizarra = (
  codeActividad,
  user,
  idClase,
  callback,
  isRespuesta
) => {
  firebaseConf
    .database()
    .ref("actividades_online/users/")
    .child(user)
    .child(codeActividad)
    .child("datosEjecucion/dataNodos/")
    .child("pizarra")
    .on("value", (diapoActiva) => {
      try {
        if (diapoActiva.val().idActividad === "null") {
          callback("null");
          isRespuesta(false);
        } else {
          if (
            diapoActiva.val().isMostarRespuesta === false ||
            diapoActiva.val().isMostarRespuesta === undefined
          ) {
            getDiapositivaByIdPizarra(
              codeActividad,
              idClase,
              diapoActiva.val().idActividad,
              "pizarra",
              diapoActiva.val().idDiapositiva,
              user,
              (datoDiapo) => {
                callback({ datoDiapo: datoDiapo });
                isRespuesta(false);
              }
            );
          } else {
            getDiapositivaByIdPizarra(
              codeActividad,
              idClase,
              diapoActiva.val().idActividad,
              "alumnos",
              diapoActiva.val().idDiapositiva,
              user,
              (datoDiapo) => {
                firebaseConf
                  .database()
                  .ref("actividades_online/users/")
                  .child(user)
                  .child(codeActividad)
                  .child("datosEjecucion/respuestas/")
                  .child(idClase)
                  .child(diapoActiva.val().idActividad)
                  .child(diapoActiva.val().idDiapositiva)
                  .child(diapoActiva.val().dataRespuesta.idEstudiante)
                  .get()
                  .then((value) => {
                    callback({
                      datoDiapo: datoDiapo,
                      dataRespuesta: {
                        nombre: value.val().nameEstudiante,
                        respuesta: value.val().respEstudiante,
                        tipoRespuesta: value.val().tipoRespuesta,
                      },
                    });
                    // console.log(value.val())
                    isRespuesta(true);
                  });
              }
            );
          }
        }
      } catch (error) {
        callback(undefined);
      }
    });
};

const getDiapositivaByIdPizarra = (
  codeActividad,
  idClase,
  idActividad,
  vista,
  idDiapositiva,
  user,
  callback
) => {
  var diapoOut = {};

  let rutaDatabase = firebaseConf
    .database()
    .ref("actividades_online/users")
    .child(user)
    .child(codeActividad)
    .child("dataClases/clases")
    .child(idClase)
    .child("actividades")
    .child(idActividad)
    .child(vista)
    .child(idDiapositiva);

  let rutaFirebase = firebaseConf
    .firestore()
    .collection("actividades_online")
    .doc(user)
    .collection(codeActividad)
    .doc(idClase)
    .collection("data")
    .doc(idActividad)
    .collection(vista)
    .doc(idDiapositiva);

  firebaseConf
    .database()
    .ref("actividades_online/users")
    .child(user)
    .child(codeActividad)
    .child("dataClases/clases")
    .child(idClase)
    .child("actividades")
    .child("lista_actividad")
    .child(idActividad)
    .get()
    .then((nombre) => {
      rutaDatabase.on("value", (diapositiva) => {
        rutaFirebase.get().then((diapositivaFire) => {
          diapoOut = {
            key: diapositiva.key,
            contenidoHTML: diapositivaFire.data().contenidoHTML,
            actividadNombre: nombre.val().nombreActividad,
            keyActividad: idActividad,
          };
          callback(diapoOut);
        });
      });
    });
};

export const setPantallaDefault = (codeActividad, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    let ruta = firebaseConf
      .database()
      .ref("actividades_online/users")
      .child(user.uid)
      .child(codeActividad)
      .child("datosEjecucion");

    ruta
      .child("dataNodos")
      .child("pizarra")
      .set({
        idActividad: "null",
        idDiapositiva: "null",
        isMostarRespuesta: false,
      })
      .then(() => {
        callback(true);
      })
      .catch(() => {
        callback(false);
      });
  });
};

export const getParticipantes = (code, idClase, uid, callback) => {
  firebaseConf
    .database()
    .ref("actividades_online")
    .child("users")
    .child(uid)
    .child(code)
    .child("datosEjecucion")
    .child("usuarios")
    .child(idClase)
    .on("value", (value) => {
      let estudiantes = [];
      value.forEach((data, index) => {
        estudiantes.push(data.val().nombreEstudiante);
      });
      callback(estudiantes);
    });
};
