import firebaseConf, { cerrarSesion } from "../../../database/firebase";

export const getClasesCursosPublicos = (id, callback) => {
  firebaseConf
    .firestore()
    .collection("cursos")
    .doc("publicos")
    .collection("data")
    .doc(id)
    .collection("clases")
    .orderBy("time", "desc")
    .onSnapshot((data) => {
      var dataClases = [];
      data.forEach((doc) => {
        dataClases.push({ data: doc.data(), key: doc.id });
      });
      callback(dataClases);
    });
};

export const setClasesCursosPublicos = (
  id_curso,
  nombre,
  objetivo,
  tematica,
  imagen,
  porcentaje,
  callback
) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      var id_clase = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("publicos")
        .collection("data")
        .doc(id_curso)
        .collection("clases")
        .doc().id;

      if (imagen !== null) {
        firebaseConf
          .storage()
          .ref("img_clases/publicos/" + id_clase)
          .put(imagen.file)
          .on(
            "state_changed",
            (snapshot) => {
              var temp =
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
              porcentaje(temp);
            },
            (error) => {
              console.log("error " + error);
              alert("Error al subir los datos " + error);
            },
            () => {
              firebaseConf
                .storage()
                .ref("img_clases/publicos/" + id_clase)
                .getDownloadURL()
                .then((url) => {
                  firebaseConf
                    .firestore()
                    .collection("cursos")
                    .doc("publicos")
                    .collection("data")
                    .doc(id_curso)
                    .collection("clases")
                    .doc(id_clase)
                    .set({
                      nombreclase: nombre,
                      objetivo: objetivo,
                      tematica: tematica,
                      imagen: url,
                      time: Date.now(),
                    })
                    .then(() => {
                      callback(true);
                    })
                    .catch((e) => callback(false));
                });
            }
          );
      } else {
        firebaseConf
          .firestore()
          .collection("cursos")
          .doc("publicos")
          .collection("data")
          .doc(id_curso)
          .collection("clases")
          .doc(id_clase)
          .set({
            nombreclase: nombre,
            objetivo: objetivo,
            tematica: tematica,
            imagen: null,
            time: Date.now(),
          })
          .then(() => {
            callback(true);
          })
          .catch((e) => callback(false));
      }
    } else {
      cerrarSesion();
    }
  });
};

export const deleteClasesPublicos = (id_curso, id_clase, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      let ruta = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("publicos")
        .collection("data")
        .doc(id_curso)
        .collection("clases")
        .doc(id_clase);

      ruta.collection("actividad").onSnapshot((actividades) => {
        actividades.forEach((contActividades) => {
          contActividades.ref
            .collection("profesor")
            .onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapositiva) => {
                contDiapositiva.ref.delete();
              });
            });
          contActividades.ref
            .collection("alumnos")
            .onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapositiva) => {
                contDiapositiva.ref.delete();
              });
            });
          contActividades.ref
            .collection("pizarra")
            .onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapositiva) => {
                contDiapositiva.ref.delete();
              });
            });

          firebaseConf
            .database()
            .ref("cursos/publicos/data/" + id_curso + "/" + id_clase)
            .remove()
            .then(() => {
              callback(true);
            })
            .catch(() => {
              callback(false);
            });

          contActividades.ref.delete();
        });
      });
      ruta.delete();
      try {
        firebaseConf
          .storage()
          .ref("img_clases/publicos/" + id_clase)
          .delete();
      } catch (error) {}
    } else {
      cerrarSesion();
    }
  });
};

export const actualizarEstado = (id_curso, estado, callback) => {
  firebaseConf
    .firestore()
    .collection("cursos")
    .doc("publicos")
    .collection("data")
    .doc(id_curso)
    .update({
      publicado: estado,
    })
    .then(() => {
      callback({ resultado: "Correcto", estadoCurso: estado });
      // window.location.reload()
    })
    .catch((e) => callback({ resultado: "Error", estadoCurso: !estado }));
};
export const getContenidoCursos = async (id, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      let nuevo_id = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("privados")
        .collection(user.uid)
        .doc().id;

      let ruta_nueva = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("privados")
        .collection(user.uid)
        .doc(nuevo_id);

      let ruta = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("publicos")
        .collection("data")
        .doc(id);

      ruta.onSnapshot((data) => {
        ruta_nueva.set(data.data()).then(() => {
          ruta_nueva.update({ id: nuevo_id });
          ruta.collection("clases").onSnapshot((clases) => {
            if (clases.empty) {
              callback(true);
            }
            clases.forEach((doc) => {
              ruta_nueva
                .collection("clases")
                .doc(doc.id)
                .set(doc.data())
                .then(() => {
                  ruta
                    .collection("clases")
                    .doc(doc.id)
                    .collection("actividad")
                    .onSnapshot((actividades) => {
                      if (actividades.empty) {
                        callback(true);
                      }
                      actividades.forEach((contenidoActividades) => {
                        ruta
                          .collection("clases")
                          .doc(doc.id)
                          .collection("actividad")
                          .doc(contenidoActividades.id)
                          .collection("profesor")
                          .onSnapshot((diapositivas) => {
                            diapositivas.forEach((contenidoDiapositiva) => {
                              ruta_nueva
                                .collection("clases")
                                .doc(doc.id)
                                .collection("actividad")
                                .doc(contenidoActividades.id)
                                .collection("profesor")
                                .doc(contenidoDiapositiva.id)
                                .set(contenidoDiapositiva.data());
                              ruta_nueva
                                .collection("clases")
                                .doc(doc.id)
                                .collection("actividad")
                                .doc(contenidoActividades.id)
                                .set({ temp: "temp" });
                            });
                          });

                        ruta
                          .collection("clases")
                          .doc(doc.id)
                          .collection("actividad")
                          .doc(contenidoActividades.id)
                          .collection("alumnos")
                          .onSnapshot((diapositivas) => {
                            diapositivas.forEach((contenidoDiapositiva) => {
                              ruta_nueva
                                .collection("clases")
                                .doc(doc.id)
                                .collection("actividad")
                                .doc(contenidoActividades.id)
                                .collection("alumnos")
                                .doc(contenidoDiapositiva.id)
                                .set(contenidoDiapositiva.data());
                            });
                          });

                        ruta
                          .collection("clases")
                          .doc(doc.id)
                          .collection("actividad")
                          .doc(contenidoActividades.id)
                          .collection("pizarra")
                          .onSnapshot((diapositivas) => {
                            diapositivas.forEach((contenidoDiapositiva) => {
                              ruta_nueva
                                .collection("clases")
                                .doc(doc.id)
                                .collection("actividad")
                                .doc(contenidoActividades.id)
                                .collection("pizarra")
                                .doc(contenidoDiapositiva.id)
                                .set(contenidoDiapositiva.data());
                            });
                          });
                      });
                    });
                })
                .catch(() => {
                  callback(false);
                })
                .finally(() => {
                  firebaseConf
                    .database()
                    .ref("cursos/publicos/data/")
                    .child(id)
                    .get()
                    .then((realTime) => {
                      firebaseConf
                        .database()
                        .ref("cursos/privados/")
                        .child(user.uid)
                        .child(nuevo_id)
                        .set(realTime.val())
                        .then(() => {
                          callback(true);
                        });
                    });
                });
            });
          });
        });
      });
    } else {
      cerrarSesion();
    }
  });
};
