import {  Button, Typography } from "@mui/material";
import React, { useMemo, useState } from "react";
import ListarDiseños from "./ListarDiseños";
import { UserContext } from "../../Funcionalidades/UseContext";
import { Box } from "@mui/material";
import EditorAdmin from "./Editor";
import VistaPreviaDiapo from "./VistaPreviaDiapo";
import VistaEnGrandeDiapositiva from "./VistaEnGrandeDiapositiva";
import EditarDiseno from "./EditarDiseno";
import { dataValueCheckbox } from "../../Funcionalidades/AccionesDiapositivas";
import { getColor } from "../../../styles/ArgumentappTheme";

function MainDiseñoDiapositivas() {
  const [banderaCrearDiseno, setbanderaCrearDiseno] = useState(false);
  const [tipoVista, setTipoVista] = useState("");

  const [nombreDiseno, setNombreDiseno] = useState("");
  const [valueCheckbox, setvalueCheckbox] = useState(dataValueCheckbox());
  const [cantBotones, setcantBotones] = useState(1);
  const [cantBotonesCheckbox, setcantBotonesCheckbox] = useState(2);
  const [isContinuar, setisContinuar] = useState(false);
  const [contenidoHTML, setContenidoHTML] = useState("");
  const [banderaEditarDiseno, setBanderaEditarDiseno] = useState(false);
  /**
   *
   * Parte de las acciones
   *
   */

  const [valorTemporalHTML, setvalorTemporalHTML] = useState("");
  const [banderaVistaPrevia, setbanderaVistaPrevia] = useState(false);
  const [acciones, setAcciones] = useState({});
  const [nombrePlantilla, setnombrePlantilla] = useState("");

  const providerValue = useMemo(
    () => ({
      setTipoVista,
      tipoVista,
      setbanderaCrearDiseno,
      nombreDiseno,
      setNombreDiseno,
      valueCheckbox,
      setvalueCheckbox,
      cantBotones,
      setcantBotones,
      cantBotonesCheckbox,
      setcantBotonesCheckbox,
      setisContinuar,
      setContenidoHTML,
      contenidoHTML,
      setvalorTemporalHTML,
      setbanderaVistaPrevia,
      setAcciones,
      setnombrePlantilla,
      setBanderaEditarDiseno,
    }),
    [
      setTipoVista,
      tipoVista,
      setbanderaCrearDiseno,
      nombreDiseno,
      setNombreDiseno,
      valueCheckbox,
      setvalueCheckbox,
      cantBotones,
      setcantBotones,
      cantBotonesCheckbox,
      setcantBotonesCheckbox,
      setisContinuar,
      setContenidoHTML,
      contenidoHTML,
      setvalorTemporalHTML,
      setbanderaVistaPrevia,
      setAcciones,
      setnombrePlantilla,
      setBanderaEditarDiseno,
    ]
  );
  return (
    <UserContext.Provider value={providerValue}>
      <div>
        {!banderaCrearDiseno ? (
          <div className="main-container">
            <div
              style={{
                width: "100%",
              }}
            >
              <Typography variant="titulo">
                <b>Administrar diseños de diapositivas</b>
              </Typography>
              <div
                style={{
                  marginTop: 50,
                  marginBottom: 10,
                }}
              >
                <div>
                  {[
                    { nombre: "Profesor", key: "profesor" },
                    { nombre: "Alumnos", key: "alumnos" },
                    { nombre: "Pizarra", key: "pizarra" },
                  ].map((value) => {
                    return (
                      <div
                        style={{
                          minHeight: "250px",
                          backgroundColor: "white",
                          marginBottom: "20px",
                          borderStyle: "solid",
                          borderWidth: 5,
                          borderColor: getColor().mainColor,
                          borderRadius: "30px",
                          padding: "30px",
                        }}
                      >
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            alignContent: "flex-start",
                            alignItems: "center",
                            marginBottom: "20px",
                          }}
                        >
                          <Typography variant="h5">{value.nombre}</Typography>
                        </div>
                        <div
                          style={{
                            marginLeft: "30px",
                            display: "flex",
                            flexDirection: "row",
                            alignContent: "flex-start",
                            alignItems: "center",
                          }}
                        >
                          <ListarDiseños
                            tipoVista={value.nombre}
                            keyVista={value.key}
                          ></ListarDiseños>
                        </div>
                      </div>
                    );
                  })}
                </div>

                <VistaEnGrandeDiapositiva
                  valorTemporalHTML={valorTemporalHTML}
                  banderaVistaPrevia={banderaVistaPrevia}
                  acciones={acciones}
                  nombrePlantilla={nombrePlantilla}
                />
                <EditarDiseno
                  banderaEditarDiseno={banderaEditarDiseno}
                  nombrePlantilla={nombrePlantilla}
                  valorTemporalHTML={valorTemporalHTML}
                  accionesEditar={acciones}
                  tipoVista={tipoVista}
                />
              </div>
            </div>
          </div>
        ) : (
          <div className="main-container">
            <div
              style={{
                width: "100%",
              }}
            >
              <div
                style={{
                  backgroundColor: "white",
                  padding: "50px 50px",
                  textAlign: "center",
                  borderRadius: 30,
                }}
              >
                {!isContinuar ? (
                  <div>

                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "space-between",
                        alignContent: "center",
                      }}
                    >
                      <Typography
                        id="modal-modal-title"
                        variant="h6"
                        component="h2"
                        color="black"
                      >
                        Crear diseño de diapositiva en vista de {tipoVista}
                      </Typography>
                      <Button
                        color="error"
                        style={{
                          backgroundColor: "red",
                          color: "white",
                          marginLeft: 40,
                        }}
                        onClick={(e) => {
                          setbanderaCrearDiseno(false);
                        }}
                      >
                        Volver
                      </Button>
                    </div>
                    <Box
                      sx={{
                        width: "100%",
                      }}
                    >
                      <div>
                        <EditorAdmin></EditorAdmin>
                      </div>
                    </Box>
                  </div>
                ) : (
                  <div>
                    <VistaPreviaDiapo></VistaPreviaDiapo>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </UserContext.Provider>
  );
}

export default MainDiseñoDiapositivas;
