import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  IconButton,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import InfoIcon from "@mui/icons-material/Info";
import {
  changeDiapositivaActivaProfesorByOrden,
  existFollowDiapositiva,
  getDiapositivaActiva,
} from "../../BDActividadesOnline";
import parse from "html-react-parser";
import AccionesDiapo from "./AccionesDiapo";
import DashboardOutlinedIcon from "@mui/icons-material/DashboardOutlined";
import SliderTabAlumnoPizarra from "./SliderTabAlumnoPizarra";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
function DiapositivaActivaProfesor({ codigo, dataActividades, idClase, data }) {
  const [banderaTermino, setbanderaTermino] = useState(false);
  const [contenidoDiapositiva, setcontenidoDiapositiva] = useState({});
  const [openSliderAnexas, setopenSliderAnexas] = useState(false);

  const [existSiguiente, setexistSiguiente] = useState(false);
  useEffect(() => {
    getDiapositivaActiva(codigo, "profesor", idClase, (value) => {
      setcontenidoDiapositiva(value);
      existFollowDiapositiva(
        codigo,
        idClase,
        value.keyActividad,
        "profesor",
        Number(value.datos.orden) + 1,
        (val) => {
          setexistSiguiente(val);
        }
      );

      setbanderaTermino(true);
    });
  }, [codigo, idClase]);

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      {banderaTermino && (
        <div style={{ overflow: "auto", marginTop: 13 }}>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "nowrap",
              justifyContent: "center",
            }}
          >
            <div style={{ margin: 0 }}>
              <div
                style={{
                  padding: 30,
                  borderRadius: 25,
                  width: "1260px",
                  height: "760px",
                  zoom: "80%",
                  backgroundColor: "white",
                }}
              >
                {parse(contenidoDiapositiva.contenidoHTML)}
                <AccionesDiapo
                  acciones={contenidoDiapositiva.datos.acciones}
                  codigoActividad={codigo}
                ></AccionesDiapo>
              </div>
            </div>
          </div>
          <div
            style={{
              padding: 20,
              marginLeft: "7%",
              marginRight: "7%",
              display: "flex",
              flexDirection: "row",
              flexWrap: "nowrap",
              alignContent: "space-between",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <IconButton
              style={{
                backgroundColor:
                  contenidoDiapositiva.datos.orden === 0 ? "" : "#9C5FA2",
                minWidth: 40,
                maxWidth: 40,
                color:
                  contenidoDiapositiva.datos.orden === 0 ? "" : "#fff",
              }}
              disabled={contenidoDiapositiva.datos.orden === 0 ? true : false}
              onClick={(e) => {
                changeDiapositivaActivaProfesorByOrden(
                  codigo,
                  contenidoDiapositiva.keyActividad,
                  idClase,
                  Number(contenidoDiapositiva.datos.orden) - 1,
                  "profesor"
                );
              }}
            >
              <ArrowBackIosIcon style={{ marginLeft: 10 }}></ArrowBackIosIcon>
            </IconButton>
            <div>
              <IconButton
                style={{
                  boxShadow: "none",
                  color: "#9C6FA6",
                }}
                onClick={handleClickOpen}
              >
                <InfoIcon
                  style={{
                    fontSize: "33px",
                  }}
                />
              </IconButton>
              <IconButton
                style={{
                  boxShadow: "none",
                  color: "#9C6FA6",
                }}
                onClick={(e) => {
                  setopenSliderAnexas(true);
                }}
              >
                <DashboardOutlinedIcon
                  style={{
                    fontSize: "33px",
                  }}
                ></DashboardOutlinedIcon>
              </IconButton>
            </div>

            <IconButton
              style={{
                backgroundColor: existSiguiente ? "#9C5FA2" : "",
                minWidth: 40,
                maxWidth: 40,
                color: existSiguiente ? "#fff" : "",
              }}
              disabled={existSiguiente ? false : true}
              onClick={(e) => {
                changeDiapositivaActivaProfesorByOrden(
                  codigo,
                  contenidoDiapositiva.keyActividad,
                  idClase,
                  Number(contenidoDiapositiva.datos.orden) + 1,
                  "profesor"
                );
              }}
            >
              <ArrowForwardIosIcon
                style={{ marginLeft: 3 }}
              ></ArrowForwardIosIcon>
            </IconButton>
          </div>
          <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            {/* <DialogTitle id="alert-dialog-title">
              {"Información de la clase"}
            </DialogTitle> */}
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                <h3
                  style={{
                    color: "black",
                    marginLeft: 0,
                    fontSize: "20px",
                  }}
                >
                  Objetivo de clase
                </h3>
                <p
                  style={{
                    textAlign: "justify",
                    fontSize: "16px",
                  }}
                >
                  {data.data.infoClase.objetivo}
                </p>
                <h3
                  style={{
                    color: "black",
                    marginLeft: 0,
                    fontSize: "20px",
                  }}
                >
                  Temática de clase
                </h3>
                <p
                  style={{
                    textAlign: "justify",
                    fontSize: "16px",
                  }}
                >
                  {data.data.infoClase.tematica}
                </p>
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button variant="contained" color="error" onClick={handleClose}>
                Cerrar
              </Button>
            </DialogActions>
          </Dialog>
          <Dialog
            open={openSliderAnexas}
            onClose={(e) => {
              setopenSliderAnexas(false);
            }}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            maxWidth={"lg"}
          >
            {/* <DialogTitle id="alert-dialog-title">
              {"Información de la clase"}
            </DialogTitle> */}
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                <SliderTabAlumnoPizarra
                  codigo={codigo}
                  idClase={idClase}
                  dataActividades={dataActividades}
                ></SliderTabAlumnoPizarra>
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button
                variant="contained"
                color="error"
                onClick={(e) => {
                  setopenSliderAnexas(false);
                }}
              >
                Cerrar
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      )}
    </div>
  );
}

export default DiapositivaActivaProfesor;
