import React, { useState, useEffect } from "react";
// import { loadCSS } from "fg-loadcss";
import logo from "../assets/images/LogoNuevo.svg";
import {
    ProSidebar,
    Menu,
    MenuItem,
    SidebarHeader,
    // SidebarFooter,
    SidebarContent,
} from "react-pro-sidebar";
import "react-pro-sidebar/dist/css/styles.css";
import "../styles/menu.css";
import firebaseConf, { cerrarSesion } from "../database/firebase";
import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import HomeOutlinedIcon from "@mui/icons-material/HomeOutlined";
import BookmarksOutlinedIcon from "@mui/icons-material/BookmarksOutlined";
import Inventory2OutlinedIcon from "@mui/icons-material/Inventory2Outlined";
import SettingsOutlinedIcon from "@mui/icons-material/SettingsOutlined";
import MenuBookOutlinedIcon from "@mui/icons-material/MenuBookOutlined";
import GroupsIcon from "@mui/icons-material/Groups";
import { Divider } from "@mui/material";
import { useHistory } from "react-router-dom";

export default function MenuArgumentapp() {
    const [collapsed, setCollapsed] = useState(false);
    const location = useLocation();
    // const seccionesSinMenu = [""];

    const handleCollapsedOpen = () => {
        setCollapsed(false);
    };

    const handleCollapsedClose = () => {
        setCollapsed(true);
    };

    useEffect(() => {
        firebaseConf.auth().onAuthStateChanged((user) => {
            if (user) {
                if (
                    window.localStorage.getItem(
                        "dataUserArgumentapp_correo"
                    ) !== user.email
                ) {
                    cerrarSesion();
                }
            } else {
                cerrarSesion();
            }
        });
    }, []);

    return (
      <div>
        {!(
          String(location.pathname).includes("/actividad/") &&
          String(location.pathname).includes("/clase/") &&
          String(location.pathname).includes("/pizarra")
        ) && (
          <div style={{ position: "relative" }}>
            <ProSidebar
              collapsed={collapsed}
              breakPoint="xs"
              //onMouseEnter={handleCollapsedOpen}
              //onMouseLeave={handleCollapsedClose}
            >
              <SidebarHeader>
                <img
                  alt="logo"
                  src={logo}
                  className={
                    "logo-argumentapp" + (collapsed ? " collapsed" : "")
                  }
                />
              </SidebarHeader>

              <SidebarContent>
                <Menu iconShape="circle">
                  <MenuItem
                    icon={
                      <i
                        class="bi bi-house-door"
                        style={{ fontSize: "1.5rem" }}
                      ></i>
                    }
                    active={location.pathname === "/" ? true : false}
                    className={
                      location.pathname === "/" && collapsed ? "collapsed" : ""
                    }
                  >
                    Inicio
                    <Link to="/" />
                  </MenuItem>
                  <MenuItem
                    icon={
                      <i
                        class="bi bi-columns-gap"
                        style={{ fontSize: "1.5rem" }}
                      ></i>
                    }
                    active={
                      String(location.pathname)
                        .toUpperCase()
                        .includes("/actividadesonline".toUpperCase()) ||
                      String(location.pathname).includes("/actividad/")
                        ? true
                        : false
                    }
                    className={
                      (String(location.pathname)
                        .toUpperCase()
                        .includes("/actividadesonline".toUpperCase()) ||
                        String(location.pathname).includes("/actividad/")) &&
                      collapsed
                        ? "collapsed"
                        : ""
                    }
                  >
                    Actividades
                    <Link to="/actividadesonline" />
                  </MenuItem>
                  <MenuItem
                    icon={
                      <i
                        class="bi bi-bookmarks"
                        style={{ fontSize: "1.5rem" }}
                      ></i>
                    }
                    active={
                      location.pathname === "/planificaciones" ||
                      location.pathname === "/vistaclases"
                        ? true
                        : false
                    }
                    className={
                      (location.pathname === "/planificaciones" ||
                        location.pathname === "/vistaclases") &&
                      collapsed
                        ? "collapsed"
                        : ""
                    }
                  >
                    Mis planificaciones
                    <Link to="/planificaciones" />
                  </MenuItem>
                  <MenuItem
                    icon={
                      <i class="bi bi-book" style={{ fontSize: "1.5rem" }}></i>
                    }
                    active={
                      String(location.pathname).includes(
                        "/contenidoargumentapp"
                      ) ||
                      String(location.pathname).includes(
                        "/clasecontenidoargumentapp"
                      )
                        ? true
                        : false
                    }
                    className={
                      (String(location.pathname).includes(
                        "/contenidoargumentapp"
                      ) ||
                        String(location.pathname).includes(
                          "/clasecontenidoargumentapp"
                        )) &&
                      collapsed
                        ? "collapsed"
                        : ""
                    }
                  >
                    Contenido Argumentapp
                    <Link to="/contenidoargumentapp" />
                  </MenuItem>
                  <MenuItem
                    icon={
                      <i
                        class="bi bi-archive"
                        style={{ fontSize: "1.5rem" }}
                      ></i>
                    }
                    active={
                      location.pathname === "/recursos" ||
                      location.pathname === "/materialargumentapp" ||
                      location.pathname === "/pedagogicos"
                        ? true
                        : false
                    }
                    className={
                      (location.pathname === "/recursos" ||
                        location.pathname === "/materialargumentapp" ||
                        location.pathname === "/pedagogicos") &&
                      collapsed
                        ? "collapsed"
                        : ""
                    }
                  >
                    Recursos de apoyo
                    <Link to="/recursos" />
                  </MenuItem>
                  <MenuItem
                    icon={<i class="bi bi-calendar4-week" style={{ fontSize: "1.5rem"}}></i>}
                    active={
                      location.pathname === "/calendario" ? true : false
                    }
                    className={
                      location.pathname === "/calendario" && collapsed
                        ? "collapsed"
                        : ""
                    }                  
                  >
                    Calendario
                    <Link to="/calendario" />
                  </MenuItem>
                  <Divider
                    className={"menu-divider" + (collapsed ? " collapsed" : "")}
                  />
                  {Number(
                    window.localStorage.getItem("dataUserArgumentapp_permisos")
                  ) !== 2 && (
                    <MenuItem
                      icon={
                        <i
                          class="bi bi-gear"
                          style={{ fontSize: "1.5rem" }}
                        ></i>
                      }
                      active={
                        location.pathname === "/configuracion" ? true : false
                      }
                      className={
                        location.pathname === "/configuracion" && collapsed
                          ? "collapsed"
                          : ""
                      }
                    >
                      Configuración
                      <Link to="/configuracion" />
                    </MenuItem>
                  )}
                </Menu>
              </SidebarContent>
            </ProSidebar>
            <div
              className={"curva-superior" + (collapsed ? " collapsed" : "")}
            />
            <div
              className={"curva-inferior" + (collapsed ? " collapsed" : "")}
            />
            <div
              className={"caja-superior" + (collapsed ? " collapsed" : "")}
            />
            <div
              className={"caja-inferior" + (collapsed ? " collapsed" : "")}
            />
          </div>
        )}
      </div>
    );
}
