import React, { useEffect, useState } from "react";
import { getContenidoPizarra } from "./BDActOnlinePizarra";
import parse from "html-react-parser";

import logo from "../../../assets/images/LogoNuevoConLetras.svg";
import { Typography } from "@mui/material";

function ContenidoPizarra({ codigo, idClase, user }) {
  const [contenidoDiapositiva, setContenidoDiapositiva] = useState({});
  const [banderaTermino, setbanderaTermino] = useState(false);
  const [isMostarRespuesta, setisMostarRespuesta] = useState('sinVariable')
  useEffect(() => {
    getContenidoPizarra(
      codigo,
      user,
      idClase,
      (diapo) => {
        setContenidoDiapositiva(diapo);
        setbanderaTermino(true);
      },
      (isMostarRespuesta) => {
        setisMostarRespuesta(isMostarRespuesta)
      }
    );
  }, [codigo, idClase, user]);

  return (
    <div>
      {banderaTermino && (
        <div>
          {contenidoDiapositiva === "null" ? (
            <div
              style={{
                height: "65vh",
                width: "70vw",
                backgroundColor: "white",
                borderRadius: 25,
                borderStyle: "groove",
                borderWidth: 5,
                display: "flex",
                flexDirection: "column",
                alignContent: "center",
                justifyContent: "center",
              }}
            >
              <div
                style={{
                  display: "flex",
                  alignContent: "center",
                  justifyContent: "center",
                }}
              >
                <img
                  style={{
                    height: "auto",
                    width: "40%",
                  }}
                  alt="LogoArgumentappMain"
                  src={logo}
                ></img>
              </div>
              <div
                style={{
                  display: "flex",
                  alignContent: "center",
                  justifyContent: "center",
                }}
              >
                <img
                  style={{
                    height: "auto",
                    width: "45%",
                  }}
                  alt="LogoArgumentapp"
                  src="https://www.argumentapp.cl/sites/all/themes/argumentapp/img/img-transformando-la-ensenianza.svg"
                ></img>
              </div>
              <h2
                style={{
                  textAlign: "center",
                  color: "black",
                  paddingLeft: 50,
                  paddingRight: 50,
                }}
              >
                Transformando la enseñanza de las ciencias a través de la
                argumentación, el debate y colaboración de ideas.
              </h2>
            </div>
          ) : (
            <div>
              <div>
                <Typography variant="titulo" style={{ marginLeft: 50 }}>
                  {contenidoDiapositiva.datoDiapo.actividadNombre}
                </Typography>
                <div
                  style={{
                    padding: 20,
                    borderRadius: 25,
                    borderStyle: "groove",
                    borderWidth: 5,
                    marginLeft: 40,
                    marginRight: 40,
                    zoom: "100%",
                    backgroundColor: "white",
                    width: "1200px",
                    height: "700px",
                    marginTop: 10,
                    overflowY: "auto",
                  }}
                >
                  {parse(contenidoDiapositiva.datoDiapo.contenidoHTML)}

                  {isMostarRespuesta === true && (
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        padding: 5,
                        paddingTop: 0,
                      }}
                    >
                      <Typography
                        variant="h4"
                        style={{ marginBottom: 10, fontSize: 30 }}
                      >
                        <b>
                          Estudiante:{" "}
                          {contenidoDiapositiva.dataRespuesta.nombre}
                        </b>
                      </Typography>
                      {contenidoDiapositiva.dataRespuesta.tipoRespuesta ===
                        "TextArea" && (
                        <Typography variant="body6">
                          <b>Respuesta: </b>
                          {contenidoDiapositiva.dataRespuesta.respuesta}
                        </Typography>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default ContenidoPizarra;
