import React, { useState, useContext } from "react";
import { Alert, Button, InputLabel, FormControl, CircularProgress } from "@mui/material";
import Box from "@mui/material/Box";
import { registrarUsuarios, iniciarSesion } from "../../database/firebase";
import { CustomInput } from "../../styles/ArgumentappTheme";
import { UserContext } from "../Funcionalidades/UseContext";

function RedBar() {
    return <Box sx={{ height: 20 }} />;
}

function RegistrarUsuario() {
    const [nombre, setNombre] = useState("");
    const [correo, setCorreo] = useState("");
    const [password, setPassword] = useState("");
    const [permisos, setPermisos] = useState(2);

    const [mensaje, setMensaje] = useState("");

    const [banderaCrearActividad, setBandera] = useState(false);
    const [nuevoRegistro, setBanderaRegistro] = useState(false);

    const { setBanderaIntro } = useContext(UserContext);
    const [procesandoRegistro, setProcesandoRegistro] = useState(false);

    const crearActividad = () => {
        setProcesandoRegistro(true)
        registrarUsuarios(nombre, correo, password, permisos, (val) => {
            if (val) {                
                setBanderaRegistro(true);
                setNombre("");
                setCorreo("");
                setPassword("");
                setPermisos(2);
                setTimeout(function () {
                    iniciarSesion(correo, password, (value) => {
                        if (
                            localStorage.getItem(
                                "dataUserArgumentapp_skipIntro"
                            ) === "false"
                        ) {
                            setBanderaIntro(true);
                        } else {
                            window.location = "/";
                        }
                    });
                }, 3000);
            } else {
                if (val === "auth/weak-password") {
                    setMensaje("La contraseña debe tener minimo 6 caracteres");
                }

                setProcesandoRegistro(false)
                setBandera(true);
                setPermisos(2);
                setTimeout(function () {
                    setBandera(false);
                }, 5000);
            }
        });
    };

    return (
        <Box className="argumentapp-center">
            {!procesandoRegistro ? (
                <div>
                    {banderaCrearActividad && (
                        <Alert severity="error">{mensaje}</Alert>
                    )}
                    {nuevoRegistro && (
                        <Alert severity="success">
                            Usuario agregado con exito a Argumentapp, en 3
                            segundos será redirigido a la aplicación...
                        </Alert>
                    )}

                    <RedBar />

                    <FormControl variant="standard">
                        <InputLabel shrink>Nombre</InputLabel>
                        <CustomInput
                            id="nombre"
                            required
                            onChange={(e) => {
                                setNombre(e.target.value);
                            }}
                        />
                    </FormControl>

                    <RedBar />

                    <FormControl variant="standard">
                        <InputLabel shrink>E-mail</InputLabel>
                        <CustomInput
                            id="email"
                            required
                            onChange={(e) => {
                                setCorreo(e.target.value);
                            }}
                        />
                    </FormControl>

                    <RedBar />

                    <FormControl variant="standard">
                        <InputLabel shrink>Contraseña</InputLabel>
                        <CustomInput
                            id="nombre"
                            type="password"
                            required
                            onChange={(e) => {
                                setPassword(e.target.value);
                            }}
                        />
                    </FormControl>
                </div>
            ) : (
                <Box
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        "& .MuiTextField-root": {
                            width: "auto",
                        },
                    }}
                    style={{
                        width: "280px",
                        height: "250px",
                        display: "flex",
                        flexDirection: "row",
                        flexWrap: "nowrap",
                        alignContent: "center",
                        justifyContent: "center",
                        alignItems: "center",
                    }}
                >
                    <div>
                        <CircularProgress />
                    </div>
                </Box>
            )}

            <RedBar />

            <div
                style={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                }}
            >
                <RedBar />

                <Button
                    variant="contained"
                    disabled={procesandoRegistro ? true : false}
                    onClick={(e) => {
                        crearActividad();
                    }}
                >
                    Registrar
                </Button>
            </div>
        </Box>
    );
}

export default RegistrarUsuario;
