import React, { useState, useEffect, useRef } from "react";
import { cerrarSesion } from "../database/firebase";
import "../styles/menu_perfil.css";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { Button, Divider } from "@mui/material";
import ArgumentappAvatar from "../pages/Funcionalidades/ArgumentappAvatar";
import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";

export default function MenuPerfil() {
  const [collapsed, setCollapsed] = useState(true);
  const ref = useRef(null);
  const tipoUsuario = window.localStorage.getItem("tipoUsuario");
  const username = window.localStorage.getItem("dataUserArgumentapp_nombre");
  const seccionesSinPerfil = ["/actividadesonline", "/unidadescurriculares"];
  const location = useLocation();

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (ref.current && !ref.current.contains(event.target)) {
        setCollapsed(true);
      }
    };

    document.addEventListener("click", handleClickOutside, true);

    return () => {
      document.removeEventListener("click", handleClickOutside, true);
    };
  }, []);

  const handleCollapsed = () => {
    setCollapsed(false);
  };

  return (
    <div>
      {!seccionesSinPerfil.includes(location.pathname) && (
        <div>
          <div
            className={
              "menu-perfil-container" + (collapsed ? " collapsed" : "")
            }
            ref={ref}
            onClick={handleCollapsed}
          >
            <div
              style={{
                left: "117px",
                top: "30px",
                position: "absolute",
                overflow: "visible",
                width: "71px",
                whiteSpace: "nowrap",
                fontSize: "15px",
                fontWeight: "bold",
                color: "rgba(102,102,102,1)",
              }}
            >
              {username}
            </div>
            <div
              style={{
                left: "117px",
                top: "60px",
                position: "absolute",
                overflow: "visible",
                width: "49px",
                whiteSpace: "nowrap",
                fontSize: "13px",
                fontWeight: "normal",
                color: "rgba(102,102,102,1)",
                textTransform: "capitalize",
              }}
            >
              {tipoUsuario}
            </div>
            <ArgumentappAvatar nombre={username} tipo="menu-perfil" />
            <div
              style={{
                left: 300,
                top: "28px",
                position: "absolute",
                overflow: "visible",
                width: "71px",
                whiteSpace: "nowrap",
                fontWeight: "bold",
                color: "rgba(102,102,102,1)",
              }}
            >
              <KeyboardArrowDownIcon fontSize="medium" />
            </div>
            <div
              className={
                "menu-perfil-contenido" + (collapsed ? " collapsed" : "")
              }
            >              
              {Number(
                window.localStorage.getItem("dataUserArgumentapp_permisos")
              ) !== 2 && (
                <div>
                  <Divider style={{ marginBottom: 25 }} />

                  <Button
                    variant="contained"
                    component={Link}
                    to="/configuracion"
                    style={{ marginBottom: 20 }}
                  >
                    Configuración
                  </Button>
                </div>
              )}
              <Button
                variant="contained"
                onClick={() => {
                  cerrarSesion();
                }}
              >
                Cerrar sesion
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
