import {
  Card,
  Grid,
  Typography,
  CardActionArea,
  Button,
  Paper,
} from "@mui/material";
import React, { useMemo, useState } from "react";
import { styled } from "@mui/material/styles";
import CrearActividadesOnline from "./CrearActividadesOnline";
import ListarActividadesOnline from "./ListarActividadesOnline";
import { UserContext } from "../Funcionalidades/UseContext";
import { ArrowBackIosNew } from "@mui/icons-material";

function MainActividadesOnline() {
  const [opcionesVista, setOpcionesVista] = useState("Pantalla_inicio");

  const providerValue = useMemo(
    () => ({
      setOpcionesVista,
    }),
    [setOpcionesVista]
  );

  return (
    <div className="main-container">
      <div
        style={{
          width: "100%",
        }}
      >
        <Typography variant="titulo">Actividades Online</Typography>

        <UserContext.Provider value={providerValue}>
          <div className="contenido">
            {opcionesVista === "Pantalla_inicio" && (
              <Paper
                style={{
                  backgroundColor: "transparent",
                  boxShadow: "none",
                }}
              >
                <Grid
                  style={{
                    padding: 30,
                    justifyContent: "center",
                  }}
                  container
                  spacing={2}
                  columns={16}
                >
                  <Grid item xs={6}>
                    <Card
                      onClick={(e) => {
                        setTimeout(() => {
                          setOpcionesVista("Crear_actividades");
                        }, 300);
                      }}
                      style={{ borderRadius: 18 }}
                    >
                      <CardActionArea
                        style={{
                          zIndex: "3",
                        }}
                      >
                        <Paper tipo="general">
                          <Typography variant="titulo_3">
                            Crear Actividad Online
                          </Typography>
                          <Typography>
                            Crea de manera online una actividad para ejecutar un
                            curso de argumentapp en tu sala!
                          </Typography>
                        </Paper>
                      </CardActionArea>
                    </Card>
                  </Grid>

                  <Grid item xs={6}>
                    <Card
                      onClick={(e) => {
                        setTimeout(() => {
                          setOpcionesVista("ListarActividades");
                        }, 300);
                      }}
                      style={{ borderRadius: 18 }}
                    >
                      <CardActionArea
                        style={{
                          zIndex: "3",
                        }}
                      >
                        <Paper tipo="general">
                          <Typography variant="titulo_3">
                            Actividades Online Registradas
                          </Typography>
                          <br></br>
                          <Typography>
                            Lorem ipsum dolor sit amet, consectetur adipiscing
                            elit, sed do eiusmod tempor incididunt ut labore et.
                          </Typography>
                        </Paper>
                      </CardActionArea>
                    </Card>
                  </Grid>
                </Grid>
              </Paper>
            )}
            {opcionesVista === "Crear_actividades" && (
              <div>
                <Button
                  title="Volver"
                  variant="contained"
                  style={{
                    minWidth: 50,
                    maxWidth: 50,
                    minHeight: 45,
                    maxHeight: 45,
                    borderRadius: 100,
                    padding: 0,
                    margin: 0,
                  }}
                  onClick={(e) => {
                    setTimeout(() => {
                      setOpcionesVista("Pantalla_inicio");
                    }, 300);
                  }}
                >
                  <ArrowBackIosNew style={{ color: "#fff" }} />
                </Button>
                <CrearActividadesOnline></CrearActividadesOnline>
              </div>
            )}
            {opcionesVista === "ListarActividades" && (
              <div>
                <Button
                  variant="contained"
                  title="Volver"
                  style={{
                    minWidth: 45,
                    maxWidth: 45,
                    minHeight: 45,
                    maxHeight: 45,
                    borderRadius: 100,
                    padding: 0,
                    margin: 0,
                  }}
                  onClick={(e) => {
                    setTimeout(() => {
                      setOpcionesVista("Pantalla_inicio");
                    }, 300);
                  }}
                >
                  <ArrowBackIosNew style={{ color: "#fff" }} />
                </Button>
                <ListarActividadesOnline></ListarActividadesOnline>
              </div>
            )}
          </div>
        </UserContext.Provider>
      </div>
    </div>
  );
}

export default MainActividadesOnline;
