import React, { useState } from "react";
import Button from "@mui/material/Button";
import Popover from "@mui/material/Popover";
import { getContenidoCursos } from "./firebaseCursosPublicos";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import { IconButton } from "@mui/material";

function CopiarCursoPublico({ nombreCurso, idCurso, estadocurso }) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const [banderaCopia, setbanderaCopia] = useState(false);

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  return (
    <div>
      <IconButton title="Copiar contenido Argumentapp" size="large" onClick={handleClick}>
        <FileCopyIcon style={{ color: "GrayText" }} fontSize="inherit" />
      </IconButton>

      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        style={{
          width: "800px",
        }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        {banderaCopia ? (
          <h4
            style={{
              color: "#555",
              padding: 30,
              textAlign: "justify",
              marginLeft: 0,
              marginTop: 0,
              marginBottom: 0,
            }}
          >
            Copiando los datos a Mis planificaciones...
          </h4>
        ) : (
          <div>
            <h4
              style={{
                color: "#555",
                padding: 10,
                textAlign: "justify",
                marginLeft: 0,
                marginTop: 0,
                marginBottom: 0,
                height: 60,
              }}
            >
              Se creará una copia editable en tus sección personal de
              planificaciones.
            </h4>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                alignContent: "center",
                justifyContent: "flex-end",
                marginBottom: 10,
                marginRight: 10,
              }}
            >
              <Button
                variant="contained"
                style={{
                  width: "fit-content",
                }}
                onClick={(e) => {
                  setbanderaCopia(true);
                  getContenidoCursos(idCurso, (value) => {
                    handleClose();
                    setbanderaCopia(!value);
                  });
                }}
              >
                Importar
              </Button>
            </div>
          </div>
        )}
      </Popover>
    </div>
  );
}

export default CopiarCursoPublico;
