import {
  Button,
  CardMedia,
  Chip,
  CircularProgress,
  Grid,
  Paper,
  Stack,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import iconDefault from "../../../assets/images/default_icon_curso_2.png";
import { existCurso } from "../BDActividadesOnline";
import ClasesActividadOnline from "./Contenido/ClasesActividadOnline";
// import ListaAlumnosMain from "./Contenido/ListaAlumnosMain";

import { getFecha } from "../../Funcionalidades/getFecha";
import { ArrowBackIosNew } from "@mui/icons-material";

function ContenidoActividadOnline({ actividad }) {
  const [existCursoPersonal, setexistCursoPersonal] = useState({
    state: "cargando",
    data: "cargando",
  });

  useEffect(() => {
    console.log(actividad);
    existCurso(actividad.data.datosCurso.id, (data) => {
      if (data) {
        setexistCursoPersonal({ state: "terminado", data: "existe" });
      } else {
        setexistCursoPersonal({ state: "terminado", data: "borrado" });
      }
    });
  }, [actividad]);

  return (
    <div style={{ marginTop: "-30px" }}>
      <Typography style={{ marginLeft: 0 }} variant="titulo">
        Actividades Online
      </Typography>
      <Paper
        style={{
          backgroundColor: "white",
          boxShadow: "none",
          borderRadius: 18,
          display: "flex",
          flexDirection: "column",
          marginTop: 20,
        }}
      >
        <div style={{ padding: 20 }}>
          <Button
            title="Volver"
            style={{
              minWidth: 50,
              maxWidth: 50,
              minHeight: 45,
              maxHeight: 45,
              borderRadius: 100,
              padding: 0,
              margin: 0,
              backgroundColor: "white",
              boxShadow: "none",
            }}
            href={"/actividadesOnline"}
          >
            <ArrowBackIosNew style={{ color: "#6C3373" }} />
          </Button>
        </div>
        <div
          style={{
            marginLeft: 80,
            marginRight: 80,
          }}
        >
          <div>
            <Grid
              style={{
                display: "flex",
                flexDirection: "column",
              }}
            >
              <Grid
                container
                spacing={{ xs: 2, md: 3 }}
                columns={{ xs: 2, sm: 12, md: 18 }}
              >
                <Grid item xs={2} sm={4} md={5}>
                  <Paper
                    style={{
                      display: "flex",
                      height: "150px",
                      flexDirection: "column",
                      alignContent: "center",
                      justifyContent: "center",
                      alignItems: "center",
                      boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
                      borderRadius: 25,
                    }}
                  >
                    <Typography
                      variant="h5"
                      style={{
                        fontWeight: "bold",
                        fontSize: 19,
                        paddingBottom: 10,
                        paddingLeft: 7,
                        paddingRight: 7,
                      }}
                    >
                      {actividad.data.datosActividad.establecimiento}
                    </Typography>
                    <Typography sx={{ mb: 1.2 }} color="text.secondary">
                      ESTABLECIMIENTO EDUCACIONAL
                    </Typography>
                  </Paper>
                </Grid>
                <Grid item xs={2} sm={4} md={8}>
                  <Paper
                    style={{
                      height: "150px",
                      display: "flex",
                      flexDirection: "column",
                      alignContent: "center",
                      justifyContent: "center",
                      alignItems: "center",
                      boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
                      borderRadius: 25,
                    }}
                  >
                    <Typography
                      variant="h5"
                      style={{
                        letterSpacing: 25,
                        fontWeight: "bold",
                        fontSize: 40,
                      }}
                    >
                      {actividad.data.datosActividad.codigoActividad}
                    </Typography>
                    <Typography
                      sx={{ mb: 1.5, marginLeft: -3 }}
                      color="text.secondary"
                    >
                      CÓDIGO DE LA ACTIVIDAD ONLINE
                    </Typography>
                  </Paper>
                </Grid>
                <Grid item xs={2} sm={4} md={5}>
                  <Paper
                    style={{
                      display: "flex",
                      height: "150px",
                      flexDirection: "column",
                      alignContent: "center",
                      justifyContent: "center",
                      alignItems: "center",
                      boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
                      borderRadius: 25,
                    }}
                  >
                    <Typography
                      variant="h5"
                      style={{
                        fontWeight: "bold",
                        paddingBottom: 10,
                        fontSize: 19,
                      }}
                    >
                      {actividad.data.datosActividad.identificadorCurso}
                    </Typography>
                    <Typography sx={{ mb: 1.2 }} color="text.secondary">
                      IDENTIFICADOR DEL CURSO
                    </Typography>
                  </Paper>
                </Grid>
              </Grid>
              <Grid
                container
                columns={{ xs: 2, sm: 10, md: 12 }}
                style={{
                  marginTop: 20,
                }}
              >
                <Grid item xs={2} sm={10} md={12}>
                  <Paper
                    style={{
                      boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
                      borderRadius: 25,
                      padding: 20,
                    }}
                  >
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        padding: 10,
                      }}
                    >
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          flexWrap: "nowrap",
                        }}
                      >
                        <CardMedia
                          component="img"
                          sx={{
                            width: 120,
                            borderRadius: "16px",
                            minHeight: 160,
                          }}
                          image={
                            actividad.data.datosCurso.imagen === undefined
                              ? iconDefault
                              : actividad.data.datosCurso.imagen
                          }
                          alt={"Miniatura " + actividad.data.datosCurso.nombre}
                        />
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "column",
                            flexWrap: "wrap",
                            marginLeft: 20,
                          }}
                        >
                          <h3
                            style={{
                              color: "#555",
                              fontSize: 28,
                              fontWeight: 600,
                              margin: 0,
                            }}
                          >
                            {actividad.data.datosCurso.nombre}
                          </h3>
                          <Stack
                            style={{
                              display: "flex",
                              flexDirection: "column",
                              flexWrap: "wrap",
                              padding: 0,
                              marginLeft: -10,
                            }}
                            alignItems="start"
                          >
                            <Chip
                              label={actividad.data.datosCurso.disciplina}
                              style={{
                                fontSize: "15px",
                                backgroundColor: "white",
                                textAlign: "start",
                                padding: 0,
                              }}
                            />
                            <Chip
                              label={actividad.data.datosCurso.nivel}
                              style={{
                                fontSize: "15px",
                                backgroundColor: "white",
                                textAlign: "start",
                              }}
                            />
                            <Chip
                              label={actividad.data.datosCurso.grado}
                              style={{
                                fontSize: "15px",
                                backgroundColor: "white",
                                textAlign: "start",
                              }}
                            />
                            <Chip
                              label={
                                existCursoPersonal.state === "cargando" ? (
                                  <CircularProgress
                                    color={"inherit"}
                                    size={23}
                                  />
                                ) : existCursoPersonal.data === "existe" ? (
                                  "Disponible"
                                ) : (
                                  "No disponible"
                                )
                              }
                              color={
                                existCursoPersonal.state === "cargando"
                                  ? "primary"
                                  : existCursoPersonal.data === "existe"
                                  ? "success"
                                  : "error"
                              }
                              style={{
                                fontSize: "15px",
                                padding: 2,
                                marginLeft: 10,
                              }}
                            />
                          </Stack>
                        </div>
                      </div>

                      <div>
                        <h2 style={{ color: "black", margin: 0 }}>
                          {
                            getFecha(actividad.data.datosCurso.fecha).split(
                              " "
                            )[0]
                          }
                          ,{" "}
                          {
                            getFecha(actividad.data.datosCurso.fecha).split(
                              " "
                            )[2]
                          }
                        </h2>
                        <h2 style={{ margin: 0, textAlign: "right" }}>
                          {
                            getFecha(actividad.data.datosCurso.fecha).split(
                              " "
                            )[4]
                          }
                        </h2>
                      </div>
                    </div>
                  </Paper>
                </Grid>
              </Grid>
            </Grid>
          </div>
          <Paper
            style={{
              padding: 30,
              backgroundColor: "white",
              marginTop: 20,
              boxShadow: 'none'
            }}
            elevation={4}
          >
            <Grid
              container
              spacing={{ xs: 2, md: 3 }}
              columns={{ xs: 4, sm: 12, md: 18 }}
            >
              <Grid item xs={4} sm={12} md={18}>
                <ClasesActividadOnline
                  actividad={actividad}
                ></ClasesActividadOnline>
              </Grid>
            </Grid>
          </Paper>
        </div>
      </Paper>
    </div>
  );
}

export default ContenidoActividadOnline;
