import firebaseConf, { cerrarSesion } from "../../../database/firebase";

export const updateSkipIntro = (estado, cb) => {
    firebaseConf.auth().onAuthStateChanged((user) => {
        if (user){
            const uid = user.uid

            firebaseConf
                .firestore()
                .collection("cuentasUsuarios")
                .doc(uid)
                .update({
                    skipIntro: estado
                })
                .then(() => cb(true))
                .catch(() => cb(false))
        } else {
            cerrarSesion()
        }
    })
}