import { Accordion, AccordionDetails, AccordionSummary } from "@mui/material";
import React from "react";
import DeleteActividad from "../../../Diapositivas/DeleteActividad";
import OrdernarDiapositivas from "../../../Diapositivas/OrdernarDiapositivas";
import ContenidoActividadPublica from "../ContenidoActividadPublica";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

function VistaActividadPublicaAlumno({
  actividades,
  idClase,
  idCurso,
  estado,
}) {

  
  return (
    <div>

      {actividades.map((value, index) => {
        return (
          <div
            style={{
              backgroundColor: "white",
              marginBottom: "20px",
            }}
          >
            <Accordion>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id={"panel1a-header"+index}
              >
                <h3 style={{ color: "black" }}>{value.data.nombreActividad}</h3>
                <div
                  style={{
                    padding: "15px",
                    display: "flex",
                    flexDirection: "row",
                    alignContent: "flex-start",
                    alignItems: "center",
                  }}
                >
                  <DeleteActividad
                    actividad={value.key}
                    idClase={idClase}
                    idCurso={idCurso}
                    state={"publicos"}
                    uid={"data"}
                    nombreActividad={value.data.nombreActividad}
                  ></DeleteActividad>
                  <OrdernarDiapositivas
                    nombreActividad={value.data.nombreActividad}
                    actividad={value.key}
                    uid={"data"}
                    idClase={idClase}
                    idCurso={idCurso}
                    tipoCurso={"publicos"}
                    tipoVista={"alumnos"}
                  ></OrdernarDiapositivas>
                </div>
              </AccordionSummary>
              <AccordionDetails>
                <div
                  style={{
                    marginLeft: "30px",
                    display: "flex",
                    flexDirection: "row",
                    alignContent: "flex-start",
                    alignItems: "center",
                  }}
                >
                  <ContenidoActividadPublica
                    idCurso={idCurso}
                    idClase={idClase}
                    keyActividad={value.key}
                    tipoVista={"alumnos"}
                    estado={estado}
                  ></ContenidoActividadPublica>
                </div>
              </AccordionDetails>
            </Accordion>
          </div>
        );
      })}
    </div>
  );
}

export default VistaActividadPublicaAlumno;
