import { CircularProgress } from "@mui/material";
import React, { useEffect, useState } from "react";
import Chart from "react-google-charts";
import { generarEsquema } from "./generarEsquema";

function RenderGrafico({
  uid,
  idCurso,
  idClase,
  idActividad,
  lugarAlamacenamiento,
  nombreCurso,
  nombreClase,
  nombreActividad,
}) {
  const [dataJson, setDataJson] = useState([
    ["Nombre", "Manager", "ToolTip"],
    [],
    [],
  ]);
  const [banderaJSon, setbanderaJSon] = useState(false);
  useEffect(() => {
    generarEsquema(
      uid,
      idCurso,
      idClase,
      idActividad,
      lugarAlamacenamiento,
      nombreCurso,
      nombreClase,
      nombreActividad,
      (result) => {
        setDataJson(result);
        setbanderaJSon(true);
      }
    );
  }, [
    uid,
    idCurso,
    idClase,
    idActividad,
    lugarAlamacenamiento,
    nombreCurso,
    nombreClase,
    nombreActividad,
  ]);

  return (
    <div>
      {banderaJSon ? (
        <Chart
          width={"100%"}
          chartType="OrgChart"
          loader={<div>Cargando esquema...</div>}
          data={dataJson}
          options={{
            allowHtml: true,
          }}
          rootProps={{ "data-testid": "1" }}
        />
      ) : (
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center",
            height: "50px",
          }}
        >
          <CircularProgress></CircularProgress>
        </div>
      )}
    </div>
  );
}

export default RenderGrafico;
