import { Modal } from "@mui/material";
import React, { useCallback, useMemo, useState } from "react";
import {
  CustomIconButton,
} from "../../styles/ArgumentappTheme";
import AddIcon from '@mui/icons-material/Add';
import { UserContext } from "../Funcionalidades/UseContext";
import BoxCrearActividad from "./BoxCrearActividad";


function CrearActividadModal({ idCurso, idClase }) {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleClose = useCallback(() => {
    setOpen(false);
  });

  const providerValue = useMemo(
    () => ({
      handleClose,
    }),
    [handleClose]
  );

  // const subirDatos = () => {
  //     if (nombreActividad !== "") {
  //         firebaseConf.auth().onAuthStateChanged((user) => {
  //             if (user) {
  //                 setActividadOnCurso(
  //                     user.uid,
  //                     idCurso,
  //                     idClase,
  //                     nombreActividad,
  //                     (data) => {
  //                         if (data) {
  //                             window.location.reload(true);
  //                         } else {
  //                             console.error("error");
  //                         }
  //                     }
  //                 );
  //             } else {
  //                 cerrarSesion();
  //             }
  //         });
  //     }
  // };

  return (
    <div>
      <CustomIconButton onClick={handleOpen}>
        <AddIcon style={{ color: "#fff" }} />
      </CustomIconButton>
      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <UserContext.Provider value={providerValue}>
            <BoxCrearActividad tipoCurso={'privados'}></BoxCrearActividad>
        </UserContext.Provider>
      </Modal>
    </div>
  );
}

export default CrearActividadModal;
