import { Box,  Paper, Typography } from "@mui/material";
import React from "react";
import AddActividadModal from "./CrearCursos/AddActividadModal";
import ArchivosAbiertosRecientemente from "./VistaCarpeta/ArchivosAbiertosRecientemente";
import ListarArchivosTable from "./VistaCarpeta/ListarArchivos";
import ListarCarpetas from "./VistaCarpeta/ListarCarpetas";

function MainVistaPlanificacionesCarpetas() {
  return (
    <div>
      <Box
        width="100%"
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          alignContent: "center",
          justifyItems: "center",
          alignItems: "center",
        }}
      >
        <Paper
          sx={{
            width: "100%",
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
            alignContent: "center",
            justifyItems: "center",
            alignItems: "center",
          }}
          style={{
            boxShadow: "none",
            backgroundColor: "transparent",
          }}
        >
          <AddActividadModal tipoCurso={'privados'}></AddActividadModal>
        </Paper>
      </Box>
      <Paper
        style={{
          width: "100%",
          borderRadius: 18,
          backgroundColor: "#F4E9F7",
          boxShadow: "none",
          padding: 10,
          paddingLeft: 20,
        }}
      >
        <Typography
          variant="titulo"
          fontSize={22}
          style={{
            padding: 0,
            margin: 0,
          }}
        >
          Abierto Recientemente
        </Typography>
        <ArchivosAbiertosRecientemente></ArchivosAbiertosRecientemente>
      </Paper>
      <Paper
        style={{
          width: "100%",
          paddingLeft: 20,
          marginTop: 15,
          boxShadow: "none",
          background: "none",
        }}
      >
        <Typography
          variant="titulo"
          fontSize={24}
          style={{
            padding: 0,
            margin: 0,
          }}
        >
          Carpetas
        </Typography>
        <ListarCarpetas></ListarCarpetas>
      </Paper>
      <Paper
        style={{
          width: "100%",
          paddingLeft: 20,
          marginTop: 15,
          boxShadow: "none",
          background: "none",
        }}
      >
        <Typography
          variant="titulo"
          fontSize={24}
          style={{
            padding: 0,
            margin: 0,
          }}
        >
          **Archivos**
        </Typography>
        <div style={{marginTop: 20}}>
          <ListarArchivosTable></ListarArchivosTable>
        </div>
      </Paper>
    </div>
  );
}

export default MainVistaPlanificacionesCarpetas;
