import {
  Button,
  Grid,
  Paper,
  TextField,
  Typography,
  Box,
  Collapse,
  CardContent,
  Card,
  CardActions,
  Grow,
} from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import Stack from "@mui/material/Stack";
import LinearProgress from "@mui/material/LinearProgress";
import { getFecha } from "../Funcionalidades/getFecha";

import {
  ImageBackdrop,
  ImageButton,
  ImageMarked,
  ImageSrc,
  ImageCustoms,
} from "../Funcionalidades/BotonesCustom";
import defaultImage from "../../assets/images/default_icon_curso_2.png";
import { generadosCodigosActividades } from "../Funcionalidades/generadorCodigosActividades";
import firebaseConf, { getMisCursos } from "../../database/firebase";

import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import { UserContext } from "../Funcionalidades/UseContext";
import { setActividadOnline } from "./BDActividadesOnline.js";

function CrearActividadesOnline() {
  const [banderaCargaDatos, setBanderaCargaDatos] = useState(false);
  const [cursoSelect, setCursoSelect] = useState({
    idCurso: "null",
    imagen: "null",
  });
  const [cursos, setCursos] = useState([]);
  const [banderaCursoSelect, setBanderaCursoSelect] = useState(false);
  const [nuevoCodigo, setnuevoCodigo] = useState("");
  const [fecha, setfecha] = useState("");
  const [establecimiento, setestablecimiento] = useState("");
  const [identificadorCurso, setidentificadorCurso] = useState("");

  const MySwal = withReactContent(Swal);

  const generarCodigo = () => {
    generadosCodigosActividades((data) => {
      if (data === "error") {
        MySwal.fire({
          icon: "error",
          title: "No quedan código disponibles",
        }).then(() => {
          setTimeout(() => {
            window.location.reload();
          }, 300);
        });
      } else {
        setnuevoCodigo(data);
      }
    });
  };

  const { setOpcionesVista } = useContext(UserContext);

  const subirActividad = () => {
    if (
      fecha === "" ||
      establecimiento === "" ||
      identificadorCurso === "" ||
      nuevoCodigo === "" ||
      cursoSelect.idCurso === "null"
    ) {
      MySwal.fire({
        icon: "error",
        title: "Error en los datos",
        text: "Todos los datos deben ser ingresados de manera obligatoria para poder crear una actividad online",
      });
    } else {
      firebaseConf.auth().onAuthStateChanged((user) => {
        Swal.fire({
          icon: "info",
          title: "Creando la actividad.",
          text: "Se esta creando la actividad online ingresada, no cierre el navegador durante este proceso.",
          showConfirmButton: false,
          allowOutsideClick: false,
          allowEscapeKey: false,
        });
        setTimeout(() => {
          setActividadOnline(
            user.uid,
            fecha,
            establecimiento,
            identificadorCurso,
            nuevoCodigo,
            cursoSelect,
            (responds) => {
              if (responds) {
                MySwal.fire({
                  icon: "success",
                  title: "Actividad Online creada",
                  confirmButtonText: "Ver actividad creada",
                }).then(() => {
                  setTimeout(() => {
                    setOpcionesVista("ListarActividades");
                  }, 300);
                });
              } else {
                MySwal.fire({
                  icon: "error",
                  title: "Error al crear actividad online",
                  text: "Se ha producido un error al crear la actividad online, intente esta acción mas tarde",
                });
              }
            }
          );
        }, 600);
      });
    }
  };

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      getMisCursos(user.uid, (data) => {
        setCursos(data);
        setBanderaCargaDatos(true);
      });
    });
  }, []);

  return (
    <div
      style={{
        paddingTop: 20,
        width: "100%",
      }}
    >
      <Paper
        elevation={"3"}
        style={{
          minHeight: "400px",
          padding: "10px",
          borderRadius: 30,
          marginBottom: 50,
        }}
      >
        {!banderaCargaDatos ? (
          <div
            style={{
              minHeight: "400px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Stack sx={{ width: "80%", color: "grey.500" }} spacing={2}>
              <LinearProgress color="info" />
            </Stack>
          </div>
        ) : (
          <div
            style={{
              padding: 25,
            }}
          >
            <Box
              component="form"
              sx={{
                "& .MuiTextField-root": { m: 1, width: "25ch" },
              }}
              noValidate
              autoComplete="off"
            >
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  flexDirection: "column",
                }}
              >
                <ImageButton
                  focusRipple
                  style={{
                    width: "30%",
                  }}
                  onClick={() => {
                    setBanderaCursoSelect(!banderaCursoSelect);
                  }}
                >
                  <ImageSrc
                    style={{
                      backgroundImage:
                        cursoSelect.idCurso === "null"
                          ? `url(${defaultImage})`
                          : cursoSelect.imagen === null
                          ? `url(${defaultImage})`
                          : `url(${cursoSelect.imagen})`,
                      borderRadius: 30,
                    }}
                  />
                  <ImageBackdrop
                    style={{ borderRadius: 30 }}
                    className="MuiImageBackdrop-root"
                  />
                  <ImageCustoms>
                    <Typography
                      component="span"
                      variant="subtitle1"
                      color="inherit"
                      sx={{
                        position: "relative",
                        p: 4,
                        pt: 2,
                        pb: (theme) => `calc(${theme.spacing(1)} + 6px)`,
                      }}
                    >
                      {cursoSelect.idCurso === "null"
                        ? "Seleccionar planificación "
                        : cursoSelect.nombre}
                      {cursoSelect.idCurso !== "null" && <br></br>}
                      {cursoSelect.idCurso === "null"
                        ? ""
                        : cursoSelect.disciplina}
                      {cursoSelect.idCurso !== "null" && <br></br>}
                      {cursoSelect.idCurso === "null"
                        ? ""
                        : getFecha(cursoSelect.fecha)}

                      <ImageMarked className="MuiImageMarked-root" />
                    </Typography>
                  </ImageCustoms>
                </ImageButton>

                <Collapse
                  style={{ width: "100%", marginTop: 30 }}
                  in={banderaCursoSelect}
                >
                  <Paper
                    style={{
                      backgroundColor: "white",
                      display: "flex",
                      borderRadius: 30,
                    }}
                    elevation={"4"}
                  >
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        flexWrap: "wrap",
                        alignContent: "center",
                        justifyContent: "center",
                        alignItems: "center",
                        padding: 30,
                      }}
                    >
                      {cursos.map((data, index) => {
                        return (
                          <Grow
                            in={banderaCursoSelect}
                            style={{ transformOrigin: "0 0 0" }}
                            {...(banderaCursoSelect
                              ? { timeout: 350 * (index + 1) }
                              : {})}
                          >
                            <Card
                              key={index}
                              sx={{ minWidth: 275 }}
                              elevation={4}
                              style={{
                                margin: 10,
                                backgroundColor:
                                  cursoSelect.idCurso === data.id
                                    ? "#A9DFBF"
                                    : cursoSelect.idCurso === "null"
                                    ? "white"
                                    : "#E5E7E9",
                                borderRadius: 30,
                                borderWidth: 2,
                                borderColor: "#6d4271",
                                borderStyle: "solid",
                              }}
                            >
                              <CardContent>
                                <Typography
                                  sx={{ fontSize: 18 }}
                                  color="text.secondary"
                                  gutterBottom
                                >
                                  {data.nombre}
                                </Typography>
                                <Typography color="text.secondary">
                                  {data.disciplina}
                                </Typography>
                                <Typography color="text.secondary">
                                  {getFecha(data.fecha)}
                                </Typography>
                              </CardContent>
                              <CardActions>
                                <Button
                                  disabled={
                                    cursoSelect.idCurso === "null" ||
                                    cursoSelect.idCurso === data.id
                                      ? false
                                      : true
                                  }
                                  onClick={() => {
                                    if (cursoSelect.idCurso === data.id) {
                                      setCursoSelect({
                                        idCurso: "null",
                                        imagen: "null",
                                        nombre: "null",
                                        fecha: "null",
                                        disciplina: "null",
                                        grado: "null",
                                        id: "null",
                                        nivel: "null",
                                      });
                                    } else {
                                      setCursoSelect({
                                        idCurso: data.id,
                                        imagen: data.imagen,
                                        nombre: data.nombre,
                                        fecha: data.fecha,
                                        disciplina: data.disciplina,
                                        grado: data.grado,
                                        id: data.id,
                                        nivel: data.nivel,
                                      });
                                    }
                                  }}
                                  size="small"
                                  variant="contained"
                                  style={{
                                    backgroundColor:
                                      cursoSelect.idCurso === data.id
                                        ? "red"
                                        : "",
                                  }}
                                >
                                  {cursoSelect.idCurso === data.id
                                    ? "Desmarcar"
                                    : "Seleccionar"}
                                </Button>
                              </CardActions>
                            </Card>
                          </Grow>
                        );
                      })}
                    </div>
                  </Paper>
                </Collapse>
              </div>
              <div
                style={{
                  paddingTop: 30,
                }}
              >
                <Grid
                  style={{
                    justifyContent: "center",
                  }}
                  container
                  spacing={5}
                  columns={16}
                >
                  <Grid item xs={6}>
                    <TextField
                      required
                      value={nuevoCodigo}
                      id="codigoActividad"
                      label="Código"
                      fullWidth
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  </Grid>
                  <Grid
                    item
                    xs={4}
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <Button
                      fullWidth={true}
                      style={{ borderStyle: "solid", borderWidth: 1 }}
                      onClick={(e) => {
                        generarCodigo();
                      }}
                      disabled={nuevoCodigo === "" ? false : true}
                    >
                      Generar Código
                    </Button>
                  </Grid>
                  <Grid item xs={6}>
                    <TextField
                      type={"date"}
                      required
                      style={{ width: "100%" }}
                      value={fecha}
                      onChange={(e) => {
                        setfecha(e.target.value);
                      }}
                    />
                  </Grid>
                </Grid>
                <Grid
                  style={{
                    justifyContent: "center",
                  }}
                  container
                  spacing={5}
                  columns={16}
                >
                  <Grid item xs={8}>
                    <TextField
                      label={"Establecimiento Educacional"}
                      required
                      style={{ width: "100%" }}
                      value={establecimiento}
                      onChange={(e) => {
                        setestablecimiento(e.target.value);
                      }}
                    />
                  </Grid>
                  <Grid item xs={4}>
                    <TextField
                      label={"Identificador del curso"}
                      required
                      style={{ width: "100%" }}
                      value={identificadorCurso}
                      onChange={(e) => {
                        setidentificadorCurso(e.target.value);
                      }}
                    />
                  </Grid>
                </Grid>
                <Grid
                  style={{
                    justifyContent: "center",
                  }}
                  container
                  spacing={5}
                  columns={9}
                >
                  <Grid item xs={3} style={{ paddingTop: 50 }}>
                    <Button
                      fullWidth={true}
                      style={{ borderStyle: "solid", borderWidth: 1 }}
                      onClick={(e) => {
                        subirActividad();
                      }}
                    >
                      Crear Actividad Online
                    </Button>
                  </Grid>
                </Grid>
              </div>
            </Box>
          </div>
        )}
      </Paper>
    </div>
  );
}

export default CrearActividadesOnline;
