import { Button, Paper, Tab, Tabs, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useMemo, useState } from "react";
import CursosPublicos from "../Cursos/CursosPublicos";
import CursosPublicosNoPublicados from "../Cursos/CursosPublicosNoPublicados";
import ListarClasesPublicos from "../Cursos/Publicos/ListarClasesPublicos";
import { UserContext } from "../Funcionalidades/UseContext";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0 }} style={{ marginTop: 15 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function MainContenidoArgumentapp(props) {
  const [banderaClases, setBanderaClases] = useState(false);
  const [value, setValue] = React.useState(0);
  const [idCursoVer, setIdCursoVer] = useState("");
  const [nombreCursoVer, setNombreCursoVer] = useState("");

  const [tipoSalida, setTipoSalida] = useState("right");

  const [tipoCursos, settipoCursos] = useState("");
  const [estadoCurso, setEstadoCurso] = useState("");

  const [indexUnidad, setindexUnidad] = useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  useEffect(() => {
    try {
      if (props.location.state.isBack === true) {
        setIdCursoVer(props.location.state.idCurso);
        setNombreCursoVer(props.location.state.nombreCurso);
        settipoCursos(props.location.state.tipoCurso);
        setEstadoCurso(props.location.state.estado);
        setTipoSalida(props.location.state.tipoSlide);
        setindexUnidad(props.location.state.indexUnidad);
        setBanderaClases(true);
      } else {
      }
    } catch (error) {
      // window.location = "/";
    }
    // eslint-disable-next-line
  }, []);

  const providerValue = useMemo(
    () => ({
      banderaClases,
      setBanderaClases,
      idCursoVer,
      setIdCursoVer,
      setNombreCursoVer,
      settipoCursos,
      setEstadoCurso,
      setindexUnidad,
      setTipoSalida,
      tipoCursos,
      estadoCurso,
    }),
    [
      banderaClases,
      setBanderaClases,
      idCursoVer,
      setIdCursoVer,
      setNombreCursoVer,
      settipoCursos,
      setEstadoCurso,
      setindexUnidad,
      setTipoSalida,
      tipoCursos,
      estadoCurso,
    ]
  );
  return (
    <UserContext.Provider value={providerValue}>
      <div className="main-container">
        <div
          style={{
            width: "100%",
            overflowX: "hidden",
          }}
        >
          <Typography variant="titulo">Contenido Argumentapp</Typography>

          <div className="contenido">
            {!banderaClases ? (
              <div>
                {Number(
                  window.localStorage.getItem("dataUserArgumentapp_permisos")
                ) !== 2 ? (
                  <div>
                    <Box sx={{ borderBottom: 1, borderColor: "transparent" }}>
                      <Tabs
                        value={value}
                        onChange={handleChange}
                        aria-label="basic tabs example"
                        style={{
                          backgroundColor: "white",
                          textAlign: "center",
                          borderRadius: 30,
                          boxShadow: "none",
                        }}
                      >
                        <Tab label="Contenido Argumentapp" {...a11yProps(1)} />
                        {Number(
                          window.localStorage.getItem(
                            "dataUserArgumentapp_permisos"
                          )
                        ) !== 2 && (
                          <Tab
                            label="Contenido Argumentapp (En Desarrollo)"
                            {...a11yProps(2)}
                          />
                        )}
                      </Tabs>
                    </Box>
                    <TabPanel value={value} index={0}>
                      <CursosPublicos></CursosPublicos>
                    </TabPanel>

                    <TabPanel value={value} index={1}>
                      <CursosPublicosNoPublicados></CursosPublicosNoPublicados>
                    </TabPanel>
                  </div>
                ) : (
                  <div>
                    <Paper
                      sx={{
                        padding: 0.5,
                        backgroundColor: "transparent",
                        marginLeft: 3,
                        marginRight: 3,
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "end",
                        boxShadow:
                          "0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 0px 0px 0px rgb(0 0 0 / 14%), 0px 0px 0px 0px rgb(0 0 0 / 12%)",
                      }}
                    >
                      <Button
                        variant="contained"
                        style={{
                          width: "fit-content",
                        }}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="20"
                          height="18"
                          viewBox="0 0 14 14"
                        >
                          <path
                            id="grid-3x3-gap"
                            d="M4,2V4H2V2ZM5,14V12a1,1,0,0,0-1-1H2a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1H4A1,1,0,0,0,5,14ZM5,9V7A1,1,0,0,0,4,6H2A1,1,0,0,0,1,7V9a1,1,0,0,0,1,1H4A1,1,0,0,0,5,9ZM5,4V2A1,1,0,0,0,4,1H2A1,1,0,0,0,1,2V4A1,1,0,0,0,2,5H4A1,1,0,0,0,5,4Zm5,10V12a1,1,0,0,0-1-1H7a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1H9A1,1,0,0,0,10,14Zm0-5V7A1,1,0,0,0,9,6H7A1,1,0,0,0,6,7V9a1,1,0,0,0,1,1H9A1,1,0,0,0,10,9Zm0-5V2A1,1,0,0,0,9,1H7A1,1,0,0,0,6,2V4A1,1,0,0,0,7,5H9A1,1,0,0,0,10,4ZM9,2V4H7V2Zm5,0V4H12V2ZM4,7V9H2V7ZM9,7V9H7V7Zm5,0H12V9h2ZM4,12v2H2V12Zm5,0v2H7V12Zm5,0v2H12V12ZM12,1a1,1,0,0,0-1,1V4a1,1,0,0,0,1,1h2a1,1,0,0,0,1-1V2a1,1,0,0,0-1-1ZM11,7a1,1,0,0,1,1-1h2a1,1,0,0,1,1,1V9a1,1,0,0,1-1,1H12a1,1,0,0,1-1-1Zm1,4a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1h2a1,1,0,0,0,1-1V12a1,1,0,0,0-1-1Z"
                            transform="translate(-1 -1)"
                            fill="#fff"
                          />
                        </svg>
                      </Button>
                      <Button
                        variant="contained"
                        style={{
                          width: "fit-content",
                          backgroundColor: "transparent",
                          boxShadow: "none",
                        }}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="18"
                          height="18"
                          viewBox="0 0 11 9"
                        >
                          <path
                            id="list"
                            d="M2.5,12a.5.5,0,0,1,.5-.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,12Zm0-4A.5.5,0,0,1,3,7.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,8Zm0-4A.5.5,0,0,1,3,3.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,4Z"
                            transform="translate(-2.5 -3.5)"
                            fill="#9c5fa2"
                            fill-rule="evenodd"
                          />
                        </svg>
                      </Button>
                    </Paper>
                    <CursosPublicos></CursosPublicos>
                  </div>
                )}
              </div>
            ) : (
              <ListarClasesPublicos
                id={idCursoVer}
                nombreCurso={nombreCursoVer}
                estado={estadoCurso}
                indexUnidad={indexUnidad}
                tipoSlide={tipoSalida}
              ></ListarClasesPublicos>
            )}
          </div>
        </div>
      </div>
    </UserContext.Provider>
  );
}

export default MainContenidoArgumentapp;
