import { Button, Dialog,  DialogContent, DialogTitle, Typography } from "@mui/material";
import React from "react";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";

function VerRespuesta({nombreEstudiante, tipoRespuesta, contRespuesta}) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
      {" "}
      <Button
        variant="contained"
        style={{ width: "fit-content" }}
        onClick={handleClickOpen}
        endIcon={
          <RemoveRedEyeIcon
            style={{ marginLeft: -8, marginRight: 4 }}
            fontSize="inherit"
          />
        }
      ></Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle
          style={{ color: "#6c3373", fontWeight: 500, fontSize: 30 }}
        >
          Estudiante: {nombreEstudiante}
        </DialogTitle>
        <DialogContent>
          {tipoRespuesta === "TextArea" && (
            <Typography variant="body6">
              <b>Respuesta:</b> {contRespuesta}
            </Typography>
          )}
          {tipoRespuesta === "CheckBox" && (
            <Typography variant="body6">
              <b>Respuesta:</b>{" "}
              {contRespuesta.index + 1 + ") " + contRespuesta.value}
            </Typography>
          )}

          {tipoRespuesta === "Photo" && (
            <Typography variant="body6">
              <b>EN DESARROLLO:</b>{" "}
              {/* {contRespuesta.index + 1 + ") " + contRespuesta.value} */}
            </Typography>
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default VerRespuesta;
