import { Button, CircularProgress, Grid, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext, useEffect, useState } from "react";
import { getDiapositivas } from "../../Diapositivas/FirebaseDiapositivas";
import { UserContext } from "../../Funcionalidades/UseContext";
import OpcionesDiapo from "./Componentes/OpcionesDiapo";
import { useLocation } from "react-router-dom";
import AddIcon from '@mui/icons-material/Add';
import { CustomIconButton } from "../../../styles/ArgumentappTheme";

function ContenidoActividad({
  uid,
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  isContenidoArgumentapp,
  estadoContendoArgumentapp,
}) {
  useEffect(() => {
    getDiapositivas(
      uid,
      idCurso,
      idClase,
      keyActividad,
      tipoVista,
      isContenidoArgumentapp ? "publicos" : "privados",
      (callback) => {
        setDiapositivas(callback);
        setbanderaCarga(true);
      }
    );
    // eslint-disable-next-line
  }, []);
    const location = useLocation();

  const {
    setBanderaContenidoDiapositiva,
    settipoSlide,
    setbanderaSlide,
    setbanderaSlideContenidoDiapo,
    setContenidoHTML,
    setAcciones,
    setNombreDiapositiva,
    setorden,
    setIdDiapositiva,
    settipoVista,
    setkeyActividad,
    setselectorEditor,
  } = useContext(UserContext);

  const [diapositivas, setDiapositivas] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(false);

  return (
    <div>
      {banderaCarga ? (
        <Grid
          container
          justifyContent="start"
          spacing={2}
          style={{
            width: "50vw",
            overflowX: "auto",
            whiteSpace: "nowrap",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
          }}
        >
          {isContenidoArgumentapp && !estadoContendoArgumentapp ? (
            <Grid
              key={"add"}
              style={{
                display: "flex",
                flexDirection: "column",
                flexWrap: "nowrap",
                justifyContent: "center",
                alignContent: "center",
                alignItems: "center",
                padding: 0,
                marginRight: 20,
              }}
              item
            >
              <CustomIconButton
                title={"Agregar Diapositiva"}
                onClick={(e) => {
                  settipoVista(tipoVista);
                  setkeyActividad(keyActividad);
                  setselectorEditor("nuevaDiapositiva");
                  settipoSlide("right");
                  setbanderaSlide(false);
                  setTimeout(() => {
                    setbanderaSlideContenidoDiapo(true);
                    setBanderaContenidoDiapositiva(true);
                  }, 200);
                }}
              >
                <AddIcon style={{ color: "#fff" }} />
              </CustomIconButton>
            </Grid>
          ) : null}

          {String(location.pathname).includes("/vistaclases") && (
            <Grid
              key={"add"}
              style={{
                display: "flex",
                flexDirection: "column",
                flexWrap: "nowrap",
                justifyContent: "center",
                alignContent: "center",
                alignItems: "center",
                padding: 0,
                marginRight: 20,
              }}
              item
            >
              <CustomIconButton
                title={"Agregar Diapositiva"}
                onClick={(e) => {
                  settipoVista(tipoVista);
                  setkeyActividad(keyActividad);
                  setselectorEditor("nuevaDiapositiva");
                  settipoSlide("right");
                  setbanderaSlide(false);
                  setTimeout(() => {
                    setbanderaSlideContenidoDiapo(true);
                    setBanderaContenidoDiapositiva(true);
                  }, 200);
                }}
              >
                <AddIcon style={{ color: "#fff" }} />
              </CustomIconButton>
            </Grid>
          )}

          {diapositivas.map((value) => (
            <Grid key={value.key} item>
              <div>
                <Button
                  style={{
                    minHeight: "200px",
                    maxHeight: "200px",
                    minWidth: "300px",
                    maxWidth: "300px",
                    borderRadius: 25,
                    backgroundColor: "white",
                    borderColor: "black",
                    borderWidth: "2px",
                    borderStyle: "solid",
                    marginBottom: "0px ",
                    display: "flex",
                    flexDirection: "column",
                  }}
                  onClick={(e) => {
                    setContenidoHTML(value.contenidoHTML);
                    setAcciones(value.datos.acciones);
                    setNombreDiapositiva(value.datos.nombre);
                    setorden(value.datos.orden);
                    setIdDiapositiva(value.key);
                    settipoVista(tipoVista);
                    setkeyActividad(keyActividad);
                    settipoSlide("right");
                    setbanderaSlide(false);
                    setTimeout(() => {
                      setBanderaContenidoDiapositiva(true);
                      setbanderaSlideContenidoDiapo(true);
                    }, 200);
                  }}
                >
                  <img
                    alt="miniatura diapositiva"
                    style={{
                      width: "300px",
                      height: "200px",
                    }}
                    src={value.miniatura}
                  ></img>
                </Button>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    flexWrap: "nowrap",
                    alignContent: "center",
                    justifyContent: "space-between",
                    alignItems: "center",
                    maxWidth: "300px",
                  }}
                >
                  <h4
                    style={{
                      color: "black",
                      textAlign: "start",
                      marginLeft: 0,
                    }}
                  >
                    {value.datos.nombre}
                  </h4>
                  {(!isContenidoArgumentapp ||
                    !estadoContendoArgumentapp === true) && (
                    <OpcionesDiapo
                      idClase={idClase}
                      idCurso={idCurso}
                      keyActividad={keyActividad}
                      tipoVista={tipoVista}
                      nombreDiapositiva={value.datos.nombre}
                      acciones={value.datos.acciones}
                      contenidoHTML={value.contenidoHTML}
                      orden={value.datos.orden}
                      idDiapositiva={value.key}
                      uid={isContenidoArgumentapp ? "data" : uid}
                      imagen={value.miniatura}
                      isContenidoArgumentapp={isContenidoArgumentapp}
                    ></OpcionesDiapo>
                  )}
                </div>
              </div>
            </Grid>
          ))}
          {diapositivas.length === 0 && (
            <div
              style={{
                whiteSpace: "nowrap",
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                justifyContent: "center",
                alignContent: "center",
                alignItems: "center",
                height: 150,
                width: "60vw",
              }}
            >
              <Typography
                style={{
                  display: "flex",
                  flexDirection: "row",
                  alignItems: "center",
                  padding: "30px",
                }}
              >
                No registra diapositivas
              </Typography>
            </div>
          )}
        </Grid>
      ) : (
        <Grid
          container
          justifyContent="start"
          spacing={6}
          style={{
            width: "60vw",
            whiteSpace: "nowrap",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            justifyContent: "center",
            alignContent: "center",
            alignItems: "center",
          }}
        >
          <div
            style={{
              textAlign: "center",
            }}
          >
            <Box sx={{ width: "auto", padding: 8 }}>
              <CircularProgress />
            </Box>
          </div>
        </Grid>
      )}
    </div>
  );
}

export default ContenidoActividad;
