import React, { useEffect } from "react";
import { BrowserRouter as Router, Switch } from "react-router-dom";
import Login from "./pages/Login";
import PrivateRoute from "./pages/Login/PrivateRoute";
import MainAdminUsuario from "./pages/Login/Registrar/MainAdminUsuario";
import SelectRoute from "./pages/Login/SelectRoute";
import MainCursos from "./pages/Cursos/MainCursos";
import Notfound from "./pages/Page404/Notfound";
import Home from "./pages/PagPrincipal/Home";
import MainCursoUnitario from "./pages/VistaUnitariaClase/MainClaseVistaUnitaria";
import MainModuloAdminGeneral from "./pages/ModuloAdminGeneral/MainModuloAdminGeneral";
import MainDiseñoDiapositivas from "./pages/ModuloAdminGeneral/DiseñoDiapositivas/MainDiseñoDiapositivas";
import MainCursoUnitarioPublico from "./pages/VistaUnitariaClase/ModuloCursosPublicos/MainCursoUnitarioPublico";
import MainRecursosUnidadesArgumentapp from "./pages/Recursos/UnidadesArgumentapp";
import MainPedagogicos from "./pages/Recursos/Pedagogicos";
import { ThemeProvider } from "@mui/material/styles";
import { CssBaseline } from "@mui/material";
import { ArgumentappTheme } from "./styles/ArgumentappTheme";
import "./styles/styles.css";
import MenuArgumentapp from "./components/Menu";
import MenuPerfil from "./components/MenuPerfil";
import MainActividadesOnline from "./pages/ActividadesOnline/MainActividadesOnline";
import MainVistaUnitariaActOn from "./pages/ActividadesOnline/VistaUnitariaActividadOnline/MainVistaUnitariaActOn";
import MainVistaClasesActOn from "./pages/ActividadesOnline/VistaUnitariaActividadOnline/Contenido/Clases/MainVistaClasesActOn";
import MainVistaPizarraAct from "./pages/ActividadesOnline/Pizarra/MainVistaPizarraAct";
import MainConfiguracion from "./pages/Configuracion";
import ActividadTerminada from "./pages/Funcionalidades/ActividadTerminada";
import MainRecursos from "./pages/Recursos";
import { useLocation } from "react-router-dom";
import MainContenidoArgumentapp from "./pages/UnidadesArgumentapp/MainContenidoArgumentapp";
import MainVistaUnidadesArgumentapp from "./pages/UnidadesArgumentapp/VistaContenidoArgumentapp/MainVistaContenidoArgumentapp";
import { cerrarSesion } from "./database/firebase";
import MainCalendario from "./pages/Calendario";
// import { cerrarSesion } from "./database/firebase";

const usePathname = () => {
  const location = useLocation();
  return location.pathname;
};

var dataUserNombre = window.localStorage.getItem("dataUserArgumentapp_nombre");

var dataUserPermisos = Number(
    window.localStorage.getItem("dataUserArgumentapp_permisos")
);

var inicioSesionArgumentapp = window.localStorage.getItem(
  "inicioSesionArgumentapp"
);

const ColorModeContext = React.createContext({ toggleColorMode: () => {} });


function App() {

  // useEffect(() => {
  //   cerrarSesion()
  // }, [])
  

  const [mode, setMode] = React.useState("light");

  const colorMode = React.useMemo(
    () => ({
      toggleColorMode: () => {
        setMode((prevMode) => (prevMode === "light" ? "dark" : "light"));
      },
    }),
    []
  );

  return (
    <ColorModeContext.Provider value={colorMode}>
      <ThemeProvider theme={ArgumentappTheme}>
        <CssBaseline />
        <Router>
          <div className="page-wrap" style={{ display: "flex" }}>
            {inicioSesionArgumentapp === "ok" && <MenuArgumentapp />}
            {inicioSesionArgumentapp === "ok" && <MenuPerfil />}
            <Switch>
              <PrivateRoute path="/" exact component={Home} />
              <PrivateRoute path="/home" exact component={Home} />
              <SelectRoute path="/login" exact component={Login} />
              <PrivateRoute path="/planificaciones" exact component={MainCursos} />
              <PrivateRoute path="/recursos" exact component={MainRecursos} />
              <PrivateRoute
                path="/materialargumentapp"
                exact
                component={MainRecursosUnidadesArgumentapp}
              />
              <PrivateRoute
                path="/pedagogicos"
                exact
                component={MainPedagogicos}
              />
              <PrivateRoute
                path="/administrarusuarios"
                exact
                component={MainAdminUsuario}
              />
              <PrivateRoute
                path="/moduloadministrativo"
                exact
                component={MainModuloAdminGeneral}
              />
              <PrivateRoute
                path="/disenodiapositivas"
                exact
                component={MainDiseñoDiapositivas}
              />
              <PrivateRoute
                path="/vistaclases"
                exact
                component={MainCursoUnitario}
              />
              <PrivateRoute
                path="/cursospublicos"
                exact
                component={MainCursoUnitarioPublico}
              />
              <PrivateRoute
                path="/actividadesOnline"
                exact
                component={MainActividadesOnline}
              />
              <PrivateRoute
                name="actividadOnline"
                path="/actividad/:codigoAct/"
                exact
                component={MainVistaUnitariaActOn}
              />
              <PrivateRoute
                name="actividadOnline"
                path="/actividad/:codigoAct/clase/:idClase"
                exact
                component={MainVistaClasesActOn}
              />
              <PrivateRoute
                path="/actividad/:codigoAct/clase/:idClase/pizarra"
                exact
                component={MainVistaPizarraAct}
              />
              <PrivateRoute
                path="/actividadesOnline/pizarra/termino"
                exact
                component={ActividadTerminada}
              />

              <PrivateRoute
                path="/configuracion"
                exact
                component={MainConfiguracion}
              />
              <PrivateRoute
                path="/contenidoargumentapp"
                exact
                component={MainContenidoArgumentapp}
              />
              <PrivateRoute
                path="/clasecontenidoargumentapp"
                exact
                component={MainVistaUnidadesArgumentapp}
              />
              <PrivateRoute
                path="/calendario"
                exact
                component={MainCalendario}
              />
              <PrivateRoute component={Notfound} />
            </Switch>
          </div>
        </Router>
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
}

export default App;
