import { CardMedia, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useState } from "react";
import LoadingArgumentapp from "../../../../components/LoadingArgumentapp";
import firebaseConf, { cerrarSesion } from "../../../../database/firebase";
import { getActividadesCursosPublicos } from "../../../VistaUnitariaClase/FirebaseVistaClase";
import TabDiapositivasPlanificaciones from "../../../VistaUnitariaClase/TabDiapositivasPlanificaciones";
import ShowMoreText from "react-show-more-text";
import iconDefault from "../../../../assets/images/default_icon_clases.png";
import TabDiapositivasImportar from "./TabDiapositivasImportar";

function ContenidoImportar(
{  nombreCurso,
  nombreClase,
  idCurso,
  idClase,
  tipoCurso,
  indexUnidad,
  indexClase,
  dataClase}
) {
  const [banderaGetActividad, setBanderaGetActividad] = useState(true);

const [actividades, setActividades] = useState([]);
      useEffect(() => {
        try {
          
        

            firebaseConf.auth().onAuthStateChanged((user) => {
              if (user) {
                getActividadesCursosPublicos(
                  idCurso,
                  idClase,
                  (value) => {
                    setActividades(value);
                    setBanderaGetActividad(false)
                  }
                );
              } else {
                cerrarSesion();
              }
            });

        } catch (error) {
          window.location = "/";
        }
        // eslint-disable-next-line
      }, []);
  return (
    <div>
      <div
        style={{
          marginBottom: 30,
        }}
      >
        <div>
          <Typography variant="tituloClase">
            <b>Unidad {indexUnidad}</b>
          </Typography>
          <Typography variant="tituloClase" style={{ marginLeft: 20 }}>
            {nombreCurso}
          </Typography>
        </div>
        <div>
          <Typography>Ver recursos de apoyo de esta unidad</Typography>
        </div>
      </div>
      <Paper tipo="general" elevation={"1"}>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
          }}
        >
          <div style={{ marginRight: 30 }}>
            <CardMedia
              component="img"
              sx={{
                height: 220,
                width: 160,
                borderRadius: "16px",
              }}
              image={
                dataClase.imagen === null || dataClase.imagen === undefined
                  ? iconDefault
                  : dataClase.imagen
              }
              alt={"Miniatura clases " + nombreClase}
            />
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
            }}
          >
            <Typography variant="textoClase">
              <Typography variant="titulo_4">Clase {indexClase}:</Typography>{" "}
              {nombreClase}
            </Typography>
            <Typography
              variant="textoClase"
              style={{
                fontWeight: 400,
                fontStyle: "italic",
                fontSize: 14,
                marginTop: 7,
              }}
            >
              {actividades.length > 1
                ? actividades.length + " actividades"
                : actividades.length === 0
                ? actividades.length + " actividades"
                : actividades.length + " actividad"}
            </Typography>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                marginTop: 13,
              }}
            >
              <div
                style={{
                  fontSize: 14,
                }}
              >
                <ShowMoreText
                  lines={3}
                  more="Ver más"
                  less="Ver menos"
                  expanded={false}
                  width={500}
                  truncatedEndingComponent={"... "}
                >
                  <p>Objetivo de la clase: {dataClase.objetivo}</p>
                </ShowMoreText>{" "}
              </div>
              <div
                style={{
                  marginTop: 11,
                  fontSize: 14,
                }}
              >
                <ShowMoreText
                  lines={3}
                  more="Ver más"
                  less="Ver menos"
                  expanded={false}
                  width={500}
                  truncatedEndingComponent={"... "}
                >
                  <p>Profundización Temática: {dataClase.tematica}</p>
                </ShowMoreText>
              </div>
            </div>
          </div>
        </div>

        {!banderaGetActividad ? (
          <div>
            {actividades.length > 0 ? (
              <div
                style={{
                  marginTop: 10,
                  marginLeft: 10,
                }}
              >
                {actividades.map((actividad, index) => {
                  return (
                    <div key={index}>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          padding: 15,
                          paddingLeft: 0,
                          paddingBottom: 0,
                        }}
                      >
                        <Typography variant="TituloActividadClase">
                          {actividad.data.nombreActividad}
                        </Typography>
                      </div>

                      <TabDiapositivasImportar
                        idClase={idClase}
                        idCurso={idCurso}
                        keyActividad={actividad.key}
                        key={index}
                      ></TabDiapositivasImportar>
                    </div>
                  );
                })}
              </div>
            ) : (
              <div
                style={{
                  marginLeft: 30,
                  marginRight: 30,
                  marginBottom: 80,
                  textAlign: "center",
                  height: "100px",
                }}
              >
                <Box
                  sx={{
                    width: "auto",
                    padding: 10,
                  }}
                >
                  <p>No registra actividad</p>
                </Box>
              </div>
            )}
          </div>
        ) : (
          <div
            style={{
              marginLeft: 30,
              marginRight: 30,
              marginTop: 80,
              marginBottom: 80,
              textAlign: "center",
              height: "100px",
            }}
          >
            <Box
              sx={{
                width: "auto",
                padding: 10,
              }}
            >
              <LoadingArgumentapp></LoadingArgumentapp>
            </Box>
          </div>
        )}
      </Paper>
    </div>
  );
}

export default ContenidoImportar;
