import React, { useEffect, useState } from "react";
import LoadingArgumentapp from "../../../../components/LoadingArgumentapp";
import firebaseConf, { cerrarSesion } from "../../../../database/firebase";
import OrgChart from "../../OrgChart/OrgChart";
import { getClasesCursosPublicos } from "../../Publicos/firebaseCursosPublicos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  Typography,
} from "@mui/material";
import ContenidoImportar from "./ContenidoImportar";

function ListarClasesImportarContenido({
  idCursoVer,
  tipoCursos,
  nombreCursoVer,
  indexUnidad,
  estadoCurso,
}) {
  const [clases, setclases] = useState([]);
  const [banderaCarga, setBanderaCarga] = useState(true);
  const [banderaContenido, setBanderaContenido] = useState(false);
  const [nombreClase, setnombreClase] = useState("");
  const [idClase, setidClase] = useState("");
  const [indexClase, setindexClase] = useState("");
  const [dataClase, setdataClase] = useState([]);

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      if (user) {
        getClasesCursosPublicos(idCursoVer, (value) => {
          setclases(value.reverse());
          setBanderaCarga(true);
        });
      } else {
        cerrarSesion();
      }
    }); // eslint-disable-next-line
  }, []);

  return (
    <div>
      <Box width="100%">
        {banderaCarga ? (
          <div>
            {!banderaContenido ? (
              <div>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    flexWrap: "wrap",
                  }}
                >
                  {clases.map((value, index) => {
                    return (
                      <Card
                        key={index}
                        sx={{
                          display: "flex",
                          borderWidth: 1,
                          padding: 1,
                          marginRight: 3,
                          marginBottom: 2,
                          width: 220,
                          borderRadius: "18px",
                        }}
                        elevation={"1"}
                      >
                        <Box
                          sx={{
                            display: "flex",
                            flexDirection: "column",
                          }}
                        >
                          <CardContent>
                            <Typography variant="h5">
                              Clase {index + 1}
                            </Typography>
                            <Typography
                              color="text.secondary"
                              style={{
                                wordWrap: "break-word",
                                overflowWrap: "break-word",
                                hyphens: "auto",
                                fontSize: 16,
                                fontWeight: "normal",
                              }}
                            >
                              {value.data.nombreclase}
                            </Typography>
                          </CardContent>
                          <CardActions
                            style={{
                              height: "-webkit-fill-available",
                              alignItems: "flex-end",
                              zoom: "80%",
                            }}
                          >
                            <OrgChart
                              nombreClase={value.data.nombreclase}
                              uid={"data"}
                              idClase={value.key}
                              idCurso={idCursoVer}
                              lugarAlamacenamiento={"publicos"}
                              nombreCurso={nombreCursoVer}
                            ></OrgChart>
                            <Button
                              style={{
                                marginLeft: 5,
                                borderStyle: "solid",
                                borderWidth: 1,
                                maxWidth: "fit-content",
                              }}
                              onClick={(e) => {
                                setBanderaContenido(true);
                                setnombreClase(value.data.nombreclase);
                                setidClase(value.key);
                                setindexClase(index + 1);
                                setdataClase(value.data);
                              }}
                              variant="outlined"
                            >
                              <ArrowForwardIosIcon></ArrowForwardIosIcon>
                            </Button>
                          </CardActions>
                        </Box>
                      </Card>
                    );
                  })}
                </div>
                {clases.length === 0 && (
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      flexWrap: "nowrap",
                      alignContent: "center",
                      justifyContent: "center",
                    }}
                  >
                    <p>No registra clases para el curso {nombreCursoVer}</p>
                  </div>
                )}{" "}
              </div>
            ) : (
              <div>
                <ContenidoImportar
                  dataClase={dataClase}
                  idClase={idClase}
                  idCurso={idCursoVer}
                  indexClase={indexClase}
                  indexUnidad={indexUnidad}
                  nombreClase={nombreClase}
                  nombreCurso={nombreCursoVer}
                  tipoCurso={tipoCursos}
                ></ContenidoImportar>
              </div>
            )}
          </div>
        ) : (
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "nowrap",
              alignContent: "center",
              justifyContent: "center",
              height: "400px",
            }}
          >
            <LoadingArgumentapp></LoadingArgumentapp>
          </div>
        )}
      </Box>
    </div>
  );
}

export default ListarClasesImportarContenido;
