import { Box, Tab, Tabs, Typography } from "@mui/material";
import React from "react";
import ContenidoActividad from "./ContenidoActividad/ContenidoActividad";

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0 }} style={{ marginTop: 15 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function TabDiapositivasPlanificaciones({
  idCurso,
  idClase,
  uid,
  keyActividad,
  isContenidoArgumentapp,
  estadoContendoArgumentapp,
}) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div>
      <Box>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
          style={{
            textAlign: "center",
          }}
        >
          <Tab label="Profesor" {...a11yProps(0)} />
          <Tab label="Alumnos" {...a11yProps(1)} />
          <Tab label="Pizarra" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <ContenidoActividad
          idCurso={idCurso}
          idClase={idClase}
          keyActividad={keyActividad}
          tipoVista={"profesor"}
          uid={isContenidoArgumentapp ? "data" : uid}
          isContenidoArgumentapp={isContenidoArgumentapp ? true : false}
          estadoContendoArgumentapp={estadoContendoArgumentapp}
        />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <ContenidoActividad
          idCurso={idCurso}
          idClase={idClase}
          keyActividad={keyActividad}
          tipoVista={"alumnos"}
          uid={isContenidoArgumentapp ? "data" : uid}
          isContenidoArgumentapp={isContenidoArgumentapp ? true : false}
          estadoContendoArgumentapp={estadoContendoArgumentapp}
        />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <ContenidoActividad
          idCurso={idCurso}
          idClase={idClase}
          keyActividad={keyActividad}
          tipoVista={"pizarra"}
          uid={isContenidoArgumentapp ? "data" : uid}
          isContenidoArgumentapp={isContenidoArgumentapp ? true : false}
          estadoContendoArgumentapp={estadoContendoArgumentapp}
        />
      </TabPanel>
    </div>
  );
}

export default TabDiapositivasPlanificaciones;
