import React from "react";
import { ArgumentappBox, CustomIconButton } from "../../styles/ArgumentappTheme";
import { Button } from "@mui/material";
import Box from "@mui/material/Box";
import MobileStepper from "@mui/material/MobileStepper";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import CloseIcon from '@mui/icons-material/Close';
import SwipeableViews from "react-swipeable-views";
import { updateSkipIntro } from "./Firebase";
import img1 from "../../assets/images/img-que-es-argumentapp.svg"
import img2 from "../../assets/images/img-transformando-la-ensenianza.svg"
import img3 from "../../assets/images/img-crear-y-hacer-clases.svg"

const images = [
  {
    label: "1. Contenido didáctico.",
    imgPath: img1,
    text: "Planificaciones de clases listas para ser descargadas y usadas en aula.",
  },
  {
    label: "2. Argumentación e indagación.",
    imgPath: img2,
    text: "Tus estudiantes podrán aprender de manera indagatoria a través de la argumentación y deliberación.",
  },
  {
    label: "3. Plenarios desafiantes.",
    imgPath: img3,
    text: "Accede a las soluciones de tus estudiantes a problemas y preguntas para orquestar la discusión y profundización de ideas.",
  },
];

function RedBar() {
    return <Box sx={{ height: 20 }} />;
}

export default function Intro() {
    const [activeStep, setActiveStep] = React.useState(0);
    const maxSteps = images.length;

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleStepChange = (step) => {
        setActiveStep(step);
    };

    return (
        <ArgumentappBox sx={{ width: 500, padding: "40px 60px" }}>
            <div style={{
                height: 50
            }}>
                {activeStep !== 0 && (
                    <CustomIconButton onClick={handleBack}>
                        <ArrowBackIosNewIcon />
                    </CustomIconButton>
                )}
                <div style={{
                    position: "relative",
                    float: "right"
                }}>
                    <CustomIconButton onClick={() => {
                        updateSkipIntro(true, (value) => {
                            if (value){
                                window.location = "/"
                            }
                        })
                    }}>
                        <CloseIcon />
                    </CustomIconButton>
                </div>
            </div>
            <RedBar />
            <SwipeableViews
                axis="x"
                index={activeStep}
                onChangeIndex={handleStepChange}
                enableMouseEvents
            >
                {images.map((step, index) => (
                    <div key={step.label}>
                        {Math.abs(activeStep - index) <= 2 ? (
                            <Box
                                component="img"
                                sx={{
                                    height: 255,
                                    display: "block",
                                    maxWidth: 400,
                                    overflow: "hidden",
                                    width: "100%",
                                }}
                                src={step.imgPath}
                                alt={step.label}
                            />
                        ) : null}
                    </div>
                ))}
            </SwipeableViews>
            <div className="argumentapp-center">
                <MobileStepper
                    steps={maxSteps}
                    position="static"
                    activeStep={activeStep}
                />
            </div>
            <RedBar />
            <Paper
                square
                elevation={0}
                sx={{
                    height: 50,
                }}
                className="argumentapp-center"
            >
                <Typography variant="h5" style={{ textAlign: "center" }}>{images[activeStep].label}</Typography>
            </Paper>
            <RedBar />
            <Paper
                square
                elevation={0}
                sx={{
                    height: 50,
                }}
                className="argumentapp-center"
            >
                <Typography sx={{ width: 300 }}>{images[activeStep].text}</Typography>
            </Paper>
            <RedBar />
            <RedBar />
            <div className="argumentapp-center">
                <Button
                    variant="contained"
                    onClick={() => {
                        if(activeStep === maxSteps - 1){
                            updateSkipIntro(true, (value) => {
                                if (value){
                                    window.location = "/"
                                }
                            })
                        } else {
                            handleNext()
                        }                        
                    }}
                >
                    {activeStep === maxSteps - 1 ? "Empecemos" : "Siguiente"}
                </Button>
            </div>
        </ArgumentappBox>
    );
}
