import React, { useContext, useEffect, useState } from "react"
import Calendar from "react-calendar"
import 'react-calendar/dist/Calendar.css'
import '../../styles/reactCalendar.css'
import dayjs from 'dayjs';
import { UserContext } from "../Funcionalidades/UseContext";

require('dayjs/locale/es')

export default function Calendario({ events }) {

    const [date, onChange] = useState(new Date());
    const { setStringDiaActual, setDayEvents } = useContext(UserContext)

    useEffect(() => {

        setStringDiaActual(dayjs(date).locale('es').format("DD [de] MMMM"))

        if(events !== null){

            var listaEventos = []

            events.map((value, i) => {
                var targetDate = dayjs(date)
                var eventDate = dayjs(value.start)

                if(targetDate.isSame(eventDate, "day")){
                    listaEventos.push(value)
                }
            })

            setDayEvents(listaEventos)
        }
    }, [date, events])

    return (
        <div>
            <Calendar
                onChange={(value, event) => { onChange(value) }}
                value={date}
            />
        </div>
    );
}