import { Button, Popover, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { delActividad } from "../VistaUnitariaClase/FirebaseVistaClase";

function DeleteActividad({ uid, idCurso, idClase, actividad, state, nombreActividad }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [msg, setMsg] = React.useState(
    "¿Eliminar la actividad - " + nombreActividad
  );
  //   const [banderaError, setBanderaError] = React.useState(false);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const identificador = open ? "simple-popover" : undefined;

  return (
    <div>
      <Button
        onClick={handleClick}
        variant="botonDelete"
        style={{

        }}
        title={"Eliminar Actividad"}
      >
        X
      </Button>
      <Popover
        id={identificador}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <div>
          <Typography sx={{ p: 2 }}>{msg}</Typography>
          <Box textAlign="center">
            {state === "privados" && (
              <Button
                onClick={(e) => {
                  delActividad(
                    uid,
                    idCurso,
                    idClase,
                    actividad,
                    "privados",
                    (callback) => {
                      // setBanderaError(val);
                      if (!callback) {
                        setMsg(
                          "Error al eliminar la clase " +
                            nombreActividad +
                            " de su registro"
                        );
                      } else {
                        handleClose();
                      }
                    }
                  );
                }}
              >
                Eliminar
              </Button>
            )}
            {state === "publicos" && (
              <Button
                onClick={(e) => {
                  delActividad(
                    "data",
                    idCurso,
                    idClase,
                    actividad,
                    "publicos",
                    (callback) => {
                      // setBanderaError(val);
                      if (!callback) {
                        setMsg(
                          "Error al eliminar la clase " +
                            nombreActividad +
                            " de su registro"
                        );
                      } else {
                        handleClose();
                      }
                    }
                  );
                }}
              >
                Eliminar
              </Button>
            )}

            <Button
              onClick={(e) => {
                setAnchorEl(null);
              }}
            >
              Cerrar
            </Button>
          </Box>
        </div>
      </Popover>
    </div>
  );
}

export default DeleteActividad;
