import { setChonkyDefaults } from "../../../components/chonky";
import { ChonkyIconFA } from "../../../components/chonky-icon-fontawesome";
import React, { useState, useEffect } from "react";
import { Box, Button, Slide, Typography } from "@mui/material";
import { VFSBrowser } from "./VFSBrowser";
import { getFileMap } from "./Firebase/Pedagogicos";
import { Link } from 'react-router-dom';

setChonkyDefaults({ iconComponent: ChonkyIconFA });

export default function Pedagogicos() {
    const [baseFileMap, setBaseFileMap] = useState();
    const [rootFolderId, setRootFolderId] = useState();
    const [finished, setFinished] = useState(false);

    useEffect(() => {
        getFileMap((d) => {
            setBaseFileMap(d.fileMap);
            setRootFolderId(d.rootFolderId);
            setFinished(true);
        });
    }, []);

    return (
        <div className="main-container">
            <div
                style={{
                    width: "100%",
                }}
            >
                <Typography variant="titulo">Recursos Pedagógicos</Typography>
                <Slide
                    direction="right"
                    in={finished}
                    mountOnEnter
                    unmountOnExit
                >
                    <div className="contenido">
                        <Button variant="contained" component={Link} to="/recursos" style={{ marginBottom: 20 }}>Volver</Button>
                        {finished && (
                            <div>
                                <Box sx={{ width: "100%" }}>
                                    <VFSBrowser
                                        baseFileMap={baseFileMap}
                                        rootFolderId={rootFolderId}
                                    />
                                </Box>
                            </div>
                        )}
                    </div>
                </Slide>
            </div>
        </div>
    );
}
