import {
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControlLabel,
  FormGroup,
  Grid,
  Input,
  Paper,
} from "@mui/material";
import { Box } from "@mui/material";
import { Tab, Tabs, Typography } from "@mui/material";
import React, { useContext, useEffect, useMemo, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import CompAcciones from "./ComponentAcciones/CompAcciones";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0 }} style={{ marginTop: 15 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function AgregarAcciones({ valueCheckbox, listBotones, listCheckBox }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [accionesIN, setaccionesIN] = useState("");
  const [dataAcciones, setDataAcciones] = useState([]);
  const [refreshComponent, setRefreshComponent] = useState(true);

  const [indexEditar, setIndexEditar] = useState(0);
  const [openDialogReferencias, setOpenDialogReferencias] =
    React.useState(false);

  const {
    setOpcionesdeVista,
    setAccionesUpload,
    accionesUpload,
    uid,
    idCurso,
    idClase,
    setoptionDirectionEditor,
    setoptionInEditor,
    setoptionDirectionAcciones,
    setoptionInAcciones,
    setoptionDirectionVistaPrevia,
    setoptionInVistaPrevia,
  } = useContext(UserContext);

  const ariaLabel = { "aria-label": "description" };

  useEffect(() => {
    var tempData = [];
    var tempAccionesReferencia = [];

    if (accionesUpload.length === 0) {
      if (valueCheckbox[0] === true) {
        for (let i = 0; i < listBotones.length; i++) {
          tempData.push({
            name: "Botón N° " + (i + 1),
            href: {
              keyDiapositiva: "null",
              keyActividad: "null",
              nombreActividad: "null",
              nombreDiapositiva: "null",
              tipoVista: "null",
            },
          });
          tempAccionesReferencia.push(false);
        }
        setDataAcciones(tempData);
        setaccionesIN("Button");
      }
      if (valueCheckbox[1] === true) {
        var tempData_checkbox = [];
        for (let i = 0; i < listCheckBox.length; i++) {
          tempData_checkbox.push({ name: "Opción N° " + (i + 1) });
        }
        setDataAcciones(tempData_checkbox);
        setaccionesIN("Checkbox");
      }
    } else {
      if (valueCheckbox[0] === true) {
        setDataAcciones(accionesUpload);
        setaccionesIN("Button");
      }
      if (valueCheckbox[1] === true) {
        setDataAcciones(accionesUpload);

        setaccionesIN("Checkbox");
      }
    }

    // eslint-disable-next-line
  }, []);

  const providerValue = useMemo(
    () => ({
      indexEditar,
      dataAcciones,
      setDataAcciones,
    }),
    [indexEditar, dataAcciones, setDataAcciones]
  );

  return (
    <div>
      <UserContext.Provider value={providerValue}>
        {accionesIN === "Button" && (
          <div>
            <div
              style={{
                textAlign: "center",
              }}
            >
              {dataAcciones.map((value, index) => {
                return (
                  <Button
                    style={{
                      backgroundColor: "green",
                      color: "white",
                      marginLeft: 10,
                    }}
                    key={index}
                  >
                    {refreshComponent && value.name}
                  </Button>
                );
              })}
            </div>
            <div
              style={{
                marginTop: 30,
              }}
            >
              <Box
                style={{
                  padding: 15,
                  marginBottom: 20,
                }}
              >
                {dataAcciones.map((value, index) => {
                  return (
                    <div>
                      <div>
                        <h3 style={{ color: "#555" }}>
                          Opciones para Botón {index + 1}
                        </h3>
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            flexWrap: "nowrap",
                            alignContent: "center",
                            alignItems: "center",
                          }}
                        >
                          <div
                            style={{
                              display: "flex",
                              flexDirection: "row",
                              alignItems: "center",
                            }}
                          >
                            <h4 style={{ color: "#555" }}> Nombre:</h4>
                            <Input
                              defaultValue={value.name}
                              inputProps={ariaLabel}
                              onBlur={(e) => {
                                setRefreshComponent(false);
                                setTimeout(() => {
                                  setRefreshComponent(true);
                                }, 50);
                              }}
                              style={{
                                color: "#555",
                                marginLeft: 50,
                              }}
                              onChange={(e) => {
                                var temp = dataAcciones;
                                temp[index].name = e.target.value;
                                setDataAcciones(temp);
                              }}
                            />
                          </div>
                          <div
                            style={{
                              display: "flex",
                              flexDirection: "row",
                              flexWrap: "nowrap",
                              alignContent: "space-between",
                              justifyContent: "space-around",
                              alignItems: "center",
                            }}
                          >
                            <h4 style={{ color: "#555", marginLeft: 30 }}>
                              {" "}
                              Referencia:
                            </h4>
                            <Button
                              variant="outlined"
                              onClick={(e) => {
                                setIndexEditar(index);
                                setOpenDialogReferencias(true);
                              }}
                              style={{ marginLeft: 30, backgroundColor: 'white' }}
                            >
                              Asignar acción
                            </Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
                <Dialog
                  open={openDialogReferencias}
                  onClose={(e) => {
                    setOpenDialogReferencias(false);
                  }}
                  maxWidth={"lg"}
                >
                  <DialogTitle style={{ color: "#6c3373" }}>
                    {"Acciones para el botón " + dataAcciones[indexEditar].name}
                  </DialogTitle>
                  <DialogContent>
                    <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
                      <Tabs
                        value={value}
                        onChange={handleChange}
                        aria-label="basic tabs example"
                        style={{
                          backgroundColor: "white",
                          textAlign: "center",
                        }}
                      >
                        <Tab label="Profesor" {...a11yProps(0)} />
                        <Tab label="Alumnos" {...a11yProps(1)} />
                        <Tab label="Pizarra" {...a11yProps(2)} />
                      </Tabs>
                    </Box>
                    <TabPanel value={value} index={0}>
                      <CompAcciones
                        tipoVista={"profesor"}
                        uid={uid}
                        idClase={idClase}
                        idCurso={idCurso}
                        acciones={dataAcciones[indexEditar].href}
                      ></CompAcciones>
                    </TabPanel>
                    <TabPanel value={value} index={1}>
                      <CompAcciones
                        tipoVista={"alumnos"}
                        uid={uid}
                        idClase={idClase}
                        idCurso={idCurso}
                        acciones={dataAcciones[indexEditar].href}
                      ></CompAcciones>
                    </TabPanel>
                    <TabPanel value={value} index={2}>
                      <CompAcciones
                        tipoVista={"pizarra"}
                        uid={uid}
                        idClase={idClase}
                        idCurso={idCurso}
                        acciones={dataAcciones[indexEditar].href}
                      ></CompAcciones>
                    </TabPanel>
                  </DialogContent>
                  <DialogActions>
                    <Button
                      onClick={(e) => {
                        setOpenDialogReferencias(false);
                      }}
                    >
                      Cerrar
                    </Button>
                  </DialogActions>
                </Dialog>
              </Box>
            </div>
          </div>
        )}
      </UserContext.Provider>

      {accionesIN === "Checkbox" && (
        <div>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "nowrap",
              alignContent: "center",
              justifyContent: "center",
              marginBottom: 20,
            }}
          >
            <div
              style={{
                textAlign: "center",
                backgroundColor: "white",
                color: "black",
                width: "fit-content",
                maxWidth: "80%",
                borderRadius: 25,
                padding: 20,
                borderColor: "#6c3373",
                borderWidth: 2,
                borderStyle: "solid",
              }}
            >
              <FormGroup
                style={{
                  display: "flex",
                  flexDirection: "row",
                }}
              >
                {dataAcciones.map((value, index) => {
                  return (
                    <FormControlLabel
                      control={<Checkbox />}
                      label={value.name}
                      key={index}
                    />
                  );
                })}
              </FormGroup>
            </div>
          </div>

          <Box
            style={{
              padding: 15,
              marginBottom: 20,
            }}
            sx={{
              display: "flex",
              flexDirection: "column",
            }}
          >
            <Grid
              container
              rowSpacing={1}
              columnSpacing={{ xs: 1, sm: 2, md: 3 }}
            >
              {dataAcciones.map((value, index) => {
                return (
                  <Grid item xs={4}>
                    <Paper
                      style={{
                        margin: 2,
                        borderStyle: "solid",
                        borderWidth: 2,
                        borderColor: "#6c3373",
                        padding: 15,
                        borderRadius: 25,
                      }}
                    >
                      <h4 style={{ color: "#555" }}>
                        Opciones para el Item {index + 1}
                      </h4>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          alignItems: "center",
                        }}
                      >
                        <h4 style={{ color: "#555" }}> Nombre:</h4>
                        <Input
                          defaultValue={value.name}
                          inputProps={ariaLabel}
                          onBlur={(e) => {
                            setRefreshComponent(false);
                            setTimeout(() => {
                              setRefreshComponent(true);
                            }, 50);
                          }}
                          style={{
                            color: "#555",
                            marginLeft: 50,
                            marginBottom: 20,
                          }}
                          onChange={(e) => {
                            var temp = dataAcciones;
                            temp[index].name = e.target.value;
                            setDataAcciones(temp);
                          }}
                        />
                      </div>
                    </Paper>
                  </Grid>
                );
              })}
            </Grid>
          </Box>
        </div>
      )}
      <div
        style={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <Button
          variant="contained"
          onClick={(e) => {
            setoptionDirectionEditor("right");
            setoptionDirectionAcciones("left");
            setoptionInAcciones(false);
            setoptionInEditor(true);
            setTimeout(() => {
              setOpcionesdeVista("Editor");
            }, 150);
          }}
        >
          Editor
        </Button>
        <Button
          variant="contained"
          onClick={(e) => {
            setAccionesUpload(dataAcciones);
            setoptionDirectionVistaPrevia("left");
            setoptionDirectionAcciones("right");
            setoptionInAcciones(false);
            setoptionInVistaPrevia(true)
            setTimeout(() => {
              setOpcionesdeVista("VistaPrevia");
            }, 150);
          }}
        >
          Vista previa
        </Button>
      </div>
    </div>
  );
}

export default AgregarAcciones;
