import firebaseConf from "../../../../../database/firebase";

export const getRespuestas = (
  codigo,
  user,
  idClase,
  idActividad,
  idDiapositiva,
  callback
) => {
  firebaseConf
    .database()
    .ref("actividades_online/users/")
    .child(user)
    .child(codigo)
    .child("datosEjecucion/respuestas/")
    .child(idClase)
    .child(idActividad)
    .child(idDiapositiva)
    .on("value", (dataRespuesta) => {
      var tempValue = [];
      if (dataRespuesta.val() === null) {
        callback(dataRespuesta.val());
      } else {
        dataRespuesta.forEach((resp) => {
          console.log(resp.val());
          tempValue.push({
            fecha: "14/04/2022 14:50:02",
            uidEstudiante: resp.key,
            nombre: resp.val().nameEstudiante,
            respuesta: {
              tipo: resp.val().tipoRespuesta,
              contRespuesta: resp.val().respEstudiante,
            },
          });
        });
        callback(tempValue);
      }
    });
};

export const removeRespuesta = (
  codigo,
  user,
  idClase,
  idActividad,
  idDiapositiva,
  idEstudiante,
  callback
) => {
  firebaseConf
    .database()
    .ref("actividades_online/users/")
    .child(user)
    .child(codigo)
    .child("datosEjecucion/respuestas/")
    .child(idClase)
    .child(idActividad)
    .child(idDiapositiva)
    .child(idEstudiante)
    .remove()
    .then(() => {
      setnullRespuestaPizarra(codigo, user, (_databack) => {
        callback(_databack);
      });
    })
    .catch(() => {
      callback(false);
    });
};
export const setRespuestaEnPizarra = (
  codigo,
  user,
  idActividad,
  idDiapositiva,
  idEstudiante,
  callback
) => {
  let ruta = firebaseConf
    .database()
    .ref("actividades_online/users")
    .child(user)
    .child(codigo)
    .child("datosEjecucion")
    .child("dataNodos")
    .child("pizarra");
  ruta
    .update({
      isMostarRespuesta: true,
      idActividad: idActividad,
      idDiapositiva: idDiapositiva,
      dataRespuesta: {
        idEstudiante: idEstudiante,
      },
    })
    .then(() => {
      callback(true);
    })
    .catch(() => {
      callback(false);
    });
};

export const setnullRespuestaPizarra = (codigo, user, callback) => {
  let ruta = firebaseConf
    .database()
    .ref("actividades_online/users")
    .child(user)
    .child(codigo)
    .child("datosEjecucion")
    .child("dataNodos")
    .child("pizarra");
  ruta
    .update({
      isMostarRespuesta: false,
      idActividad: "null",
      idDiapositiva: "null",
      dataRespuesta: {
        idEstudiante: "null",
      },
    })
    .then(() => {
      callback(true);
    })
    .catch(() => {
      callback(false);
    });
};
