import {
    Box,
    Button,
    Card,
    CardActions,
    CardContent,
    CardMedia,
    CircularProgress,
    Paper,
    Slide,
    Typography,
} from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import firebaseConf, {
    cerrarSesion,
    getClasesCursosPrivados,
} from "../../../database/firebase";
import { styled } from "@mui/material/styles";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { useHistory } from "react-router-dom";

import iconDefault from "../../../assets/images/default_icon_clases.png";

import DeleteClases from "./DeleteClases";
import OrgChart from "../OrgChart/OrgChart";
import { UserContext } from "../../Funcionalidades/UseContext";
import CrearClases from "./CrearClases";
import { CustomIconButton } from "../../../styles/ArgumentappTheme";
import { ArrowBackIosNew } from "@mui/icons-material";

function ListarClases({ id, nombreCurso, indexUnidad, tipoSlide }) {
    useEffect(() => {
        firebaseConf.auth().onAuthStateChanged((user) => {
            if (user) {
                setUid(user.uid);
                getClasesCursosPrivados(user.uid, id, (value) => {
                    setclases(value.reverse());
                    setBanderaCarga(true);
                    setTimeout(() => {
                        setBanderaCargaSlide(true);
                    }, 100);
                });
            } else {
                cerrarSesion();
            }
        }); // eslint-disable-next-line
    }, []);

    const [clases, setclases] = useState([]);
    const [banderaCarga, setBanderaCarga] = useState(false);
    const [banderaCargaSlide, setBanderaCargaSlide] = useState(false);
    const [tipoSalida, settipoSalida] = useState(tipoSlide);
    const {
        setBanderaClases,
        setIdCursoVer,
        setNombreCursoVer,
        settipoCursos,
    } = useContext(UserContext);
    const history = useHistory();

    const [uid, setUid] = useState("");
    const Demo = styled("div")(({ theme }) => ({
        width: "100%",
        height: "auto",
    }));
    useEffect(() => {
        settipoSalida(tipoSlide);
    }, [tipoSlide]);

    return (
        <div style={{ marginTop: 80 }}>
            <Paper
                sx={{
                    padding: 0.5,
                    backgroundColor: "transparent",
                    marginLeft: 3,
                    marginRight: 3,
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "end",
                    boxShadow:
                        "0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 0px 0px 0px rgb(0 0 0 / 14%), 0px 0px 0px 0px rgb(0 0 0 / 12%)",
                }}
            >
                <Button
                    variant="contained"
                    style={{
                        width: "fit-content",
                    }}
                >
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="20"
                        height="18"
                        viewBox="0 0 14 14"
                    >
                        <path
                            id="grid-3x3-gap"
                            d="M4,2V4H2V2ZM5,14V12a1,1,0,0,0-1-1H2a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1H4A1,1,0,0,0,5,14ZM5,9V7A1,1,0,0,0,4,6H2A1,1,0,0,0,1,7V9a1,1,0,0,0,1,1H4A1,1,0,0,0,5,9ZM5,4V2A1,1,0,0,0,4,1H2A1,1,0,0,0,1,2V4A1,1,0,0,0,2,5H4A1,1,0,0,0,5,4Zm5,10V12a1,1,0,0,0-1-1H7a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1H9A1,1,0,0,0,10,14Zm0-5V7A1,1,0,0,0,9,6H7A1,1,0,0,0,6,7V9a1,1,0,0,0,1,1H9A1,1,0,0,0,10,9Zm0-5V2A1,1,0,0,0,9,1H7A1,1,0,0,0,6,2V4A1,1,0,0,0,7,5H9A1,1,0,0,0,10,4ZM9,2V4H7V2Zm5,0V4H12V2ZM4,7V9H2V7ZM9,7V9H7V7Zm5,0H12V9h2ZM4,12v2H2V12Zm5,0v2H7V12Zm5,0v2H12V12ZM12,1a1,1,0,0,0-1,1V4a1,1,0,0,0,1,1h2a1,1,0,0,0,1-1V2a1,1,0,0,0-1-1ZM11,7a1,1,0,0,1,1-1h2a1,1,0,0,1,1,1V9a1,1,0,0,1-1,1H12a1,1,0,0,1-1-1Zm1,4a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1h2a1,1,0,0,0,1-1V12a1,1,0,0,0-1-1Z"
                            transform="translate(-1 -1)"
                            fill="#fff"
                        />
                    </svg>
                </Button>
                <Button
                    variant="contained"
                    style={{
                        width: "fit-content",
                        backgroundColor: "transparent",
                        boxShadow: "none",
                    }}
                >
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="18"
                        height="18"
                        viewBox="0 0 11 9"
                    >
                        <path
                            id="list"
                            d="M2.5,12a.5.5,0,0,1,.5-.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,12Zm0-4A.5.5,0,0,1,3,7.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,8Zm0-4A.5.5,0,0,1,3,3.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,4Z"
                            transform="translate(-2.5 -3.5)"
                            fill="#9c5fa2"
                            fill-rule="evenodd"
                        />
                    </svg>
                </Button>
            </Paper>

            <div
                style={{
                    display: "flex",
                    flexDirection: "row",
                    marginTop: 24,
                }}
            >
                <div
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        paddingRight: 40,
                        paddingLeft: 40,
                    }}
                >                    
                    <CustomIconButton
                        onClick={() => {
                            setIdCursoVer("");
                            setNombreCursoVer("");
                            settipoCursos("");
                            settipoSalida("left");
                            setBanderaCargaSlide(false);
                            setTimeout(() => {
                                setBanderaClases(false);
                            }, 100);
                        }}
                        style={{
                            marginTop: 30,
                            marginBottom: 20,
                        }}
                    >
                        <ArrowBackIosNew style={{ color: "#fff" }} />
                    </CustomIconButton>
                    <Paper
                        sx={{
                            width: "fit-content",
                            marginTop: "10px",
                            marginBottom: "10px",
                            boxShadow: "none",
                            backgroundColor: "transparent",
                        }}
                    >
                        <CrearClases idCurso={id}></CrearClases>
                    </Paper>
                </div>
                <Box width="100%">
                    {banderaCarga ? (
                        <div>
                            <Slide
                                direction={tipoSalida}
                                in={banderaCargaSlide}
                                mountOnEnter
                                unmountOnExit
                            >
                                <Demo>
                                    <Paper
                                        sx={{
                                            padding: 3,
                                            backgroundColor: "transparent",
                                            boxShadow: "none",
                                        }}
                                    >
                                        <div>
                                            <div
                                                style={{
                                                    display: "flex",
                                                    flexDirection: "row",
                                                    flexWrap: "wrap",
                                                }}
                                            >
                                                {clases.map((value, index) => {
                                                    return (
                                                        <Card
                                                            key={index}
                                                            sx={{
                                                                display: "flex",
                                                                borderWidth: 1,
                                                                padding: 3,
                                                                marginRight: 3,
                                                                marginBottom: 2,
                                                                minHeight: 250,
                                                                maxWidth: 420,
                                                                borderRadius:
                                                                    "30px",
                                                            }}
                                                            elevation={"1"}
                                                        >
                                                            <CardMedia
                                                                component="img"
                                                                sx={{
                                                                    width: 150,
                                                                    borderRadius:
                                                                        "16px",
                                                                }}
                                                                image={
                                                                    value.data
                                                                        .imagen ===
                                                                        null ||
                                                                    value.data
                                                                        .imagen ===
                                                                        undefined
                                                                        ? iconDefault
                                                                        : value
                                                                              .data
                                                                              .imagen
                                                                }
                                                                alt={
                                                                    "Miniatura clases " +
                                                                    value.data
                                                                        .nombreclase
                                                                }
                                                            />
                                                            <Box
                                                                sx={{
                                                                    display:
                                                                        "flex",
                                                                    flexDirection:
                                                                        "column",
                                                                }}
                                                            >
                                                                <CardContent>
                                                                    <Typography variant="h5">
                                                                        Clase{" "}
                                                                        {index +
                                                                            1}
                                                                    </Typography>
                                                                    <Typography
                                                                        color="text.secondary"
                                                                        style={{
                                                                            maxWidth: 230,
                                                                            wordWrap:
                                                                                "break-word",
                                                                            overflowWrap:
                                                                                "break-word",
                                                                            hyphens:
                                                                                "auto",
                                                                            fontSize: 14,
                                                                            fontWeight:
                                                                                "normal",
                                                                        }}
                                                                    >
                                                                        {
                                                                            value
                                                                                .data
                                                                                .nombreclase
                                                                        }
                                                                    </Typography>
                                                                </CardContent>
                                                                <CardActions
                                                                    style={{
                                                                        height: "-webkit-fill-available",
                                                                        alignItems:
                                                                            "flex-end",
                                                                    }}
                                                                >
                                                                    <Button
                                                                        style={{
                                                                            marginRight: 5,
                                                                            borderStyle:
                                                                                "solid",
                                                                            borderWidth: 1,
                                                                            maxWidth:
                                                                                "fit-content",
                                                                        }}
                                                                        onClick={(
                                                                            e
                                                                        ) => {
                                                                            settipoSalida(
                                                                                "right"
                                                                            );
                                                                            setBanderaCargaSlide(
                                                                                false
                                                                            );
                                                                            setTimeout(
                                                                                () => {
                                                                                    history.push(
                                                                                        "/vistaclases",
                                                                                        {
                                                                                            nombreCurso:
                                                                                                nombreCurso,
                                                                                            nombreClase:
                                                                                                value
                                                                                                    .data
                                                                                                    .nombreclase,
                                                                                            idCurso:
                                                                                                id,
                                                                                            isSuccessful: true,
                                                                                            idClase:
                                                                                                value.key,
                                                                                            tipoCurso:
                                                                                                "privados",
                                                                                            indexUnidad:
                                                                                                indexUnidad,
                                                                                            indexClase:
                                                                                                index +
                                                                                                1,
                                                                                            dataClase:
                                                                                                value.data,
                                                                                        }
                                                                                    );
                                                                                },
                                                                                100
                                                                            );
                                                                        }}
                                                                        variant="outlined"
                                                                    >
                                                                        <ArrowForwardIosIcon></ArrowForwardIosIcon>
                                                                    </Button>
                                                                    <OrgChart
                                                                        nombreClase={
                                                                            value
                                                                                .data
                                                                                .nombreclase
                                                                        }
                                                                        uid={
                                                                            uid
                                                                        }
                                                                        idClase={
                                                                            value.key
                                                                        }
                                                                        idCurso={
                                                                            id
                                                                        }
                                                                        lugarAlamacenamiento={
                                                                            "privados"
                                                                        }
                                                                        nombreCurso={
                                                                            nombreCurso
                                                                        }
                                                                    ></OrgChart>

                                                                    <DeleteClases
                                                                        id={id}
                                                                        idClase={
                                                                            value.key
                                                                        }
                                                                        state={
                                                                            "privado"
                                                                        }
                                                                        nombre={
                                                                            value
                                                                                .data
                                                                                .nombreclase
                                                                        }
                                                                    ></DeleteClases>
                                                                </CardActions>
                                                            </Box>
                                                        </Card>
                                                    );
                                                })}
                                            </div>
                                        </div>
                                        {clases.length === 0 && (
                                            <div
                                                style={{
                                                    display: "flex",
                                                    flexDirection: "row",
                                                    flexWrap: "nowrap",
                                                    alignContent: "center",
                                                    justifyContent: "center",
                                                }}
                                            >
                                                <p>
                                                    No registra clases para el
                                                    curso {nombreCurso}
                                                </p>
                                            </div>
                                        )}{" "}
                                    </Paper>
                                </Demo>
                            </Slide>
                        </div>
                    ) : (
                        <div
                            style={{
                                display: "flex",
                                flexDirection: "row",
                                flexWrap: "nowrap",
                                alignContent: "center",
                                justifyContent: "center",
                                height: "400px",
                            }}
                        >
                            <CircularProgress></CircularProgress>
                        </div>
                    )}
                </Box>
            </div>
        </div>
    );
}

export default ListarClases;
