import firebaseConf, { getClasesCursosPrivados } from "../../database/firebase";
import { getDiapositivas } from "../Diapositivas/FirebaseDiapositivas";
import { getActividades } from "../VistaUnitariaClase/FirebaseVistaClase";

export const setActividadOnline = (
  uid,
  fecha,
  establecimiento,
  identificadorCurso,
  nuevoCodigo,
  cursoSelect,
  callback
) => {
  let bdListado = firebaseConf
    .database()
    .ref("actividades_online/users")
    .child(uid)
    .child(nuevoCodigo);

  firebaseConf
    .database()
    .ref("actividades_online/")
    .child("listado")
    .child(nuevoCodigo)
    .set({
      code: nuevoCodigo,
      user: uid,
      isStart: false,
      isPantallaEspera: true,
    });

  bdListado
    .set({
      datosCurso: cursoSelect,
      datosActividad: {
        fecha: fecha,
        establecimiento: establecimiento,
        identificadorCurso: identificadorCurso,
        codigoActividad: nuevoCodigo,
      },
      datosEjecucion: {
        claseActiva: { nombre: "null" },
      },
      dataClases: {
        isFetchClases: false,
      },
    })
    .then(() => {
      callback(true);
    })
    .catch(() => {
      callback(false);
    });
};

export const getActividadesOnline = (callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    firebaseConf
      .database()
      .ref("actividades_online/users")
      .child(user.uid)
      .on("value", (snapshot) => {
        var temp = [];
        snapshot.forEach((ids) => {
          temp.push({ data: ids.val(), key: ids.key });
        });
        callback(temp);
      });
  });
};

export const deleteActividadOnline = (key, code, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    firebaseConf
      .database()
      .ref("actividades_online/")
      .child("listado")
      .child(code)
      .remove();

    let ruta = firebaseConf
      .firestore()
      .collection("actividades_online")
      .doc(user.uid)
      .collection(String(code));

    ruta.get().then((clases) => {
      clases.docs.forEach((clase) => {
        clase.ref
          .collection("data")
          .get()
          .then((actividades) => {
            actividades.docs.forEach((actividad) => {
              ["profesor", "alumnos", "pizarra"].forEach((vista) => {
                actividad.ref
                  .collection(vista)
                  .get()
                  .then((diapositivas) => {
                    diapositivas.docs.forEach((infoDiapo) => {
                      infoDiapo.ref.delete();
                    });
                    actividad.ref.delete();
                  });
              });
            });
          });
        clase.ref.delete();
      });
    });

    firebaseConf
      .database()
      .ref("actividades_online/users")
      .child(user.uid)
      .child(key)
      .remove()
      .then(() => {
        firebaseConf
          .database()
          .ref("actividades_online")
          .child(user.uid)
          .child(key)
          .remove()
          .then(() => {
            callback(true);
          })
          .catch(() => {
            callback(false);
          });
      })
      .catch(() => {
        callback(false);
      });
  });
};

export const isActividadOnUser = (uid, code, callback) => {
  firebaseConf
    .database()
    .ref("actividades_online/")
    .child("listado")
    .child(code)
    .get()
    .then((data) => {
      if (data.val() === null) {
        callback(false);
      } else {
        if (data.val().user === uid) {
          callback(true);
        } else {
          callback(false);
        }
      }
    });
};

