export const getClasesWithMergue = (
  idCurso,
  codigoAct,
  returnState,
  callback
) => {
  let banderaSalida = [];

  returnState("Obteniendo las clases...");

  firebaseConf.auth().onAuthStateChanged((user) => {
    let ruta = firebaseConf
      .database()
      .ref("actividades_online/users/" + user.uid)
      .child(codigoAct)
      .child("dataClases/clases");

    getClasesCursosPrivados(user.uid, idCurso, (clases) => {
      clases.forEach((clase, index) => {
        getActividades(user.uid, idCurso, clase.key, (actividades) => {
          actividades.forEach((actividad) => {
            ruta
              .child(clase.key)
              .child("actividades")
              .child("lista_actividad")
              .child(actividad.key)
              .set(actividad.data);
            returnState("Obteniendo diapositivas...");
            ["profesor", "alumnos", "pizarra"].forEach((vista) => {
              getDiapositivas(
                user.uid,
                idCurso,
                clase.key,
                actividad.key,
                vista,
                "privados",
                (diapositivas) => {
                  if (diapositivas.length > 0) {
                    diapositivas.forEach((diapo) => {
                      firebaseConf
                        .firestore()
                        .collection("actividades_online")
                        .doc(user.uid)
                        .set({ temp: "temp" });

                      firebaseConf
                        .firestore()
                        .collection("actividades_online")
                        .doc(user.uid)
                        .collection(String(codigoAct))
                        .doc(clase.key)
                        .set({ temp: "temp" });
                      firebaseConf
                        .firestore()
                        .collection("actividades_online")
                        .doc(user.uid)
                        .collection(String(codigoAct))
                        .doc(clase.key)
                        .collection("data")
                        .doc(actividad.key)
                        .set({ temp: "temp" });
                      firebaseConf
                        .firestore()
                        .collection("actividades_online")
                        .doc(user.uid)
                        .collection(String(codigoAct))
                        .doc(clase.key)
                        .collection("data")
                        .doc(actividad.key)
                        .collection(vista)
                        .doc(diapo.key)
                        .set({
                          contenidoHTML: diapo.contenidoHTML,
                          miniatura: diapo.miniatura,
                        });

                      ruta
                        .child(clase.key)
                        .child("actividades")
                        .child(actividad.key)
                        .child(vista)
                        .child(diapo.key)
                        .set(diapo.datos);
                    });
                  }
                }
              );
            });
          });
        });
        ruta.child(clase.key).child("infoClase").set(clase.data);
        banderaSalida.push(index);
      });
      if (clases.length === banderaSalida.length) {
        returnState("Cambiando estado de la actividad...");

        firebaseConf
          .database()
          .ref("actividades_online/users")
          .child(user.uid)
          .child(codigoAct)
          .child("dataClases")
          .update({
            isFetchClases: true,
          })
          .then(() => {
            setTimeout(() => {
              callback(true);
            }, 1500);
          })
          .catch(() => {
            setTimeout(() => {
              callback(false);
            }, 1500);
          });
      }
    });
  });
};

export const getClasesforList = (codigoAct, callback) => {
  var dataReturn = [];

  firebaseConf.auth().onAuthStateChanged((user) => {
    let rutaRealtime = firebaseConf
      .database()
      .ref("actividades_online/users/" + user.uid)
      .child(codigoAct)
      .child("dataClases/clases");

    rutaRealtime
      .orderByChild("infoClase/time")
      .get()
      .then((dataClases) => {
        dataClases.forEach((claseSingle) => {
          dataReturn.push({ key: claseSingle.key, data: claseSingle.val() });
        });
        callback(dataReturn.reverse());
      });
  });
};

export const actualizarClasesforActOn = (idCurso, codigoAct, callback) => {
  let banderaSalida = [];

  firebaseConf.auth().onAuthStateChanged((user) => {
    existCurso(idCurso, (exist) => {
      if (exist) {
        firebaseConf
          .database()
          .ref("actividades_online/users/" + user.uid)
          .child(codigoAct)
          .child("dataClases/clases")
          .remove()
          .then(() => {
            let ruta = firebaseConf
              .database()
              .ref("actividades_online/users/" + user.uid)
              .child(codigoAct)
              .child("dataClases/clases");

            getClasesCursosPrivados(user.uid, idCurso, (clases) => {
              if (clases.length === 0) {
                callback({ state: false, terminoSucc: true });
              } else {
                clases.forEach((clase, index) => {
                  banderaSalida.push(index);
                  getActividades(
                    user.uid,
                    idCurso,
                    clase.key,
                    (actividades) => {
                      actividades.forEach((actividad) => {
                        ruta
                          .child(clase.key)
                          .child("actividades")
                          .child("lista_actividad")
                          .child(actividad.key)
                          .set(actividad.data);

                        ["profesor", "alumnos", "pizarra"].forEach((vista) => {
                          getDiapositivas(
                            user.uid,
                            idCurso,
                            clase.key,
                            actividad.key,
                            vista,
                            "privados",
                            (diapositivas) => {
                              if (diapositivas.length > 0) {
                                diapositivas.forEach((diapo) => {
                                  firebaseConf
                                    .firestore()
                                    .collection("actividades_online")
                                    .doc(user.uid)
                                    .set({
                                      temp: "temp",
                                    });
                                  firebaseConf
                                    .firestore()
                                    .collection("actividades_online")
                                    .doc(user.uid)
                                    .collection(String(codigoAct))
                                    .doc(clase.key)
                                    .set({
                                      temp: "temp",
                                    });
                                  firebaseConf
                                    .firestore()
                                    .collection("actividades_online")
                                    .doc(user.uid)
                                    .collection(String(codigoAct))
                                    .doc(clase.key)
                                    .collection("data")
                                    .doc(actividad.key)
                                    .set({
                                      temp: "temp",
                                    });
                                  firebaseConf
                                    .firestore()
                                    .collection("actividades_online")
                                    .doc(user.uid)
                                    .collection(String(codigoAct))
                                    .doc(clase.key)
                                    .collection("data")
                                    .doc(actividad.key)
                                    .collection(vista)
                                    .doc(diapo.key)
                                    .set({
                                      contenidoHTML: diapo.contenidoHTML,
                                      miniatura: diapo.miniatura,
                                    });

                                  ruta
                                    .child(clase.key)
                                    .child("actividades")
                                    .child(actividad.key)
                                    .child(vista)
                                    .child(diapo.key)
                                    .set(diapo.datos);
                                });
                              }
                            }
                          );
                        });
                      });
                    }
                  );
                  ruta.child(clase.key).child("infoClase").set(clase.data);
                });
                if (clases.length === banderaSalida.length) {
                  setTimeout(() => {
                    callback({ state: true, terminoSucc: true });
                  }, 700);
                }
              }
            });
          });
      } else {
        callback({ state: false, terminoSucc: true });
      }
    });
  });
};

