export const getDiapositivasActOnline = (
  codigoActividad,
  idClase,
  idActividad,
  vista,
  callback
) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    let ruta = firebaseConf
      .database()
      .ref("actividades_online/users")
      .child(user.uid)
      .child(codigoActividad)
      .child("dataClases/clases/" + idClase + "/actividades/" + idActividad)
      .child(vista);

    let firestoreDiapo = firebaseConf
      .firestore()
      .collection("actividades_online")
      .doc(user.uid)
      .collection(codigoActividad)
      .doc(idClase)
      .collection("data")
      .doc(idActividad)
      .collection(vista);

    let tempDiapositivas = [];
    let arrayValueFromDatabase = [];
    ruta
      .orderByChild("orden")
      .get()
      .then((snapshot) => {
        snapshot.forEach((childSnapshot) => {
          var childKey = childSnapshot.key;
          var childData = childSnapshot.val();

          arrayValueFromDatabase.push({ datos: childData, key: childKey });
        });
        arrayValueFromDatabase.forEach((valor) => {
          firestoreDiapo
            .doc(valor.key)
            .get()
            .then((value) => {
              // if (value.data() = []) {

              // }

              // console.log(value.data());
              tempDiapositivas.push({
                datos: valor.datos,
                key: valor.key,
                contenidoHTML: value.data().contenidoHTML,
                miniatura: value.data().miniatura,
              });
            })
            .finally(() => {
              if (tempDiapositivas.length === arrayValueFromDatabase.length) {
                callback(tempDiapositivas);
              }
            });
        });

        if (arrayValueFromDatabase.length === 0) {
          callback(tempDiapositivas);
        }
      });
  });
};

export const activarClaseActividadOnline = (
  nombreClase,
  idClase,
  codeActividad,
  callback,
  stateDiapositiva
) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    let ruta = firebaseConf
      .database()
      .ref("actividades_online/users")
      .child(user.uid)
      .child(codeActividad)
      .child("datosEjecucion");
    let bd = firebaseConf
      .database()
      .ref("actividades_online/users")
      .child(user.uid)
      .child(codeActividad)
      .child("dataClases/clases")
      .child(idClase)
      .child("actividades");

    existDiapositivaProfesor(bd, (stateReturn) => {
      if (stateReturn === true) {
        firebaseConf
          .database()
          .ref("actividades_online/listado/" + codeActividad)
          .update({
            isStart: true,
            isPantallaEspera: true,
            idClaseActiva: idClase,
          });

        ruta.child("dataNodos").remove();

        ruta
          .child("claseActiva")
          .set({
            nombre: nombreClase,
            idClase: idClase,
            isStart: false,
          })
          .then(() => {
            let cont = 0;
            let cont_2 = 0;

            bd.child("lista_actividad")
              .orderByChild("fechaCreacion")
              .get()
              .then((actividades) => {
                actividades.forEach((actividad) => {
                  if (cont === 0) {
                    bd.child(actividad.key)
                      .child("profesor")
                      .orderByChild("orden")
                      .get()
                      .then((diapositivas) => {
                        diapositivas.forEach((diapositiva) => {
                          if (cont_2 === 0) {
                            ////////////////////////////////////////////////////////////// hacer transacción////////////////////////////////////////////////

                            ruta
                              .child("dataNodos")
                              .child("profesor")
                              .set({
                                idActividad: actividad.key,
                                idDiapositiva: diapositiva.key,
                              })
                              .then(() => {
                                ruta
                                  .child("dataNodos")
                                  .child("pizarra")
                                  .set({
                                    idActividad: "null",
                                    idDiapositiva: "null",
                                    isMostarRespuesta: false
                                  })
                                  .then(() => {
                                    ruta
                                      .child("dataNodos")
                                      .child("alumnos")
                                      .set({
                                        idActividad: "null",
                                        idDiapositiva: "null",
                                      })
                                      .then(() => {
                                        callback(true);
                                      });
                                  });
                              });

                            cont_2 = 1;
                          }
                        });
                      });
                    cont = 1;
                  }
                });
              });

            // getDiapositivaById(user.uid, codeActividad, idClase)
          });
      } else {
        if (stateReturn === "sinActividad") {
        }
        if (stateReturn === "sinDiapositiva") {
          stateDiapositiva("sinDiapositivas");
        }
      }
    });
  });
};

const existDiapositivaProfesor = (bd, stateDiapositiva) => {
  let cont = 0;
  bd.child("lista_actividad")
    .orderByChild("fechaCreacion")
    .get()
    .then((actividades) => {
      actividades.forEach((actividad) => {
        if (cont === 0) {
          bd.child(actividad.key)
            .child("profesor")
            .orderByChild("orden")
            .get()
            .then((diapositivas) => {
              if (diapositivas.val() === null) {
                stateDiapositiva("sinDiapositiva");
              } else {
                stateDiapositiva(true);
              }
            });
          cont = 1;
        }
      });
    });
};

