import firebase from "firebase";
import { deleteClasesPublicos } from "../pages/Cursos/Publicos/firebaseCursosPublicos";
import { ValidarLogin } from "../pages/Login/ValidarLogin";
import data from "./argumentappWeb.json";

const config = {
  apiKey: data.apiKey,
  authDomain: data.authDomain,
  projectId: data.projectId,
  storageBucket: data.storageBucket,
  messagingSenderId: data.messagingSenderId,
  appId: data.appId,
  measurementId: data.measurementId,
};

var firebaseConf = require("firebase/app").default;

if (!firebase.apps.length) {
  firebaseConf = firebase.initializeApp(config);
}
export default firebaseConf;

export const iniciarSesion = (userNick, userPassword, callback) => {
  firebaseConf
    .auth()
    .signInWithEmailAndPassword(userNick, userPassword)
    .then((userCredential) => {
      window.localStorage.setItem("inicioSesionArgumentapp", "ok");
    })
    .catch(() => {
      callback(false);
    })
    .finally(() => {
      if (window.localStorage.getItem("inicioSesionArgumentapp")) {
        ValidarLogin().then((value) => {
          if (value) {
            var uid = value.uid;
            firebaseConf
              .firestore()
              .collection("cuentasUsuarios")
              .where("correo", "==", userNick)
              .get()
              .then((data) => {
                data.forEach((doc) => {
                  window.localStorage.setItem(
                    "dataUserArgumentapp_correo",
                    userNick
                  );
                  window.localStorage.setItem(
                    "dataUserArgumentapp_nombre",
                    doc.data().nombre
                  );
                  window.localStorage.setItem(
                    "dataUserArgumentapp_permisos",
                    doc.data().permisos
                  );
                  window.localStorage.setItem(
                    "dataUserArgumentapp_skipIntro",
                    doc.data().skipIntro ? "true" : "false"
                  );
                });
                callback(true);

                // window.location = "/";
              });

            firebaseConf
              .firestore()
              .collection("cuentasUsuarios")
              .doc(uid)
              .update({ ultimoacceso: Date.now() });
          }
        });
      }
    });
};

export const cerrarSesion = async (callback) => {
  await firebaseConf
    .auth()
    .signOut()
    .then(() => {
      window.localStorage.removeItem("inicioSesionArgumentapp");
      window.localStorage.removeItem("dataUserArgumentapp");
      window.localStorage.removeItem("dataUserArgumentapp_permisos");
      window.localStorage.removeItem("dataUserArgumentapp_nombre");
      window.localStorage.removeItem("dataUserArgumentapp_correo");
      window.localStorage.removeItem("dataUserArgumentapp_skipIntro");
      window.location = "/login";
    })
    .catch((e) => callback(e));
};

export const registrarUsuarios = async (
  nombre,
  correo,
  password,
  permisos,
  callback
) => {
  await firebaseConf
    .auth()
    .createUserWithEmailAndPassword(correo, password)
    .then((userCredential) => {
      // Signed in
      var uid_in = userCredential.user.uid;

      firebaseConf
        .firestore()
        .collection("cuentasUsuarios")
        .doc(uid_in)
        .set({
          correo: correo,
          fechaCreacion: Date.now(),
          uid: uid_in,
          nombre: nombre,
          permisos: permisos,
          ultimoacceso: "",
          skipIntro: false,
        })
        .then(() => {
          callback(true);
        })
        .catch((error) => {
          callback(false);
        });
    })
    .catch((error) => {
      callback(false);
    });
};

export const recuperarPassword = async (correo, callback) => {
  await firebaseConf
    .auth()
    .sendPasswordResetEmail(correo)
    .then(() => {
      callback(true);
    })
    .catch((error) => {
      callback(false);
    });
};

export const listarTotalUsuarios = async (callback) => {
  var listaUsuarios = [];
  await firebaseConf
    .firestore()
    .collection("cuentasUsuarios")
    .get()
    .then((data) => {
      data.forEach((usuario) => {
        listaUsuarios.push(usuario.data());
      });
      callback(listaUsuarios);
    });
};

export const getMisCursos = (uid, callback) => {
  firebaseConf
    .firestore()
    .collection("cursos")
    .doc("privados")
    .collection(uid)
    .orderBy("fecha", "desc")
    .onSnapshot((querySnapshot) => {
      var cursosIN = [];
      querySnapshot.forEach((doc) => {
        cursosIN.push(doc.data());
      });
      callback(cursosIN);
    });
};

export const getCursosPublicos = (callback) => {
  firebaseConf
    .firestore()
    .collection("cursos")
    .doc("publicos")
    .collection("data")
    .where("publicado", "==", true)
    .onSnapshot((result) => {
      var cursosIN = [];
      result.forEach((value) => {
        cursosIN.push(value.data());
      });
      callback(cursosIN);
    });
};

export const getCursosPublicosNoPublicados = (callback) => {
  firebaseConf
    .firestore()
    .collection("cursos")
    .doc("publicos")
    .collection("data")
    .where("publicado", "==", false)
    // .orderBy("fecha", "desc")
    .onSnapshot((result) => {
      var cursosIN = [];
      result.forEach((value) => {
        cursosIN.push(value.data());
      });
      callback(cursosIN);
    });
};

export const setMisCursosFirebase = (
  nombre,
  nivel,
  asignatura,
  presentacion,
  referencias,
  progreHipotetica,
  imagen,
  porcentaje,
  callback
) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      var id = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("privados")
        .collection(user.uid)
        .doc().id;

      if (imagen !== null) {
        firebaseConf
          .storage()
          .ref("cursos/privados/" + id)
          .put(imagen.file)
          .on(
            "state_changed",
            (snapshot) => {
              var temp =
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
              porcentaje(temp);
            },
            (error) => {
              console.log("error " + error);
              alert("Error al subir los datos " + error);
            },
            () => {
              firebaseConf
                .storage()
                .ref("cursos/privados/" + id)
                .getDownloadURL()
                .then((url) => {
                  firebaseConf
                    .firestore()
                    .collection("cursos")
                    .doc("privados")
                    .collection(user.uid)
                    .doc(id)
                    .set({
                      asignatura: asignatura,
                      fecha: Date.now(),
                      id: id,
                      nivel: nivel,
                      nombre: nombre,
                      uid: user.uid,
                      imagen: url,
                      presentacion: presentacion,
                      referencias: referencias,
                      progreHipotetica: progreHipotetica,
                    })
                    .then(() => {
                      callback(true);
                    })
                    .catch((e) => callback(false));
                });
            }
          );
      } else {
        firebaseConf
          .firestore()
          .collection("cursos")
          .doc("privados")
          .collection(user.uid)
          .doc(id)
          .set({
            asignatura: asignatura,
            fecha: Date.now(),
            id: id,
            nivel: nivel,
            nombre: nombre,
            uid: user.uid,
            imagen: imagen,
            presentacion: presentacion,
            referencias: referencias,
            progreHipotetica: progreHipotetica,
          })
          .then(() => {
            callback(true);
          })
          .catch((e) => callback(false));
      }
    } else {
      cerrarSesion();
    }
  });
};

export const deleteUnCurso = (id, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      let ruta = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("privados")
        .collection(user.uid)
        .doc(id);

      var cont = 0;

      ruta.collection("clases").onSnapshot((clases) => {
        clases.forEach((contenidoClases) => {
          deleteClasesPrivados(id, contenidoClases.id, (state) => {
            if (state) {
              contenidoClases.ref.delete();
              cont++;
            } else {
              callback(false);
            }
          });
        });

        if (cont === clases.docs.length) {
          try {
            firebaseConf
              .storage()
              .ref("cursos/privados")
              .child(id)
              .getDownloadURL()
              .then(onResolve, onReject);

            function onResolve(foundURL) {
              firebaseConf.storage().ref("cursos/privados").child(id).delete();
            }

            function onReject(error) {
              console.clear();
              // console.log(error.code);
            }

            ruta.delete();
            callback(true);
          } catch (error) {}
        }
      });
    } else {
      cerrarSesion();
    }
  });
};

export const deleteCursoPublico = (id, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      let ruta = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("publicos")
        .collection("data")
        .doc(id);

      var cont = 0;

      ruta.collection("clases").onSnapshot((clases) => {
        clases.forEach((contenidoClases) => {
          deleteClasesPublicos(id, contenidoClases.id, (state) => {
            if (state) {
              contenidoClases.ref.delete();
              cont++;
            } else {
              callback(false);
            }
          });
        });

        if (cont === clases.docs.length) {
          try {
            firebaseConf
              .storage()
              .ref("cursos/publico/" + id)
              .delete();
            ruta.delete();
            callback(true);
          } catch (error) {}
        }
      });
    } else {
      cerrarSesion();
    }
  });
};

