export const getClasesCursosPrivados = (uid, id, callback) => {
  firebaseConf
    .firestore()
    .collection("cursos")
    .doc("privados")
    .collection(uid)
    .doc(id)
    .collection("clases")
    .orderBy("time", "desc")
    .onSnapshot((data) => {
      var dataClases = [];
      data.forEach((doc) => {
        dataClases.push({ data: doc.data(), key: doc.id });
      });
      callback(dataClases);
    });
};

export const setClasesCursosPrivados = (
  id_curso,
  nombre,
  tematica,
  objetivoClase,
  imagen,
  porcentaje,
  callback
) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      var id_clase = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("privados")
        .collection(user.uid)
        .doc(id_curso)
        .collection("clases")
        .doc().id;

      if (imagen !== null) {
        firebaseConf
          .storage()
          .ref("img_clases/privados/" + id_clase)
          .put(imagen.file)
          .on(
            "state_changed",
            (snapshot) => {
              var temp =
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
              porcentaje(temp);
            },
            (error) => {
              console.log("error " + error);
              alert("Error al subir los datos " + error);
            },
            () => {
              firebaseConf
                .storage()
                .ref("img_clases/privados/" + id_clase)
                .getDownloadURL()
                .then((url) => {
                  firebaseConf
                    .firestore()
                    .collection("cursos")
                    .doc("privados")
                    .collection(user.uid)
                    .doc(id_curso)
                    .collection("clases")
                    .doc(id_clase)
                    .set({
                      nombreclase: nombre,
                      tematica: tematica,
                      objetivo: objetivoClase,
                      imagen: url,
                      time: Date.now(),
                    })
                    .then(() => {
                      callback(true);
                    })
                    .catch((e) => callback(false));
                });
            }
          );
      } else {
        firebaseConf
          .firestore()
          .collection("cursos")
          .doc("privados")
          .collection(user.uid)
          .doc(id_curso)
          .collection("clases")
          .doc(id_clase)
          .set({
            nombreclase: nombre,
            tematica: tematica,
            objetivo: objetivoClase,
            imagen: null,
            time: Date.now(),
          })
          .then(() => {
            callback(true);
          })
          .catch((e) => callback(false));
      }
    } else {
      cerrarSesion();
    }
  });
};

