export const deleteClasesPrivados = (id_curso, id_clase, callback) => {
  firebaseConf.auth().onAuthStateChanged((user) => {
    if (user) {
      let ruta = firebaseConf
        .firestore()
        .collection("cursos")
        .doc("privados")
        .collection(user.uid)
        .doc(id_curso)
        .collection("clases")
        .doc(id_clase);

      ruta.collection("actividad").onSnapshot((actividades) => {
        actividades.forEach((contActividades) => {
          contActividades.ref
            .collection("profesor")
            .onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapositiva) => {
                contDiapositiva.ref.delete();
              });
            });
          contActividades.ref
            .collection("alumnos")
            .onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapositiva) => {
                contDiapositiva.ref.delete();
              });
            });
          contActividades.ref
            .collection("pizarra")
            .onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapositiva) => {
                contDiapositiva.ref.delete();
              });
            });
          firebaseConf
            .database()
            .ref(
              "cursos/privados/" + user.uid + "/" + id_curso + "/" + id_clase
            )
            .remove()
            .then(() => {
              callback(true);
            })
            .catch(() => {
              callback(false);
            });
          contActividades.ref.delete();
        });
      });
      ruta.delete();

      firebaseConf
        .storage()
        .ref("img_clases/privados/" + id_clase)
        .delete();
    } else {
      cerrarSesion();
    }
  });
};

