import React, { useState } from "react";

import { Alert, Button, FormControl, InputLabel } from "@mui/material";
import Box from "@mui/material/Box";
import { recuperarPassword } from "../../database/firebase";
import { CustomInput } from "../../styles/ArgumentappTheme"

function RedBar() {
    return <Box sx={{ height: 20 }} />;
}

function RecuperarPassword() {
    const [correo, setCorreo] = useState("");

    const [mensaje, setMensaje] = useState("");

    const [banderaCrearActividad, setBandera] = useState(false);
    const [banderaRecuperar, setBanderaRecuperar] = useState(false);

    const crearActividad = () => {
        recuperarPassword(correo, (val) => {
            if (val) {
                setBanderaRecuperar(true);
                setCorreo("");
                setTimeout(function () {
                    window.location = "/";
                }, 5000);
            } else {
                setBandera(true);
                setMensaje(
                    "No se ha podido  enviar el correo electronico con "
                );
                setTimeout(function () {
                    setBandera(false);
                }, 5000);
            }
        });
    };

    return (
        <Box className="argumentapp-center">
            {banderaCrearActividad && <Alert severity="error">{mensaje}</Alert>}
            {banderaRecuperar && (
                <Alert severity="success">
                    Correo de recuperación enviado... En 5 segundos será
                    redirigido al login
                </Alert>
            )}

            <RedBar />

            <FormControl variant="standard">
                <InputLabel shrink>
                    Ingresa tu correo
                </InputLabel>
                <CustomInput
                    id="correo"
                    required
                    onChange={(e) => {
                        setCorreo(e.target.value);
                    }}
                />
            </FormControl>           

            <div style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center"
            }}>
                <RedBar />
                <RedBar />
                <Button
                    variant="contained"
                    onClick={(e) => {
                        crearActividad();
                    }}
                >
                    Enviar correo
                </Button>
            </div>
        </Box>
    );
}

export default RecuperarPassword;
