import { Button, Popover } from "@mui/material";
import React from "react";
import BotonBorrarDiapoPublic from "./BotonBorrarDiapoPublic";
import BotonEditarDiapoPublic from "./BotonEditarDiapoPublic";
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';import ClonarDiapositiva from "../../../Funcionalidades/ClonarDiapositiva";
function OpcionesDiapoPublic({
  idClase,
  idCurso,
  keyActividad,
  tipoVista,
  nombreDiapositiva,
  acciones,
  contenidoHTML,
  orden,
  idDiapositiva,
  imagen,
}) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  return (
    <div>
      <Button
        style={{
          marginLeft: 5,
          minWidth: 30,
          maxWidth: 30,
          minHeight: 30,
          maxHeight: 30,
        }}
        aria-describedby={id}
        variant="contained"
        onClick={handleClick}
        title={"Opciones"}
      >
        <MoreHorizIcon />
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <BotonEditarDiapoPublic
          idClase={idClase}
          idCurso={idCurso}
          keyActividad={keyActividad}
          tipoVista={tipoVista}
          nombreDiapositiva={nombreDiapositiva}
          acciones={acciones}
          contenidoHTML={contenidoHTML}
          orden={orden}
          idDiapositiva={idDiapositiva}
          state={true}
        ></BotonEditarDiapoPublic>
        <ClonarDiapositiva
          idClase={idClase}
          idCurso={idCurso}
          keyActividad={keyActividad}
          tipoVista={tipoVista}
          nombreDiapositiva={nombreDiapositiva}
          acciones={acciones.item}
          contenidoHTML={contenidoHTML}
          uid={"data"}
          tipoAcciones={acciones.tipo}
          imagen={imagen}
          lugarAlmacenamiento={"publicos"}
        ></ClonarDiapositiva>
        <BotonBorrarDiapoPublic
          idClase={idClase}
          idCurso={idCurso}
          keyActividad={keyActividad}
          keyDiapositiva={idDiapositiva}
          tipoVista={tipoVista}
          nombreDiapositiva={nombreDiapositiva}
        ></BotonBorrarDiapoPublic>
      </Popover>
    </div>
  );
}

export default OpcionesDiapoPublic;
