import firebaseConf from "../../database/firebase";

export const getPaneles = (callback) => {
  var arrayCallback = [];

  const DB = firebaseConf
    .database()
    .ref("disenos/moduloAdmin/opciones")
    .orderByChild("orden");

  DB.once("value", (data) => {
    data.forEach((value) => {
      var childKey = value.key;
      var childData = value.val();
      arrayCallback.push({ data: childData, key: childKey });
    });
    callback(arrayCallback)
  });
};

export const getIMGtoPanel = (ruta, callback) => {
  var storage = firebaseConf.storage();
  storage
    .ref(ruta)
    .getDownloadURL()
    .then((data) => {
      callback(data);
    });
};
