import * as React from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import RenderDiapositivas from "./RenderDiapositivas";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}
function SliderTabAlumnoPizarra({ codigo, idClase, dataActividades }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{
        flexGrow: 1,
        bgcolor: "background.paper",
        display: "flex",
      }}
    >
      <Tabs
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        sx={{ borderRight: 1, borderColor: "divider" }}
      >
        <Tab label="Dispositivo Alumno" {...a11yProps(0)} />
        <Tab label="Dispositivo Pizarra" {...a11yProps(1)} />
      </Tabs>
      <TabPanel value={value} index={0}>
        {dataActividades.map((data, index) => {
          return (
            <div key={index} style={{
            }}>
              <h4 style={{ marginTop: 0, marginLeft: 0, color: "black" }}>
                {data.dataActividad.nombreActividad}
              </h4>

              {[{ id: "alumnos", name: "Alumno" }].map((vistasData, index) => {
                return (
                  <RenderDiapositivas
                    key={index}
                    codigo={codigo}
                    idClase={idClase}
                    idActividad={data.key}
                    idVista={vistasData.id}
                    nameVista={vistasData.name}
                  />
                );
              })}
            </div>
          );
        })}
      </TabPanel>
      <TabPanel value={value} index={1}>
        {dataActividades.map((data, index) => {
          return (
            <div key={index}>
              <h4 style={{ marginTop: 0, marginLeft: 0, color: "black" }}>
                {data.dataActividad.nombreActividad}
              </h4>

              {[{ id: "pizarra", name: "Pizarra" }].map((vistasData, index) => {
                return (
                  <RenderDiapositivas
                    key={index}
                    codigo={codigo}
                    idClase={idClase}
                    idActividad={data.key}
                    idVista={vistasData.id}
                    nameVista={vistasData.name}
                  />
                );
              })}
            </div>
          );
        })}
      </TabPanel>
    </Box>
  );
}

export default SliderTabAlumnoPizarra;
