import {
  AppBar,
  Button,
  Dialog,
  IconButton,
  Paper,
  Popover,
  Slide,
  Toolbar,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import Swal from "sweetalert2";
import {
  desactivarClaseActividadOnline,
  getVarPantallaDeEsperar,
  pantallaDeEsperaActivar,
  pantallaDeEsperaDesactivar,
} from "../../BDActividadesOnline";
import { setPantallaDefault } from "../../Pizarra/BDActOnlinePizarra";
import DiapositivaActivaProfesor from "./DiapositivaActivaProfesor";
import SidebarDiapositivas from "./SidebarDiapositivas";
import CloseIcon from "@mui/icons-material/Close";
import MainRespuestasEstudiantes from "./Respuestas/MainRespuestasEstudiantes";

function MainProfesorActOnline({
  codigoAct,
  idClase,
  data,
  dataActividades,
  infoCurso,
}) {
  useEffect(() => {
    getVarPantallaDeEsperar(codigoAct, (value) => {
      setIsPantallaEspera(value);
    });
  }, [codigoAct]);

  const [isPantallaEspera, setIsPantallaEspera] = useState("esperandoData");

  const terminarActividad = () => {
    const swalWithBootstrapButtons = Swal.mixin({});

    swalWithBootstrapButtons
      .fire({
        title: "¿Terminar actividad?",
        text: "Proin fringilla diam finibus magna condimentum efficitur. Suspendisse potenti. Vestibulum iaculis non mi ac tincidunt. Nunc fermentum nibh ac orci imperdiet faucibus. Ut et semper metus. ",
        icon: "info",
        showCancelButton: true,
        confirmButtonText: "Terminar",
        cancelButtonText: "No, continuar",
        reverseButtons: true,
        confirmButtonColor: "#d32f2f",
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire({
            title: "Terminando actividad, espere un momento...",
            icon: "info",
            showCancelButton: false,
            showConfirmButton: false,
            allowOutsideClick: false,
            allowEscapeKey: false,
            allowEnterKey: false,
          });

          setTimeout(() => {
            desactivarClaseActividadOnline(codigoAct, (resp) => {
              if (resp) {
                Swal.fire({
                  position: "top-end",
                  icon: "success",
                  title: "Actividad terminada",
                  text: "La actividad online para la clase indicada ha terminado con exito.",
                  showConfirmButton: false,
                  timer: 1500,
                });
              } else {
                Swal.fire(
                  "The Internet?",
                  "That thing is still around?",
                  "question"
                );
              }
            });
          }, 600);
        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
          let timerInterval;

          swalWithBootstrapButtons
            .fire({
              title: "Actividad reanudada",
              html:
                "Se ha continuado la actividad con la clase " +
                data.data.infoClase.nombreclase +
                " en " +
                codigoAct +
                ".",
              timer: 2000,
              timerProgressBar: true,

              willClose: () => {
                clearInterval(timerInterval);
              },
            })
            .then((result) => {
              /* Read more about handling dismissals below */
              if (result.dismiss === Swal.DismissReason.timer) {
                console.log("I was closed by the timer");
              }
            });
        }
      });
  };

  const pantallaEspera = () => {
    const popPantallaEspera = Swal.mixin({});
    popPantallaEspera
      .fire({
        title: "Iniciar actividad",
        text: "Proin fringilla diam finibus magna condimentum efficitur. Suspendisse potenti. Vestibulum iaculis non mi ac tincidunt. Nunc fermentum nibh ac orci imperdiet faucibus. Ut et semper metus. ",
        icon: "info",
        showCancelButton: true,
        confirmButtonText: "Si",
        cancelButtonText: "No, cancelar",
        reverseButtons: true,
        confirmButtonColor: "#27AE60",
      })
      .then((result) => {
        if (result.isConfirmed) {
          popPantallaEspera.fire({
            title: "Activando dispositivos de Argumentapp",
            icon: "info",
            showCancelButton: false,
            showConfirmButton: false,
            allowOutsideClick: false,
            allowEscapeKey: false,
            allowEnterKey: false,
          });

          setTimeout(() => {
            pantallaDeEsperaDesactivar(codigoAct, (resp) => {
              if (resp) {
                Swal.fire({
                  position: "top-end",
                  icon: "success",
                  title: "Dispositivos activados",
                  showConfirmButton: false,
                  timer: 2500,
                });
              } else {
                Swal.fire(
                  "Error al activar los dispositivos",
                  "Ha ocurrido un error al activar los dispositivos de argumentapp, intentelo nuevamente.",
                  "error"
                );
              }
            });
          }, 1000);
        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
          let timerInterval;

          popPantallaEspera.fire({
            title: "Dispositivos no activados",
            timer: 1500,
            timerProgressBar: true,

            willClose: () => {
              clearInterval(timerInterval);
            },
          });
        }
      });
  };

  const activarPantallaEspera = () => {
    const popPantallaEspera = Swal.mixin({});
    popPantallaEspera
      .fire({
        title: "Activar pantalla de espera",
        text: "Proin fringilla diam finibus magna condimentum efficitur. Suspendisse potenti. Vestibulum iaculis non mi ac tincidunt. Nunc fermentum nibh ac orci imperdiet faucibus. Ut et semper metus. ",
        icon: "info",
        showCancelButton: true,
        confirmButtonText: "Si",
        cancelButtonText: "No, cerrar",
        reverseButtons: true,
        confirmButtonColor: "#27AE60",
      })
      .then((result) => {
        if (result.isConfirmed) {
          popPantallaEspera.fire({
            title: "Cargando pantalla de espera en los dispositivos",
            icon: "info",
            showCancelButton: false,
            showConfirmButton: false,
            allowOutsideClick: false,
            allowEscapeKey: false,
            allowEnterKey: false,
          });

          setTimeout(() => {
            pantallaDeEsperaActivar(codigoAct, (resp) => {
              if (resp) {
                Swal.fire({
                  position: "top-end",
                  icon: "success",
                  title: "Pantalla de espera activadas",
                  showConfirmButton: false,
                  timer: 2500,
                });
              } else {
                Swal.fire(
                  "Error al desactivar los dispositivos",
                  "Ha ocurrido un error al desactivar los dispositivos de argumentapp, intentelo nuevamente.",
                  "error"
                );
              }
            });
          }, 1000);
        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
          let timerInterval;

          popPantallaEspera.fire({
            title: "Actividad continuada",
            timer: 1500,
            timerProgressBar: true,

            willClose: () => {
              clearInterval(timerInterval);
            },
          });
        }
      });
  };
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorElAcciones, setAnchorElAcciones] = React.useState(null);
  const [isOpenRespuestas, setisOpenRespuestas] = useState(false);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickAcciones = (event) => {
    setAnchorElAcciones(event.currentTarget);
  };

  const handleCloseAcciones = () => {
    setAnchorElAcciones(null);
  };

  const openPopOverPizarra = Boolean(anchorEl);
  const openPopOverAcciones = Boolean(anchorElAcciones);
  const id = openPopOverPizarra ? "simple-popover" : undefined;

  return (
    <div style={{ marginTop: "-40px", marginBottom: 5, overflowX: "auto" }}>
      <div>
        <Dialog
          fullScreen
          open={isOpenRespuestas}
          onClose={(e) => {
            setisOpenRespuestas(false);
          }}
        >
          <AppBar sx={{ position: "relative" }}>
            <Toolbar>
              <IconButton
                edge="start"
                color="inherit"
                onClick={(e) => {
                  setisOpenRespuestas(false);
                }}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
              <Typography
                sx={{ ml: 2, flex: 1 }}
                variant="h5"
                style={{ color: "white" }}
              >
                Respuestas
              </Typography>
              <Button
                style={{ backgroundColor: "white" }}
                variant="outlined"
                onClick={(e) => {
                  setisOpenRespuestas(false);
                }}
              >
                Cerrar
              </Button>
            </Toolbar>
          </AppBar>
          <MainRespuestasEstudiantes
            codigo={codigoAct}
            dataActividades={dataActividades}
            idClase={idClase}
          ></MainRespuestasEstudiantes>
        </Dialog>
        <div
          style={{
            display: "flex",
          }}
        >
          <div
            style={{
              width: "100%",
            }}
          >
            <div
              style={{
                marginTop: 0,
                backgroundColor: "transparent",
                marginBottom: 20,
                display: "flex",
                flexDirection: "row",
                justifyContent: "center",
              }}
            >
              <Button
                onClick={(e) => {
                  terminarActividad();
                }}
                variant={"outlined"}
                style={{ width: 220, backgroundColor: "white" }}
              >
                Terminar
              </Button>
              <Button
                aria-describedby={"box"}
                variant="contained"
                onClick={handleClickAcciones}
                style={{ marginLeft: 15, width: 220 }}
              >
                Acciones
              </Button>
              <Popover
                id={id}
                open={openPopOverAcciones}
                anchorEl={anchorElAcciones}
                onClose={handleCloseAcciones}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    margin: 10,
                    backgroundColor: "transparent",
                    width: 200,
                  }}
                >
                  {isPantallaEspera === false && (
                    <Button
                      onClick={(e) => {
                        setPantallaDefault(codigoAct, (call) => {
                          if (call) {
                            Swal.fire({
                              position: "top-end",
                              icon: "success",
                              title: "Pantalla predeterminada activada",
                              showConfirmButton: false,
                              timer: 1500,
                            });
                          } else {
                            Swal.fire({
                              position: "top-end",
                              icon: "error",
                              title: "Pantalla no activada",
                              showConfirmButton: false,
                              timer: 1500,
                            });
                          }
                        });
                      }}
                      variant={"outlined"}
                      style={{ marginBottom: 5 }}
                    >
                      Activar Pantalla predeterminada
                    </Button>
                  )}
                  {isPantallaEspera === true ? (
                    <Button
                      onClick={(e) => {
                        pantallaEspera();
                      }}
                      variant={"outlined"}
                      style={{ marginBottom: 5 }}
                    >
                      Desactivar pantalla de espera estudiantes
                    </Button>
                  ) : (
                    <Button
                      onClick={(e) => {
                        activarPantallaEspera();
                      }}
                      style={{ marginBottom: 5 }}
                      variant={"outlined"}
                      disabled={isPantallaEspera === "esperandoData" && true}
                    >
                      Activar pantalla de espera estudiantes
                    </Button>
                  )}
                  <Button
                    onClick={(e) => {
                      setAnchorElAcciones(null);
                      setisOpenRespuestas(true);
                    }}
                    variant="outlined"
                  >
                    Ver Respuestas de Estudiantes
                  </Button>
                </div>
              </Popover>
              <Button
                aria-describedby={"box"}
                variant="contained"
                onClick={handleClick}
                style={{ marginLeft: 15, width: 220 }}
              >
                Pizarra
              </Button>
              <Popover
                id={id}
                open={openPopOverPizarra}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    margin: 10,
                    backgroundColor: "transparent",
                    width: 200,
                  }}
                >
                  <Button
                    onClick={(e) => {
                      let a = document.createElement("a");
                      a.target = "_blank";
                      a.href =
                        "/actividad/" +
                        codigoAct +
                        "/clase/" +
                        idClase +
                        "/pizarra";
                      a.click();
                    }}
                    variant={"outlined"}
                    style={{ marginBottom: 5 }}
                  >
                    Ver contenido de la pizarra
                  </Button>
                </div>
              </Popover>
            </div>

            <Paper
              style={{
                minHeight: "400px",
                backgroundColor: "transparent",
                boxShadow: "none",
              }}
            >
              <DiapositivaActivaProfesor
                codigo={codigoAct}
                dataActividades={dataActividades}
                idClase={idClase}
                data={data}
              ></DiapositivaActivaProfesor>
            </Paper>
          </div>
          <div style={{marginTop: 50}}>
            <SidebarDiapositivas
              codigo={codigoAct}
              dataActividades={dataActividades}
              idClase={idClase}
            ></SidebarDiapositivas>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MainProfesorActOnline;
