import React from "react";

import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { Box, Button, Typography } from "@mui/material";

import logo from "../../../assets/images/logo-argumentapp-c.svg";
import backImage from "../../../assets/images/login_background.png";
import RegistrarUsuario from "./RegistrarUsuario";
import ListarUsuarios from "./ListarUsuarios";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}


function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}


export default function MainAdminUsuario() {

  const [value, setValue] = React.useState(0);


  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div
      style={{
        height: "100%",
        minHeight: "100vh",
        minWidth: "fit-content",
        width: "100%",
        backgroundColor: "black",
        backgroundImage: `url(${backImage})`,
        backgroundSize: "repeat",
      }}
    >
      <div
        style={{
          width: "fit-content",
          position: "fixed",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
        }}
      >
        <div
          style={{
            backgroundColor: "white",
            padding: "50px 50px",
            textAlign: "center",
            borderRadius: 25,
          }}
        >
          <img
            alt="logoArgumentapp"
            src={logo}
            style={{ width: "-webkit-fill-available", maxWidth: "450px" }}
          />
          <Typography variant="h6" color="black">
            Admistrador de usuarios
          </Typography>

          <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
            <Tabs
              value={value}
              onChange={handleChange}
              aria-label="basic tabs example"
            >
              <Tab label="Registrar usuario" {...a11yProps(0)} />
              <Tab label="Usuarios registrados" {...a11yProps(1)} />
            </Tabs>
          </Box>
          <TabPanel value={value} index={0}>
            <RegistrarUsuario></RegistrarUsuario>
          </TabPanel>
          <TabPanel value={value} index={1}>
            <ListarUsuarios></ListarUsuarios>
          </TabPanel>
          <Button href="/" style={{ backgroundColor: "green", color: "white" }}>
            Volver al Home
          </Button>
        </div>
      </div>
    </div>
  );
}
