import React, { useState } from "react";

import { Alert, Button, InputLabel, MenuItem, Select } from "@mui/material";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import { registrarUsuarios } from "../../../database/firebase";

function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function RegistrarUsuario() {
  const [nombre, setNombre] = useState("");
  const [correo, setCorreo] = useState("");
  const [password, setPassword] = useState("");
  const [permisos, setPermisos] = useState(undefined);

  const [mensaje, setMensaje] = useState("")

  const [banderaCrearActividad, setBandera] = useState(false);
  const [nuevoRegistro, setBanderaRegistro] = useState(false);

  const crearActividad = () => {
    registrarUsuarios(nombre, correo, password, permisos, (val) => {
      if (val === true) {
        setBanderaRegistro(true);
        setNombre("");
        setCorreo("");
        setPassword("");
        setPermisos(undefined);
        setTimeout(
          function () {
            setBanderaRegistro(false);
          },
          5000
        );
      } else {
        if (val === "auth/weak-password") {
          setMensaje(
            "La contraseña debe tener minimo 6 caracteres"
          );
        }
       
        setBandera(true);
        setPermisos(undefined);
        setTimeout(
          function () {
            setBandera(false);
          },
          5000
        );
      }
    });
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        "& .MuiTextField-root": { width: "auto" },
      }}
      style={{ width: "350px" }}
    >
      {banderaCrearActividad && <Alert severity="error">{mensaje}</Alert>}
      {nuevoRegistro && (
        <Alert severity="success">
          Usuario agregado con exito a Argumentapp
        </Alert>
      )}

      <RedBar />
      <TextField
        label={"Nombre"}
        required
        value={nombre}
        id="nombre"
        margin="normal"
        onChange={(e) => {
          setNombre(e.target.value);
        }}
      />
      <TextField
        label={"Correo"}
        required
        value={correo}
        id="correo"
        margin="normal"
        onChange={(e) => {
          setCorreo(e.target.value);
        }}
      />
      <TextField
        type="password"
        required
        value={password}
        label={"Contraseña"}
        id="contrasena"
        margin="normal"
        onChange={(e) => {
          setPassword(e.target.value);
        }}
      />
      <InputLabel style={{ textAlign: "start", marginBottom: 2 }}>
        Permisos
      </InputLabel>
      <Select
        labelId="permisos"
        id="permisos"
        label="Permisos"
        required
        onChange={(e) => {
          setPermisos(e.target.value);
        }}
        style={{ textAlign: "start" }}
      >
        <MenuItem value={2}>Profesor</MenuItem>
        <MenuItem value={0}>Administrador</MenuItem>
        <MenuItem value={1}>Moderador</MenuItem>
      </Select>
      <RedBar />
      <Button
        variant="contained"
        style={{ backgroundColor: "rgb(0 102 255)", color: "white" }}
        onClick={(e) => {
          crearActividad();
        }}
      >
        Registrar
      </Button>
    </Box>
  );
}

export default RegistrarUsuario;
