import { Box, Button, Modal, TextField, Typography } from "@mui/material";
import React, { useCallback, useMemo, useState } from "react";
import backImage from "../../../assets/images/login_background.png";
import { CustomIconButton } from "../../../styles/ArgumentappTheme";
import { setActividadOnCursoPublico } from "../FirebaseVistaClase";
import AddIcon from "@mui/icons-material/Add";
import { UserContext } from "../../Funcionalidades/UseContext";
import BoxCrearActividad from "../BoxCrearActividad";

function RedBar() {
  return <Box sx={{ height: 20 }} />;
}
function CrearActividadCursosPublicos({ idCurso, idClase }) {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleClose = useCallback(() => {
    setOpen(false);
  });
  const providerValue = useMemo(
    () => ({
      handleClose,
    }),
    [handleClose]
  );

  // const subirDatos = () => {
  //     if (nombreActividad !== "") {
  //         setActividadOnCursoPublico(
  //             idCurso,
  //             idClase,
  //             nombreActividad,
  //             (data) => {
  //                 if (data) {
  //                     window.location.reload(true);
  //                 } else {
  //                     console.error("error");
  //                 }
  //             }
  //         );
  //     }
  // };

  return (
    <UserContext.Provider value={providerValue}>
      <div>
        <CustomIconButton onClick={handleOpen} title={"Agregar Actividad"}>
          <AddIcon style={{ color: "#fff" }} />
        </CustomIconButton>
        <Modal
          open={open}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
            <BoxCrearActividad tipoCurso={'publicos'}></BoxCrearActividad>
        </Modal>
      </div>
    </UserContext.Provider>
  );
}

export default CrearActividadCursosPublicos;
