import {
  Box,
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Grow,
  Typography,
} from "@mui/material";
import React from "react";
import iconDefault from "../../../../assets/images/default_icon_clases.png";
import { useHistory } from "react-router-dom";

function CardClasesActividadesOnline({ clases, codigo }) {
  const history = useHistory();

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
      }}
    >
      {clases.map((claseInfo, index) => {
        return (
          <Grow
            in={true}
            style={{ transformOrigin: "0 0 0" }}
            timeout={400 * (index + 1)}
            key={index}
          >
      
            <Card
              elevation={"4"}
              sx={{
                borderWidth: 1,
                marginRight: 5,
                marginTop: 3,
                marginBottom: 3,
                minHeight: 180,
                maxWidth: 260,
                borderRadius: "30px",
              }}
            >
              <CardActionArea
                onClick={() => {
                  history.push(
                    "/actividad/" + codigo + "/clase/" + claseInfo.key,
                    {
                      indexUnidad: 1,
                      indexClase: index + 1,
                      dataClase: claseInfo.data,
                    }
                  );
                }}
              >
                <Box
                  display={{
                    display: "flex",
                    padding: 16
                  }}
                >
                  <CardMedia
                    component="img"
                    sx={{ width: 110, borderRadius: "18px", height: 150 }}
                    image={
                      claseInfo.data.infoClase.imagen === null ||
                      claseInfo.data.infoClase.imagen === undefined
                        ? iconDefault
                        : claseInfo.data.infoClase.imagen
                    }
                    alt={
                      "Miniatura clases " + claseInfo.data.infoClase.nombreclase
                    }
                  />
                  <CardContent sx={{ flex: "1 0 auto", paddingRight: 0 }}>
                    <Typography variant="h5" style={{ fontSize: "18px" }}>
                      Clase {index + 1}
                    </Typography>

                    <Typography
                      variant="subtitle1"
                      color="text.secondary"
                      style={{
                        maxWidth: 110,
                        wordWrap: "break-word",
                        overflowWrap: "break-word",
                        hyphens: "auto",
                      }}
                    >
                      <p style={{ fontSize: "14px", margin: 0, padding: 0 }}>
                        <b>{claseInfo.data.infoClase.nombreclase}</b>
                      </p>
                    </Typography>
                  </CardContent>
                </Box>
              </CardActionArea>
            </Card>
          </Grow>
        );
      })}
    </div>
  );
}

export default CardClasesActividadesOnline;
