import {
  Button,
  Slide,
  Typography,
  Box,
  CardMedia,
  CircularProgress,
  Paper,
  IconButton,
  Modal,
} from "@mui/material";
import React, { useEffect, useMemo, useState } from "react";
import { useHistory } from "react-router-dom";
import ShowMoreText from "react-show-more-text";
import firebaseConf, { cerrarSesion } from "../../../database/firebase";
import { UserContext } from "../../Funcionalidades/UseContext";
import CrearActividadCursosPublicos from "../../VistaUnitariaClase/ModuloCursosPublicos/CrearActividadCursosPublicos";
import iconDefault from "../../../assets/images/default_icon_clases.png";
import DeleteActividad from "../../Diapositivas/DeleteActividad";
import OrdernarDiapositivas from "../../Diapositivas/OrdernarDiapositivas";
import ContenidoDiapositiva from "../../VistaUnitariaClase/ContenidoActividad/ContenidoDiapositiva";
import { getActividadesCursosPublicos } from "../../VistaUnitariaClase/FirebaseVistaClase";
import TabDiapositivasPlanificaciones from "../../VistaUnitariaClase/TabDiapositivasPlanificaciones";
import ListarClasesSide from "../../VistaUnitariaClase/ListarClasesSide";
import MainDiapositivas from "../../Diapositivas/MainDiapositivas";
import BotonEditarDiapoPublic from "../../../pages/VistaUnitariaClase/ModuloCursosPublicos/Componentes/BotonEditarDiapoPublic";
import { CustomIconButton } from "../../../styles/ArgumentappTheme";
import { ArrowBackIosNew } from "@mui/icons-material";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import LoadingArgumentapp from "../../../components/LoadingArgumentapp";
import {
  existDiapositiva,
  getDiapositivaByOrden,
} from "../../Diapositivas/FirebaseDiapositivas";


function MainVistaUnidadesArgumentapp(props) {
  const [nombreCurso, setNombreCurso] = useState("");
  const [nombreClase, setNombreClase] = useState("");
  const [idCurso, setIdCurso] = useState("");
  const [idClase, setIdClase] = useState("");
  const [banderaGetActividad, setBanderaGetActividad] = useState(true);
  const [tipoCurso, setTipoCurso] = useState("");
  const [indexUnidad, setindexUnidad] = useState(0);
  const [dataClase, setdataClase] = useState([]);
  const [indexClase, setindexClase] = useState("");

  const [banderaSlide, setbanderaSlide] = useState(false);
  const [banderaSlideContenidoDiapo, setbanderaSlideContenidoDiapo] =
    useState(true);
  const [banderaContenidoDiapositiva, setBanderaContenidoDiapositiva] =
    useState(false);
  const [tipoSlide, settipoSlide] = useState("left");
  const history = useHistory();

  const [actividades, setActividades] = useState([]);

  const [contenidoHTML, setContenidoHTML] = useState("");
  const [acciones, setAcciones] = useState([]);
  const [banderaDialogEdit, setBanderaDialogEdit] = useState(false);
  const [nombreDiapositiva, setNombreDiapositiva] = useState("");
  const [orden, setorden] = useState(null);
  const [idDiapositiva, setIdDiapositiva] = useState("");
  const [tipoVista, settipoVista] = useState("");
  const [keyActividad, setkeyActividad] = useState("");
  const [tipoSlideDiapositiva, settipoSlideDiapositiva] = useState("left");
  const [selectorEditor, setselectorEditor] = useState(null);

  const [initialPage, setinitialPage] = useState(true);

  const [existSiguiente, setexistSiguiente] = useState("waiting");
  const [changeDiapositiva, setChangeDiapositiva] = useState(false);
  const [direccionSalidaDiapo, setdireccionSalidaDiapo] = useState("left");
  const providerValue = useMemo(
    () => ({
      banderaContenidoDiapositiva,
      setBanderaContenidoDiapositiva,
      settipoSlide,
      setbanderaSlide,
      setbanderaSlideContenidoDiapo,
      contenidoHTML,
      setContenidoHTML,
      acciones,
      setAcciones,
      settipoVista,
      banderaDialogEdit,
      setBanderaDialogEdit,
      nombreDiapositiva,
      setNombreDiapositiva,
      orden,
      setorden,
      idDiapositiva,
      setIdDiapositiva,
      setkeyActividad,
      settipoSlideDiapositiva,
      setselectorEditor,
      setNombreCurso,
      setNombreClase,
      setIdCurso,
      setIdClase,
      setTipoCurso,
      setindexUnidad,
      setdataClase,
      setindexClase,
    }),
    [
      banderaContenidoDiapositiva,
      setBanderaContenidoDiapositiva,
      settipoSlide,
      setbanderaSlide,
      setbanderaSlideContenidoDiapo,
      contenidoHTML,
      setContenidoHTML,
      acciones,
      setAcciones,
      settipoVista,
      banderaDialogEdit,
      setBanderaDialogEdit,
      nombreDiapositiva,
      setNombreDiapositiva,
      orden,
      setorden,
      idDiapositiva,
      setIdDiapositiva,
      setkeyActividad,
      settipoSlideDiapositiva,
      setselectorEditor,
      setNombreCurso,
      setNombreClase,
      setIdCurso,
      setIdClase,
      setTipoCurso,
      setindexUnidad,
      setdataClase,
      setindexClase,
    ]
  );
  useEffect(() => {
    try {
      if (props.location.state.isSuccessful === true) {
        setNombreCurso(props.location.state.nombreCurso);
        setNombreClase(props.location.state.nombreClase);
        setIdCurso(props.location.state.idCurso);
        setIdClase(props.location.state.idClase);
        setTipoCurso(props.location.state.tipoCurso);
        setindexUnidad(props.location.state.indexUnidad);
        setdataClase(props.location.state.dataClase);
        setindexClase(props.location.state.indexClase);

        firebaseConf.auth().onAuthStateChanged((user) => {
          if (user) {
            getActividadesCursosPublicos(
              props.location.state.idCurso,
              props.location.state.idClase,
              (value) => {
                setActividades(value);
                setBanderaGetActividad(false);
                setbanderaSlide(true);
                setinitialPage(false);
              }
            );
          } else {
            cerrarSesion();
          }
        });
      } else {
        window.location = "/";
      }
    } catch (error) {
      window.location = "/";
    }
    // eslint-disable-next-line
  }, []);
  useEffect(() => {
    if (!initialPage) {
      setBanderaGetActividad(true);
      setbanderaSlide(false);
      firebaseConf.auth().onAuthStateChanged((user) => {
        if (user) {
          //cambiar dependiendo de el tipo de curso

          getActividadesCursosPublicos(idCurso, idClase, (value) => {
            setActividades(value);
            setBanderaGetActividad(false);
            setbanderaSlide(true);
          });
        } else {
          cerrarSesion();
        }
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [indexClase]);

  useEffect(() => {
    if (banderaContenidoDiapositiva) {
      existDiapositiva(
        "data",
        "publicos",
        idCurso,
        idClase,
        keyActividad,
        tipoVista,
        Number(orden) + 1,
        (data) => {
          console.log(data);
          setexistSiguiente(data);
        }
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [banderaContenidoDiapositiva]);

  const existDiapositivaLocal = (ordenIn) => {
    existDiapositiva(
      "data",
      "publicos",
      idCurso,
      idClase,
      keyActividad,
      tipoVista,
      Number(ordenIn) + 1,
      (data) => {
        setexistSiguiente(data);
      }
    );
  };

  const changeDiapo = (numDiapo, salidaSlide) => {
    setChangeDiapositiva(true);
    setTimeout(() => {
      getDiapositivaByOrden(
        "data",
        "publicos",
        idCurso,
        idClase,
        keyActividad,
        tipoVista,
        numDiapo,
        (dataResult) => {
          if (dataResult !== false) {
            existDiapositivaLocal(dataResult.datos.orden);
            setContenidoHTML(dataResult.contenidoHTML);
            setAcciones(dataResult.datos.acciones);
            setNombreDiapositiva(dataResult.datos.nombre);
            setorden(dataResult.datos.orden);
            setIdDiapositiva(dataResult.key);
            setTimeout(() => {
              setdireccionSalidaDiapo(salidaSlide);
              setChangeDiapositiva(false);
              setbanderaSlideContenidoDiapo(true);
            }, 250);
          } else {
          }
        }
      );
    }, 250);
  };

  return (
    <div className="main-container">
      <UserContext.Provider value={providerValue}>
        <div
          style={{
            width: "100%",
            overflowX: "hidden",
          }}
        >
          <Typography variant="titulo">Contenido Argumentapp</Typography>
          <div className="contenido" style={{ marginTop: 15 }}>
            {!banderaContenidoDiapositiva ? (
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  width: "inherit",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <CustomIconButton
                    onClick={(e) => {
                      settipoSlide("left");
                      setbanderaSlide(false);

                      //cambiar dependiendo de el tipo de curso

                      setTimeout(() => {
                        history.push("/contenidoargumentapp", {
                          idCurso: idCurso,
                          nombreCurso: nombreCurso,
                          isBack: true,
                          tipoCurso: tipoCurso,
                          tipoSlide: "right",
                          indexUnidad: indexUnidad,
                          estado: props.location.state.estado,
                        });
                      }, 50);
                    }}
                    style={{
                      marginTop: 30,
                      marginBottom: 20,
                    }}
                  >
                    <ArrowBackIosNew style={{ color: "#fff" }} />
                  </CustomIconButton>

                  {!props.location.state.estado && (
                    <CrearActividadCursosPublicos
                      idClase={idClase}
                      idCurso={idCurso}
                    />
                  )}
                </div>
                <Slide
                  direction={tipoSlide}
                  in={banderaSlide}
                  mountOnEnter
                  unmountOnExit
                >
                  <div
                    style={{
                      width: "-webkit-fill-available",
                      display: "flex",
                      flexDirection: "column",
                      padding: 20,
                      paddingLeft: 20,
                    }}
                  >
                    <div
                      style={{
                        marginBottom: 30,
                      }}
                    >
                      <div>
                        <Typography variant="tituloClase">
                          <b>Unidad {indexUnidad}</b>
                        </Typography>
                        <Typography
                          variant="tituloClase"
                          style={{ marginLeft: 20 }}
                        >
                          {nombreCurso}
                        </Typography>
                      </div>
                      <div>
                        <Typography>
                          Ver recursos de apoyo de esta unidad
                        </Typography>
                      </div>
                    </div>
                    <Paper tipo="general" elevation={"1"}>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                        }}
                      >
                        <div style={{ marginRight: 30 }}>
                          <CardMedia
                            component="img"
                            sx={{
                              height: 220,
                              width: 160,
                              borderRadius: "16px",
                            }}
                            image={
                              dataClase.imagen === null ||
                              dataClase.imagen === undefined
                                ? iconDefault
                                : dataClase.imagen
                            }
                            alt={"Miniatura clases " + nombreClase}
                          />
                        </div>
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "column",
                          }}
                        >
                          <Typography variant="textoClase">
                            <Typography variant="titulo_4">
                              Clase {indexClase}:
                            </Typography>{" "}
                            {nombreClase}
                          </Typography>
                          <Typography
                            variant="textoClase"
                            style={{
                              fontWeight: 400,
                              fontStyle: "italic",
                              fontSize: 14,
                              marginTop: 7,
                            }}
                          >
                            {actividades.length > 1
                              ? actividades.length + " actividades"
                              : actividades.length === 0
                              ? actividades.length + " actividades"
                              : actividades.length + " actividad"}
                          </Typography>
                          <div
                            style={{
                              display: "flex",
                              flexDirection: "column",
                              marginTop: 13,
                            }}
                          >
                            <div
                              style={{
                                fontSize: 14,
                              }}
                            >
                              <ShowMoreText
                                lines={3}
                                more="Ver más"
                                less="Ver menos"
                                expanded={false}
                                width={500}
                                truncatedEndingComponent={"... "}
                              >
                                <p>
                                  Objetivo de la clase: {dataClase.objetivo}
                                </p>
                              </ShowMoreText>{" "}
                            </div>
                            <div
                              style={{
                                marginTop: 11,
                                fontSize: 14,
                              }}
                            >
                              <ShowMoreText
                                lines={3}
                                more="Ver más"
                                less="Ver menos"
                                expanded={false}
                                width={500}
                                truncatedEndingComponent={"... "}
                              >
                                <p>
                                  Profundización Temática: {dataClase.tematica}
                                </p>
                              </ShowMoreText>
                            </div>
                          </div>
                        </div>
                      </div>

                      {!banderaGetActividad ? (
                        <div>
                          {actividades.length > 0 ? (
                            <div
                              style={{
                                marginTop: 10,
                                marginLeft: 10,
                              }}
                            >
                              {actividades.map((actividad, index) => {
                                return (
                                  <div key={index}>
                                    <div
                                      style={{
                                        display: "flex",
                                        flexDirection: "row",
                                        padding: 15,
                                        paddingLeft: 0,
                                        paddingBottom: 0,
                                      }}
                                    >
                                      <Typography variant="TituloActividadClase">
                                        {actividad.data.nombreActividad}
                                      </Typography>
                                      {!props.location.state.estado && (
                                        <div
                                          style={{
                                            display: "flex",
                                            flexDirection: "row",
                                            alignContent: "flex-start",
                                          }}
                                        >
                                          {/* //cambiar dependiendo de el tipo de curso */}

                                          <DeleteActividad
                                            actividad={actividad.key}
                                            idClase={idClase}
                                            idCurso={idCurso}
                                            state={"publicos"}
                                            uid={"data"}
                                            nombreActividad={
                                              actividad.data.nombreActividad
                                            }
                                          ></DeleteActividad>
                                          <OrdernarDiapositivas
                                            nombreActividad={
                                              actividad.data.nombreActividad
                                            }
                                            actividad={actividad.key}
                                            uid={"data"}
                                            idClase={idClase}
                                            idCurso={idCurso}
                                            tipoCurso={"publicos"}
                                            tipoVista={"profesor"}
                                          ></OrdernarDiapositivas>

                                          {/* //cambiar dependiendo de el tipo de curso */}
                                        </div>
                                      )}
                                    </div>
                                    {/* //cambiar dependiendo de el tipo de curso */}

                                    <TabDiapositivasPlanificaciones
                                      idClase={idClase}
                                      idCurso={idCurso}
                                      keyActividad={actividad.key}
                                      uid={"data"}
                                      key={index}
                                      isContenidoArgumentapp={true}
                                      estadoContendoArgumentapp={
                                        props.location.state.estado
                                      }
                                    ></TabDiapositivasPlanificaciones>
                                    {/* //cambiar dependiendo de el tipo de curso */}
                                  </div>
                                );
                              })}
                            </div>
                          ) : (
                            <div
                              style={{
                                marginLeft: 30,
                                marginRight: 30,
                                marginBottom: 80,
                                textAlign: "center",
                                height: "100px",
                              }}
                            >
                              <Box
                                sx={{
                                  width: "auto",
                                  padding: 10,
                                }}
                              >
                                <p>No registra actividad</p>
                              </Box>
                            </div>
                          )}
                        </div>
                      ) : (
                        <div
                          style={{
                            marginLeft: 30,
                            marginRight: 30,
                            marginTop: 80,
                            marginBottom: 80,
                            textAlign: "center",
                            height: "100px",
                          }}
                        >
                          <Box
                            sx={{
                              width: "auto",
                              padding: 10,
                            }}
                          >
                            <CircularProgress color="inherit" />
                          </Box>
                        </div>
                      )}
                    </Paper>
                  </div>
                </Slide>
                {!banderaGetActividad && banderaSlide && (
                  <div
                    style={{
                      width: "320px",
                    }}
                  >
                    {/* //cambiar dependiendo de el tipo de curso */}

                    <ListarClasesSide
                      id={idCurso}
                      idClase={idClase}
                      isContenidoArgumentapp={true}
                    ></ListarClasesSide>

                    {/* //cambiar dependiendo de el tipo de curso */}
                  </div>
                )}
              </div>
            ) : (
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  width: "inherit",
                }}
              >
                <Button
                  onClick={(e) => {
                    setdireccionSalidaDiapo('left');
                    setbanderaSlideContenidoDiapo(false);
                    setBanderaDialogEdit(false);
                    setContenidoHTML("");
                    setAcciones("");
                    setNombreDiapositiva("");
                    setorden("");
                    setIdDiapositiva("");
                    setselectorEditor(null);
                    setTimeout(() => {
                      setBanderaContenidoDiapositiva(false);
                      setbanderaSlide(true);
                    }, 150);
                  }}
                  style={{
                    boxShadow: "none",
                    minWidth: 40,
                    maxWidth: 40,
                    padding: 0,
                    minHeight: 40,
                    maxHeight: 40,
                    marginBottom: 30,
                    marginTop: 23,
                    marginRight: 30,
                  }}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="9.002"
                    height="15.002"
                    viewBox="0 0 9.002 15.002"
                    style={{
                      transform: "rotate(180deg)",
                      zoom: 2,
                    }}
                  >
                    <path
                      id="chevron-right"
                      d="M4.646,1.646a.5.5,0,0,1,.708,0l6,6a.5.5,0,0,1,0,.708l-6,6a.5.5,0,1,1-.708-.708L10.293,8,4.646,2.354a.5.5,0,0,1,0-.708Z"
                      transform="translate(-4.499 -1.499)"
                      fill="#6c3373"
                      fill-rule="evenodd"
                    />
                  </svg>
                </Button>
                {changeDiapositiva === true ? (
                  <div>
                    <Modal open={changeDiapositiva}>
                      <Box
                        style={{
                          position: "absolute",
                          top: "50%",
                          left: "50%",
                          transform: "translate(-50%, -50%)",
                          width: 275,
                          backgroundColor: "#fff",
                          borderRadius: 18,
                          boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
                          padding: 30,
                          borderStyle: "none",
                        }}
                      >
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "center",
                            alignItems: "center",
                            alignContent: "center",
                          }}
                        >
                          <LoadingArgumentapp></LoadingArgumentapp>
                          <Typography
                            id="modal-modal-title"
                            variant="h5"
                            component="h2"
                            style={{
                              textAlign: "center",
                              marginTop: 20,
                              fontWeight: 400,
                              letterSpacing: 3,
                            }}
                          >
                            Cambiando Diapositiva. . .
                          </Typography>
                        </div>
                      </Box>
                    </Modal>
                  </div>
                ) : (
                  <div
                    style={{
                      marginTop: 30,
                      width: "100%",
                      display: "flex",
                      flexDirection: "row",
                      justifyContent: "center",
                    }}
                  >
                    <div
                      style={{
                        marginRight: 30,
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignContent: "center",
                        height: "90%",
                      }}
                    >
                      <IconButton
                        style={{
                          backgroundColor: orden !== 0 ? "#9C5FA2" : "#B0B0B0",
                          minWidth: 40,
                          maxWidth: 40,
                          color: "#fff",
                        }}
                        disabled={orden !== 0 ? false : true}
                        onClick={(e) => {
                          setdireccionSalidaDiapo("left");
                          setbanderaSlideContenidoDiapo(false);
                          setTimeout(() => {
                            changeDiapo(orden - 1, "right");
                          }, 150);
                        }}
                      >
                        <ArrowBackIosIcon
                          style={{ marginLeft: 8 }}
                        ></ArrowBackIosIcon>
                      </IconButton>
                    </div>
                    <div>
                      {selectorEditor === "nuevaDiapositiva" ? (
                        <div
                          style={{
                            marginBottom: 30,
                            width: "fit-content",
                          }}
                        >
                          <MainDiapositivas
                            idCurso={idCurso}
                            idClase={idClase}
                            uid={"data"}
                            keyActividad={keyActividad}
                            tipoVista={tipoVista}
                            lugarAlmacenamiento={"publicos"}
                          ></MainDiapositivas>
                        </div>
                      ) : (
                        <Slide
                          direction={direccionSalidaDiapo}
                          in={banderaSlideContenidoDiapo}
                          mountOnEnter
                          unmountOnExit
                        >
                          <div>
                            <div
                              style={{
                                display: "flex",
                                flexDirection: "row",
                                justifyContent: "center",
                              }}
                            >
                              <div>
                                <ContenidoDiapositiva
                                  contenidoHTML={contenidoHTML}
                                  acciones={acciones}
                                ></ContenidoDiapositiva>
                                <div
                                  style={{
                                    marginTop: 15,
                                  }}
                                >
                                  <BotonEditarDiapoPublic
                                    idClase={idClase}
                                    idCurso={idCurso}
                                    keyActividad={keyActividad}
                                    tipoVista={tipoVista}
                                    nombreDiapositiva={nombreDiapositiva}
                                    acciones={acciones}
                                    contenidoHTML={contenidoHTML}
                                    orden={orden}
                                    idDiapositiva={idDiapositiva}
                                    state={!props.location.state.estado}
                                  ></BotonEditarDiapoPublic>
                                </div>
                              </div>
                            </div>
                          </div>
                        </Slide>
                      )}
                    </div>
                    <div
                      style={{
                        marginLeft: 30,
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignContent: "center",
                        height: "90%",
                      }}
                    >
                      <IconButton
                        style={{
                          backgroundColor:
                            existSiguiente === "waiting"
                              ? "#B0B0B0"
                              : existSiguiente === true
                              ? "#9C5FA2"
                              : "#B0B0B0",
                          minWidth: 40,
                          maxWidth: 40,
                          color: "#fff",
                        }}
                        disabled={
                          existSiguiente === "waiting"
                            ? true
                            : existSiguiente === false
                            ? true
                            : false
                        }
                        onClick={(e) => {
                          setdireccionSalidaDiapo("right");
                          setbanderaSlideContenidoDiapo(false);
                          setTimeout(() => {
                            changeDiapo(orden + 1, "left");
                          }, 150);
                        }}
                      >
                        <ArrowForwardIosIcon
                          style={{ marginLeft: 3 }}
                        ></ArrowForwardIosIcon>
                      </IconButton>
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </UserContext.Provider>
    </div>
  );
}

export default MainVistaUnidadesArgumentapp;
