import { Button, Grid, Modal, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useState } from "react";
import CaratulaDiapositiva from "../../../../../components/CaratulaDiapositiva";
import { getDiapositivasActOnline } from "../../../BDActividadesOnline";

function DiapositivasActOnline({
  idClase,
  codigo,
  idActividad,
  idVista,
  nameVista,
}) {
  const [diapositivas, setDiapositivas] = useState([]);

  useEffect(() => {
    getDiapositivasActOnline(codigo, idClase, idActividad, idVista, (diapo) => {
      setDiapositivas(diapo);
    });
  }, [codigo, idClase, idActividad, idVista]);

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    bgcolor: "transparent",
    boxShadow: 24,
    p: 0,
  };

  const [open, setOpen] = React.useState(false);
  const handleClose = () => setOpen(false);
  const [contenidoHTML, setContenidoHTML] = useState("");
  const [acciones, setAcciones] = useState("");

  return (
    <div>
      <h3 style={{ color: "black" }}>
        {/* {diapositivas.length !== 0 && nameVista} */}
      </h3>
      <Grid container justifyContent="start" spacing={2}>
        {diapositivas.map((value) => (
          <Grid key={value.key} item>
            <div>
              <Button
                style={{
                  minHeight: "215px",
                  maxHeight: "215px",
                  minWidth: "350px",
                  maxWidth: "350px",
                  marginBottom: "0px ",
                  boxShadow: "none",
                }}
                onClick={(e) => {
                  setContenidoHTML(value.contenidoHTML);
                  setAcciones(value.datos.acciones);
                  setOpen(true)
                }}
              >
                <CaratulaDiapositiva
                  htmlCode={value.contenidoHTML}
                  acciones={value.datos.acciones}
                  zoomValue={"27%"}
                ></CaratulaDiapositiva>
              </Button>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  flexWrap: "nowrap",
                  alignContent: "center",
                  justifyContent: "space-between",
                  alignItems: "center",
                  maxWidth: "300px",
                }}
              >
                <h4
                  style={{
                    color: "#707070",
                    textAlign: "start",
                    marginLeft: 0,
                  }}
                >
                  {value.datos.nombre}
                </h4>
              </div>
            </div>
          </Grid>
        ))}
      </Grid>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style}>
          <CaratulaDiapositiva
            htmlCode={contenidoHTML}
            acciones={acciones}
            zoomValue={"80%"}
          ></CaratulaDiapositiva>
        </Box>
      </Modal>
    </div>
  );
}

export default DiapositivasActOnline;
