import firebaseConf from "../../database/firebase";
import { generarKey } from "../Funcionalidades/generarClaveFirebase";

export const setActividadOnCurso = (
  uid,
  idCurso,
  idClase,
  nombreActividad,
  callback
) => {
  var database = firebaseConf.database();
  var nombreKey = generarKey(nombreActividad);

  database
    .ref(
      "/cursos/privados/" +
        uid +
        "/" +
        idCurso +
        "/" +
        idClase +
        "/lista_actividad/" +
        nombreKey
    )
    .set({
      nombreActividad: nombreActividad,
      fechaCreacion: Date.now(),
    })
    .then(() => {
      callback(true);
    })
    .catch(() => {
      callback(false);
    });
};

export const getActividades = (uid, idCurso, idClase, callback) => {
  var database = firebaseConf.database();
  var arrayValue = [];
  database
    .ref(
      "/cursos/privados/" +
        uid +
        "/" +
        idCurso +
        "/" +
        idClase +
        "/lista_actividad"
    )
    .orderByChild("fechaCreacion")
    .once("value", (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        var childKey = childSnapshot.key;
        var childData = childSnapshot.val();
        arrayValue.push({ data: childData, key: childKey });
      });
    })
    .then(() => {
      callback(arrayValue);
    })
    .catch(() => {
      callback(false);
    });
};

export const delActividad = (
  uid,
  idCurso,
  idClase,
  keyIdActividad,
  tipoCurso,
  callback
) => {
  let vistas = ["profesor", "alumnos", "pizarra"];
  var contador = 0;

  var database = firebaseConf
    .database()
    .ref("/cursos/" + tipoCurso + "/" + uid + "/" + idCurso + "/" + idClase);
  let ruta = firebaseConf
    .firestore()
    .collection("cursos")
    .doc(tipoCurso)
    .collection(uid)
    .doc(idCurso)
    .collection("clases")
    .doc(idClase)
    .collection("actividad")
    .doc(keyIdActividad);

  database
    .child("lista_actividad")
    .child(keyIdActividad)
    .remove()
    .then(() => {
      database
        .child(keyIdActividad)
        .remove()
        .then(() => {
          for (let index = 0; index < vistas.length; index++) {
            // eslint-disable-next-line no-loop-func
            ruta.collection(vistas[index]).onSnapshot((diapositivas) => {
              diapositivas.forEach((contDiapo) => {
                contDiapo.ref.delete();
              });
              contador = contador + 1;
              if (contador === vistas.length) {
                ruta.delete().then(() => {
                  window.location.reload();
                });
              }
            });
          }
        })
        .catch(() => {
          callback(true);
        });
    })
    .catch(() => {
      callback(true);
    });
};

