export const setActividadOnCursoPublico = (
  idCurso,
  idClase,
  nombreActividad,
  callback
) => {
  var database = firebaseConf.database();
  var nombreKey = generarKey(nombreActividad);

  database
    .ref(
      "/cursos/publicos/data/" +
        idCurso +
        "/" +
        idClase +
        "/lista_actividad/" +
        nombreKey
    )
    .set({
      nombreActividad: nombreActividad,
      fechaCreacion: Date.now(),
    })
    .then(() => {
      callback(true);
    })
    .catch(() => {
      callback(false);
    });
};

