import firebaseConf from "../../../../database/firebase";
import { generarKey } from "../../../Funcionalidades/generarClaveFirebase";

export const getFileMap = (cb) => {
    const ref = firebaseConf.database().ref("pedagogicos");

    ref.on("value", (snapshot) => {
        cb(snapshot.val());
    });
};

export const subirFileMap = (fileMap, cb) => {
    firebaseConf.database().ref("pedagogicos/fileMap").set(fileMap);
};

export const subirDocumento = (
    documento,
    imagen,
    cb
) => {
    const splitDocumento = documento.name.split(".")
    const extDocumento = splitDocumento[splitDocumento.length-1]
    const splitImagen = imagen.name.split(".")
    const extImagen = splitImagen[splitImagen.length-1]
    const id = generarKey(documento.name) + new Date().getTime()
    const idImagen = generarKey(imagen.name) + new Date().getTime()
    const refDocumento = "pedagogicos/documentos/" + id + "." + extDocumento
    const refImagen = "pedagogicos/imagenes/" + idImagen + "." + extImagen

    firebaseConf
        .storage()
        .ref(refDocumento)
        .put(documento.file)
        .on(
        "state_changed",
        (snapshot) => {
            // var temp =
            // (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            //porcentaje(temp);
        },
        (error) => {
            console.log("error " + error);
            alert("Error al subir los datos " + error);
        },
        () => {

            let urlDocumento = ""

            firebaseConf
                .storage()
                .ref(refDocumento)
                .getDownloadURL()
                .then((url) => {
                    urlDocumento = url
                })
                .catch(() => cb(false));            

            firebaseConf
                .storage()
                .ref(refImagen)
                .put(imagen.file)
                .on(
                "state_changed",
                (snapshot) => {
                    // var temp =
                    // (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                    //porcentaje(temp);
                },
                (error) => {
                    console.log("error " + error);
                    alert("Error al subir los datos " + error);
                },
                () => {
                    firebaseConf
                        .storage()
                        .ref(refImagen)
                        .getDownloadURL()
                        .then((url) => {
                            cb({urlDocumento: urlDocumento, urlImagen: url});
                        })
                        .catch(() => cb(false));
                }
                );            
            }
        );
}
