import { Button, Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import parse from "html-react-parser";
import domtoimage from "dom-to-image";
import { setDisenoDiapo } from "./FirebaseDiseñoDiapositivas";
import { Box } from "@mui/material";
import {
  renderButtonList,
  renderCheckBoxList,
  renderTextArea,
  renderUploadPhoto,
} from "../../Funcionalidades/AccionesDiapositivas";
function RedBar() {
  return <Box sx={{ height: 10 }} />;
}

function VistaPreviaDiapo() {
  const {
    tipoVista,
    nombreDiseno,
    valueCheckbox,
    cantBotones,
    cantBotonesCheckbox,
    setisContinuar,
    contenidoHTML,
  } = useContext(UserContext);

  const [listBotones, setlistBotones] = useState([]);
  const [listCheckBox, setlistCheckBox] = useState([]);

  useEffect(() => {
    var listBotonestemp = [];
    for (let index = 0; index < cantBotones; index++) {
      listBotonestemp.push(index + 1);
    }
    setlistBotones(listBotonestemp);

    var listCheckBoxtemp = [];
    for (let index = 1; index <= cantBotonesCheckbox; index++) {
      listCheckBoxtemp.push(index);
    }
    setlistCheckBox(listCheckBoxtemp);
  }, [cantBotonesCheckbox, cantBotones]);

  const subirDatos = () => {
    var node = document.getElementById("my-node");

    domtoimage
      .toPng(node, { quality: 0.7 })
      .then(function (dataUrl) {
        var tipoAccion = null;
        var cantidadItem = null;
        if (valueCheckbox[0]) {
          tipoAccion = "Button";
          cantidadItem = cantBotones;
        }
        if (valueCheckbox[1]) {
          tipoAccion = "CheckBox";
          cantidadItem = cantBotonesCheckbox;
        }
        if (valueCheckbox[3]) {
          tipoAccion = "TextArea";
        }
        if (valueCheckbox[4]) {
          tipoAccion = "Photo";
        }
        setDisenoDiapo(
          tipoVista,
          nombreDiseno,
          tipoAccion,
          cantidadItem,
          contenidoHTML,
          dataUrl
        );
      })
      .catch(function (error) {
        console.error("oops, something went wrong!", error);
      });
  };

  return (
    <div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
          alignContent: "center",
          
          marginBottom: 0,
        }}
      >
        <Typography variant="tituloClase">
          Crear diseño de diapositiva en vista de {tipoVista}
        </Typography>
        <Button
          color="error"
          style={{
            backgroundColor: "red",
            color: "white",
            marginLeft: 40,
            height: "fit-content",
          }}
          onClick={(e) => {
            setisContinuar(false);
          }}
        >
          Volver
        </Button>
      </div>

      <div
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: 'center'
        }}
      >
        <Typography
          variant="tituloClase"
          style={{ marginTop: 20, marginBottom: 20 }}
        >
          Nombre del diseño: {nombreDiseno}{" "}
        </Typography>
        <div
          style={{
            borderStyle: "solid",
            borderRadius: 25,
            minWidth: 1200,
            minHeight: 700,
            maxWidth: 1200,
            maxHeight: 700,
            width: 1200,
            height: 700,
          }}
        >
          {" "}
          <div className="my-node" id="my-node">
            <div style={{}}>
              {" "}
              <div
                style={{
                  minWidth: 1200,
                  minHeight: 700,
                  maxWidth: 1200,
                  maxHeight: 700,
                  width: 1200,
                  height: 700,
                  padding: 50,
                }}
              >
                {parse(contenidoHTML)}
                {/**Vista previa antes de crear un diseño nuevo  */}

                {valueCheckbox[0] && renderButtonList(listBotones)}
                {valueCheckbox[1] && renderCheckBoxList(listCheckBox)}
                {valueCheckbox[3] && renderTextArea()}
                {valueCheckbox[4] && renderUploadPhoto()}
              </div>
            </div>
            <RedBar></RedBar>
          </div>
        </div>
      </div>

      <Button
        style={{
          backgroundColor: "blue",
          color: "white",
          marginTop: 10,
        }}
        onClick={(e) => {
          subirDatos();
        }}
      >
        Subir plantilla
      </Button>
    </div>
  );
}

export default VistaPreviaDiapo;
