import {
  Box,
  Card,
  CardActionArea,
  CardContent,
  Paper,
  Typography,
} from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import firebaseConf, { getClasesCursosPrivados } from "../../database/firebase";
import { getClasesCursosPublicos } from "../Cursos/Publicos/firebaseCursosPublicos";
import { UserContext } from "../Funcionalidades/UseContext";

function ListarClasesSide({ id, idClase, isContenidoArgumentapp }) {
  // const [uid, setUid] = useState(null);
  const [clases, setclases] = useState([]);
  const [banderaCarga, setBanderaCarga] = useState(false);

  const { setNombreClase, setIdClase, setdataClase, setindexClase } =
    useContext(UserContext);

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      if (user) {
        // setUid(user.uid);
        if (isContenidoArgumentapp) {
          getClasesCursosPublicos(id, (value) => {
            setclases(value.reverse());
            setBanderaCarga(true);
          });
        } else {
          getClasesCursosPrivados(user.uid, id, (value) => {
            setclases(value.reverse());
            setBanderaCarga(true);
          });
        }
      }
    }); // eslint-disable-next-line
  }, []);

  return (
    <div>
      <Paper
        elevation={"3"}
        style={{
          backgroundColor: "transparent",
          boxShadow: "none",
          padding: 10,
          marginTop: 100,
          overflow: "auto",
          maxHeight: "55vh",
        }}
      >
        <div>
          {!banderaCarga ? (
            <div></div>
          ) : (
            <div>
              {clases.map((value, index) => {
                return (
                  <Card
                    key={index}
                    onClick={(e) => {
                      setNombreClase(value.data.nombreclase);
                      setIdClase(value.key);
                      setdataClase(value.data);
                      setindexClase(index + 1);
                    }}
                    sx={{
                      display: "flex",
                      marginBottom: 1,
                      minHeight: 100,
                      maxHeight: 130,
                      minWidth: 230,
                      maxWidth: 230,
                      borderRadius: "25px",
                      backgroundColor: "#fff",
                      border:
                        idClase !== value.key
                          ? "none"
                          : "3px solid #6c3373",
                      boxShadow: " 0px 3px 6px rgba(0, 0, 0, 0.16)",
                    }}
                    elevation={"4"}
                  >
                    <CardActionArea>
                      <Box
                        sx={{
                          display: "flex",
                          flexDirection: "column",
                        }}
                      >
                        <CardContent
                          style={{
                            paddingTop: 1,
                          }}
                        >
                          <Typography
                            variant="h5"
                            style={{ fontSize: 22, color: "#9c5fa2", fontWeight: 600 }}
                          >
                            Clase {index + 1}
                          </Typography>
                          <Typography
                            color="text.secondary"
                            style={{
                              maxWidth: 200,
                              wordWrap: "break-word",
                              overflowWrap: "break-word",
                              hyphens: "auto",
                              fontSize: 13,
                              fontWeight: "normal",
                            }}
                          >
                            {value.data.nombreclase}
                          </Typography>
                        </CardContent>
                        {/* 

                    onClick={(e) => {
                      settipoSalida("right");
                      setBanderaCargaSlide(false);
                      setTimeout(() => {
                        history.push("/vistaclases", {
                          nombreCurso: nombreCurso,
                          nombreClase: value.data.nombreclase,
                          idCurso: id,
                          isSuccessful: true,
                          idClase: value.key,
                          tipoCurso: "privados",
                          indexUnidad: indexUnidad,
                          indexClase: index + 1,
                          dataClase: value.data,
                        });
                      }, 100);
                    }}
                  
                  */}
                      </Box>
                    </CardActionArea>
                  </Card>
                );
              })}
            </div>
          )}
        </div>
      </Paper>
    </div>
  );
}

export default ListarClasesSide;
