import {
    Box,
    Button,
    ButtonGroup,
    Card,
    CardActions,
    CardContent,
    CardMedia,
    CircularProgress,
    IconButton,
    Paper,
    Slide,
    Typography,
} from "@mui/material";
import { useContext, useEffect, useMemo, useState } from "react";
import firebaseConf, { cerrarSesion } from "../../../database/firebase";
import { styled } from "@mui/material/styles";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { getMaterialClase } from "./Firebase";
import { UserContext } from "../../Funcionalidades/UseContext";
import iconDefault from "../../../assets/images/default_icon_clases.png";
import { CustomIconButton } from "../../../styles/ArgumentappTheme";
import { ArrowBackIosNew } from "@mui/icons-material";

function openInNewTab(url) {
    window.open(url, "_blank");
}

export default function ListarMaterial({ id, nombreClase, tipoSlide }) {    
    const [material, setMaterial] = useState([]);
    const [banderaCarga, setBanderaCarga] = useState(true);
    const [banderaCargaSlide, setBanderaCargaSlide] = useState(false);
    const [tipoSalida, setTipoSalida] = useState(tipoSlide);

    const {
        setBanderaMaterial,
        setIdClaseVer,
        setNombreClaseVer,
        nombreClaseVer,
        setBanderaClases
    } = useContext(UserContext);

    const Demo = styled("div")(({ theme }) => ({
        width: "100%",
        height: "auto",
    }));

    useEffect(() => {
        firebaseConf.auth().onAuthStateChanged((user) => {
            if (user) {
                getMaterialClase(id, (value) => {
                    setMaterial(value.reverse());
                    setBanderaCarga(true);
                    setTimeout(() => {
                        setBanderaCargaSlide(true);
                    }, 100);
                });
            } else {
                cerrarSesion();
            }
        }); // eslint-disable-next-line
    }, []);

    return (
        <div style={{ marginTop: 80 }}>
            <div
                style={{
                    display: "flex",
                    flexDirection: "row",
                    marginTop: 24,
                }}
            >
                <div
                    style={{
                        paddingRight: 40,
                        paddingLeft: 40,
                    }}
                >
                    <CustomIconButton
                        onClick={() => {
                            setIdClaseVer("");
                            setNombreClaseVer("");
                            setTipoSalida("left");
                            setBanderaCargaSlide(false);
                            setTimeout(() => {
                                setBanderaMaterial(false);
                                setBanderaClases(true)
                            }, 100);
                        }}
                        style={{
                            marginTop: 30,
                            marginBottom: 20,
                        }}

                        variant="outline"
                    >
                        <ArrowBackIosNew />
                    </CustomIconButton>                    
                </div>
                <Box style={{
                    width: "100%",
                    marginTop: 30
                }}>
                    <Typography variant="titulo_3">Material de clase {nombreClaseVer}</Typography>
                    {banderaCarga ? (
                        <div>
                            <Slide
                                direction={tipoSalida}
                                in={banderaCargaSlide}
                                mountOnEnter
                                unmountOnExit
                            >
                                <Demo>
                                    <Paper
                                        sx={{
                                            paddingTop: 4,
                                            backgroundColor: "transparent",
                                            boxShadow: "none",
                                        }}
                                    >
                                        <div>
                                            <div
                                                style={{
                                                    display: "flex",
                                                    flexDirection: "row",
                                                    flexWrap: "wrap",
                                                }}
                                            >
                                                {material.map((value, index) => {
                                                    return (
                                                        <Card
                                                            key={index}
                                                            sx={{
                                                                display: "flex",
                                                                borderWidth: 1,
                                                                padding: 3,
                                                                marginRight: 3,
                                                                marginBottom: 2,
                                                                minHeight: 250,
                                                                maxWidth: 420,
                                                                borderRadius:
                                                                    "30px",
                                                            }}
                                                            elevation={"1"}
                                                        >
                                                            <CardMedia
                                                                component="img"
                                                                sx={{
                                                                    width: 150,
                                                                    borderRadius:
                                                                        "16px",
                                                                }}
                                                                image={
                                                                    value.data
                                                                        .imagen ===
                                                                        null ||
                                                                    value.data
                                                                        .imagen ===
                                                                        undefined
                                                                        ? iconDefault
                                                                        : value
                                                                              .data
                                                                              .imagen
                                                                }
                                                                alt={
                                                                    "Miniatura clases " +
                                                                    value.data
                                                                        .nombreclase
                                                                }
                                                            />
                                                            <Box
                                                                sx={{
                                                                    display:
                                                                        "flex",
                                                                    flexDirection:
                                                                        "column",
                                                                }}
                                                            >
                                                                <CardContent>
                                                                    <Typography variant="h6">
                                                                        {
                                                                            value
                                                                                .data
                                                                                .tipo
                                                                        }
                                                                    </Typography>
                                                                    <Typography variant="h5">
                                                                        {
                                                                            value
                                                                                .data
                                                                                .nombre
                                                                        }
                                                                    </Typography>
                                                                    <Typography
                                                                        color="text.secondary"
                                                                        style={{
                                                                            maxWidth: 230,
                                                                            wordWrap:
                                                                                "break-word",
                                                                            overflowWrap:
                                                                                "break-word",
                                                                            hyphens:
                                                                                "auto",
                                                                            fontSize: 14,
                                                                            fontWeight:
                                                                                "normal",
                                                                        }}
                                                                    >
                                                                        {
                                                                            value
                                                                                .data
                                                                                .nombreclase
                                                                        }
                                                                    </Typography>
                                                                </CardContent>

                                                                <Box
                                                                    sx={{
                                                                        display:
                                                                            "flex",
                                                                        flexDirection:
                                                                            "row",
                                                                        flexWrap:
                                                                            "wrap",
                                                                        justifyContent:
                                                                            "flex-end",
                                                                        pl: 1,
                                                                        pb: 1,
                                                                        height: "-webkit-fill-available",
                                                                        alignItems:
                                                                            "flex-end",
                                                                    }}
                                                                >
                                                                    <ButtonGroup
                                                                        disableElevation
                                                                    >
                                                                        <IconButton
                                                                            size="large"
                                                                            onClick={() => {
                                                                                openInNewTab(
                                                                                    value
                                                                                        .data
                                                                                        .url
                                                                                );
                                                                            }}
                                                                        >
                                                                            <ArrowForwardIosIcon
                                                                                style={{
                                                                                    color: "GrayText",
                                                                                }}
                                                                                fontSize="inherit"
                                                                            />
                                                                        </IconButton>
                                                                    </ButtonGroup>
                                                                </Box>
                                                            </Box>
                                                        </Card>
                                                    );
                                                })}
                                            </div>
                                        </div>
                                        {material.length === 0 && (
                                            <div
                                                style={{
                                                    display: "flex",
                                                    flexDirection: "row",
                                                    flexWrap: "nowrap",
                                                    alignContent: "center",
                                                    justifyContent: "center",
                                                }}
                                            >
                                                <p>
                                                    La clase{" "}
                                                    <b>{nombreClase}</b> no
                                                    registra material
                                                </p>
                                            </div>
                                        )}{" "}
                                    </Paper>
                                </Demo>
                            </Slide>
                        </div>
                    ) : (
                        <div
                            style={{
                                display: "flex",
                                flexDirection: "row",
                                flexWrap: "nowrap",
                                alignContent: "center",
                                justifyContent: "center",
                                height: "400px",
                            }}
                        >
                            <CircularProgress></CircularProgress>
                        </div>
                    )}
                </Box>
            </div>
        </div>
    );
}
