import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  CircularProgress,
  Paper,
  Slide,
  Typography,
} from "@mui/material";
import React, { useContext, useEffect, useMemo, useState } from "react";
import firebaseConf, { cerrarSesion } from "../../../database/firebase";
import { styled } from "@mui/material/styles";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

import { useHistory } from "react-router-dom";
import CrearClasesPublicas from "./CrearClasesPublicas";
import { getClasesCursosPublicos } from "./firebaseCursosPublicos";
import { UserContext } from "../../Funcionalidades/UseContext";

import iconDefault from "../../../assets/images/default_icon_clases.png";

import DeleteClases from "../Clases/DeleteClases";
import OrgChart from "../OrgChart/OrgChart";
import { CustomIconButton } from "../../../styles/ArgumentappTheme";
import { ArrowBackIosNew } from "@mui/icons-material";

function ListarClasesPublicos({
  id,
  nombreCurso,
  estado,
  indexUnidad,
  tipoSlide,
}) {
  const [estadoCurso, setestadoCurso] = useState(estado);

  const { setBanderaClases, setIdCursoVer, setNombreCursoVer, settipoCursos } =
    useContext(UserContext);

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      if (user) {
        getClasesCursosPublicos(id, (value) => {
          setclases(value.reverse());
          setBanderaCarga(true);
          setTimeout(() => {
            setBanderaCargaSlide(true);
          }, 100);
        });
      } else {
        cerrarSesion();
      }
    }); // eslint-disable-next-line
  }, []);

  useEffect(() => {
    setestadoCurso(estado);
  }, [estado]);

  const providerValue = useMemo(
    () => ({
      setestadoCurso,
    }),
    [setestadoCurso]
  );

  const [clases, setclases] = useState([]);
  const [banderaCarga, setBanderaCarga] = useState(true);
  const [banderaCargaSlide, setBanderaCargaSlide] = useState(false);
  const history = useHistory();

  const [tipoSalida, settipoSalida] = useState(tipoSlide);
  const [uid, setUid] = useState("data");

  const Demo = styled("div")(({ theme }) => ({
    width: "100%",
    height: "auto",
  }));
  return (
    <div style={{ marginTop: 80 }}>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          marginTop: 24,
        }}
      >
        <div
          style={{
            paddingRight: 40,
            paddingLeft: 40,
          }}
        >          
          <CustomIconButton
              onClick={() => {
                setIdCursoVer("");
                setNombreCursoVer("");
                settipoCursos("");
                settipoSalida("left");
                setBanderaCargaSlide(false);
                setTimeout(() => {
                  setBanderaClases(false);
                }, 100);
              }}
              style={{
                  marginTop: 30,
                  marginBottom: 20,
              }}
          >
              <ArrowBackIosNew style={{ color: "#fff" }} />
          </CustomIconButton>
          <UserContext.Provider value={providerValue}>
            <Paper
              sx={{
                width: "fit-content",
                marginTop: "10px",
                marginBottom: "10px",
                boxShadow: "none",
                backgroundColor: "transparent",
              }}
            >
              {Number(
                window.localStorage.getItem("dataUserArgumentapp_permisos")
              ) !== 2 && (
                <CrearClasesPublicas
                  idCurso={id}
                  estado={estadoCurso}
                ></CrearClasesPublicas>
              )}{" "}
            </Paper>
          </UserContext.Provider>
        </div>
        <Box width="100%">
          {banderaCarga ? (
            <div>
              <Slide
                direction={tipoSalida}
                in={banderaCargaSlide}
                mountOnEnter
                unmountOnExit
              >
                <Demo>
                  <Paper
                    sx={{
                      padding: 3,
                      backgroundColor: "transparent",
                      boxShadow: "none",
                    }}
                  >
                    <div>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          flexWrap: "wrap",
                        }}
                      >
                        {clases.map((value, index) => {
                          return (
                            <Card
                              key={index}
                              sx={{
                                display: "flex",
                                borderWidth: 1,
                                padding: 3,
                                marginRight: 3,
                                marginBottom: 2,
                                minHeight: 250,
                                maxWidth: 420,
                                borderRadius: "30px",
                              }}
                              elevation={"1"}
                            >
                              <CardMedia
                                component="img"
                                sx={{ width: 150, borderRadius: "16px" }}
                                image={
                                  value.data.imagen === null ||
                                  value.data.imagen === undefined
                                    ? iconDefault
                                    : value.data.imagen
                                }
                                alt={
                                  "Miniatura clases " + value.data.nombreclase
                                }
                              />
                              <Box
                                sx={{
                                  display: "flex",
                                  flexDirection: "column",
                                }}
                              >
                                <CardContent>
                                  <Typography variant="h5">
                                    Clase {index + 1}
                                  </Typography>
                                  <Typography
                                    color="text.secondary"
                                    style={{
                                      maxWidth: 230,
                                      wordWrap: "break-word",
                                      overflowWrap: "break-word",
                                      hyphens: "auto",
                                      fontSize: 14,
                                      fontWeight: "normal",
                                    }}
                                  >
                                    {value.data.nombreclase}
                                  </Typography>
                                </CardContent>
                                <CardActions
                                  style={{
                                    height: "-webkit-fill-available",
                                    alignItems: "flex-end",
                                  }}
                                >
                                  <Button
                                    style={{
                                      marginRight: 5,
                                      borderStyle: "solid",
                                      borderWidth: 1,
                                      maxWidth: "fit-content",
                                    }}
                                    onClick={(e) => {
                                      settipoSalida("right");
                                      setBanderaCargaSlide(false);
                                      setTimeout(() => {
                                        history.push(
                                          "/clasecontenidoargumentapp",
                                          {
                                            nombreCurso: nombreCurso,
                                            nombreClase: value.data.nombreclase,
                                            idCurso: id,
                                            isSuccessful: true,
                                            idClase: value.key,
                                            tipoCurso: "publicados",
                                            estado: estadoCurso,
                                            indexUnidad: indexUnidad,
                                              indexClase: index + 1,
                                              dataClase: value.data,
                                          }
                                        );
                                      }, 100);
                                    }}
                                    variant="outlined"
                                  >
                                    <ArrowForwardIosIcon></ArrowForwardIosIcon>
                                  </Button>
                                  <OrgChart
                                    nombreClase={value.data.nombreclase}
                                    uid={"data"}
                                    idClase={value.key}
                                    idCurso={id}
                                    lugarAlamacenamiento={"publicos"}
                                    nombreCurso={nombreCurso}
                                  ></OrgChart>

                                  {!estadoCurso && (
                                    <DeleteClases
                                      id={id}
                                      idClase={value.key}
                                      state={"publico"}
                                      nombre={value.data.nombreclase}
                                    ></DeleteClases>
                                  )}
                                </CardActions>
                              </Box>
                            </Card>
                          );
                        })}
                      </div>
                    </div>
                    {clases.length === 0 && (
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          flexWrap: "nowrap",
                          alignContent: "center",
                          justifyContent: "center",
                        }}
                      >
                        <p>No registra clases para el curso {nombreCurso}</p>
                      </div>
                    )}{" "}
                  </Paper>
                </Demo>
              </Slide>
            </div>
          ) : (
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                alignContent: "center",
                justifyContent: "center",
                height: "400px",
              }}
            >
              <CircularProgress></CircularProgress>
            </div>
          )}
        </Box>
      </div>
    </div>
  );
}

export default ListarClasesPublicos;

