import React from "react";
import parse from "html-react-parser";

import {
  renderContenidoAccionesButton,
  renderContenidoAccionesCheckBox,
  renderTextArea,
  renderUploadPhoto,
} from "../../Funcionalidades/AccionesDiapositivas";

function ContenidoDiapositiva({ contenidoHTML, acciones }) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
      }}
    >
      <div
        style={{
          borderStyle: "solid",
          borderRadius: 25,
          backgroundColor: "white",
        }}
      >
        <div className="my-node" id="my-node">
          <div
            style={{
              margin: 30,
              zoom: '90%'
            }}
          >
            <div
              style={{
                minWidth: 1200,
                minHeight: 700,
                maxWidth: 1200,
                maxHeight: 700,
                width: 1200,
                height: 700,
              }}
            >
              {parse(contenidoHTML)}
              {/**Vista previa de una diapositiva creada */}
              {acciones.tipo === "Button" &&
                renderContenidoAccionesButton(acciones.item)}
              {acciones.tipo === "CheckBox" &&
                renderContenidoAccionesCheckBox(acciones.item)}
              {acciones.tipo === "TextArea" && renderTextArea()}
              {acciones.tipo === "Photo" && renderUploadPhoto()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ContenidoDiapositiva;
