import { Box, Button, Input, Slide, Typography } from "@mui/material";
import React, { useContext, useEffect, useMemo, useRef, useState } from "react";
import { Editor } from "@tinymce/tinymce-react";
import tinymce from "tinymce";
import { UserContext } from "../../Funcionalidades/UseContext";
import AgregarAcciones from "./AgregarAcciones";
import VistaPreviaDiapositiva from "./VistaPreviaDiapositiva";
import {
  dataValueButton,
  dataValueCheckbox,
  dataValueLista,
  dataValueSinAccion,
  dataValueTextArea,
  dataValueUploadPhoto,
} from "../../Funcionalidades/AccionesDiapositivas";
import Swal from "sweetalert2";
import "../../../styles/CustomClassSweetAlert2.css";
function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function AddDiapositivaProfesor({
  contenidoHTML,
  acciones,
  uid,
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  lugarAlmacenamiento,
}) {
  const [optionDirectionEditor, setoptionDirectionEditor] = useState("left");
  const [optionInEditor, setoptionInEditor] = useState(true);
  const [optionDirectionAcciones, setoptionDirectionAcciones] =
    useState("left");
  const [optionInAcciones, setoptionInAcciones] = useState(true);
  const [optionDirectionVistaPrevia, setoptionDirectionVistaPrevia] =
    useState("left");
  const [optionInVistaPrevia, setoptionInVistaPrevia] = useState(true);

  const editorRef = useRef(null);

  const continuarAcciones = () => {
    if (editorRef.current) {
      setContenidoHTMLLocal(editorRef.current.getContent());
      if (editorRef.current.getContent() !== "" && nombreDiapositiva !== "") {
        if (valueCheckbox[2]) {
          setoptionDirectionEditor("right");
          setoptionInEditor(false);
          setoptionDirectionVistaPrevia("left");
          setoptionInVistaPrevia(true);

          setTimeout(() => {
            setOpcionesdeVista("VistaPrevia");
          }, 150);
        } else {
          setoptionDirectionEditor("right");
          setoptionInEditor(false);
          setoptionDirectionAcciones("left");
          setoptionInAcciones(true);

          setTimeout(() => {
            setOpcionesdeVista("Acciones");
          }, 150);
        }
      } else {
        if (nombreDiapositiva === "") {
          Swal.fire({
            position: "top-end",
            customClass: {
              container: "my-swal",
            },
            icon: "error",
            title: "Error nombre diapositiva",
            text: "Para poder subir una diapositiva, debes ingresar un nombre valido",
            showConfirmButton: false,
            timer: 2000,
          });
        } else {
          Swal.fire({
            position: "top-end",
            customClass: {
              container: "my-swal",
            },
            icon: "error",
            title: "Contenido obligatorio",
            text: "La diapositiva debe tener contenido de manera obligatoría",
            showConfirmButton: false,
            timer: 2300,
          });
        }

        //Poner alvertencia de que todos los datos son obligatorios
      }
    }
  };

  const [valueCheckbox, setvalueCheckbox] = useState(dataValueCheckbox());
  const [listBotones, setlistBotones] = useState([]);
  const [listCheckBox, setlistCheckBox] = useState([]);
  const [opcionesdeVista, setOpcionesdeVista] = useState("Editor");

  const [contenidoHTMLLocal, setContenidoHTMLLocal] = useState(contenidoHTML);
  const [accionesUpload, setAccionesUpload] = useState([]);
  const [nombreDiapositiva, setNombreDiapositiva] = useState("");
  const [orden, setOrden] = useState(null);
  const [imageData, setimageData] = useState(null);

  const { setbanderaDiapositivaDiseno } = useContext(UserContext);

  const VariablesNavegacion = useMemo(
    () => ({
      setOpcionesdeVista,
      setAccionesUpload,
      setNombreDiapositiva,
      setOrden,
      setimageData,
      valueCheckbox,
      contenidoHTMLLocal,
      accionesUpload,
      nombreDiapositiva,
      orden,
      imageData,
      uid,
      idCurso,
      idClase,
      keyActividad,
      tipoVista,
      setoptionDirectionEditor,
      setoptionInEditor,
      setoptionDirectionAcciones,
      setoptionInAcciones,
      setoptionDirectionVistaPrevia,
      setoptionInVistaPrevia,
    }),
    [
      setOpcionesdeVista,
      setAccionesUpload,
      setNombreDiapositiva,
      setOrden,
      setimageData,
      valueCheckbox,
      contenidoHTMLLocal,
      accionesUpload,
      nombreDiapositiva,
      orden,
      imageData,
      uid,
      idCurso,
      idClase,
      keyActividad,
      tipoVista,
      setoptionDirectionEditor,
      setoptionInEditor,
      setoptionDirectionAcciones,
      setoptionInAcciones,
      setoptionDirectionVistaPrevia,
      setoptionInVistaPrevia,
    ]
  );

  useEffect(() => {
    setContenidoHTMLLocal(contenidoHTML);

    if (acciones === undefined) {
      setvalueCheckbox(dataValueSinAccion());
    } else {
      if (acciones.tipoAccion === "Button") {
        setvalueCheckbox(dataValueButton());
        var listBotonestemp = [];
        for (let index = 0; index < acciones.cantidadItem; index++) {
          listBotonestemp.push(index + 1);
        }
        setlistBotones(listBotonestemp);
      }
      if (acciones.tipoAccion === "CheckBox") {
        setvalueCheckbox(dataValueLista());
        var listCheckBoxtemp = [];
        for (let index = 1; index <= acciones.cantidadItem; index++) {
          listCheckBoxtemp.push(index);
        }
        setlistCheckBox(listCheckBoxtemp);
      }
      if (acciones.tipoAccion === "TextArea") {
        setvalueCheckbox(dataValueTextArea());
      }
      if (acciones.tipoAccion === "Photo") {
        setvalueCheckbox(dataValueUploadPhoto());
      }
    }
  }, [acciones, contenidoHTML]);

  return (
    <UserContext.Provider value={VariablesNavegacion}>
      <div>
        {opcionesdeVista === "Editor" && (
          <div>
            <Slide
              direction={optionDirectionEditor}
              in={optionInEditor}
              mountOnEnter
              unmountOnExit
            >
              <div>
                <div
                  style={{
                    display: "flex",
                    marginBottom: 20,
                    alignItems: "center",
                  }}
                >
                  <Button
                    variant="contained"
                    onClick={() => setbanderaDiapositivaDiseno(true)}
                    style={{ marginRight: 70 }}
                  >
                    Lista de diseños
                  </Button>
                  <Typography variant="body5" style={{ marginRight: 15 }}>
                    Nombre de la diapositiva:
                  </Typography>
                  <div
                    style={{
                      height: 54,
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      width: 400,
                    }}
                  >
                    <Input
                      style={{ width: "100%" }}
                      value={nombreDiapositiva}
                      onChange={(e) => {
                        setNombreDiapositiva(e.target.value);
                      }}
                    />
                  </div>
                </div>
                <Editor
                  onInit={(evt, editor) => (editorRef.current = editor)}
                  apiKey="awyk0fee0yvyjg1nr6hk79jxp2h2ftmvuu7go89blr8pfawr"
                  initialValue={contenidoHTMLLocal}
                  init={{
                    height: 750,
                    max_height: 750,
                    width: 1100,
                    max_width: 1100,
                    resize: false,
                    menubar: true,
                    language: "es_MX",
                    fontsize_formats:
                      "8pt 9pt 10pt 11pt 12pt 14pt 15pt 16pt 17pt 18pt 20pt 21pt 22pt 24pt 26pt 28pt 30pt 32pt 36pt 40pt 48pt 60pt 72pt 96pt",

                    plugins: [
                      "advlist autolink lists link image",
                      "charmap print preview anchor help",
                      "searchreplace visualblocks code",
                      "insertdatetime media table paste wordcount",
                      "image code",
                    ],
                    toolbar:
                      "insertfile undo redo | styleselect | fontsizeselect | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link image",

                    image_title: true,
                    automatic_uploads: true,
                    file_picker_types: "image",

                    file_picker_callback: function (cb, value, meta) {
                      var input = document.createElement("input");
                      input.setAttribute("type", "file");
                      input.setAttribute("accept", "image/*");

                      input.onchange = function () {
                        var file = this.files[0];

                        var reader = new FileReader();

                        reader.onload = function () {
                          var id = "blobid" + new Date().getTime();
                          var blobCache =
                            tinymce.activeEditor.editorUpload.blobCache;
                          var base64 = reader.result.split(",")[1];
                          var blobInfo = blobCache.create(id, file, base64);
                          blobCache.add(blobInfo);
                          cb(blobInfo.blobUri(), { title: file.name });
                        };
                        reader.readAsDataURL(file);
                      };

                      input.click();
                    },

                    content_style:
                      "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",
                  }}
                />
                <RedBar></RedBar>
                <div style={{ textAlign: "end" }}>
                  <Button
                    variant="contained"
                    style={{
                      marginRight: "10px",
                    }}
                    onClick={continuarAcciones}
                  >
                    {!valueCheckbox[2] ? "Acciones" : "Vista Previa"}
                  </Button>
                </div>
              </div>
            </Slide>
          </div>
        )}
        {opcionesdeVista === "Acciones" && (
          <div>
            <Slide
              direction={optionDirectionAcciones}
              in={optionInAcciones}
              mountOnEnter
              unmountOnExit
            >
              <div>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    marginBottom: 35,
                  }}
                >
                  {valueCheckbox[0] && (
                    <Typography variant="body5" style={{ marginRight: 15 }}>
                      Modificar acciones de los botones
                    </Typography>
                  )}
                  {valueCheckbox[1] && (
                    <Typography variant="body5" style={{ marginRight: 15 }}>
                      Modificar nombre de las alternativas
                    </Typography>
                  )}
                  {valueCheckbox[2] && (
                    <Typography variant="body5" style={{ marginRight: 15 }}>
                      La plantilla elegida no tiene acciones para añadir.
                    </Typography>
                  )}
                </div>
                <AgregarAcciones
                  listBotones={listBotones}
                  listCheckBox={listCheckBox}
                  valueCheckbox={valueCheckbox}
                ></AgregarAcciones>
              </div>
            </Slide>
          </div>
        )}

        {opcionesdeVista === "VistaPrevia" && (
          <div>
            <Slide
              direction={optionDirectionVistaPrevia}
              in={optionInVistaPrevia}
              mountOnEnter
              unmountOnExit
            >
              <div>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                  }}
                >
                  <Button
                    variant="contained"
                    onClick={(e) => {
                      if (valueCheckbox[2]) {
                        setoptionDirectionEditor("right");
                        setoptionInEditor(true);
                        setoptionDirectionVistaPrevia("left");
                        setoptionInVistaPrevia(false);
                        setTimeout(() => {
                          setOpcionesdeVista("Editor");
                        }, 150);
                      } else {
                        setoptionDirectionAcciones("right");
                        setoptionInAcciones(true);
                        setoptionDirectionVistaPrevia("left");
                        setoptionInVistaPrevia(false);
                        setTimeout(() => {
                          setOpcionesdeVista("Acciones");
                        }, 150);
                      }
                    }}
                  >
                    {valueCheckbox[2] ? "Editor" : "Acciones"}
                  </Button>
                  <Typography
                    style={{ alignSelf: "center", marginLeft: 30 }}
                    variant="h5"
                  >
                    Vista previa de la diapositiva
                  </Typography>
                </div>

                <VistaPreviaDiapositiva
                  lugarAlmacenamiento={lugarAlmacenamiento}
                ></VistaPreviaDiapositiva>
              </div>
            </Slide>
          </div>
        )}
      </div>
    </UserContext.Provider>
  );
}

export default AddDiapositivaProfesor;
