import {
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControlLabel,
  FormGroup,
  Grid,
  Input,
  Paper,
} from "@mui/material";
import { Box } from "@mui/material";
import { Tab, Tabs, Typography } from "@mui/material";
import React, { useContext, useEffect, useMemo, useState } from "react";
import { UserContext } from "../../../Funcionalidades/UseContext";
import RenderEditorButton from "./RenderEditorButton";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0 }} style={{ marginTop: 15 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function EditorAccionesDiapositiva({ uid, idClase, idCurso }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const { accionesEditada, setsecciones, setAccionesEditadas } =
    useContext(UserContext);

  const [refreshComponent, setRefreshComponent] = useState(true);

  const [dataAcciones, setDataAcciones] = useState(accionesEditada.item);

  const [indexEditar, setIndexEditar] = useState(0);
  const [openDialogReferencias, setOpenDialogReferencias] =
    React.useState(false);

  const ariaLabel = { "aria-label": "description" };

  const AccionesEditar = useMemo(
    () => ({
      indexEditar,
      dataAcciones,
      setDataAcciones,
    }),
    [indexEditar, dataAcciones, setDataAcciones]
  );

  useEffect(() => {
    var temp_acciones = [];
    if (accionesEditada.tipo === "Button") {
      accionesEditada.item.forEach((element) => {
        temp_acciones.push({
          name: element.name,
          href: element.href,
        });
      });
      setDataAcciones(temp_acciones);
    }
    if (accionesEditada.tipo === "CheckBox") {
      accionesEditada.item.forEach((element) => {
        temp_acciones.push({
          name: element.name,
        });
      });
      setDataAcciones(temp_acciones);
    }
    if (accionesEditada.tipo === "Sin acciones") {
      setDataAcciones(temp_acciones);
    }
  }, [accionesEditada]);

  return (
    <div>
      <div>
        <h3
          style={{
            color: "black",
          }}
        >
          Edición de las acciones
        </h3>
      </div>
      <div>
        {accionesEditada.tipo === "Button" && (
          <div>
            <div
              style={{
                textAlign: "center",
              }}
            >
              {dataAcciones.map((value, index) => {
                return (
                  <Button
                    style={{
                      backgroundColor: "green",
                      color: "white",
                      marginLeft: 10,
                    }}
                    key={index}
                  >
                    {refreshComponent && value.name}
                  </Button>
                );
              })}
            </div>
            <div
              style={{
                marginTop: 30,
              }}
            >
              <Box
                style={{
                  backgroundColor: "white",
                  padding: 15,
                  marginBottom: 20,
                }}
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  "& .MuiTextField-root": { width: "25ch" },
                }}
              >
                {dataAcciones.map((value, index) => {
                  return (
                    <div key={index}>
                      <div>
                        <h3 style={{ color: "black" }}>
                          {" "}
                          Opciones para Botón {index + 1}
                        </h3>
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            flexWrap: "nowrap",
                            alignContent: "center",
                            alignItems: "center",
                          }}
                        >
                          <div
                            style={{
                              display: "contents",
                            }}
                          >
                            <h4 style={{ color: "black" }}> Nombre:</h4>
                            <Input
                              defaultValue={value.name}
                              inputProps={ariaLabel}
                              onBlur={(e) => {
                                setRefreshComponent(false);
                                setTimeout(() => {
                                  setRefreshComponent(true);
                                }, 50);
                              }}
                              style={{
                                color: "black",
                                width: "40%",
                                marginLeft: 50,
                              }}
                              onChange={(e) => {
                                var temp = dataAcciones;
                                temp[index].name = e.target.value;
                                setDataAcciones(temp);
                              }}
                            />
                          </div>
                          <div
                            style={{
                              display: "flex",
                              flexDirection: "row",
                              flexWrap: "nowrap",
                              alignContent: "space-between",
                              justifyContent: "space-around",
                              alignItems: "center",
                              width: "40%",
                            }}
                          >
                            <h4 style={{ color: "black" }}> Referencia:</h4>
                            <Button
                              variant="outlined"
                              style={{
                                marginLeft: 10,
                              }}
                              onClick={(e) => {
                                setIndexEditar(index);
                                setOpenDialogReferencias(true);
                              }}
                            >
                              Asignar acción
                            </Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
                <UserContext.Provider value={AccionesEditar}>
                  <Dialog
                    open={openDialogReferencias}
                    onClose={(e) => {
                      setOpenDialogReferencias(false);
                    }}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                  >
                    <DialogTitle id="alert-dialog-title">
                      {"Edición de acciones para el botón " +
                        dataAcciones[indexEditar].name}
                    </DialogTitle>
                    <DialogContent>
                      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
                        <Tabs
                          value={value}
                          onChange={handleChange}
                          aria-label="basic tabs example"
                          style={{
                            backgroundColor: "white",
                            textAlign: "center",
                          }}
                        >
                          <Tab label="Profesor" {...a11yProps(0)} />
                          <Tab label="Alumnos" {...a11yProps(1)} />
                          <Tab label="Pizarra" {...a11yProps(2)} />
                        </Tabs>
                      </Box>
                      <TabPanel value={value} index={0}>
                        <RenderEditorButton
                          tipoVista={"profesor"}
                          uid={uid}
                          idClase={idClase}
                          idCurso={idCurso}
                          acciones={dataAcciones[indexEditar].href}
                        ></RenderEditorButton>
                      </TabPanel>
                      <TabPanel value={value} index={1}>
                        <RenderEditorButton
                          tipoVista={"alumnos"}
                          uid={uid}
                          idClase={idClase}
                          idCurso={idCurso}
                          acciones={dataAcciones[indexEditar].href}
                        ></RenderEditorButton>
                      </TabPanel>
                      <TabPanel value={value} index={2}>
                        <RenderEditorButton
                          tipoVista={"pizarra"}
                          uid={uid}
                          idClase={idClase}
                          idCurso={idCurso}
                          acciones={dataAcciones[indexEditar].href}
                        ></RenderEditorButton>
                      </TabPanel>
                    </DialogContent>
                    <DialogActions>
                      <Button
                        onClick={(e) => {
                          setOpenDialogReferencias(false);
                        }}
                      >
                        Cerrar
                      </Button>
                    </DialogActions>
                  </Dialog>
                </UserContext.Provider>
              </Box>
            </div>
          </div>
        )}
        {accionesEditada.tipo === "CheckBox" && (
          <div>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                alignContent: "center",
                justifyContent: "center",
                marginBottom: 20,
              }}
            >
              <div
                style={{
                  textAlign: "center",
                  backgroundColor: "white",
                  color: "black",
                  width: "fit-content",
                  maxWidth: "80%",
                  borderRadius: 25,
                  padding: 15,
                }}
              >
                <FormGroup
                  style={{
                    display: "flex",
                    flexDirection: "row",
                  }}
                >
                  {dataAcciones.map((value, index) => {
                    return (
                      <FormControlLabel
                        control={<Checkbox />}
                        label={value.name}
                        key={index}
                      />
                    );
                  })}
                </FormGroup>
              </div>
            </div>

            <Box
              style={{
                backgroundColor: "white",
                padding: 15,
                marginBottom: 20,
              }}
              sx={{
                display: "flex",
                flexDirection: "column",
                "& .MuiTextField-root": { width: "25ch" },
              }}
            >
              <Grid
                container
                rowSpacing={1}
                columnSpacing={{ xs: 1, sm: 2, md: 3 }}
              >
                {dataAcciones.map((value, index) => {
                  return (
                    <Grid item xs={6} key={index}>
                      <Paper
                        style={{
                          margin: 2,
                          borderStyle: "solid",
                          borderWidth: 1,
                          borderColor: "black",
                        }}
                      >
                        <h4 style={{ color: "black" }}>
                          {" "}
                          Opciones para el Item {index + 1}
                        </h4>

                        <h4 style={{ color: "black" }}> Nombre:</h4>
                        <Input
                          defaultValue={value.name}
                          inputProps={ariaLabel}
                          onBlur={(e) => {
                            setRefreshComponent(false);
                            setTimeout(() => {
                              setRefreshComponent(true);
                            }, 50);
                          }}
                          style={{
                            color: "black",
                            width: "80%",
                            marginLeft: 50,
                            marginBottom: 20,
                          }}
                          onChange={(e) => {
                            var temp = dataAcciones;
                            temp[index].name = e.target.value;
                            setDataAcciones(temp);
                          }}
                        />
                      </Paper>
                    </Grid>
                  );
                })}
              </Grid>
            </Box>
          </div>
        )}
      </div>
      {accionesEditada.tipo !== "Button" &&
        accionesEditada.tipo !== "CheckBox" && (
          <div
            style={{
              height: 120,
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            {" "}
            <Typography variant="titulo_2">
              Diapositiva sin acciones editable
            </Typography>{" "}
          </div>
        )}
      <div
        style={{
          marginTop: 20,
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <Button
          style={{
            borderStyle: "solid",
            borderWidth: 1,
          }}
          onClick={(e) => {
            setsecciones("EditordeTexto");
          }}
        >
          Volver
        </Button>
        <Button
          style={{
            borderStyle: "solid",
            borderWidth: 1,
          }}
          variant='contained'
          onClick={(e) => {
            setAccionesEditadas({
              tipo: accionesEditada.tipo,
              item: dataAcciones,
            });
            setsecciones("VistaPrevia");
          }}
        >
          Continuar
        </Button>
      </div>
    </div>
  );
}

export default EditorAccionesDiapositiva;
