import { Button } from "@mui/material";
import React, { useEffect, useState } from "react";
import {
  changeDiapositivaActivaProfesorById,
  getDiapositivasActOnline,
} from "../../BDActividadesOnline";

function RenderDiapositivas({ codigo, idClase, idActividad, idVista }) {
  useEffect(() => {
    getDiapositivasActOnline(codigo, idClase, idActividad, idVista, (diapo) => {
      setDiapositivas(diapo);
      setbanderaCarga(false);
    });
  }, [codigo, idClase, idActividad, idVista]);
  const [diapositivas, setDiapositivas] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(true);

  return (
    <div
      style={
        idVista === "profesor"
          ? {
              display: "flex",
              flexDirection: "column",
            }
          : {
              display: "flex",
              flexDirection: "row",
              maxWidth: "43vw",
              overflow: "auto",
              overflowY: "hidden",
            }
      }
    >
      {!banderaCarga && (
        <div style={{ textAlign: "center" }}>
          {diapositivas.length === 0 && (
            <h4 style={{ color: "black", marginBottom: 20, marginTop: 0 }}>
              Actividad no registra diapositivas
            </h4>
          )}
        </div>
      )}
      {diapositivas.map((value) => (
        <div key={value.key}>
          <Button
            style={{
              minHeight: "200px",
              maxHeight: "200px",
              minWidth: "300px",
              maxWidth: "300px",
              borderRadius: 25,
              backgroundColor: "white",
              borderColor: "black",
              borderWidth: "2px",
              borderStyle: "solid",
              marginBottom: "0px ",
              display: "flex",
              flexDirection: "column",
              marginLeft: idVista === "profesor" ? 0 : 5,
              marginRight: idVista !== "profesor" ? "20px" : 0,
            }}
            onClick={(e) => {
              changeDiapositivaActivaProfesorById(
                codigo,
                idActividad,
                value.key,
                idVista
              );
            }}
          >
            <img
              alt="miniatura diapositiva"
              style={{
                width: "300px",
                height: "200px",
              }}
              src={value.miniatura}
            ></img>
          </Button>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "nowrap",
              alignContent: "center",
              alignItems: "center",
              maxWidth: "300px",
              justifyContent: "center",
            }}
          >
            <h4
              style={{
                color: "black",
                textAlign: "center",
                marginLeft: 0,
              }}
            >
              {value.datos.nombre}
            </h4>
          </div>
        </div>
      ))}
    </div>
  );
}

export default RenderDiapositivas;
