import { Button, Grid, Paper, Typography } from "@mui/material";
import { useEffect, useMemo, useState } from "react";
import Calendario from "./Calendario";
import { UserContext } from "../Funcionalidades/UseContext";
import dayjs from "dayjs";
import { LoadingButton } from "@mui/lab";

export default function MainCalendario() {
    const SCOPES =
        "https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/calendar.readonly https://www.googleapis.com/auth/calendar.events https://www.googleapis.com/auth/calendar";
    const [events, setEvents] = useState(null);
    const [dayEvents, setDayEvents] = useState([]);
    const API_KEY = "AIzaSyDN9jl6NGCd_Ob5Jxr0hnCO1Rn78QGYgbk";
    const CLIENT_ID =
        "815249271922-7mm01vho8eere4cp857l4t5gjbunvbtf.apps.googleusercontent.com";
    const [sync, setSync] = useState(false);
    const [loading, setLoading] = useState(false);
    const [stringDiaActual, setStringDiaActual] = useState("")
    const providerValue = useMemo(
        () => ({
            setStringDiaActual,
            setDayEvents,
        }),
        [setStringDiaActual, setDayEvents]
    );

    useEffect(() => {
        const script = document.createElement("script");
        script.async = true;
        script.defer = true;
        script.src = "https://apis.google.com/js/api.js";

        document.body.appendChild(script);

        script.addEventListener("load", () => {
            if (localStorage.getItem("access_token")) {
                setLoading(true);
                sincronizar();
            }
        });
    }, []);

    const sincronizar = () => {
        if (window.gapi) handleClientLoad();
    };

    const desincronizar = () => {
        setLoading(true);

        if (localStorage.getItem("access_token")) {
            localStorage.removeItem("access_token");
            setSync(false);
            setEvents(null);
            setDayEvents(null);
            setLoading(false);
        }
    };

    const handleClientLoad = () => {
        window.gapi.load("client:auth2", initClient);
    };

    const openSignInPopup = () => {
        setLoading(true);

        window.gapi.auth2.authorize(
            { client_id: CLIENT_ID, scope: SCOPES },
            (res) => {
                if (res.error) {
                    setLoading(false);
                } else {
                    if (res.access_token)
                        localStorage.setItem("access_token", res.access_token);

                    window.gapi.client
                        .init({
                            discoveryDocs: [
                                "https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest",
                            ],
                        })
                        .then(() => {
                            listUpcomingEvents();
                            setSync(true);
                            setLoading(false);
                        });
                }
            }
        );
    };

    const initClient = () => {
        if (!localStorage.getItem("access_token")) {
            openSignInPopup();
        } else {
            // Get events if access token is found without sign in popup
            fetch(
                `https://www.googleapis.com/calendar/v3/calendars/primary/events?key=${API_KEY}&orderBy=startTime&singleEvents=true`,
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem(
                            "access_token"
                        )}`,
                    },
                }
            )
                .then((res) => {
                    // Check if unauthorized status code is return open sign in popup
                    if (res.status !== 401) {
                        return res.json();
                    } else {
                        localStorage.removeItem("access_token");

                        openSignInPopup();
                    }
                })
                .then((data) => {
                    if (data?.items) {
                        setEvents(formatEvents(data.items));
                    }

                    setSync(true);
                    setLoading(false);
                });
        }
    };

    const listUpcomingEvents = () => {
        window.gapi.client.calendar.events
            .list({
                // Fetch events from user's primary calendar
                calendarId: "primary",
                singleEvents: true,
                orderBy: "startTime",
            })
            .then(function (response) {
                let events = response.result.items;

                if (events.length > 0) {
                    setEvents(formatEvents(events));
                }
            });
    };

    const formatEvents = (list) => {
        return list.map((item) => ({
            title: item.summary,
            start: item.start.dateTime || item.start.date,
            end: item.end.dateTime || item.end.date,
        }));
    };

    const addEvent = () => {
        if (window.gapi.client || localStorage.getItem("access_token")) {
            let today = new Date();

            fetch(
                `https://www.googleapis.com/calendar/v3/calendars/primary/events?key=${API_KEY}&timeMax=${new Date(
                    "Apr 14, 2021"
                ).toISOString()}&timeMin=${new Date(
                    "Apr 15, 2021"
                ).toISOString()}`,
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem(
                            "access_token"
                        )}`,
                    },
                }
            )
                .then((res) => res.json())
                .then((data) => console.log(data));
            //   fetch(
            //     `https://www.googleapis.com/calendar/v3/calendars/primary/events?key=${API_KEY}`,
            //     {
            //       method: "POST",
            //       headers: {
            //         Authorization: `Bearer ${localStorage.getItem("access_token")}`,
            //       },
            //       body: JSON.stringify({
            //         end: {
            //           dateTime: new Date("Apr 16, 2021"),
            //         },
            //         start: {
            //           dateTime: new Date("Apr 15, 2021"),
            //         },
            //         summary: "Test",
            //       }),
            //     }
            //   );
        }
    };

    return (
        <UserContext.Provider value={providerValue}>
            <div className="main-container">
                <div
                    style={{
                        width: "100%",
                        overflowX: "hidden",
                    }}
                >
                    <Typography variant="titulo">Calendario</Typography>

                    <div className="contenido">
                        <Grid container>
                            <Grid item xs={6}>
                                <Calendario events={events} />
                                <div
                                    style={{
                                        position: "relative",
                                        left: 0,
                                        top: 10,
                                    }}
                                >
                                    {sync === false ? (
                                        <LoadingButton
                                            variant="contained"
                                            loading={loading}
                                            onClick={() => {
                                                sincronizar();
                                            }}
                                        >
                                            Sincronizar calendario
                                        </LoadingButton>
                                    ) : (
                                        <Button
                                            variant="contained"
                                            loading={loading}
                                            onClick={() => {
                                                desincronizar();
                                            }}
                                        >
                                            Desincronizar
                                        </Button>
                                    )}
                                </div>
                            </Grid>
                            <Grid item xs={6}>
                                <Typography variant="titulo_3">
                                    Próximas Planificaciones ({stringDiaActual})
                                </Typography>
                                <div
                                    style={{
                                        marginTop: 30,
                                    }}
                                >
                                    {dayEvents !== null &&
                                    dayEvents.length > 0 ? (
                                        <div>
                                            {dayEvents.map((value, i) => (
                                                <Paper
                                                    tipo="general"
                                                    style={{
                                                        marginBottom: 30,
                                                    }}
                                                >
                                                    <div
                                                        id="Grupo_40146"
                                                        style={{
                                                            position:
                                                                "relative",
                                                            width: "12.327px",
                                                            height: "12.327px",
                                                            left: "0px",
                                                            top: "20px",
                                                            overflow: "visible",
                                                        }}
                                                    >
                                                        <svg
                                                            class="Trazado_32578"
                                                            viewBox="0 0 7.327 7.327"
                                                            style={{
                                                                overflow:
                                                                    "visible",
                                                                position:
                                                                    "relative",
                                                                width: "12",
                                                                height: "12",
                                                                left: "0px",
                                                                top: "0px",
                                                                transform:
                                                                    "matrix(1,0,0,1,0,0)",
                                                            }}
                                                        >
                                                            <path
                                                                style={{
                                                                    fill: "rgba(156,95,162,1)",
                                                                }}
                                                                id="Trazado_32578"
                                                                d="M 6.163330078125 -2.435176860160482e-08 C 9.567243576049805 -2.435176860160482e-08 12.32666015625 2.759417295455933 12.32666015625 6.163330078125 C 12.32666015625 9.567244529724121 9.567243576049805 12.32666015625 6.163330078125 12.32666015625 C 2.759417295455933 12.32666015625 0 9.567244529724121 0 6.163330078125 C 0 2.759417295455933 2.759417295455933 -2.435176860160482e-08 6.163330078125 -2.435176860160482e-08 Z"
                                                            ></path>
                                                        </svg>
                                                        <svg
                                                            class="Trazado_32579"
                                                            viewBox="0 0 7.327 7.327"
                                                            style={{
                                                                overflow:
                                                                    "visible",
                                                                position:
                                                                    "relative",
                                                                width: "11px",
                                                                height: "11px",
                                                                left: "5px",
                                                                top: -15.5,
                                                                transform:
                                                                    "matrix(1,0,0,1,0,0)",
                                                            }}
                                                        >
                                                            <path
                                                                style={{
                                                                    fill: "rgba(255,255,255,1)",
                                                                }}
                                                                id="Trazado_32579"
                                                                d="M 3.36774468421936 -2.435177393067534e-08 C 5.22769832611084 -2.435177393067534e-08 6.735489368438721 1.507790803909302 6.735489368438721 3.36774468421936 C 6.735489368438721 5.227698802947998 5.22769832611084 6.735489368438721 3.36774468421936 6.735489368438721 C 1.507790803909302 6.735489368438721 0 5.227698802947998 0 3.36774468421936 C 0 1.507790803909302 1.507790803909302 -2.435177393067534e-08 3.36774468421936 -2.435177393067534e-08 Z"
                                                            ></path>
                                                        </svg>
                                                    </div>
                                                    <div
                                                        style={{
                                                            position:
                                                                "relative",
                                                            top: -10,
                                                            left: 60,
                                                        }}
                                                    >
                                                        <Typography
                                                            variant="body5"
                                                            style={{
                                                                color: "#555",
                                                            }}
                                                        >
                                                            {value.title}
                                                        </Typography>
                                                    </div>
                                                    <div
                                                        style={{
                                                            position:
                                                                "relative",
                                                            top: 0,
                                                            left: 60,
                                                        }}
                                                    >
                                                        <Typography
                                                            variant="body1"
                                                            style={{
                                                                color: "#999",
                                                            }}
                                                        >
                                                            {dayjs(
                                                                value.start
                                                            ).format(
                                                                "h:mm a"
                                                            )}{" "}
                                                            -{" "}
                                                            {dayjs(
                                                                value.end
                                                            ).format("h:mm a")}
                                                        </Typography>
                                                    </div>
                                                </Paper>
                                            ))}
                                        </div>
                                    ) : (
                                        <p>
                                            No se registran eventos para este
                                            día
                                        </p>
                                    )}
                                </div>
                            </Grid>
                        </Grid>
                    </div>
                </div>
            </div>
        </UserContext.Provider>
    );
}
