import React, { useEffect } from "react";

import { Button, Typography } from "@mui/material";
import Box from "@mui/material/Box";
import { LinkContainer } from "react-router-bootstrap";

import logo from "../../assets/images/LogoNuevoConLetras.svg";

function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function ActividadTerminada() {
  useEffect(() => {
    document.title = "Actividad Terminada";

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
      <div
        style={{
          width: "fit-content",
          position: "fixed",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
        }}
      >
        <div className="contenido">
          <div
            style={{
              backgroundColor: "white",
              padding: "50px 50px",
              textAlign: "center",
              borderRadius: 25,
            }}
          >
            <img alt="logoArgumentapp" src={logo} style={{ width: "400px" }} />
            <RedBar></RedBar>
            <Typography variant="h5">Actividad Online Terminada</Typography>
            <RedBar></RedBar>

            <LinkContainer to="/">
              <Button variant="contained">Volver a la página principal</Button>
            </LinkContainer>
          </div>
        </div>
      </div>
  );
}

export default ActividadTerminada;
