export const CustomInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    borderRadius: 30,
    position: "relative",
    backgroundColor: theme.palette.mode === "light" ? "#f3f3f3" : "#2b2b2b",
    border: 0,
    fontSize: 13,
    width: "350px",
    padding: "15px 17px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
    ]),
    "&:focus": {
      boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.1rem`,
      borderColor: theme.palette.primary.main,
    },
  },
}));

export const CustomInput2 = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    borderRadius: 10,
    position: "relative",
    backgroundColor: theme.palette.mode === "light" ? "#fff" : "#2b2b2b",
    border: 1,
    borderStyle: "solid",
    borderColor: "#555",
    fontSize: 13,
    color: theme.palette.primary.main,
    fontWeight: 600,
    width: "447px",
    padding: "8px 10px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
    ]),
    "&:focus": {
      boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.1rem`,
      borderColor: theme.palette.primary.main,
    },
  },
}));

export const ButtonSimple = styled(Button)(({ theme }) => ({
  borderRadius: 0,
  boxShadow: "none",
  borderBottom: "2px solid #333",
  fontWeight: "normal",
  color: "#555",
  padding: 0,
}));

export const ArgumentappBox = styled(Box)(({ theme }) => ({
  height: "fit-content",
  borderRadius: "25px 25px 25px 25px",
  background: "#fff",
  boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
}));

export const CustomIconButton = styled(IconButton, {
  name: "CustomIconButton",
  slot: "Root",
})(({ theme }) => ({
  background: theme.palette.primary.main,
  boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.16)",
  color: theme.palette.primary.contrastText,
  ":hover": {
    background: hoverPrimario,
  },
}));

export const LoginTopButton = styled(Button, {
  shouldForwardProp: (prop) => prop !== "selected" && prop !== "sx",
  name: "LoginTopButton",
  slot: "Root",
})(({ theme }) => ({
  borderRadius: "25px 25px 0 0",
  backgroundColor: theme.palette.primary.main,
  boxShadow: "none",
  fontWeight: 500,
  fontSize: "15px",
  textAlign: "right",
  color: theme.palette.primary.contrastText,
  width: 250,
  height: 80,
  ":hover": {
    background: hoverPrimario,
  },
}));

export const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  ...(open && {
    ...openedMixin(theme),
    "& .MuiDrawer-paper": openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    "& .MuiDrawer-paper": closedMixin(theme),
  }),
}));

export const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: "flex-start",
}));
