import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Typography,
} from "@mui/material";
import React from "react";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import RenderDiapositivasRespuesta from "./RenderDiapositivasRespuesta";

function MainRespuestasEstudiantes({ codigo, dataActividades, idClase }) {
  return (
    <div style={{padding: 50, paddingTop: 20}}>
      {dataActividades.map((data, index) => {
        return (
          <Accordion
            style={{
              width: "100%",
              marginBottom: 20,
              borderRadius: 30,
              padding: 20
            }}
            key={index}
          >
            <AccordionSummary expandIcon={<ExpandMoreIcon />}>
              <Typography variant="h5">
                {data.dataActividad.nombreActividad}
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <RenderDiapositivasRespuesta
                codigo={codigo}
                idClase={idClase}
                idActividad={data.key}
                idVista={'alumnos'}
              ></RenderDiapositivasRespuesta>
            </AccordionDetails>
          </Accordion>
        );
      })}
    </div>
  );
}

export default MainRespuestasEstudiantes;
