import { Button, Popover } from "@mui/material";
import React from "react";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import {
  clonarDiapositiva,
  getCantidadDiapositivas,
} from "../Diapositivas/FirebaseDiapositivas";
function ClonarDiapositiva({
  nombreDiapositiva,
  tipoVista,
  idClase,
  idCurso,
  keyActividad,
  uid,
  tipoAcciones,
  acciones,
  contenidoHTML,
  lugarAlmacenamiento,
  imagen,
}) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const ClonarDiapo = () => {
    getCantidadDiapositivas(
      uid,
      idCurso,
      idClase,
      keyActividad,
      tipoVista,
      lugarAlmacenamiento,
      (orden) => {
        clonarDiapositiva(
          uid,
          idCurso,
          idClase,
          keyActividad,
          tipoVista,
          nombreDiapositiva,
          acciones,
          tipoAcciones,
          contenidoHTML,
          orden,
          imagen,
          lugarAlmacenamiento
        );
      }
    );
  };

  return (
    <div>
      <Button
        aria-describedby={id}
        onClick={handleClick}
        style={{
          padding: "8px",
          width: "-webkit-fill-available",
          margin: "5px",
          marginTop: 0,
          borderStyle: "solid",
          borderWidth: 1,
        }}
      >
        <FileCopyIcon style={{ marginRight: 4 }} />
        Clonar diapositiva
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "center",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "center",
          horizontal: "left",
        }}
      >
        <div
          style={{
            padding: 10,
            width: "250px",
          }}
        >
          <p
            style={{
              textAlign: "justify",
            }}
          >
            Clonar diapositiva {nombreDiapositiva} en vista de {tipoVista}
          </p>
          <Button
            onClick={(e) => {
              ClonarDiapo();
            }}
          >
            Clonar
          </Button>
          <Button onClick={handleClose}>Cerrar</Button>
        </div>
      </Popover>
    </div>
  );
}

export default ClonarDiapositiva;
