import {
  Box,
  Button,
  ButtonGroup,
  Card,
  CardContent,
  CardMedia,
  IconButton,
  Modal,
  Paper,
  TablePagination,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";
import { getCursosPublicos } from "../../../../database/firebase";
import LoadingArgumentapp from "../../../../components/LoadingArgumentapp";
import iconDefault from "../../../../assets/images/default_icon_clases.png";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { ArrowBackIosNew } from "@mui/icons-material";
import { CustomIconButton } from "../../../../styles/ArgumentappTheme";
import ListarClasesImportarContenido from "./ListarClasesImportarContenido";
import CopyContArgumentapp from "./CopyContArgumentapp";


function ImportarContenidoArgumentapp() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "#fff",
    border: "none",
    boxShadow: "3px 3px 10px rgba(0, 0, 0, 0.16)",
    borderRadius: 18,
    padding: 30,
  };

  const [banderaCarga, setBanderaCarga] = useState(false);
  const [cursosPublicos, setCursosPublicos] = useState([]);
  const [banderaCargaCursos, setBanderaCargaCursos] = useState("waiting");
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);
  const [banderaClases, setBanderaClases] = useState(false);
  const [idCursoVer, setIdCursoVer] = useState("");
  const [nombreCursoVer, setNombreCursoVer] = useState("");
  const [tipoCursos, settipoCursos] = useState("");
  const [estadoCurso, setEstadoCurso] = useState("");
  const [indexUnidad, setindexUnidad] = useState(0);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
    //
  };

  useEffect(() => {
    getCursosPublicos((value) => {
      setCursosPublicos(value);
      setBanderaCarga(true);
      setTimeout(() => {
        setBanderaCargaCursos(true);
      }, 100);
    });
  }, []);

  return (
    <div>
      <Button
        onClick={handleOpen}
        variant="contained"
        tipo="mini"
        style={{ marginLeft: 15 }}
      >
        Importar
      </Button>
      <Modal
        open={open}
        style={{
          backgroundColor: "#6c337375",
        }}
      >
        <Box style={style}>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              alignContent: "center",
              flexWrap: "nowrap",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <Typography
              id="modal-modal-title"
              variant="titulo_3"
              component="h2"
            >
              Contenidos Argumentapp
            </Typography>
            <IconButton onClick={handleClose} aria-label="delete">
              <CloseIcon />
            </IconButton>
          </div>

          <div>
            {banderaCargaCursos === true && (
              <div>
                <Paper
                  sx={{
                    padding: 1,
                    backgroundColor: "transparent",
                    boxShadow: "none",
                  }}
                >
                  {banderaCarga ? (
                    <div>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          flexWrap: "wrap",
                        }}
                      >
                        {cursosPublicos
                          .slice(
                            page * rowsPerPage,
                            page * rowsPerPage + rowsPerPage
                          )
                          .map((curso, index) => {
                            return (
                              <Card
                                key={curso.id}
                                sx={{
                                  display: "flex",
                                  borderWidth: 1,
                                  padding: 3,
                                  marginRight: 5,
                                  marginTop: 3,
                                  marginBottom: 3,
                                  minHeight: 250,
                                  maxWidth: 360,
                                  borderRadius: "30px",
                                  zoom: "80%",
                                }}
                                elevation={"1"}
                              >
                                <CardMedia
                                  component="img"
                                  sx={{ width: 150, borderRadius: "16px" }}
                                  image={
                                    curso.imagen === null
                                      ? iconDefault
                                      : curso.imagen
                                  }
                                  alt={"Miniatura " + curso.nombre}
                                />
                                <Box
                                  sx={{
                                    display: "flex",
                                    flexDirection: "column",
                                  }}
                                >
                                  <CardContent
                                    sx={{ flex: "1 0 auto", paddingRight: 0 }}
                                  >
                                    <Typography variant="h5">
                                      Unidad {index + 1}
                                    </Typography>

                                    <Typography
                                      variant="subtitle1"
                                      color="text.secondary"
                                      style={{
                                        maxWidth: 120,
                                        wordWrap: "break-word",
                                        overflowWrap: "break-word",
                                        hyphens: "auto",
                                      }}
                                    >
                                      <b>{curso.nombre}</b>
                                    </Typography>
                                  </CardContent>
                                  <Box
                                    sx={{
                                      display: "flex",
                                      flexDirection: "row",
                                      flexWrap: "wrap",
                                      justifyContent: "flex-end",
                                      pl: 1,
                                      pb: 1,
                                      height: "-webkit-fill-available",
                                      alignItems: "flex-end",
                                    }}
                                  >
                                    <ButtonGroup
                                      key={"bttn" + curso.code}
                                      disableElevation
                                    >
                                      <IconButton
                                        size="large"
                                        title="Ir al contenido Argumentapp"
                                        onClick={() => {
                                          setBanderaCargaCursos(false);
                                          setBanderaClases("waiting");
                                          setIdCursoVer(curso.id);
                                          settipoCursos("publicados");
                                          setNombreCursoVer(curso.nombre);
                                          setindexUnidad(index + 1);
                                          setEstadoCurso(curso.publicado);
                                          setTimeout(() => {
                                            setBanderaClases(true);
                                          }, 100);
                                        }}
                                      >
                                        <ArrowForwardIosIcon
                                          style={{ color: "GrayText" }}
                                          fontSize="inherit"
                                        />
                                      </IconButton>
                                    </ButtonGroup>
                                  </Box>
                                </Box>
                              </Card>
                            );
                          })}
                        {cursosPublicos.length === 0 && (
                          <div
                            style={{
                              display: "flex",
                              width: "100%",
                              flexDirection: "row",
                              flexWrap: "nowrap",
                              alignContent: "center",
                              justifyContent: "center",
                            }}
                          >
                            <Box
                              style={{
                                height: "20vh",
                                display: "flex",
                                flexDirection: "row",
                                flexWrap: "nowrap",
                                alignItems: "center",
                              }}
                            >
                              <p>No registra planificación ingresados</p>
                            </Box>
                          </div>
                        )}
                      </div>
                      {cursosPublicos.length > 6 && (
                        <TablePagination
                          rowsPerPageOptions={[6, 12, 18]}
                          count={cursosPublicos.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onPageChange={handleChangePage}
                          onRowsPerPageChange={handleChangeRowsPerPage}
                          labelDisplayedRows={({ from, to, count }) => {
                            return "" + from + "-" + to + " de " + count;
                          }}
                          labelRowsPerPage={"Elementos por páginas"}
                        ></TablePagination>
                      )}
                    </div>
                  ) : (
                    <div>
                      <Box
                        style={{
                          width: "auto",
                          height: "20vh",
                          display: "flex",
                          flexDirection: "row",
                          flexWrap: "nowrap",
                          justifyContent: "space-around",
                          alignContent: "center",
                          alignItems: "center",
                        }}
                      >
                        <LoadingArgumentapp></LoadingArgumentapp>
                      </Box>
                    </div>
                  )}
                </Paper>
              </div>
            )}

            {banderaCargaCursos === "waiting" && (
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "center",
                  height: 150,
                  alignContent: "center",
                  alignItems: "center",
                  justifyItems: "center",
                }}
              >
                <LoadingArgumentapp></LoadingArgumentapp>
              </div>
            )}

            {banderaClases && (
              <div>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <CustomIconButton
                    onClick={() => {
                      setBanderaCargaCursos("waiting");
                      setBanderaClases(false);
                      setTimeout(() => {
                        setIdCursoVer("");
                        setNombreCursoVer("");
                        setBanderaCargaCursos(true);
                      }, 1000);
                    }}
                    style={{
                      marginTop: 10,
                      marginBottom: 10,
                    }}
                  >
                    <ArrowBackIosNew style={{ color: "#fff" }} />
                  </CustomIconButton>
                  <CopyContArgumentapp
                    style={{ marginTop: 10, marginBottom: 10 }}
                    nombreCurso={nombreCursoVer}
                    idCurso={idCursoVer}
                    estadocurso={"publicado"}
                  ></CopyContArgumentapp>
                </div>
                <div style={{}}>
                  <ListarClasesImportarContenido
                    idCursoVer={idCursoVer}
                    tipoCursos={tipoCursos}
                    nombreCursoVer={nombreCursoVer}
                    indexUnidad={indexUnidad}
                    estadoCurso={estadoCurso}
                  ></ListarClasesImportarContenido>
                </div>
              </div>
            )}
          </div>
        </Box>
      </Modal>
    </div>
  );
}

export default ImportarContenidoArgumentapp;
