import firebaseConf from "../../database/firebase";

export const generadosCodigosActividades = (callback) => {
  var codigosExistentes = [];
  var code_random = null;

  firebaseConf.auth().onAuthStateChanged((user) => {
    firebaseConf
      .database()
      .ref("actividades_online")
      .child("listado")
      .get()
      .then((data) => {
        data.forEach((ids) => {
          // codigosExistentes.push(ids);
          codigosExistentes.push(Number(ids.val().code));
        });

        var bandera_bucle = false;

        let limite = 1000000
        let inferior = 100000

        if (codigosExistentes.length === limite) {
          callback('error')
        }else{
        do {
          code_random = Math.floor(Math.random() * (limite - inferior)) + inferior;
          bandera_bucle = false; // eslint-disable-next-line
          codigosExistentes.forEach((data) => {
            if (parseInt(data) === code_random) {
              bandera_bucle = true;
            }
          });
        } while (bandera_bucle);
        callback(code_random);
        }


      });
  });
};
