import {
  Button,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
} from "@mui/material";
import React from "react";
import { changeDiapositivaActivaProfesorById } from "../../BDActividadesOnline";

function AccionesDiapo({ acciones, codigoActividad }) {
  if (acciones.tipo === "Button") {
    return (
      <div
        style={{
          display: "flex",
          flexDirection: "row",
        }}
      >
        {acciones.item.map((value) => {
          return (
            <Button
              variant="contained"
              style={{ marginLeft: 10, width: 'auto' }}
              onClick={
                () =>
                  value.href.keyActividad !== "null" &&
                changeDiapositivaActivaProfesorById(
                  codigoActividad,
                  value.href.keyActividad,
                  value.href.keyDiapositiva,
                  value.href.tipoVista
                )
              }
            >
              {value.name}
            </Button>
          );
        })}
      </div>
    );
  }
  if (acciones.tipo === "CheckBox") {
    return (
      <div
        style={{
          textAlign: "start",
        }}
      >
        <FormControl>
          <FormGroup>
            {acciones.item.map((value) => {
              return <FormControlLabel control={<Checkbox />} label={value.name} />;
            })}
          </FormGroup>
        </FormControl>
      </div>
    );
  }
  if (acciones.tipo === "Sin acciones") {
    return <div></div>;
  }
}

export default AccionesDiapo;
