import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from "@mui/material";
import React, { useState } from "react";
import { useEffect } from "react";
import { listarTotalUsuarios } from "../../../database/firebase";
import BorrarUsuario from "./BorrarUsuarioModal";

const columns = [
  {
    id: "nombre",
    label: "Nombre",
    minWidth: 50,
    format: (value) => value.toLocaleString("en-US"),
  },
  { id: "correo", label: "Correo", minWidth: 50 },
  { id: "permisos", label: "Permisos", minWidth: 50 },
  {
    id: "fechaCreacion",
    label: "Creado",
    minWidth: 50,
  },
  {
    id: "ultimoacceso",
    label: "Último acceso",
    minWidth: 50,
  },
];

function ListarUsuarios() {
  const [lista, setLista] = useState([]);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);
  let permisosNombre = ["Administrador", "Moderador", "Profesor"];

  var getFecha = (seconds) => {
    var date = new Date(seconds);
    var fechaString = "";
    var months_arr = [
      "Enero",
      "Febrero",
      "Marzo",
      "Abril",
      "Mayo",
      "Junio",
      "Julio",
      "Agosto",
      "Septiembre",
      "Octubre",
      "Noviembre",
      "Diciembre",
    ];
    var year = date.getFullYear();
    var month = months_arr[date.getMonth()];
    var day = date.getDate();
    var minutos = date.getMinutes();
    var segundos = date.getSeconds();
    var hora = date.getHours();
    if (Number(segundos) < 10) {
      segundos = "0" + segundos;
    }
    if (Number(minutos) < 10) {
      minutos = "0" + minutos;
    }

    fechaString =
      day +
      " de " +
      month +
      " del " +
      year +
      " / " +
      hora +
      ":" +
      minutos +
      ":" +
      segundos;
    return fechaString;
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };
  useEffect(() => {
    listarTotalUsuarios((data) => {
      setLista(data);
    });
  }, []);
  return (
    <div>
      <Paper sx={{ width: "100%" }}>
        <TableContainer sx={{ height: 380 }}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ top: 0, minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
              <TableCell
                key={"accion"}
                align={"center"}
                style={{ top: 0, minWidth: 70 }}
              ></TableCell>
            </TableHead>
            <TableBody>
              {lista
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row) => {
                  return (
                    <TableRow hover role="checkbox" tabIndex={-1} key={row.uid}>
                      {columns.map((column) => {
                        var value = row[column.id];

                        if (column.id === "permisos") {
                          value = permisosNombre[Number(row[column.id])];
                        }
                        if (
                          row[column.id] === "" &&
                          column.id === "ultimoacceso"
                        ) {
                          value = "No registra";
                        }
                        if (
                          column.id === "ultimoacceso" &&
                          row[column.id] !== ""
                        ) {
                          value = getFecha(Number(row[column.id]));
                        }
                        if (
                          column.id === "fechaCreacion" &&
                          row[column.id] !== ""
                        ) {
                          value = getFecha(Number(row[column.id]));
                        }
                        return (
                          <TableCell key={column.id} align={column.align}>
                            {column.format && typeof value === "number"
                              ? column.format(value)
                              : value}
                          </TableCell>
                        );
                      })}

                      <TableCell style={{ textAlign: "center" }}>
                        <BorrarUsuario
                          uid={row.uid}
                          nombre={row.nombre}
                        ></BorrarUsuario>
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15]}
          component="div"
          count={lista.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
          labelDisplayedRows={({ from, to, count }) => {
            return "" + from + "-" + to + " de " + count;
          }}
          labelRowsPerPage={"Elementos por páginas"}
        />
      </Paper>
    </div>
  );
}

export default ListarUsuarios;
