import { Button } from "@mui/material";
import React, { useContext } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";

export default function Menu({ activo }) {
    const {
        setBanderaPerfil,
        setBanderaPlanes,
        setActivo
    } = useContext(UserContext);

    return (
        <div className="grupo-botones-config">
            <Button
                variant={activo === "perfil" ? "contained" : "contained2"}
                tipo="config"
                onClick={() => {
                    setBanderaPerfil(true);
                    setBanderaPlanes(false);
                    setActivo("perfil")
                }}
            >
                Perfil
            </Button>            
            <Button
                variant={activo === "planes" ? "contained" : "contained2"}
                tipo="config"
                onClick={() => {
                    setBanderaPerfil(false);
                    setBanderaPlanes(true);
                    setActivo("planes")
                }}
            >
                Planes
            </Button>
        </div>
    );
}
