import {
    Card,
    styled,
    Typography,
    CardActionArea,
    Box,
    Grid,
    Paper,
} from "@mui/material";
import { Link } from "react-router-dom";

export default function MainRecursos() {
    return (
        <div className="main-container">
            <div
                style={{
                    width: "100%",
                }}
            >
                <Typography variant="titulo">Recursos de Apoyo</Typography>

                <div className="contenido">
                    <Grid
                        container
                        spacing={2}
                        columns={16}
                        style={{
                            padding: 30,
                            justifyContent: "center",
                        }}
                    >
                        <Grid item xs={6}>
                            <Card style={{ borderRadius: 18 }}>
                                <CardActionArea
                                    style={{
                                        zIndex: "3",
                                    }}
                                    component={Link}
                                    to="/materialargumentapp"
                                >
                                    <Paper tipo="general">
                                        <Typography variant="titulo_3">
                                            Material Argumentapp
                                        </Typography>
                                        <Typography>
                                            Lorem ipsum dolor sit amet,
                                            consectetur adipiscing elit.
                                            Pellentesque pretium molestie
                                            eleifend.
                                        </Typography>
                                    </Paper>
                                </CardActionArea>
                            </Card>
                        </Grid>

                        {Number(
                            window.localStorage.getItem(
                                "dataUserArgumentapp_permisos"
                            )
                        ) !== 2 && (
                            <Grid item xs={6}>
                                <Card style={{ borderRadius: 18 }}>
                                    <CardActionArea
                                        style={{
                                            zIndex: "3",
                                        }}
                                        component={Link}
                                        to="/pedagogicos"
                                    >
                                        <Paper tipo="general">
                                            <Typography variant="titulo_3">
                                                Recursos Pedagógicos
                                            </Typography>
                                            <Typography>
                                                Lorem ipsum dolor sit amet,
                                                consectetur adipiscing elit.
                                                Pellentesque pretium molestie
                                                eleifend.
                                            </Typography>
                                        </Paper>
                                    </CardActionArea>
                                </Card>
                            </Grid>
                        )}
                    </Grid>
                </div>
            </div>
        </div>
    );
}
