import {  Typography } from "@mui/material";
import { Box } from "@mui/material";
import React, { useMemo, useState } from "react";
import ListaDisenoDiapositiva from "./ModalAddDiapositiva/ListaDisenoDiapositiva";
import { UserContext } from "../Funcionalidades/UseContext";
import AddDiapositivaProfesor from "./ModalAddDiapositiva/AddDiapositivaProfesor";

function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function MainDiapositivas({
  uid,
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  lugarAlmacenamiento,
}) {

  const [banderaDiapositivaDiseno, setbanderaDiapositivaDiseno] =
    useState(true);
  const [contenidoHTML, setContenidoHTML] = useState("");
  const [listaAcciones, setListadoAcciones] = useState([]);

  const providerValue = useMemo(
    () => ({
      setbanderaDiapositivaDiseno,
      setContenidoHTML,
      setListadoAcciones,
    }),
    [setbanderaDiapositivaDiseno, setContenidoHTML, setListadoAcciones]
  );



  const style = {

  };

  return (
    <div>
        <UserContext.Provider value={providerValue}>
          <Box sx={style}>
            <Typography
              variant="h5"
              style={{
                marginLeft: 30
              }}
            >
              Insertar diapositiva en vista del {tipoVista}
            </Typography>

            <div>
              {banderaDiapositivaDiseno ? (
                <div style={{marginTop: 80}}>
                  <ListaDisenoDiapositiva
                    tipoVista={tipoVista}
                  ></ListaDisenoDiapositiva>
                </div>
              ) : (
                <div>
                  <RedBar />
                  <AddDiapositivaProfesor
                    contenidoHTML={contenidoHTML}
                    acciones={listaAcciones}
                    uid={uid}
                    idClase={idClase}
                    idCurso={idCurso}
                    keyActividad={keyActividad}
                    tipoVista={tipoVista}
                    lugarAlmacenamiento={lugarAlmacenamiento}
                  />
                  <RedBar />
                </div>
              )}
            </div>
          </Box>
        </UserContext.Provider>
    </div>
  );
}

export default MainDiapositivas;
