import { Box } from "@mui/material";
import React, { useEffect, useState } from "react";
import parse from "html-react-parser";

import {
  dataValueButton,
  dataValueCheckbox,
  dataValueLista,
  dataValueSinAccion,
  dataValueTextArea,
  dataValueUploadPhoto,
  renderButtonList,
  renderCheckBoxList,
  renderTextArea,
  renderUploadPhoto,
} from "../Funcionalidades/AccionesDiapositivas";

function RedBar() {
  return <Box sx={{ height: 10 }} />;
}

function VistaDiapositiva({ valorHtml, acciones }) {
  const [valueCheckbox, setvalueCheckbox] = useState(dataValueCheckbox());
  const [listBotones, setlistBotones] = useState([]);
  const [listCheckBox, setlistCheckBox] = useState([]);

  useEffect(() => {
    if (acciones === undefined) {
      setvalueCheckbox(dataValueSinAccion());
    } else {
      if (acciones.tipoAccion === "Button") {
        setvalueCheckbox(dataValueButton());
        var listBotonestemp = [];
        for (let index = 0; index < acciones.cantidadItem; index++) {
          listBotonestemp.push(index + 1);
        }
        setlistBotones(listBotonestemp);
      }
      if (acciones.tipoAccion === "CheckBox") {
        setvalueCheckbox(dataValueLista());
        var listCheckBoxtemp = [];
        for (let index = 1; index <= acciones.cantidadItem; index++) {
          listCheckBoxtemp.push(index);
        }
        setlistCheckBox(listCheckBoxtemp);
      }
      if (acciones.tipoAccion === "TextArea") {
        setvalueCheckbox(dataValueTextArea());
      }
      if (acciones.tipoAccion === "Photo") {
        setvalueCheckbox(dataValueUploadPhoto());
      }
    }
  }, [acciones]);

  const style = {};

  return (
    <div>
      <Box sx={style}>
        <div
          style={{
            borderStyle: "solid",
            borderRadius: 25,
          }}
        >
          <div className="my-node" id="my-node">
            <div
              style={{
                margin: 30,
              }}
            >
              {/**Vista previa de la miniatura del diseño */}
              {parse(valorHtml)}
              {valueCheckbox[0] && renderButtonList(listBotones)}
              {valueCheckbox[1] && renderCheckBoxList(listCheckBox)}
              {valueCheckbox[3] && renderTextArea()}
              {valueCheckbox[4] && renderUploadPhoto()}
              <RedBar></RedBar> <RedBar></RedBar>
            </div>
          </div>
        </div>
      </Box>
    </div>
  );
}

export default VistaDiapositiva;
