import { Button } from "@mui/material";
import PhotoAlbumOutlinedIcon from "@mui/icons-material/PhotoAlbumOutlined";
import React from "react";

function ArchivosAbiertosRecientemente() {
  return (
    <div
      style={{
        padding: 10,
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
      }}
    >
      {[0, 1, 2, 3, 4, 5, 6, 7].map(() => {
        return (
          <div style={{ margin: 5, marginLeft: 0, marginRight: 12 }}>
            <Button
              variant="outlined"
              startIcon={
                <PhotoAlbumOutlinedIcon style={{ color: "#9c5fa2" }} />
              }
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "flex-start",
                backgroundColor: "white",
                borderStyle: "none",
                color: "#555",
                width: "fit-content",
              }}
            >
              Nombre Planificación
            </Button>
          </div>
        );
      })}
    </div>
  );
}

export default ArchivosAbiertosRecientemente;
