import React, { useContext } from "react";
import parse from "html-react-parser";
import domtoimage from "dom-to-image";

import { Button } from "@mui/material";
import { UserContext } from "../../Funcionalidades/UseContext";
import {
  getCantidadDiapositivas,
  setDiapositiva,
} from "../FirebaseDiapositivas";
import { Box } from "@mui/material";
import {
  renderContenidoAccionesButton,
  renderContenidoAccionesCheckBox,
  renderTextArea,
  renderUploadPhoto,
} from "../../Funcionalidades/AccionesDiapositivas";
import Swal from "sweetalert2";
import "../../../styles/CustomClassSweetAlert2.css";

function VistaPreviaDiapositiva({ lugarAlmacenamiento }) {
  const {
    valueCheckbox,
    contenidoHTMLLocal,
    accionesUpload,
    nombreDiapositiva,
    uid,
    idCurso,
    idClase,
    keyActividad,
    tipoVista,
  } = useContext(UserContext);

  function RedBar() {
    return <Box sx={{ height: 38 }} />;
  }

  const subirDatos = () => {
    if (
      nombreDiapositiva !== "" &&
      accionesUpload !== [] &&
      contenidoHTMLLocal !== ""
    ) {
      let timerInterval;
      var valueState = "Obteniendo la miniatura...";
      Swal.fire({
        customClass: {
          container: "my-swal",
        },
        title: "Creando diapositiva.",
        html: "<b></b>",
        timerProgressBar: false,
        didOpen: () => {
          Swal.showLoading();
          const b = Swal.getHtmlContainer().querySelector("b");
          timerInterval = setInterval(() => {
            b.textContent = valueState;
          }, 50);
        },
        willClose: () => {
          clearInterval(timerInterval);
        },
      });

      var node = document.getElementById("my-node");
      domtoimage
        .toPng(node, { quality: 0.7 })
        .then(function (dataUrl) {
          var tipoAccion = "Sin acciones";
          if (valueCheckbox[0]) {
            tipoAccion = "Button";
          }
          if (valueCheckbox[1]) {
            tipoAccion = "CheckBox";
          }
          if (valueCheckbox[3]) {
            tipoAccion = "TextArea";
          }
          if (valueCheckbox[4]) {
            tipoAccion = "Photo";
          }
          valueState = "Obteniendo el orden de la diapositiva...";
          getCantidadDiapositivas(
            uid,
            idCurso,
            idClase,
            keyActividad,
            tipoVista,
            lugarAlmacenamiento,
            (orden) => {
              valueState = "Subiendo los datos ingresados...";

              setDiapositiva(
                uid,
                idCurso,
                idClase,
                keyActividad,
                tipoVista,
                nombreDiapositiva,
                accionesUpload,
                tipoAccion,
                contenidoHTMLLocal,
                orden,
                dataUrl,
                lugarAlmacenamiento,
                (value) => {
                  if (value) {
                    Swal.fire({
                      customClass: {
                        container: "my-swal",
                      },
                      icon: "success",
                      title: "Diapositiva creada",
                      text: "La diapositiva ingresada ha sido agregada con exito a la actividad indicada.",
                      showConfirmButton: true,
                      allowOutsideClick: false,
                      allowEscapeKey: false,
                      confirmButtonText: "Continuar",
                    }).then((result) => {
                      /* Read more about isConfirmed, isDenied below */
                      if (result.isConfirmed) {
                        window.location.reload();
                      }
                    });
                  } else {
                    Swal.fire({
                      customClass: {
                        container: "my-swal",
                      },
                      icon: "error",
                      title: "Diapositiva no fue creada",
                      text: "Ha ocurrido un error al momento de crear la diapositiva, intente nuevamente. Si el problema perciste, contante con soporte de arguemntapp. ",
                      showConfirmButton: true,
                      allowOutsideClick: true,
                      allowEscapeKey: true,
                      confirmButtonText: "Cerrar",
                    }).then((result) => {
                      /* Read more about isConfirmed, isDenied below */
                    });
                  }
                  console.log(value);
                },
                (state) => {
                  valueState = state;
                }
              );
            }
          );
        })
        .catch(function (error) {
          valueState =
            "Los datos no se ingresaron de manera correcta, vuelva a intentarlo.";
        });
    }
  };

  return (
    <div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          marginTop: 30,
        }}
      >
        <div
          style={{
            borderStyle: "solid",
            borderRadius: 25,
            minWidth: 1200,
            minHeight: 700,
            maxWidth: 1200,
            maxHeight: 700,
            width: 1200,
            height: 700,
            backgroundColor: "white",
            marginBottom: 30,
          }}
        >
          <div className="my-node" id="my-node">
            <div
              style={{
                margin: 30,
              }}
            >
              <div style={{}}>
                {parse(contenidoHTMLLocal)}
                {/**Vista previa de antes de subir diapositiva ADD */}
                {valueCheckbox[0] &&
                  renderContenidoAccionesButton(accionesUpload)}
                {valueCheckbox[1] &&
                  renderContenidoAccionesCheckBox(accionesUpload)}
                {valueCheckbox[3] && renderTextArea()}
                {valueCheckbox[4] && renderUploadPhoto()}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div style={{ display: "flex", width: 1000, justifyContent: 'flex-end'}}>
        <Button
          variant="contained"
          onClick={(e) => {
            subirDatos();
          }}
        >
          Subir diapositiva
        </Button>
      </div>
    </div>
  );
}

export default VistaPreviaDiapositiva;
