import {
  Box,
  Button,
  ButtonGroup,
  Card,
  CardContent,
  CardMedia,
  CircularProgress,
  IconButton,
  Paper,
  Slide,
  TablePagination,
  Typography,
} from "@mui/material";
import React, { useEffect, useState, useContext } from "react";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { getCursosPublicosNoPublicados } from "../../database/firebase";
import AddCursoPublico from "./CrearCursos/AddCursoPublico";
import { UserContext } from "../Funcionalidades/UseContext";
import DeleteCursoModal from "./CrearCursos/DeleteCursoModal";

import iconDefault from "../../assets/images/default_icon_curso_2.png";
import AddActividadModal from "./CrearCursos/AddActividadModal";

function CursosPublicosNoPublicados() {
  const [banderaCarga, setBanderaCarga] = useState(false);
  const [cursosPublicosNoPublicados, setCursosPublicosNoPublicados] = useState(
    []
  );
  const [banderaCargaSlide, setBanderaCargaSlide] = useState(false);

  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const {
    setBanderaClases,
    setIdCursoVer,
    setNombreCursoVer,
    settipoCursos,
    setEstadoCurso,
    setindexUnidad,
    setTipoSalida,
  } = useContext(UserContext);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
    //
  };

  useEffect(() => {
    getCursosPublicosNoPublicados((value) => {
      setCursosPublicosNoPublicados(value);
      setBanderaCarga(true);
      setTimeout(() => {
        setBanderaCargaSlide(true);
      }, 100);
    });
  }, []);

  return (
    <div>
      <Box width="100%">
        <Paper
          sx={{
            width: "fit-content",
            marginTop: "10px",
            marginBottom: "10px",
            boxShadow: "none",
            backgroundColor: "transparent",
          }}
        >
          <AddActividadModal tipoCurso={'publicos'}></AddActividadModal>
          {/* <AddCursoPublico state="no-publicado"></AddCursoPublico> */}
        </Paper>
      </Box>
      <Paper
        sx={{ padding: 3, backgroundColor: "transparent", boxShadow: "none" }}
      >
        {banderaCarga ? (
          <Slide
            direction="right"
            in={banderaCargaSlide}
            mountOnEnter
            unmountOnExit
          >
            <div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  flexWrap: "wrap",
                }}
              >
                {cursosPublicosNoPublicados
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((curso, index) => {
                    return (
                      <Card
                        key={curso.id}
                        sx={{
                          display: "flex",
                          borderWidth: 1,
                          padding: 3,
                          marginRight: 3,
                          marginBottom: 2,
                          minHeight: 250,
                          maxWidth: 340,
                          borderRadius: "30px",
                        }}
                      >
                        <CardMedia
                          component="img"
                          sx={{ width: 150, borderRadius: "16px" }}
                          image={
                            curso.imagen === null ? iconDefault : curso.imagen
                          }
                          alt={"Miniatura " + curso.nombre}
                        />
                        <Box sx={{ display: "flex", flexDirection: "column" }}>
                          <CardContent
                            sx={{ flex: "1 0 auto", paddingRight: 0 }}
                          >
                            <Typography variant="h5">
                              Unidad {index + 1}
                            </Typography>
                            <Typography
                              variant="subtitle1"
                              color="text.secondary"
                              style={{
                                maxWidth: 120,
                                wordWrap: "break-word",
                                overflowWrap: "break-word",
                                hyphens: "auto",
                              }}
                            >
                              <b>{curso.nombre}</b>
                            </Typography>
                          </CardContent>
                          <Box
                            sx={{
                              display: "flex",
                              flexDirection: "row",
                              flexWrap: "wrap",
                              justifyContent: "flex-end",
                              pl: 1,
                              pb: 1,
                              height: "-webkit-fill-available",
                              alignItems: "flex-end",
                            }}
                          >
                            <ButtonGroup
                              key={"bttn" + curso.code}
                              disableElevation
                            >
                              <DeleteCursoModal
                                id={curso.id}
                                nombre={curso.nombre}
                                state="noPublicadoPublico"
                              ></DeleteCursoModal>
                              {/* <Button
                                color="success"
                                style={{
                                  backgroundColor: "green",
                                  color: "white",
                                  height: "35px",
                                }}
                                onClick={() => {
                                  setIdCursoVer(curso.id);
                                  setBanderaClases(true);
                                  settipoCursos("no-publicados");
                                  setNombreCursoVer(curso.nombre);
                                  setEstadoCurso(curso.publicado);
                                }}
                              >
                                Ver
                              </Button> */}
                              <IconButton
                                size="large"
                                onClick={() => {
                                  setIdCursoVer(curso.id);
                                  settipoCursos("publicados");
                                  setNombreCursoVer(curso.nombre);
                                  setindexUnidad(index + 1);
                                  setTipoSalida("left");
                                  setBanderaCargaSlide(false);
                                  setEstadoCurso(curso.publicado);
                                  setTimeout(() => {
                                    setBanderaClases(true);
                                  }, 100);
                                }}
                              >
                                <ArrowForwardIosIcon
                                  style={{ color: "GrayText" }}
                                  fontSize="inherit"
                                />
                              </IconButton>
                            </ButtonGroup>
                          </Box>
                        </Box>
                      </Card>
                    );
                  })}
                {cursosPublicosNoPublicados.length === 0 && (
                  <div
                    style={{
                      display: "flex",
                      width: "100%",
                      flexDirection: "row",
                      flexWrap: "nowrap",
                      alignContent: "center",
                      justifyContent: "center",
                    }}
                  >
                    <Box
                      style={{
                        height: "20vh",
                        display: "flex",
                        flexDirection: "row",
                        flexWrap: "nowrap",
                        alignItems: "center",
                      }}
                    >
                      <p>No registra planificación ingresados</p>
                    </Box>
                  </div>
                )}
              </div>
              {cursosPublicosNoPublicados.length > 6 && (
                <TablePagination
                  rowsPerPageOptions={[6, 12, 18]}
                  count={cursosPublicosNoPublicados.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onPageChange={handleChangePage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                  labelDisplayedRows={({ from, to, count }) => {
                    return "" + from + "-" + to + " de " + count;
                  }}
                  labelRowsPerPage={"Elementos por páginas"}
                ></TablePagination>
              )}
            </div>
          </Slide>
        ) : (
          <div>
            <Box
              style={{
                width: "auto",
                height: "20vh",
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                justifyContent: "space-around",
                alignContent: "center",
                alignItems: "center",
              }}
            >
              <CircularProgress></CircularProgress>
            </Box>
          </div>
        )}
      </Paper>
    </div>
  );
}

export default CursosPublicosNoPublicados;
