import {
  Alert,
  Box,
  Button,
  IconButton,
  LinearProgress,
  Modal,
  TextareaAutosize,
  Typography,
} from "@mui/material";
import React, { useCallback, useEffect, useMemo, useState } from "react";
import { setMisCursosFirebase } from "../../../database/firebase";
import CloseIcon from "@mui/icons-material/Close";
import ImportarContenidoArgumentapp from "./ModalImportarContArgumentapp/ImportarContenidoArgumentapp";
import {
  CustomIconButton,
  CustomInput2,
} from "../../../styles/ArgumentappTheme";
import { ArrowBackIosNew } from "@mui/icons-material";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import DoNotDisturbOnIcon from "@mui/icons-material/DoNotDisturbOn";
import CheckIcon from "@mui/icons-material/Check";
import { useHistory } from "react-router-dom";
import BoxCrearClase from "../Clases/BoxCrearClase";
import { UserContext } from "../../Funcionalidades/UseContext";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function AddActividadModal({ tipoCurso }) {
  const [open, setOpen] = useState(false);
  const [selectorBox, setSelectorBox] = useState("unidades");
  const handleOpen = () => setOpen(true);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleClose = useCallback(() => {
    resetValuesModal();
    setOpen(false);
  });
  const [nombre, setNombre] = useState("");
  const [nivel, setNivel] = useState("");
  const [asignatura, setAsignatura] = useState("");
  const [file, setFile] = useState(null);
  const [nameFile, setNameFile] = useState("");
  const [urlImageUpload, seturlImageUpload] = useState(null);
  const [progreso, setProgreso] = useState(null);

  const [selectorView, setselectorView] = useState("step1");
  const [banderaAlerta, setbanderaAlerta] = useState({
    state: false,
    msg: "",
  });
  const [presentacion, setPresentacion] = useState("");
  const [referencias, setReferencias] = useState("");
  const [progresiones, setProgresiones] = useState(["", ""]);
  const [refresh, setrefresh] = useState(true);
  const history = useHistory();

  const providerValue = useMemo(
    () => ({
      handleClose,
      tipoCurso,
    }),
    [handleClose, tipoCurso]
  );

  var dataProgresiones = progresiones;
  const getFile = (e) => {
    setFile(e.target.files[0]);
    setNameFile(e.target.files[0].name);
    seturlImageUpload(URL.createObjectURL(e.target.files[0]));
  };

  const resetValuesModal = () => {
    setNombre("");
    setNivel("");
    setAsignatura("");
    setFile(null);
    setNameFile("");
    setProgreso(null);
    setselectorView("step1");
    setbanderaAlerta({
      state: false,
      msg: "",
    });
    setReferencias("");
    setPresentacion("");
    setProgresiones(["", "", "", "", "", ""]);
    setSelectorBox("unidades");
  };

  useEffect(() => {
    setProgresiones(dataProgresiones);
  }, [dataProgresiones, refresh]);

  const renderProgresiones = () => {
    var render = []; // eslint-disable-next-line
    progresiones.map((value, index) => {
      render.push(
        <TextareaAutosize
          style={{
            width: 310,
            height: 100,
            resize: "none",
            overflow: "auto",
            overflowY: "auto",
            borderRadius: 10,
            color: "#9c5fa2",
            padding: 10,
            fontWeight: 600,
            fontSize: 14,
            marginBottom: 15,
          }}
          key={index}
          onBlur={(e) => {
            let tempProgresiones = progresiones;
            tempProgresiones[index] = e.target.value;
            setProgresiones(tempProgresiones);
            console.log(e.target.value);
          }}
        />
      );
    });
    return render;
  };

  const resizeProgresiones = (state) => {
    let tempData = dataProgresiones;
    if (state === "less") {
      tempData.pop();
      dataProgresiones = tempData;
      setrefresh(!refresh);
    } else {
      tempData.push("");
      dataProgresiones = tempData;
      setrefresh(!refresh);
    }
  };

  const fileInputRef = React.createRef();

  const subirDatos = () => {
    var imagen = nameFile === "" ? null : { file: file, name: nameFile };

    if (tipoCurso === "privados") {
      setMisCursosFirebase(
        nombre,
        nivel,
        asignatura,
        presentacion,
        referencias,
        progresiones,
        imagen,
        (porcentaje) => {
          setProgreso(porcentaje);
        },
        (callback) => {
          if (callback) {
            resetValuesModal();
            setselectorView("finish");
          } else {
            console.error("Error");
          }
        }
      );
    }
  };

  const isOkStep = () => {
    if (selectorView === "step1") {
      if (nombre !== "" && asignatura !== "" && nivel !== "") {
        setbanderaAlerta({
          state: false,
          msg: "",
        });
        setselectorView("step2");
      } else {
        setbanderaAlerta({
          state: true,
          msg: "El nombre de la unidad, la asignatura y el nivel son obligatorios. Vuelva a intentarlo.",
        });
        setTimeout(() => {
          setbanderaAlerta({
            state: false,
            msg: "",
          });
        }, 5000);
      }
    } else {
      if (selectorView === "step2") {
        setselectorView("step3");
      } else {
        if (selectorView === "step3") {
          subirDatos();
        } else {
          if (selectorView === "finish") {
          }
        }
      }
    }
  };

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "auto",
    boxShadow: 24,
    p: 4,
    height: "auto",
    minWidth: "fit-content",
    backgroundColor: "#fff",
    borderRadius: "18px",
  };

  return (
    <div style={{ display: "flex" }}>
      {" "}
      <UserContext.Provider value={providerValue}>
        {tipoCurso === "privados" ? (
          <Button onClick={handleOpen} variant="contained" tipo="mini">
            Agregar
          </Button>
        ) : (
          <Button onClick={handleOpen} variant="contained">
            Crear unidad argumentapp
          </Button>
        )}

        <Modal
          open={open}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <div>
            {selectorBox === "unidades" && (
              <Box sx={style}>
                {selectorView !== "finish" && (
                  <div
                    style={{
                      marginBottom: "10px",
                      display: "flex",
                      flexDirection: "row",
                      flexWrap: "nowrap",
                      alignContent: "center",
                      justifyContent: "space-between",
                      alignItems: "center",
                    }}
                  >
                    <div></div>
                    <Typography id="modal-modal-title" variant="titulo_4">
                      Agregar{" "}
                      {tipoCurso === "privados"
                        ? "Unidad"
                        : "Unidad Argumentapp"}
                    </Typography>

                    <IconButton onClick={handleClose} aria-label="delete">
                      <CloseIcon />
                    </IconButton>
                  </div>
                )}

                {banderaAlerta.state === false ? (
                  <div
                    style={{ height: selectorView === "step1" ? 53 : 0 }}
                  ></div>
                ) : (
                  <Alert
                    style={{ marginBottom: 5, height: 48 }}
                    severity="error"
                  >
                    {banderaAlerta.msg}
                  </Alert>
                )}
                {selectorView === "step1" && (
                  <div>
                    <div style={{ display: "flex", flexDirection: "row" }}>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          justifyContent: "space-between",
                        }}
                      >
                        <div
                          style={{ display: "flex", flexDirection: "column" }}
                        >
                          <Typography variant="textoForm">
                            Nombre de la Unidad*
                          </Typography>
                          <CustomInput2
                            required
                            value={nombre}
                            onChange={(e) => {
                              setNombre(e.target.value);
                            }}
                          />
                        </div>
                        <div
                          style={{ display: "flex", flexDirection: "column" }}
                        >
                          <Typography variant="textoForm">
                            Asignatura*
                          </Typography>
                          <CustomInput2
                            required
                            label="Disciplina"
                            value={asignatura}
                            onChange={(e) => {
                              setAsignatura(e.target.value);
                            }}
                            variant="filled"
                            style={{
                              backgroundColor: "white",
                              minWidth: "300px",
                            }}
                          />
                        </div>
                        <div
                          style={{ display: "flex", flexDirection: "column" }}
                        >
                          <Typography variant="textoForm">Nivel*</Typography>

                          <CustomInput2
                            required
                            label="Nivel de estudio"
                            variant="filled"
                            onChange={(e) => {
                              setNivel(e.target.value);
                            }}
                            value={nivel}
                            style={{
                              backgroundColor: "white",
                              minWidth: "300px",
                            }}
                          />
                        </div>
                      </div>
                      <div
                        style={{
                          display: "flex",
                          paddingTop: 0,
                          marginLeft: 20,
                          alignItems: "center",
                        }}
                      >
                        {/*

              {nameFile !== "" && (
                <p
                  style={{
                    color: "white",
                    marginLeft: 10,
                  }}
                >
                  {nameFile}
                </p>
              )}

              {progreso !== null && (
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    backgroundColor: "#fff",
                  }}
                >
                  <Box sx={{ width: "100%", mr: 1 }}>
                    <LinearProgress variant="determinate" value={progreso} />
                  </Box>
                  <Box sx={{ minWidth: 35 }}>
                    <Typography
                      variant="body2"
                      color="text.secondary"
                    >{`${Math.round(progreso)}%`}</Typography>
                  </Box>
                </Box>
              )} */}
                        {file === null ? (
                          <div
                            style={{
                              width: 213,
                              height: 248,
                              borderRadius: 16,
                              background: "#f3f3f3",
                              display: "flex",
                              flexDirection: "row",
                              justifyContent: "center",
                              alignItems: "center",
                            }}
                          >
                            <Button
                              variant="outlined"
                              startIcon={<CloudUploadIcon />}
                              style={{
                                width: "fit-content",
                                height: "fit-content",
                                backgroundColor: "#fff",
                              }}
                              onClick={() => fileInputRef.current.click()}
                            >
                              Subir carátula
                            </Button>

                            {/* {progreso !== null && Math.round(progreso) !== 100 && (
                            <Box
                              sx={{
                                display: "flex",
                                alignItems: "center",
                                backgroundColor: "#fff",
                              }}
                            >
                              <Box sx={{ width: "100%", mr: 1 }}>
                                <LinearProgress
                                  variant="determinate"
                                  value={progreso}
                                />
                              </Box>
                              <Box sx={{ minWidth: 35 }}>
                                <Typography
                                  variant="body2"
                                  color="text.secondary"
                                >{`${Math.round(progreso)}%`}</Typography>
                              </Box>
                            </Box>
                          )} */}

                            <input
                              ref={fileInputRef}
                              accept="image/*"
                              type="file"
                              hidden
                              onChange={getFile}
                            />
                          </div>
                        ) : (
                          <div
                            style={{
                              width: 213,
                              height: 248,
                              borderRadius: 16,
                              background: `url(${urlImageUpload})`,
                              backgroundSize: "cover",
                              backgroundPosition: "center",
                              display: "flex",
                              flexDirection: "row",
                              justifyContent: "end",
                            }}
                          >
                            <div style={{ padding: 8 }}>
                              <IconButton
                                onClick={(e) => {
                                  setFile(null);
                                  setNameFile("");
                                  seturlImageUpload(null);
                                }}
                                style={{
                                  height: "fit-content",
                                  width: "fit content",
                                  backgroundColor: "#9c5fa2",
                                }}
                                size="medium"
                              >
                                <CloseIcon
                                  style={{ color: "#fff" }}
                                  fontSize="inherit"
                                />
                              </IconButton>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                    <RedBar></RedBar>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "center",
                        padding: 25,
                      }}
                    >
                      <Button
                        variant="contained"
                        // onClick={(e) => subirDatos()}
                        onClick={(e) => isOkStep()}
                      >
                        Continuar
                      </Button>
                    </div>
                  </div>
                )}
                {selectorView === "step2" && (
                  <div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        alignItems: "center",
                      }}
                    >
                      <CustomIconButton
                        onClick={() => {
                          setselectorView("step1");
                        }}
                        style={{ height: 40, width: 40 }}
                      >
                        <ArrowBackIosNew style={{ color: "#fff" }} />
                      </CustomIconButton>
                    </div>
                    <div style={{ marginTop: 10, marginBottom: 10 }}>
                      <Typography
                        variant="textoForm"
                        style={{
                          color: "#9c5fa2",
                          fontSize: 14,
                        }}
                      >
                        Te recomendamos….
                      </Typography>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "space-between",
                        height: 380,
                      }}
                    >
                      <div style={{ display: "flex", flexDirection: "column" }}>
                        <div>
                          <Typography variant="textoForm">
                            Presentación{" "}
                          </Typography>
                          <Typography
                            variant="textoForm"
                            style={{ fontSize: 11 }}
                          >
                            (opcional){" "}
                          </Typography>
                        </div>
                        <TextareaAutosize
                          style={{
                            width: 700,
                            height: 180,
                            resize: "none",
                            overflow: "auto",
                            overflowY: "auto",
                            borderRadius: 10,
                            color: "#9c5fa2",
                            padding: 10,
                            fontWeight: 600,
                            fontSize: 14,
                          }}
                          onChange={(e) => {
                            setPresentacion(e.target.value);
                          }}
                        />
                      </div>
                      <div style={{ display: "flex", flexDirection: "column" }}>
                        {" "}
                        <div>
                          <Typography variant="textoForm">
                            Referencias
                          </Typography>{" "}
                          <Typography
                            variant="textoForm"
                            style={{ fontSize: 11 }}
                          >
                            (opcional){" "}
                          </Typography>
                        </div>
                        <TextareaAutosize
                          style={{
                            width: 700,
                            height: 130,
                            resize: "none",
                            overflow: "auto",
                            overflowY: "auto",
                            borderRadius: 10,
                            color: "#9c5fa2",
                            padding: 10,
                            fontWeight: 600,
                            fontSize: 14,
                          }}
                          onChange={(e) => {
                            setReferencias(e.target.value);
                          }}
                        />
                      </div>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "center",
                        padding: 25,
                        paddingBottom: 0,
                      }}
                    >
                      <Button
                        variant="contained"
                        // onClick={(e) => subirDatos()}
                        onClick={(e) => isOkStep()}
                      >
                        Continuar
                      </Button>
                    </div>
                  </div>
                )}
                {selectorView === "step3" && (
                  <div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        alignItems: "center",
                      }}
                    >
                      <CustomIconButton
                        onClick={() => {
                          setselectorView("step2");
                        }}
                        style={{ height: 40, width: 40 }}
                      >
                        <ArrowBackIosNew style={{ color: "#fff" }} />
                      </CustomIconButton>
                    </div>
                    <div style={{ marginTop: 10, marginBottom: 10 }}>
                      <Typography
                        variant="textoForm"
                        style={{
                          color: "#9c5fa2",
                          fontSize: 14,
                        }}
                      >
                        Te recomendamos….
                      </Typography>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                      }}
                    >
                      <div style={{ display: "flex", flexDirection: "column" }}>
                        <div>
                          <Typography variant="textoForm">
                            Progresión Hipotética de Aprendizaje{" "}
                          </Typography>
                          <Typography
                            variant="textoForm"
                            style={{ fontSize: 11 }}
                          >
                            (opcional){" "}
                          </Typography>
                        </div>
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            flexWrap: "wrap",
                            width: 640,
                            justifyContent: "space-between",
                            marginTop: 15,
                            maxHeight: 350,
                            overflowY: "auto",
                          }}
                        >
                          {renderProgresiones()}
                        </div>
                      </div>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          justifyContent: "space-between",
                          width: "100%",
                        }}
                      >
                        {progresiones.length > 1 ? (
                          <Button
                            variant="contained"
                            startIcon={
                              <DoNotDisturbOnIcon
                                style={{ color: "#9c5fa2" }}
                              />
                            }
                            style={{
                              width: "fit-content",
                              fontSize: 12,
                              padding: 7,
                              height: "fit-content",
                              color: "#9c5fa2",
                              backgroundColor: "transparent",
                              boxShadow: "none",
                            }}
                            onClick={(e) => {
                              resizeProgresiones("less");
                            }}
                          >
                            Quitar
                          </Button>
                        ) : (
                          <div></div>
                        )}
                        <Button
                          variant="contained"
                          startIcon={
                            <AddCircleIcon style={{ color: "#9c5fa2" }} />
                          }
                          style={{
                            width: "fit-content",
                            fontSize: 12,
                            padding: 7,
                            height: "fit-content",
                            color: "#9c5fa2",
                            backgroundColor: "transparent",
                            boxShadow: "none",
                          }}
                          onClick={(e) => {
                            resizeProgresiones("more");
                          }}
                        >
                          Crear más
                        </Button>
                      </div>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "center",
                        padding: 25,
                        paddingBottom: 0,
                      }}
                    >
                      <Button variant="contained" onClick={(e) => isOkStep()}>
                        Crear Unidad
                      </Button>
                    </div>
                  </div>
                )}
                {selectorView === "finish" && (
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "space-between",
                      alignItems: "center",
                      height: 300,
                      width: 400,
                      padding: 25,
                    }}
                  >
                    <div>
                      <CheckIcon
                        fontSize="large"
                        style={{
                          color: "#9c5fa2",
                          borderRadius: 50,
                          borderStyle: "solid",
                          borderWidth: 1,
                          height: 60,
                          width: 60,
                          padding: 10,
                        }}
                      ></CheckIcon>
                    </div>
                    <div>
                      <Typography variant="textoClase">
                        Unidad fue creada correctamente
                      </Typography>
                    </div>
                    <div style={{ width: "100%" }}>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          justifyContent: "space-around",
                        }}
                      >
                        <Button
                          variant="outlined"
                          style={{ width: 150 }}
                          onClick={(e) => {
                            history.go(0);
                          }}
                        >
                          Ir a la unidad
                        </Button>
                        <Button
                          variant="contained"
                          style={{ width: 150 }}
                          onClick={(e) => {
                            setSelectorBox("clases");
                          }}
                        >
                          Crear una clase
                        </Button>
                      </div>
                    </div>
                    <div style={{ textAlign: "center" }}>
                      <Typography variant="infoTextoRosa">
                        * Si vas a la unidad, vas a poder crear clases más
                        adelante en el mismo panel.
                      </Typography>
                    </div>
                  </div>
                )}
              </Box>
            )}
            {selectorBox === "clases" && (
              <BoxCrearClase tipoCurso={tipoCurso} />
            )}
          </div>
        </Modal>

        {tipoCurso === "privados" && (
          <ImportarContenidoArgumentapp></ImportarContenidoArgumentapp>
        )}
      </UserContext.Provider>
    </div>
  );
}

export default AddActividadModal;
