import { Box, CircularProgress, Grid, Slide } from "@mui/material";
import React, { useEffect, useState } from "react";
import { getDisenosDiapositivasXVista } from "../FirebaseDiapositivas";
import ItemDisenoEnLista from "./ItemDisenoEnLista";

function ListaDisenoDiapositiva({ tipoVista }) {
  const [disenoDiapositiva, setDisenoDiapositiva] = useState([]);
  const [banderaCarga, setBanderaCarga] = useState(true);

  useEffect(() => {
    getDisenosDiapositivasXVista(tipoVista, (data) => {
      setDisenoDiapositiva(data);
      setBanderaCarga(false);
    });
    // eslint-disable-next-line
  }, []);

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        alignContent: "center",
        justifyContent: 'center',
        alignItems: 'center',
        width: '80vw',
        marginBottom: 10,
      }}
    >
      {!banderaCarga ? (
        <Slide direction={"left"} in={true} mountOnEnter unmountOnExit>
          <Grid container justifyContent="center" spacing={3}>
            {disenoDiapositiva.map((value, index) => {
              return (
                <ItemDisenoEnLista
                  value={value}
                  index={index}
                ></ItemDisenoEnLista>
              );
            })}
          </Grid>
        </Slide>
      ) : (
        <div>
          <Box
            sx={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              justifyItems: "center",
              padding: 10,
            }}
          >
            <CircularProgress />
          </Box>
        </div>
      )}
    </div>
  );
}

export default ListaDisenoDiapositiva;
