import {
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import parse from "html-react-parser";
import React, { useEffect, useState } from "react";
import firebaseConf from "../../../../../database/firebase";
import { getColor } from "../../../../../styles/ArgumentappTheme";
import {
  renderContenidoAccionesButton,
  renderContenidoAccionesCheckBox,
  renderTextArea,
  renderUploadPhoto,
} from "../../../../Funcionalidades/AccionesDiapositivas";
import {
  getRespuestas,
  removeRespuesta,
  setRespuestaEnPizarra,
} from "./RespuestasEstudiantesBD";
import CoPresentIcon from "@mui/icons-material/CoPresent";
import DeleteIcon from "@mui/icons-material/Delete";
import VerRespuesta from "./VerRespuesta";
import Swal from "sweetalert2";
import "../../../../../styles/CustomClassSweetAlert2.css";
function RenderRespuestas({
  keyDiapositiva,
  nombreDiapositiva,
  acciones,
  contenidoHTML,
  codigo,
  idClase,
  idActividad,
}) {
  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      setuid(user.uid);
      getRespuestas(
        codigo,
        user.uid,
        idClase,
        idActividad,
        keyDiapositiva,
        (data) => {
          setRespuestas(data);
          console.log(data);
        }
      );
    });
  }, [codigo, idActividad, idClase, keyDiapositiva]);

  const [respuestas, setRespuestas] = useState([]);
  const color = getColor();
  const [uid, setuid] = useState(null);

  const deleteRespuesta = (idEstudiante) => {
    Swal.fire({
      customClass: {
        container: "my-swal",
      },
      title: "¿Enviar respuesta?",
      showDenyButton: true,
      confirmButtonText: "Enviar",
      denyButtonText: `Cerrar`,
    }).then((result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
        removeRespuesta(
          codigo,
          uid,
          idClase,
          idActividad,
          keyDiapositiva,
          idEstudiante,
          (_resp) => {
            if (_resp) {
              Swal.fire({
                customClass: {
                  container: "my-swal",
                },
                position: "top-end",
                icon: "success",
                title: "Respuesta borrada",
                showConfirmButton: false,
                timer: 1500,
              });
            } else {
              Swal.fire({
                customClass: {
                  container: "my-swal",
                },
                position: "top-end",
                icon: "error",
                title: "Respuesta no fue borrada",
                showConfirmButton: false,
                timer: 1500,
              });
            }
          }
        );
      } else if (result.isDenied) {
        Swal.fire({
          customClass: {
            container: "my-swal",
          },
          position: "top-end",
          icon: "error",
          title: "Acción cancelada",
          showConfirmButton: false,
          timer: 1500,
        });
      }
    });
  };

  return (
    <div>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignContent: "center",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            minWidth: 1050,
            minHeight: 650,
            maxWidth: 1050,
            maxHeight: 650,
            width: 1050,
            height: 650,
            borderRadius: 30,
            borderWidth: 5,
            borderColor: "black",
            borderStyle: "solid",
            padding: 20,
            textAlign: "center",
            zoom: "50%",
          }}
        >
          <div>
            {parse(contenidoHTML)}
            {/**Vista previa de una diapositiva creada */}
            {acciones.tipo === "Button" &&
              renderContenidoAccionesButton(acciones.item)}
            {acciones.tipo === "CheckBox" &&
              renderContenidoAccionesCheckBox(acciones.item)}
            {acciones.tipo === "TextArea" && renderTextArea()}
            {acciones.tipo === "Photo" && renderUploadPhoto()}
          </div>
        </div>
        {respuestas === null ? (
          <div
            style={{
              height: 100,
              width: "100%",
              display: "flex",
              flexDirection: "row",
              alignContent: "center",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Typography variant="h5">Diapositiva sin Respuestas</Typography>
          </div>
        ) : (
          <div style={{ marginTop: 20, maxWidth: 1000, width: "100%" }}>
            <div style={{ padding: 10 }}>
              <Button variant="contained">
                Proyectar todas las respuestas
              </Button>
            </div>
            <TableContainer component={Paper}>
              <Table
                sx={{
                  minWidth: 650,
                  backgroundColor: color.hoverSecundario,
                  borderColor: color.hoverPrimario,
                  borderWidth: 2,
                  borderStyle: "solid",
                }}
                size={"medium"}
              >
                <TableHead>
                  <TableRow
                    style={{
                      borderColor: color.hoverPrimario,
                      borderWidth: 2,
                      borderStyle: "solid",
                    }}
                  >
                    <TableCell
                      align="center"
                      style={{
                        borderColor: color.hoverPrimario,
                        borderWidth: 0,
                        borderRightWidth: 2,
                        borderStyle: "solid",
                      }}
                      colSpan={2}
                    >
                      <Typography variant="h5">Datos</Typography>
                    </TableCell>
                    <TableCell align="center" colSpan={3}>
                      <Typography variant="h5">Acciones</Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow
                    style={{
                      borderColor: color.hoverPrimario,
                      borderWidth: 2,
                      borderStyle: "solid",
                    }}
                  >
                    <TableCell>
                      <Typography variant="h5" style={{ fontSize: 18 }}>
                        {" "}
                        <b>Fecha</b>
                      </Typography>
                    </TableCell>
                    <TableCell
                      style={{
                        borderColor: color.hoverPrimario,
                        borderWidth: 0,
                        borderRightWidth: 2,
                        borderStyle: "solid",
                      }}
                    >
                      <Typography variant="h5" style={{ fontSize: 18 }}>
                        {" "}
                        <b>Nombre Estudiante</b>
                      </Typography>
                    </TableCell>
                    <TableCell
                      align="center"
                      style={{
                        borderColor: color.hoverPrimario,
                        borderWidth: 0,
                        borderRightWidth: 2,
                        borderStyle: "solid",
                      }}
                    >
                      <Typography variant="h5" style={{ fontSize: 18 }}>
                        <b>Respuesta</b>
                      </Typography>
                    </TableCell>
                    <TableCell
                      align="center"
                      style={{
                        borderColor: color.hoverPrimario,
                        borderWidth: 0,
                        borderRightWidth: 2,
                        borderStyle: "solid",
                      }}
                    >
                      <Typography variant="h5" style={{ fontSize: 18 }}>
                        <b>Proyectar</b>
                      </Typography>
                    </TableCell>
                    <TableCell align="center">
                      <Typography variant="h5" style={{ fontSize: 18 }}>
                        <b>Eliminar</b>
                      </Typography>
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {respuestas.map((row, index) => (
                    <TableRow
                      key={row.fecha}
                      sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                    >
                      <TableCell width={"80px"}>
                        <Typography variant="h5" style={{ fontSize: 18 }}>
                          {row.fecha}
                        </Typography>
                      </TableCell>
                      <TableCell
                        component="th"
                        scope="row"
                        style={{
                          borderColor: color.hoverPrimario,
                          borderWidth: 0,
                          borderRightWidth: 2,
                          borderStyle: "solid",
                        }}
                      >
                        <Typography variant="h5" style={{ fontSize: 18 }}>
                          {row.nombre}
                        </Typography>
                      </TableCell>
                      <TableCell
                        style={{
                          borderColor: color.hoverPrimario,
                          borderWidth: 0,
                          borderRightWidth: 2,
                          borderStyle: "solid",
                          width: 100,
                        }}
                        width={"100px"}
                        align="center"
                      >
                        <VerRespuesta
                          key={index}
                          nombreEstudiante={row.nombre}
                          tipoRespuesta={row.respuesta.tipo}
                          contRespuesta={row.respuesta.contRespuesta}
                        ></VerRespuesta>
                      </TableCell>
                      <TableCell
                        style={{
                          borderColor: color.hoverPrimario,
                          borderWidth: 0,
                          borderRightWidth: 2,
                          borderStyle: "solid",
                          width: 100,
                        }}
                        align="center"
                      >
                        {row.respuesta.tipo !== "Photo" ? (
                          <Button
                            variant="contained"
                            onClick={(e) => {
                              //
                              //
                              //
                              setRespuestaEnPizarra(
                                codigo,
                                uid,
                                idActividad,
                                keyDiapositiva,
                                row.uidEstudiante,
                                () => {}
                              );
                              //
                              //
                              //
                            }}
                            endIcon={
                              <CoPresentIcon
                                style={{ marginLeft: -8, marginRight: 4 }}
                                fontSize="inherit"
                              />
                            }
                            style={{ width: "fit-content" }}
                          ></Button>
                        ) : (
                          <div></div>
                        )}
                      </TableCell>
                      <TableCell
                        width={"100px"}
                        align="center"
                        style={{ width: 100 }}
                      >
                        <Button
                          variant="contained"
                          style={{
                            width: "fit-content",
                            backgroundColor: "#d32f2f",
                          }}
                          onClick={(e) => {
                            deleteRespuesta(row.uidEstudiante);
                          }}
                          endIcon={
                            <DeleteIcon
                              style={{ marginLeft: -8, marginRight: 4 }}
                              fontSize="inherit"
                            />
                          }
                        ></Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </div>
        )}
      </div>
    </div>
  );
}

export default RenderRespuestas;
