import React, { useEffect, useState } from "react";
import { getParticipantes } from "./BDActOnlinePizarra";

function DisplayEstudiantes({ codigoActividad, uid, idClase }) {
  const [banderaCargaStudents, setBanderaCargaStudents] = useState(false);
  const [estudiantes, setestudiantes] = useState([]);

  useEffect(() => {
    getParticipantes(codigoActividad, idClase, uid, (estudiantes) => {
      setestudiantes(estudiantes);
      setBanderaCargaStudents(true);
    });
  }, [codigoActividad, uid, idClase]);

  return (
    <div>
      {banderaCargaStudents && (
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            flexWrap: "wrap",
            alignContent: "center",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {estudiantes.map((students, index) => {
            return (
              <p
                style={{ fontSize: 22, fontWeight: 600, marginRight: 20 }}
                key={index}
              >
                {students}
              </p>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default DisplayEstudiantes;
