import React, { useContext, useEffect, useState } from "react";

import {
  Box,
  Button,
  ButtonGroup,
  Card,
  CardContent,
  CardMedia,
  CircularProgress,
  Slide,
  IconButton,
  Paper,
  TablePagination,
  Typography,
  // CardActionArea,
} from "@mui/material";

import firebaseConf, {
  cerrarSesion,
  getMisCursos,
} from "../../database/firebase";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

import AddActividadModal from "./CrearCursos/AddActividadModal";
import DeleteCursoModal from "./CrearCursos/DeleteCursoModal";
import { UserContext } from "../Funcionalidades/UseContext";

import iconDefault from "../../assets/images/default_icon_curso_2.png";

export default function ContenidoMisCursos2() {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const {
    setBanderaClases,
    setIdCursoVer,
    setNombreCursoVer,
    settipoCursos,
    setindexUnidad,
    setTipoSalida,
  } = useContext(UserContext);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const [misCursosData, setMisCursosData] = useState([]);
  const [banderaCarga, setBanderaCarga] = useState(false);
  const [banderaCargaSlide, setBanderaCargaSlide] = useState(false);

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      if (user) {
        getMisCursos(user.uid, (value) => {
          setMisCursosData(value);
          setBanderaCarga(true);
          setTimeout(() => {
            setBanderaCargaSlide(true);
          }, 100);
        });
      } else {
        cerrarSesion();
      }
    });
  }, []);

  return (
    <div>
      <Box
        width="100%"
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          alignContent: "center",
          justifyItems: "center",
          alignItems: "center",
        }}
      >
        <Paper
          sx={{
            width: "100%",
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
            alignContent: "center",
            justifyItems: "center",
            alignItems: "center",
          }}
          style={{
            boxShadow: "none",
            backgroundColor: "transparent",
          }}
        >
          <AddActividadModal tipoCurso={'privados'}></AddActividadModal>
        </Paper>
      </Box>
      <Paper
        sx={{
          padding: 0.5,
          backgroundColor: "transparent",
          marginLeft: 3,
          marginRight: 3,
          display: "flex",
          flexDirection: "row",
          justifyContent: "end",
          boxShadow:
            "0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 0px 0px 0px rgb(0 0 0 / 14%), 0px 0px 0px 0px rgb(0 0 0 / 12%)",
        }}
      >
        <Button
          variant="contained"
          style={{
            width: "fit-content",
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="18"
            viewBox="0 0 14 14"
          >
            <path
              id="grid-3x3-gap"
              d="M4,2V4H2V2ZM5,14V12a1,1,0,0,0-1-1H2a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1H4A1,1,0,0,0,5,14ZM5,9V7A1,1,0,0,0,4,6H2A1,1,0,0,0,1,7V9a1,1,0,0,0,1,1H4A1,1,0,0,0,5,9ZM5,4V2A1,1,0,0,0,4,1H2A1,1,0,0,0,1,2V4A1,1,0,0,0,2,5H4A1,1,0,0,0,5,4Zm5,10V12a1,1,0,0,0-1-1H7a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1H9A1,1,0,0,0,10,14Zm0-5V7A1,1,0,0,0,9,6H7A1,1,0,0,0,6,7V9a1,1,0,0,0,1,1H9A1,1,0,0,0,10,9Zm0-5V2A1,1,0,0,0,9,1H7A1,1,0,0,0,6,2V4A1,1,0,0,0,7,5H9A1,1,0,0,0,10,4ZM9,2V4H7V2Zm5,0V4H12V2ZM4,7V9H2V7ZM9,7V9H7V7Zm5,0H12V9h2ZM4,12v2H2V12Zm5,0v2H7V12Zm5,0v2H12V12ZM12,1a1,1,0,0,0-1,1V4a1,1,0,0,0,1,1h2a1,1,0,0,0,1-1V2a1,1,0,0,0-1-1ZM11,7a1,1,0,0,1,1-1h2a1,1,0,0,1,1,1V9a1,1,0,0,1-1,1H12a1,1,0,0,1-1-1Zm1,4a1,1,0,0,0-1,1v2a1,1,0,0,0,1,1h2a1,1,0,0,0,1-1V12a1,1,0,0,0-1-1Z"
              transform="translate(-1 -1)"
              fill="#fff"
            />
          </svg>
        </Button>
        <Button
          variant="contained"
          style={{
            width: "fit-content",
            backgroundColor: "transparent",
            boxShadow: "none",
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="18"
            height="18"
            viewBox="0 0 11 9"
          >
            <path
              id="list"
              d="M2.5,12a.5.5,0,0,1,.5-.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,12Zm0-4A.5.5,0,0,1,3,7.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,8Zm0-4A.5.5,0,0,1,3,3.5H13a.5.5,0,0,1,0,1H3A.5.5,0,0,1,2.5,4Z"
              transform="translate(-2.5 -3.5)"
              fill="#9c5fa2"
              fill-rule="evenodd"
            />
          </svg>
        </Button>
      </Paper>

      <Paper
        sx={{ padding: 3, backgroundColor: "transparent", boxShadow: "none" }}
      >
        {banderaCarga ? (
          <Slide
            direction="right"
            in={banderaCargaSlide}
            mountOnEnter
            unmountOnExit
          >
            <div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  flexWrap: "wrap",
                }}
              >
                {misCursosData
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((curso, index) => {
                    return (
                      <Card
                        key={curso.id}
                        sx={{
                          display: "flex",
                          borderWidth: 1,
                          padding: 3,
                          marginRight: 5,
                          marginTop: 3,
                          marginBottom: 3,
                          minHeight: 250,
                          maxWidth: 340,
                          borderRadius: "30px",
                        }}
                        elevation={"1"}
                      >
                        <CardMedia
                          component="img"
                          sx={{ width: 150, borderRadius: "16px" }}
                          image={
                            curso.imagen === null ? iconDefault : curso.imagen
                          }
                          alt={"Miniatura " + curso.nombre}
                        />
                        <Box sx={{ display: "flex", flexDirection: "column" }}>
                          <CardContent
                            sx={{ flex: "1 0 auto", paddingRight: 0 }}
                          >
                            <Typography variant="h5">
                              Unidad {index + 1}
                            </Typography>

                            <Typography
                              variant="subtitle1"
                              color="text.secondary"
                              style={{
                                maxWidth: 120,
                                wordWrap: "break-word",
                                overflowWrap: "break-word",
                                hyphens: "auto",
                              }}
                            >
                              <b>{curso.nombre}</b>
                            </Typography>
                          </CardContent>

                          <Box
                            sx={{
                              display: "flex",
                              flexDirection: "row",
                              flexWrap: "wrap",
                              justifyContent: "flex-end",
                              pl: 1,
                              pb: 1,
                              height: "-webkit-fill-available",
                              alignItems: "flex-end",
                            }}
                          >
                            <ButtonGroup
                              key={"bttn" + curso.code}
                              disableElevation
                            >
                              <DeleteCursoModal
                                key={curso.id}
                                id={curso.id}
                                nombre={curso.nombre}
                                state="privado"
                              ></DeleteCursoModal>
                              <IconButton
                                size="large"
                                onClick={() => {
                                  setIdCursoVer(curso.id);
                                  settipoCursos("privados");
                                  setNombreCursoVer(curso.nombre);
                                  setindexUnidad(index + 1);
                                  setTipoSalida("left");
                                  setBanderaCargaSlide(false);
                                  setTimeout(() => {
                                    setBanderaClases(true);
                                  }, 100);
                                }}
                              >
                                <ArrowForwardIosIcon
                                  style={{ color: "GrayText" }}
                                  fontSize="inherit"
                                />
                              </IconButton>
                            </ButtonGroup>
                          </Box>
                        </Box>
                      </Card>
                    );
                  })}
                {misCursosData.length === 0 && (
                  <div
                    style={{
                      display: "flex",
                      width: "100%",
                      flexDirection: "row",
                      flexWrap: "nowrap",
                      alignContent: "center",
                      justifyContent: "center",
                    }}
                  >
                    <Box
                      style={{
                        height: "20vh",
                        display: "flex",
                        flexDirection: "row",
                        flexWrap: "nowrap",
                        alignItems: "center",
                      }}
                    >
                      <p>No registra planificación ingresados</p>
                    </Box>
                  </div>
                )}
              </div>
              {misCursosData.length > 6 && (
                <TablePagination
                  rowsPerPageOptions={[6, 12, 18]}
                  count={misCursosData.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onPageChange={handleChangePage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                  labelDisplayedRows={({ from, to, count }) => {
                    return "" + from + "-" + to + " de " + count;
                  }}
                  labelRowsPerPage={"Elementos por páginas"}
                ></TablePagination>
              )}
            </div>
          </Slide>
        ) : (
          <div>
            <Box
              style={{
                width: "auto",
                height: "20vh",
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                justifyContent: "space-around",
                alignContent: "center",
                alignItems: "center",
              }}
            >
              <CircularProgress></CircularProgress>
            </Box>
          </div>
        )}
      </Paper>
    </div>
  );
}
