import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
  getDataInformativaPizarra,
  getValueStateActivity,
} from "./BDActOnlinePizarra";
import backImage from "../../../assets/images/login_background.png";
import {
  Backdrop,
  Box,
  Card,
  CardContent,
  CardMedia,
  CircularProgress,
  Typography,
} from "@mui/material";

import styled, { keyframes } from "styled-components";
import defaultImage from "../../../assets/images/default_icon_curso_2.png";
import ContenidoPizarra from "./ContenidoPizarra";
import DisplayEstudiantes from "./DisplayEstudiantes";

function MainVistaPizarraAct() {
  let { codigoAct, idClase } = useParams();

  const [reglasActividadInicio, setReglasActividadInicio] = useState({
    pantallaEspera: "waiting",
    estadoActividad: "waiting",
    userId: "null",
  });

  const [banderaCarga, setbanderaCarga] = useState(true);
  const [informacionActividad, setinformacionActividad] = useState({});
  useEffect(() => {
    document.title = "Pizarra - Argumentapp";

    getValueStateActivity(codigoAct, (val) => {
      if (val.estadoActividad === false || val.estadoActividad === undefined) {
        window.location = "/actividadesOnline/pizarra/termino";
      } else {
        setReglasActividadInicio(val);
        if (
          val.estadoActividad !== false &&
          val.estadoActividad !== undefined
        ) {
          getDataInformativaPizarra(codigoAct, val.userId, idClase, (value) => {
            setinformacionActividad(value);
            setbanderaCarga(false);
          });
        }
      }
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  return (
    <div>
      {!(reglasActividadInicio.estadoActividad === "waiting") && (
        <div className="main-container">
          <div
            style={{
              width: "fit-content",
              position: "fixed",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
            }}
          >
            {!banderaCarga ? (
              <div style={{ display: "flex", flexDirection: "column" }}>
                {reglasActividadInicio.pantallaEspera === true ? (
                  <div>
                    <div
                      style={{
                        backgroundColor: "white",
                        padding: "10px 10px",
                        textAlign: "center",
                        borderRadius: 5,
                        width: "-webkit-fill-available",
                        maxWidth: "820px",
                      }}
                    >
                      <Card
                        key={
                          informacionActividad.datosActividad.codigoActividad
                        }
                        sx={{
                          display: "flex",
                          boxShadow: "none",
                        }}
                      >
                        <CardMedia
                          component="img"
                          sx={{ width: 200 }}
                          image={
                            informacionActividad.datosCurso.imagen === undefined
                              ? defaultImage
                              : informacionActividad.datosCurso.imagen
                          }
                          alt={
                            "Miniatura " +
                            informacionActividad.datosCurso.nombre
                          }
                        />
                        <Box sx={{ display: "flex", flexDirection: "column" }}>
                          <CardContent
                            sx={{
                              display: "flex",
                              flexDirection: "column",
                              flexWrap: "nowrap",
                              alignContent: "center",
                              justifyContent: "center",
                              alignItems: "flex-end",
                              flex: "1 0 auto",
                            }}
                          >
                            <Typography
                              variant="h3"
                              color={"black"}
                              fontSize={"2.7em"}
                            >
                              {informacionActividad.datosCurso.nombre}
                            </Typography>

                            <Typography
                              variant="h5"
                              color="text.secondary"
                              component="p"
                            >
                              {informacionActividad.datosClase.nombreclase}
                            </Typography>

                            <Typography
                              variant="h5"
                              color="text.secondary"
                              component="p"
                            >
                              {informacionActividad.datosActividad
                                .identificadorCurso +
                                " - " +
                                informacionActividad.datosActividad
                                  .establecimiento}
                            </Typography>
                          </CardContent>
                        </Box>
                      </Card>
                    </div>
                    <div
                      style={{ marginTop: 6, width: "-webkit-fill-available" }}
                    >
                      <div
                        style={{ display: "flex", justifyContent: "center" }}
                      >
                        <div
                          style={{
                            display: "flex",
                            filter:
                              "drop-shadow(rgba(0, 0, 0, 0.15) 0px 2px 4px)",
                          }}
                        >
                          <div
                            style={{
                              backgroundColor: "white",
                              padding: "1.7vmin 2.5vmin 1.7vmin 2.8vmin",
                              borderRadius: "0.5vmin 0px 0px 0.5vmin",
                              display: "flex",
                              WebkitBoxAlign: "center",
                              alignItems: "center",
                              marginRight: "-1vmin",
                              clipPath:
                                "polygon(calc(100% - 2.5vmin) 0px, 100% 50%, calc(100% - 2.5vmin) 100%, 0px 100%, 0px 0px)",
                            }}
                          >
                            <div
                              style={{
                                display: "inline-flex",
                                flexDirection: "column",
                                fontSize: "2.5vmin",
                                lineHeight: "3vmin",
                                textAlign: "left",
                              }}
                            >
                              <div>
                                Ingresa a la <strong>actividad </strong>en
                              </div>
                              <div>
                                <strong>
                                  play-argumentapp.netlify.app
                                </strong>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div
                          style={{
                            display: "flex",
                            filter:
                              "drop-shadow(rgba(0, 0, 0, 0.15) 0px 2px 4px)",
                          }}
                        >
                          <div
                            style={{
                              position: "relative",
                              minHeight: "100%",
                              display: "flex",
                            }}
                          >
                            <div
                              style={{
                                fontSize: "8.5vmin",
                                fontWeight: "900px",
                                lineHeight: "normal",
                                letterSpacing: "0.286vmin",
                                display: "flex",
                                WebkitBoxAlign: "center",
                                alignItems: "center",
                                backgroundColor: "rgb(255, 255, 255)",
                                borderRadius: "0.5vmin 0px 0px 0.5vmin",
                                padding: "1.1vmin 2.8vmin 1.1vmin 3.5vmin",
                                marginLeft: "-1vmin",
                                clipPath:
                                  "polygon(0px 0%, 100% 0px, 100% 100%, 0px 100%, 2.5vmin 50%)",
                                boxShadow: "rgb(0 0 0 / 15%) 0px 2px 4px 0px",
                              }}
                            >
                              <div>
                                <div
                                  style={{
                                    fontSize: "2.34vmin",
                                    lineHeight: "3vmin",
                                    letterSpacing: "normal",
                                    fontWeight: "bold",
                                    marginBottom: "-1.5vmin",
                                    textAlign: "left",
                                    color: "rgb(51, 51, 51)",
                                  }}
                                >
                                  PIN de actividad:
                                </div>
                                <div
                                  aria-expanded={"false"}
                                  style={{
                                    background: "transparent",
                                    fontWeight: "inherit",
                                    fontSize: "inherit",
                                    fontFamily: "inherit",
                                  }}
                                >
                                  <span style={{ fontWeight: "750" }}>
                                    {codigoAct}
                                  </span>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div
                      style={{
                        backgroundColor: "white",
                        padding: "10px 10px",
                        textAlign: "center",
                        borderRadius: 5,
                        width: "-webkit-fill-available",
                        marginTop: 30,
                        maxWidth: "820px",
                      }}
                    >
                      <Threepoint
                        style={{ fontSize: 32, fontWeight: 600, padding: 20 }}
                      >
                        Esperando a estudiantes
                      </Threepoint>
                      <DisplayEstudiantes
                        codigoActividad={codigoAct}
                        idClase={idClase}
                        uid={reglasActividadInicio.userId}
                      ></DisplayEstudiantes>
                      <div></div>
                    </div>
                  </div>
                ) : (
                  <div>
                    <ContenidoPizarra
                      codigo={codigoAct}
                      idClase={idClase}
                      user={reglasActividadInicio.userId}
                    ></ContenidoPizarra>
                  </div>
                )}
              </div>
            ) : (
              <div style={{ height: 50, width: 50 }}>
                <Backdrop
                  sx={{
                    color: "#fff",
                    zIndex: 100,
                  }}
                  open={banderaCarga}
                >
                  <CircularProgress color="inherit" />
                </Backdrop>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

const ellipsis = keyframes`
to {
    width: 30px;    
  }
`;

const Threepoint = styled.div`
  &:after {
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    -webkit-animation: ${ellipsis} steps(10, end) 900ms infinite;
    animation: ${ellipsis} steps(10, end) 1500ms infinite;
    content: "...";
    width: 1px;
  }
`;

export default MainVistaPizarraAct;
