import {
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { getColor } from "../../styles/ArgumentappTheme";
import { getIMGtoPanel } from "./FirebasePanelAdmin";
import { ItemEspera } from "./ItemEspera";

function ItemModuloAdmin({description, href, image, image_alt, title}) {
  // eslint-disable-next-line no-unused-vars
  const [img, setImg] = useState(null);
  const [banderaCarga, setbanderaCarga] = useState(true);

  useEffect(() => {
    getIMGtoPanel(image, (value) => {
      setImg(value);
      setbanderaCarga(false);
    }); // eslint-disable-next-line
  }, []);

  return (
    <div>
      {!banderaCarga ? (
        <Card style={{borderRadius: 20, borderColor: getColor().mainColor, borderWidth: 5, borderStyle: 'solid'}}>
          <CardActionArea href={href}>
            {/* <CardMedia
              component="img"
              height="250px"
              image={img}
              alt={image_alt}
            /> */}
            <CardContent>
              <Typography gutterBottom variant="h5" >
                {title}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {description}
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      ) : (
        <ItemEspera></ItemEspera>
      )}
    </div>
  );
}

export default ItemModuloAdmin;
