import {
  Box,
  ButtonGroup,
  Card,
  CardContent,
  CardMedia,
  CircularProgress,
  IconButton,
  Paper,
  Slide,
  TablePagination,
  Typography,
} from "@mui/material";
import React, { useEffect, useState, useContext } from "react";
import { getCursosPublicos } from "../../database/firebase";
import { UserContext } from "../Funcionalidades/UseContext";

import iconDefault from "../../assets/images/default_icon_curso_2.png";
import CopiarCursoPublico from "./Publicos/CopiarCursoPublico";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

function CursosPublicos() {
  const [banderaCarga, setBanderaCarga] = useState(false);
  const [cursosPublicos, setCursosPublicos] = useState([]);
  const [banderaCargaSlide, setBanderaCargaSlide] = useState(false);

  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const {
    setBanderaClases,
    setIdCursoVer,
    setNombreCursoVer,
    settipoCursos,
    setEstadoCurso,
    setindexUnidad,
    setTipoSalida,
  } = useContext(UserContext);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
    //
  };
  useEffect(() => {
    getCursosPublicos((value) => {
      setCursosPublicos(value);
      setBanderaCarga(true);
      setTimeout(() => {
        setBanderaCargaSlide(true);
      }, 100);
    });
  }, []);
  return (
    <Slide direction="right" in={banderaCargaSlide} mountOnEnter unmountOnExit>
      <Paper
        sx={{ padding: 1, backgroundColor: "transparent", boxShadow: "none" }}
      >
        {banderaCarga ? (
          <div>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                flexWrap: "wrap",
              }}
            >
              {cursosPublicos
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((curso, index) => {
                  return (
                    <Card
                      key={curso.id}
                      sx={{
                        display: "flex",
                        borderWidth: 1,
                        padding: 3,
                        marginRight: 5,
                        marginTop: 3,
                        marginBottom: 3,
                        minHeight: 250,
                        maxWidth: 340,
                        borderRadius: "30px",
                      }}
                      elevation={"1"}
                    >
                      <CardMedia
                        component="img"
                        sx={{ width: 150, borderRadius: "16px" }}
                        image={
                          curso.imagen === null ? iconDefault : curso.imagen
                        }
                        alt={"Miniatura " + curso.nombre}
                      />
                      <Box sx={{ display: "flex", flexDirection: "column" }}>
                        <CardContent sx={{ flex: "1 0 auto", paddingRight: 0 }}>
                          <Typography variant="h5">
                            Unidad {index + 1}
                          </Typography>

                          <Typography
                            variant="subtitle1"
                            color="text.secondary"
                            style={{
                              maxWidth: 120,
                              wordWrap: "break-word",
                              overflowWrap: "break-word",
                              hyphens: "auto",
                            }}
                          >
                            <b>{curso.nombre}</b>
                          </Typography>
                        </CardContent>
                        <Box
                          sx={{
                            display: "flex",
                            flexDirection: "row",
                            flexWrap: "wrap",
                            justifyContent: "flex-end",
                            pl: 1,
                            pb: 1,
                            height: "-webkit-fill-available",
                            alignItems: "flex-end",
                          }}
                        >
                          <ButtonGroup
                            key={"bttn" + curso.code}
                            disableElevation
                          >
                            <CopiarCursoPublico
                              nombreCurso={curso.nombre}
                              idCurso={curso.id}
                              estadocurso={curso.publicado}
                            ></CopiarCursoPublico>
                            <IconButton
                              size="large"
                              title="Ir al contenido Argumentapp"
                              onClick={() => {
                                setIdCursoVer(curso.id);
                                settipoCursos("publicados");
                                setNombreCursoVer(curso.nombre);
                                setindexUnidad(index + 1);
                                setTipoSalida("left");
                                setBanderaCargaSlide(false);
                                setEstadoCurso(curso.publicado);
                                setTimeout(() => {
                                  setBanderaClases(true);
                                }, 100);
                              }}
                            >
                              <ArrowForwardIosIcon
                                style={{ color: "GrayText" }}
                                fontSize="inherit"
                              />
                            </IconButton>
                          </ButtonGroup>
                        </Box>
                      </Box>
                    </Card>
                  );
                })}
              {cursosPublicos.length === 0 && (
                <div
                  style={{
                    display: "flex",
                    width: "100%",
                    flexDirection: "row",
                    flexWrap: "nowrap",
                    alignContent: "center",
                    justifyContent: "center",
                  }}
                >
                  <Box
                    style={{
                      height: "20vh",
                      display: "flex",
                      flexDirection: "row",
                      flexWrap: "nowrap",
                      alignItems: "center",
                    }}
                  >
                    <p>No registra planificación ingresados</p>
                  </Box>
                </div>
              )}
            </div>
            {cursosPublicos.length > 6 && (
              <TablePagination
                rowsPerPageOptions={[6, 12, 18]}
                count={cursosPublicos.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
                labelDisplayedRows={({ from, to, count }) => {
                  return "" + from + "-" + to + " de " + count;
                }}
                labelRowsPerPage={"Elementos por páginas"}
              ></TablePagination>
            )}
          </div>
        ) : (
          <div>
            <Box
              style={{
                width: "auto",
                height: "20vh",
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                justifyContent: "space-around",
                alignContent: "center",
                alignItems: "center",
              }}
            >
              <CircularProgress></CircularProgress>
            </Box>
          </div>
        )}
      </Paper>
    </Slide>
  );
}

export default CursosPublicos;
