import { Button } from "@mui/material";
import React, { useContext } from "react";

import { UserContext } from "../../Funcionalidades/UseContext";


function CrearDiseñoDiapositiva({ tipoVista }) {

    const { setTipoVista, setbanderaCrearDiseno } = useContext(UserContext);


  return (
    <div>
      <Button
        style={{
          minWidth: 50,
          minHeight: 50,
          maxHeight: 50,
          maxWidth: 50,
          backgroundColor: "green",
          borderColor: "green",
          borderWidth: "2px",
          borderStyle: "solid",
          marginBottom: "30px ",
        }}
        title={"Crear diseño de diapositiva"}
        onClick={(e) => {
          setTipoVista(tipoVista);
          setbanderaCrearDiseno(true);
        }}
      >
        <h1
          style={{
            textAlign: "center",
            marginBlock: 0,
            color: "white",
          }}
        >
          +
        </h1>
      </Button>
    </div>
  );
}

export default CrearDiseñoDiapositiva;
