import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  FormGroup,
  Input,
  TextField,
} from "@mui/material";
import React, { useContext, useRef } from "react";
import { Editor } from "@tinymce/tinymce-react";
import tinymce from "tinymce";
import { UserContext } from "../../Funcionalidades/UseContext";
import { editorDiseñoDiapositiva } from "../../Funcionalidades/AccionesDiapositivas";

function RedBar() {
  return <Box sx={{ height: 10 }} />;
}

function EditorAdmin() {
  const editorRef = useRef(null);

  const {
    setContenidoHTML,
    contenidoHTML,
    nombreDiseno,
    setNombreDiseno,
    valueCheckbox,
    setvalueCheckbox,
    cantBotones,
    setcantBotones,
    cantBotonesCheckbox,
    setcantBotonesCheckbox,
    tipoVista,
    setisContinuar,
  } = useContext(UserContext);

  const ariaLabel = { "aria-label": "description" };

  const agregarDiapositiva = () => {
    if (editorRef.current) {
      setContenidoHTML(editorRef.current.getContent());
    }
    if (nombreDiseno !== "" && editorRef.current.getContent() !== "") {
      setisContinuar(true);
    }

  };

  return (
    <div>
      <RedBar></RedBar>
      <h4
        style={{
          color: "black",
          marginLeft: 0,
          marginTop: 0,
          textAlign: "start",
          marginBottom: 10,
        }}
      >
        Nombre del diseño predefinido
      </h4>
      <TextField
        variant="filled"
        required
        value={nombreDiseno}
        id="nombre"
        margin="none"
        fullWidth
        style={{
          backgroundColor: "white",
        }}
        onChange={(e) => {
          setNombreDiseno(e.target.value);
        }}
      />{" "}
      <RedBar></RedBar>
      <Editor
        onInit={(evt, editor) => (editorRef.current = editor)}
        apiKey="awyk0fee0yvyjg1nr6hk79jxp2h2ftmvuu7go89blr8pfawr"
        initialValue={contenidoHTML}
        init={{
          height: 600,
          max_height: 1000,
          resize: false,
          menubar: true,
          language: "es_MX",
          fontsize_formats:
            "8pt 9pt 10pt 11pt 12pt 14pt 15pt 16pt 17pt 18pt 20pt 21pt 22pt 24pt 26pt 28pt 30pt 32pt 36pt 40pt 48pt 60pt 72pt 96pt",
          plugins: [
            "advlist autolink lists link image",
            "charmap print preview anchor help",
            "searchreplace visualblocks code",
            "insertdatetime media table paste wordcount",
            "image code",
          ],
          toolbar:
            "insertfile undo redo | styleselect | fontsizeselect | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link image",

          image_title: true,
          automatic_uploads: true,
          file_picker_types: "image",

          file_picker_callback: function (cb, value, meta) {
            var input = document.createElement("input");
            input.setAttribute("type", "file");
            input.setAttribute("accept", "image/*");

            input.onchange = function () {
              var file = this.files[0];

              var reader = new FileReader();

              reader.onload = function () {
                var id = "blobid" + new Date().getTime();
                var blobCache = tinymce.activeEditor.editorUpload.blobCache;
                var base64 = reader.result.split(",")[1];
                var blobInfo = blobCache.create(id, file, base64);
                blobCache.add(blobInfo);
                cb(blobInfo.blobUri(), { title: file.name });
              };
              reader.readAsDataURL(file);
            };

            input.click();
          },

          content_style:
            "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",
        }}
      />
      <RedBar></RedBar>
      <h4
        style={{
          color: "black",
          marginLeft: 0,
          textAlign: "start",
          marginBottom: 10,
        }}
      >
        Acciones para la diapositiva
      </h4>
      <FormGroup style={{ flexDirection: "row" }}>
        <FormControlLabel
          control={
            <Checkbox
              onClick={(e) => {
                setvalueCheckbox(editorDiseñoDiapositiva(0, e.target.checked));
              }}
              checked={valueCheckbox[0]}
            />
          }
          label="Botón"
        />
        <FormControlLabel
          control={
            <Checkbox
              onClick={(e) => {
                setvalueCheckbox(editorDiseñoDiapositiva(1, e.target.checked));
              }}
              checked={valueCheckbox[1]}
            />
          }
          label="Alternativas"
        />{" "}
        {tipoVista === "alumnos" && (
          <FormControlLabel
            control={
              <Checkbox
                onClick={(e) => {
                  setvalueCheckbox(
                    editorDiseñoDiapositiva(3, e.target.checked)
                  );
                }}
                checked={valueCheckbox[3]}
              />
            }
            label="Campo de texto"
          />
        )}
        {tipoVista === "alumnos" && (
          <FormControlLabel
            control={
              <Checkbox
                onClick={(e) => {
                  setvalueCheckbox(
                    editorDiseñoDiapositiva(4, e.target.checked)
                  );
                }}
                checked={valueCheckbox[4]}
              />
            }
            label="Fotografía"
          />
        )}
        <FormControlLabel
          control={
            <Checkbox
              defaultChecked
              onClick={(e) => {
                setvalueCheckbox(editorDiseñoDiapositiva(2, e.target.checked));
              }}
              checked={valueCheckbox[2]}
            />
          }
          label="Sin acciones"
        />
      </FormGroup>
      {valueCheckbox[0] && (
        <div
          style={{
            textAlign: "start",
          }}
        >
          <h4
            style={{
              color: "black",
              marginLeft: 0,
              marginBottom: 10,
            }}
          >
            Cantidad de botones
          </h4>
          <Input
            style={{ textAlign: "start" }}
            defaultValue={1}
            value={cantBotones}
            inputProps={ariaLabel}
            type="number"
            onChange={(e) => {
              if (e.target.value > 3) {
                setcantBotones(3);
              } else {
                if (e.target.value < 1) {
                  setcantBotones(1);
                } else {
                  setcantBotones(e.target.value);
                }
              }
            }}
          />
        </div>
      )}
      {valueCheckbox[1] && (
        <div
          style={{
            textAlign: "start",
          }}
        >
          <h4
            style={{
              color: "black",
              marginLeft: 0,
              marginBottom: 10,
            }}
          >
            Cantidad de alternativas
          </h4>
          <Input
            style={{ textAlign: "start" }}
            defaultValue={1}
            value={cantBotonesCheckbox}
            inputProps={ariaLabel}
            type="number"
            onChange={(e) => {
              if (e.target.value > 5) {
                setcantBotonesCheckbox(5);
              } else {
                if (e.target.value < 2) {
                  setcantBotonesCheckbox(2);
                } else {
                  setcantBotonesCheckbox(e.target.value);
                }
              }
            }}
          />
        </div>
      )}
      <Button
        color="success"
        style={{
          backgroundColor: "green",
          color: "white",
          marginRight: "10px",
        }}
        onClick={agregarDiapositiva}
      >
        Continuar
      </Button>
    </div>
  );
}

export default EditorAdmin;
