import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import * as React from 'react';
import VistaDiapositiva from '../../Diapositivas/VistaDiapositiva';
import { UserContext } from '../../Funcionalidades/UseContext';


function VistaEnGrandeDiapositiva({
  valorTemporalHTML,
  banderaVistaPrevia,
  acciones,
  nombrePlantilla
}) {



  const {setbanderaVistaPrevia} =
    React.useContext(UserContext);

  const handleClose = () => setbanderaVistaPrevia(false);

  return (
    <div>

      <Dialog
        open={banderaVistaPrevia}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        maxWidth={"lg"}
      >
        <DialogTitle id="alert-dialog-title">{nombrePlantilla}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <VistaDiapositiva
              valorHtml={valorTemporalHTML}
              acciones={acciones}
            ></VistaDiapositiva>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleClose}
          >
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default VistaEnGrandeDiapositiva
