import {
  Button,
  LinearProgress,
  Stack,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import Swal from "sweetalert2";
import {
  actualizarClasesforActOn,
  getClasesforList,
  getClasesWithMergue,
} from "../../BDActividadesOnline";
import CardClasesActividadesOnline from "./CardClasesActividadesOnline";

import "../../../../styles/CustomClassSweetAlert2.css";

function ClasesActividadOnline({ actividad }) {
  const [clases, setClases] = useState([]);
  const [banderaCargaClases, setbanderaCargaClases] = useState(false);
  useEffect(() => {
    if (actividad.data.dataClases.isFetchClases) {
      getClasesforList(
        actividad.data.datosActividad.codigoActividad,
        (dataIn) => {
          setClases(dataIn.reverse());
          setTimeout(() => {
            setbanderaCargaClases(true);
          }, 400);
        }
      );
    }
  }, [actividad]);

  const actualizarClases = () => {
    Swal.fire({
      icon: "info",
      title: "Actualizando clases...",
      text: "Se están actualizando las clases asociadas a el curso de la actividad online.",
      showConfirmButton: false,
      allowOutsideClick: false,
      allowEscapeKey: false,
    });
    setTimeout(() => {
      actualizarClasesforActOn(
        actividad.data.datosCurso.id,
        actividad.data.datosActividad.codigoActividad,
        (callback) => {
          if (callback.state === true && callback.terminoSucc === true) {
            Swal.fire({
              icon: "success",
              title: "Datos actualizados",
              text:
                "Las clases del curso fueron actualizadas y copiadas de manera correcta en la actividad " +
                actividad.data.datosActividad.codigoActividad +
                ".",
              showConfirmButton: true,
              confirmButtonText: "Salir",
            });
          }
          if (callback.state === false && callback.terminoSucc === true) {
            Swal.fire({
              icon: "warning",
              title: "No existe el curso en sus registros para actualizar",
              text:
                "Las clases del curso no fueron actualizadas, dado que no existe el curso " +
                actividad.data.datosCurso.nombre +
                " en sus registros personales.",
              showConfirmButton: true,
              confirmButtonText: "Salir",
            });
          }
        }
      );
    }, 1000);
  };

  const changeStateFetch = () => {

          let timerInterval;
          var mensaje = "Obteniendo la miniatura...";
          Swal.fire({
            customClass: {
              container: "my-swal",
            },
            icon: 'info',
            title: "Obteniendo datos",
            html: "<b></b>",
            timerProgressBar: false,
            didOpen: () => {
              Swal.showLoading();
              const b = Swal.getHtmlContainer().querySelector("b");
              timerInterval = setInterval(() => {
                b.textContent = mensaje;
              }, 50);
            },
            willClose: () => {
              clearInterval(timerInterval);
            },
          });

    setTimeout(() => {
      getClasesWithMergue(
        actividad.data.datosCurso.id,
        actividad.data.datosActividad.codigoActividad,
        (msgState) => {
          mensaje = msgState
        },
        (state) => {
          if (state) {
            Swal.fire({
              icon: "success",
              title: "Clases obtenidas correctamente",
              text:
                "Las clases del curso fueron copiadas y creadas de manera correcta en la actividad " +
                actividad.data.datosActividad.codigoActividad +
                ".",
              showConfirmButton: true,
              confirmButtonText: "Continuar",
            });
          }
        }
      );
    }, 1000);
  };
  return (
    <div>
      {actividad.data.dataClases.isFetchClases ? (
        <div>
          <div style={{ display: "flex", flexDirection: "column", alignItems: 'center' }}>
            <Typography
              style={{
                fontSize: 28,
                display: "flex",
                paddingBottom: 10,
                fontWeight: "bold",
              }}
              color="#6c3373"
            >
              Clase Activa:
              {actividad.data.datosEjecucion.claseActiva.nombre === "null" ? (
                <p style={{ color: "#9b1616", margin: 0, marginLeft: 10 }}>
                  No registra
                </p>
              ) : (
                actividad.data.datosEjecucion.claseActiva.nombre
              )}
            </Typography>
            <Button
              style={{
                paddingTop: 5,
                paddingBottom: 5,
                marginBottom: 15,
              }}
              variant="contained"
              onClick={(e) => {
                actualizarClases();
              }}
            >
              Actualizar clases
            </Button>
          </div>
          <div
            style={{
              padding: 20,
              backgroundColor: "transparent",
              boxShadow: "none",
            }}
          >
            {banderaCargaClases ? (
              <CardClasesActividadesOnline
                codigo={actividad.data.datosActividad.codigoActividad}
                clases={clases}
              ></CardClasesActividadesOnline>
            ) : (
              <div
                style={{
                  minHeight: "100px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <Stack sx={{ width: "80%", color: "grey.500" }} spacing={2}>
                  <LinearProgress color="info" />
                </Stack>
              </div>
            )}
          </div>
        </div>
      ) : (
        <div
          style={{
            height: 200,
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            alignContent: "center",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Button
            variant="contained"
            onClick={() => {
              changeStateFetch();
            }}
          >
            Obtener Clases
          </Button>
        </div>
      )}
    </div>
  );
}

export default ClasesActividadOnline;
