import { Accordion, AccordionDetails, AccordionSummary, Button, Paper } from "@mui/material";
import React, { useState } from "react";
import RenderDiapositivas from "./RenderDiapositivas";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

function SidebarDiapositivas({ codigo, idClase, dataActividades }) {
  const [isClicked, setIsClicked] = useState(true);
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
      setExpanded(isExpanded ? panel : false);
    };
  return (
    <div>
      <Paper
        style={
          !isClicked
            ? {
                minHeight: "400px",
                width: 350,
                marginTop: 20,
                padding: 20,
                transition: "width 0.7s cubic-bezier(0.16, 0.91, 0.58, 1) 0s",
                borderRadius: 30,
                boxShadow: "none",
              }
            : {
                minHeight: "400px",
                width: 50,
                marginTop: 20,
                padding: 20,
                backgroundColor: "transparent",
                boxShadow: "none",
                transition: "width 0.7s cubic-bezier(0.16, 0.91, 0.58, 1) 0s",
                borderRadius: 30,
              }
        }
      >
        <div>
          <Button
            style={{
              maxWidth: 30,
              minWidth: 30,
              maxHeight: 30,
              padding: 0,
              boxShadow: "none",
            }}
            onClick={(e) => {
              setIsClicked(!isClicked);
            }}
          >

            <svg
              width="30px"
              height="30px"
              version="1.1"
              viewBox="0 0 20 20"
              x="0px"
              y="0px"
              class="ScIconSVG-sc-1bgeryd-1 ifdSJl"
            >
              <g>
                {!isClicked ? (
                  <path
                    fill="#9C5FA2"
                    d="M4 16V4H2v12h2zM13 15l-1.5-1.5L14 11H6V9h8l-2.5-2.5L13 5l5 5-5 5z"
                  ></path>
                ) : (
                  <path
                    fill="#9C5FA2"
                    d="M16 16V4h2v12h-2zM6 9l2.501-2.5-1.5-1.5-5 5 5 5 1.5-1.5-2.5-2.5h8V9H6z"
                  ></path>
                )}
              </g>
            </svg>
          </Button>
        </div>
        {!isClicked && (
          <div>
            <h3 style={{ color: "#6f7070" }}>Diapositivas</h3>
            <div>
              {dataActividades.map((data, index) => {
                return (
                  <Accordion
                    style={{
                      marginBottom: 20,
                      height: "auto",
                      boxShadow: "none",
                    }}
                    key={index}
                    expanded={expanded === index}
                    onChange={handleChange(index)}
                  >
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="panel1a-content"
                      id="panel1a-header"
                    >
                      <h4
                        style={{
                          marginTop: 0,
                          marginLeft: 0,
                          color: "#6f7070",
                        }}
                      >
                        {data.dataActividad.nombreActividad}
                      </h4>
                    </AccordionSummary>
                    <AccordionDetails style={{ paddingLeft: 5 }}>
                      {[
                        { id: "profesor", name: "Profesor" },
                        // { id: "alumno", name: "Alumno" },
                        // { id: "pizarra", name: "Pizarra" },
                      ].map((vistasData, index) => {
                        return (
                          <RenderDiapositivas
                            key={index}
                            codigo={codigo}
                            idClase={idClase}
                            idActividad={data.key}
                            idVista={vistasData.id}
                            nameVista={vistasData.name}
                          />
                        );
                      })}
                    </AccordionDetails>
                  </Accordion>
                );
              })}
            </div>
          </div>
        )}
      </Paper>
    </div>
  );
}

export default SidebarDiapositivas;
