import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import React, { useState } from 'react'
import { deleteDiapositiva } from '../../../Diapositivas/FirebaseDiapositivas';

function BotonBorrarDiapositiva({
  keyDiapositiva,
  tipoVista,
  uid,
  idCurso,
  idClase,
  keyActividad,
  nombreDiapositiva
}) {
  const [isBorrar, setIsBorrar] = useState(false);
  return (
    <div>
      <Button
        onClick={() => {
          setIsBorrar(true);
        }}
        style={{
          padding: "8px",
          width: "-webkit-fill-available",
          margin: "5px",
          marginTop: 0,
          borderStyle: "solid",
          borderWidth: 1,
        }}
      >
        <i
          style={{
            fontSize: "22px",
            marginLeft: 5,
          }}
          className="fas fa-trash"
        ></i>
        Borrar diapositiva
      </Button>
      <Dialog
        open={isBorrar}
        onClose={(e) => setIsBorrar(false)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"¿Eliminar diapositiva?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Estás seguro que quieres borrar la diapositiva {nombreDiapositiva}{" "}
            en el perfil de {tipoVista}.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={(e) => setIsBorrar(false)}>Cancelar</Button>
          <Button
            onClick={(e) =>
              deleteDiapositiva(
                uid,
                idCurso,
                idClase,
                keyActividad,
                tipoVista,
                "privados",
                keyDiapositiva
              )
            }
            autoFocus
          >
            Borrar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default BotonBorrarDiapositiva
