import { Box } from "@mui/material";
import { Button } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
// import { generarKey } from "../../../Funcionalidades/generarClaveFirebase";
import { UserContext } from "../../../Funcionalidades/UseContext";
import {
  getActividades,
  getActividadesCursosPublicos,
} from "../../../VistaUnitariaClase/FirebaseVistaClase";
import { getNombreDiapositiva } from "../../FirebaseDiapositivas";

function CompAcciones({ uid, idCurso, idClase, tipoVista }) {
  const [dataDiapositivas, setDataDiapositivas] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(false);

  const { indexEditar, dataAcciones, setDataAcciones } =
    useContext(UserContext);

  // let accionesPizarra = [
  //   {
  //     tipoVista: "pizarra",
  //     nombreDiapositiva: "Terminar Clase",
  //     keyDiapositiva: generarKey("Terminar Clase"),
  //     keyActividad: "Acciones Pizarra",
  //     nombreActividad: "Acciones Pizarra",
  //   },
  //   {
  //     tipoVista: "pizarra",
  //     nombreDiapositiva: "Ver respuesta",
  //     keyDiapositiva: generarKey("Ver respuesta"),
  //     keyActividad: "Acciones Pizarra",
  //     nombreActividad: "Acciones Pizarra",
  //   },
  // ];

  useEffect(() => {
    var temp = [];
    console.log(uid);
    if (uid !== "data") {
      getActividades(uid, idCurso, idClase, (value) => {
        value.forEach((element) => {
          getNombreDiapositiva(
            uid,
            idCurso,
            idClase,
            element.key,
            tipoVista,
            "privados",
            (diapositivas) => {
              temp.push({
                id: element.key,
                nombre: element.data.nombreActividad,
                dataDiapositiva: diapositivas,
              });

              if (temp.length === value.length) {
                setDataDiapositivas(temp);
                setbanderaCarga(true);
              }
            }
          );
        });
      });
    } else {
      getActividadesCursosPublicos(idCurso, idClase, (value) => {
        value.forEach((element) => {
          getNombreDiapositiva(
            uid,
            idCurso,
            idClase,
            element.key,
            tipoVista,
            "publicos",
            (diapositivas) => {
              temp.push({
                id: element.key,
                nombre: element.data.nombreActividad,
                dataDiapositiva: diapositivas,
              });
              if (temp.length === value.length) {
                setDataDiapositivas(temp);
                setbanderaCarga(true);
              }
            }
          );
        });
      });
    }
  }, [uid, idCurso, idClase, tipoVista]);

  return (
    <div>
      {!banderaCarga ? (
        <div>Cargando diapositivas...</div>
      ) : (
        <div>
          {" "}
          <Box
            style={{
              display: "flex",
            }}
          >
            <h4
              style={{
                color: "black",
                marginLeft: 0,
                margin: 0,
                marginRight: 10,
              }}
            >
              Acción seleccionada:
            </h4>

            {dataAcciones[indexEditar].href.nombreDiapositiva === "null" ? (
              <p style={{ margin: 0, color: "#555" }}>Sin acción</p>
            ) : (
              <p style={{ margin: 0 }}>
                {dataAcciones[indexEditar].href.nombreDiapositiva}
              </p>
            )}
          </Box>
          {dataDiapositivas.map((value, index) => {
            return (
              <div key={index} >
                <p>{value.nombre}</p>
                <div
                  style={{
                    display: "flex",
                    flexWrap: 'wrap',
                  }}
                >
                  {value.dataDiapositiva.map((data, index_2) => {
                    return (
                      <Button
                        onClick={(e) => {
                          var temp = dataAcciones;
                          temp[indexEditar].href = !(
                            dataAcciones[indexEditar].href.keyDiapositiva ===
                              data.key &&
                            dataAcciones[indexEditar].href.keyActividad ===
                              value.id
                          )
                            ? {
                                keyDiapositiva: data.key,
                                keyActividad: value.id,
                                nombreActividad: value.nombre,
                                nombreDiapositiva: data.datos.nombre,
                                tipoVista: tipoVista,
                              }
                            : {
                                keyDiapositiva: "null",
                                keyActividad: "null",
                                nombreActividad: "null",
                                nombreDiapositiva: "null",
                                tipoVista: "null",
                              };
                          setDataAcciones(temp);
                          setbanderaCarga(false);
                          setTimeout(() => {
                            setbanderaCarga(true);
                          }, 600);
                        }}
                        style={
                          !(
                            dataAcciones[indexEditar].href.keyDiapositiva ===
                              data.key &&
                            dataAcciones[indexEditar].href.keyActividad ===
                              value.id
                          )
                            ? {
                                borderStyle: "solid",
                                borderWidth: 2,
                                borderRadius: 15,
                                padding: 5,
                                marginRight: 5,
                                fontSize: 12,
                                minHeight: 70,
                                minWidth: 140,
                                marginBottom:10
                              }
                            : {
                                borderStyle: "solid",
                                borderWidth: 2,
                                borderRadius: 15,
                                padding: 5,
                                marginRight: 5,
                                borderColor: "#6d4271",
                                backgroundColor: "#6d4271",
                                color: "white",
                                fontSize: 12,
                                minHeight: 70,
                                minWidth: 140,
                                marginBottom:10
                              }
                        }
                        key={index_2}
                      >
                        {data.datos.nombre}
                      </Button>
                    );
                  })}

                  {value.dataDiapositiva.length === 0 && (
                    <div style={{ textAlign: "center", width: "100%" }}>
                      <p>No registra diapositivas</p>
                    </div>
                  )}
                </div>
              </div>
            );
          })}
          {/* {tipoVista === "pizarra" && (
            <div>
              <h4
                style={{
                  color: "black",
                  marginLeft: 0,
                  margin: 0,
                  marginRight: 10,
                }}
              >
                Acciones generadas
              </h4>{" "}
              {accionesPizarra.map((value, index) => {
                return (
                  <Button
                    onClick={(e) => {
                      var temp = dataAcciones;
                      temp[indexEditar].href = !(
                        dataAcciones[indexEditar].href.keyDiapositiva ===
                          value.keyDiapositiva &&
                        dataAcciones[indexEditar].href.keyActividad ===
                          value.keyActividad
                      )
                        ? {
                            keyDiapositiva: value.keyDiapositiva,
                            keyActividad: value.keyActividad,
                            nombreActividad: value.nombreActividad,
                            nombreDiapositiva: value.nombreDiapositiva,
                            tipoVista: tipoVista,
                          }
                        : {
                            keyDiapositiva: 'null',
                            keyActividad: 'null',
                            nombreActividad: 'null',
                            nombreDiapositiva: 'null',
                            tipoVista: 'null',
                          };
                      setDataAcciones(temp);
                      setbanderaCarga(false);
                      setTimeout(() => {
                        setbanderaCarga(true);
                      }, 600);
                    }}
                    style={
                      !(
                        dataAcciones[indexEditar].href.keyDiapositiva ===
                          value.keyDiapositiva &&
                        dataAcciones[indexEditar].href.keyActividad ===
                          value.keyActividad
                      )
                        ? {
                            borderStyle: "solid",
                            borderWidth: 2,
                            borderRadius: 15,
                            padding: 5,
                            marginRight: 5,
                            borderColor: "blue",
                            backgroundColor: "blue",
                            color: "white",
                          }
                        : {
                            borderStyle: "solid",
                            borderWidth: 2,
                            borderRadius: 15,
                            padding: 5,
                            marginRight: 5,
                            borderColor: "green",
                            backgroundColor: "green",
                            color: "white",
                          }
                    }
                    key={index}
                  >
                    {value.nombreDiapositiva}
                  </Button>
                );
              })}
            </div>
          )} */}
        </div>
      )}
    </div>
  );
}

export default CompAcciones;
