export const getFecha = (seconds) => {
  var date = new Date(seconds);
  var fechaString = "";
  var months_arr = [
    "Enero",
    "Febrero",
    "Marzo",
    "Abril",
    "Mayo",
    "Junio",
    "Julio",
    "Agosto",
    "Septiembre",
    "Octubre",
    "Noviembre",
    "Diciembre",
  ];
  var year = date.getFullYear();
  var month = months_arr[date.getMonth()];
  var day = date.getDate();
  var minutos = date.getMinutes();
  var segundos = date.getSeconds();
  // var hora = date.getHours();
  if (Number(segundos) < 10) {
    segundos = "0" + segundos;
  }
  if (Number(minutos) < 10) {
    minutos = "0" + minutos;
  }

  fechaString =
    day +
    " de " +
    month +
    " del " +
    year 
    // " / " +
    // hora +
    // ":" +
    // minutos +
    // ":" +
    // segundos;
  return fechaString;
};
