import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { getDiapositivas } from "../../Diapositivas/FirebaseDiapositivas";
import MainDiapositivas from "../../Diapositivas/MainDiapositivas";
import ContenidoDiapositiva from "../ContenidoActividad/ContenidoDiapositiva";
import BotonEditarDiapoPublic from "./Componentes/BotonEditarDiapoPublic";
import OpcionesDiapoPublic from "./Componentes/OpcionesDiapoPublic";

function ContenidoActividadPublica({
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  estado,
}) {
  useEffect(() => {
    getDiapositivas(
      "data",
      idCurso,
      idClase,
      keyActividad,
      tipoVista,
      "publicos",
      (callback) => {
        setDiapositivas(callback);
      }
    );
    // eslint-disable-next-line
  }, []);

  const [diapositivas, setDiapositivas] = useState([]);
  const [contenidoHTML, setContenidoHTML] = useState("");
  const [acciones, setAcciones] = useState([]);
  const [banderaDialog, setBanderaDialog] = useState(false);
  const [nombreDiapositiva, setNombreDiapositiva] = useState("");
  const [orden, setorden] = useState(null);
  const [idDiapositiva, setIdDiapositiva] = useState("");

  const [banderaDialogEdit, setBanderaDialogEdit] = useState(false);

  useEffect(() => {
    console.log("...............................");
  }, []);

  return (
    <Grid container justifyContent="start" spacing={6}>
      {diapositivas.map((value, index) => (
        <Grid key={index} item>
          <div>
            <Button
              style={{
                minHeight: "200px",
                maxHeight: "200px",
                minWidth: "300px",
                maxWidth: "300px",
                borderRadius: 25,
                backgroundColor: "white",
                borderColor: "black",
                borderWidth: "2px",
                borderStyle: "solid",
                marginBottom: "0px ",
                display: "flex",
                flexDirection: "column",
              }}
              onClick={(e) => {
                setContenidoHTML(value.contenidoHTML);
                setAcciones(value.datos.acciones);
                setNombreDiapositiva(value.datos.nombre);
                setorden(value.datos.orden);
                setIdDiapositiva(value.key);
                setBanderaDialog(true);
              }}
            >
              <img
                alt="miniatura diapositiva"
                style={{
                  width: "300px",
                  height: "200px",
                }}
                src={value.miniatura}
              ></img>
            </Button>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                alignContent: "center",
                justifyContent: "space-between",
                alignItems: "center",
                maxWidth: "300px",
              }}
            >
              <h4
                style={{
                  color: "black",
                  textAlign: "start",
                  marginLeft: 0,
                }}
              >
                {value.datos.nombre}
              </h4>
              {!estado && (
                <div>
                  <OpcionesDiapoPublic
                    idClase={idClase}
                    idCurso={idCurso}
                    keyActividad={keyActividad}
                    tipoVista={tipoVista}
                    nombreDiapositiva={value.datos.nombre}
                    acciones={value.datos.acciones}
                    contenidoHTML={value.contenidoHTML}
                    orden={value.datos.orden}
                    idDiapositiva={value.key}
                    imagen={value.miniatura}
                  ></OpcionesDiapoPublic>
                </div>
              )}
            </div>
          </div>
        </Grid>
      ))}
      <Grid
        key={"add"}
        style={{
          display: "flex",
          flexDirection: "column",
          flexWrap: "nowrap",
          justifyContent: "center",
          alignContent: "center",
          alignItems: "center",
          minHeight: "200px",
          minWidth: "200px",
        }}
        item
      >
        {!estado && (
          <MainDiapositivas
            idCurso={idCurso}
            idClase={idClase}
            uid={"data"}
            keyActividad={keyActividad}
            tipoVista={tipoVista}
            lugarAlmacenamiento="publicos"
          ></MainDiapositivas>
        )}
      </Grid>

      {banderaDialogEdit && (
        <BotonEditarDiapoPublic
          idClase={idClase}
          idCurso={idCurso}
          keyActividad={keyActividad}
          tipoVista={tipoVista}
          nombreDiapositiva={nombreDiapositiva}
          acciones={acciones}
          contenidoHTML={contenidoHTML}
          orden={orden}
          idDiapositiva={idDiapositiva}
          state={false}
        ></BotonEditarDiapoPublic>
      )}

      <Dialog
        open={banderaDialog}
        maxWidth={"lg"}
        onClose={(e) => {
          setBanderaDialog(false);
        }}
      >
        <DialogTitle id="alert-dialog-title">{nombreDiapositiva}</DialogTitle>
        <DialogContent>
          <ContenidoDiapositiva
            contenidoHTML={contenidoHTML}
            acciones={acciones}
          ></ContenidoDiapositiva>
        </DialogContent>
        <DialogActions>
          {!estado && (
            <Button
              onClick={(e) => {
                setBanderaDialogEdit(false);
                setBanderaDialog(false);
                setTimeout(() => {
                  setBanderaDialogEdit(true);
                }, 500);
              }}
            >
              Editar
            </Button>
          )}

          <Button
            onClick={(e) => {
              setBanderaDialog(false);
            }}
          >
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>
    </Grid>
  );
}

export default ContenidoActividadPublica;
