import { Box, Button, IconButton } from "@mui/material";
import Popover from "@mui/material/Popover";
import Typography from "@mui/material/Typography";
import React from "react";
import { deleteCursoPublico, deleteUnCurso } from "../../../database/firebase";
import DeleteIcon from "@mui/icons-material/Delete";

function DeleteCursoModal({ id, nombre, state }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [msg, setMsg] = React.useState("¿Eliminar el Curso - " + nombre);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const identificador = open ? "simple-popover" : undefined;

  return (
    <div>
      <IconButton size="large" onClick={handleClick}>
        <DeleteIcon fontSize="large" />
      </IconButton>{" "}
      <Popover
        id={identificador}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        sx={{
          borderRadius: "25px !important",
        }}
        
      >
        <div style={{ padding: 20, paddingBottom: 5 }}>
          <Typography>{msg}</Typography>
          <Box textAlign="center" style={{ marginTop: 10, marginBottom: 10 }}>
            {state === "privado" && (
              <Button
                style={{
                  width: "fit-content",
                }}
                variant="contained"
                onClick={(e) => {
                  deleteUnCurso(id, (callback) => {
                    if (!callback) {
                      setMsg(
                        "Error al eliminar el curso " +
                          nombre +
                          " de su registro"
                      );
                    } else {
                      handleClose();
                    }
                  });
                }}
              >
                Eliminar
              </Button>
            )}
            {state === "publico" && (
              <Button
                style={{
                  width: "fit-content",
                }}
                variant="contained"
                onClick={(e) => {
                  deleteCursoPublico(id, (callback) => {
                    if (!callback) {
                      setMsg(
                        "Error al eliminar el curso " +
                          nombre +
                          " de su registro"
                      );
                    } else {
                      handleClose();
                    }
                  });
                }}
              >
                Eliminar
              </Button>
            )}
            {state === "noPublicadoPublico" && (
              <Button
                style={{
                  width: "fit-content",
                }}
                variant="contained"
                onClick={(e) => {
                  deleteCursoPublico(id, (callback) => {
                    if (!callback) {
                      setMsg(
                        "Error al eliminar el curso " +
                          nombre +
                          " de su registro"
                      );
                    } else {
                      handleClose();
                    }
                  });
                }}
              >
                Eliminar
              </Button>
            )}

            <Button
              style={{
                width: "fit-content",
                marginLeft: 5
              }}
              variant="outlined"
              onClick={(e) => {
                setAnchorEl(null);
              }}
            >
              Cerrar
            </Button>
          </Box>
        </div>
      </Popover>
    </div>
  );
}

export default DeleteCursoModal;
