import {
  Backdrop,
  Button,
  CircularProgress,
  Grid,
  Paper,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import Notfound from "../../../../Page404/Notfound";
import iconDefault from "../../../../../assets/images/default_icon_clases.png";

import {
  existClaseOnCursoActOn,
  getDataActividadxCodigo,
  getDataClaseActOnline,
  isActividadOnUser,
  activarClaseActividadOnline,
  isActiveActivityOnline,
} from "../../../BDActividadesOnline";
import { styled } from "@mui/material/styles";
import DiapositivasActOnline from "./DiapositivasActOnline";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import MainProfesorActOnline from "../../ProfesorActOnline/MainProfesorActOnline";
import firebaseConf from "../../../../../database/firebase";
import { ArrowBackIosNew } from "@mui/icons-material";

function MainVistaClasesActOn(props) {
  const [isOwner, setisOwner] = useState(false);
  const [isLoader, setisLoader] = useState({
    dataInfoCurso: true,
    dataClase: true,
  });
  let { codigoAct, idClase } = useParams();
  const [data, setdata] = useState({});
  const [dataActividades, setdataActividades] = useState([]);
  const [exists, setexists] = useState(false);
  const [infoCurso, setinfoCurso] = useState({});
  const [actividadIniciada, setActividadIniciada] = useState('none');

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      isActividadOnUser(user.uid, codigoAct, (resp) => {
        setTimeout(() => {
          if (resp) {
            getDataActividadxCodigo(codigoAct, (resp) => {
              setinfoCurso(resp);

              isActiveActivityOnline(codigoAct, (isStart) => {
                setActividadIniciada(isStart);
              });

              existClaseOnCursoActOn(
                resp.data.datosCurso.id,
                idClase,
                (state) => {
                  setexists(state);
                }
              );

              let tempAct = [];
              getDataClaseActOnline(codigoAct, idClase, (respData) => {
                setdata(respData);

                try {
                  for (var i in respData.data.actividades.lista_actividad) {
                    tempAct.push({
                      dataActividad:
                        respData.data.actividades.lista_actividad[i],
                      key: i,
                    });
                  }
                  setdataActividades(tempAct);
                } catch {
                  setisOwner(false);
                  setisLoader({
                    dataInfoCurso: false,
                    dataClase: false,
                  });
                }

                if (tempAct.length === 0) {
                  setisOwner(false);
                  setisLoader({
                    dataInfoCurso: false,
                    dataClase: false,
                  });
                } else {
                  setisOwner(resp);
                  setisLoader({
                    dataInfoCurso: false,
                    dataClase: false,
                  });
                }
              });
            });
          } else {
            setisOwner(false);
            setisLoader({
              dataInfoCurso: false,
              dataClase: false,
            });
          }
        }, 300);
      });
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [codigoAct, idClase]);

  const iniciarActividadConClase = () => {
    const swalWithBootstrapButtons = Swal.mixin({});

    swalWithBootstrapButtons
      .fire({
        title: "¿Iniciar actividad con esta clase?",
        text: "Proin fringilla diam finibus magna condimentum efficitur. Suspendisse potenti. Vestibulum iaculis non mi ac tincidunt. Nunc fermentum nibh ac orci imperdiet faucibus. Ut et semper metus. ",
        icon: "info",
        showCancelButton: true,
        confirmButtonText: "Iniciar",
        cancelButtonText: "No, cerrar",
        reverseButtons: true,
        confirmButtonColor: "#2e7d32",
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire({
            title: "Iniciando actividad, espere un momento...",
            icon: "info",
            showCancelButton: false,
            showConfirmButton: false,
            allowOutsideClick: false,
            allowEscapeKey: false,
            allowEnterKey: false,
          });

          setTimeout(() => {
            activarClaseActividadOnline(
              data.data.infoClase.nombreclase,
              idClase,
              codigoAct,
              (resp) => {
                if (resp) {
                  Swal.fire({
                    position: "top-end",
                    icon: "success",
                    title: "Actividad iniciada",
                    text: "La actividad online ha sido iniciada con exito.",
                    showConfirmButton: false,
                    timer: 1500,
                  });

                  setTimeout(() => {
                    let a = document.createElement("a");
                    a.target = "_blank";
                    a.href =
                      "/actividad/" +
                      codigoAct +
                      "/clase/" +
                      idClase +
                      "/pizarra";
                    a.click();
                  }, 1200);
                } else {
                  Swal.fire(
                    "Error al iniciar la actividad con la clase " +
                      data.data.infoClase.nombreclase,
                    "",
                    "error"
                  );
                }
              },
              (stateAct) => {
                if (stateAct === "sinDiapositivas") {
                  Swal.fire({
                    position: "top-end",
                    icon: "error",
                    title: "Actividad sin diapositivas",
                    text: "La actividad principal debe tener una diapositiva para el profesor",
                    showConfirmButton: false,
                    timer: 1500,
                  });
                }
              }
            );
          }, 600);
        } else if (
          /* Read more about handling dismissals below */
          result.dismiss === Swal.DismissReason.cancel
        ) {
          let timerInterval;

          swalWithBootstrapButtons
            .fire({
              title: "Comienzo cancelado!",
              html:
                "Se ha cancelado el inicio de la actividad con " +
                data.data.infoClase.nombreclase +
                " en " +
                codigoAct +
                ".",
              timer: 3000,
              timerProgressBar: true,

              willClose: () => {
                clearInterval(timerInterval);
              },
            })
            .then((result) => {
              /* Read more about handling dismissals below */
              if (result.dismiss === Swal.DismissReason.timer) {
                console.log("I was closed by the timer");
              }
            });
        }
      });
  };

  return (
    <div className="main-container">
      {!(isLoader.dataClase === true || isLoader.dataInfoCurso === true) ? (
        <div
          className="contenido"
          style={{
            marginLeft: 100,
            marginRight: 20,
            marginBottom: 10,
          }}
        >
          {isOwner ? (
            <div
              style={{
                width: "100%",
                marginTop: "-30px",
              }}
            >
              {actividadIniciada !== true && (
                <Typography
                  style={{ marginLeft: 0, fontSize: 28 }}
                  variant="titulo"
                >
                  Actividad Online
                </Typography>
              )}

              <div
                style={{
                  marginTop: 30,
                  backgroundColor: !actividadIniciada ? "#fff" : "transparent",
                  borderRadius: 25,
                  height: !actividadIniciada ? "75vh" : "auto",
                }}
              >
                {actividadIniciada !== true && (
                  <div style={{ padding: 20, paddingBottom: 5 }}>
                    <Button
                      title="Volver"
                      style={{
                        minWidth: 50,
                        maxWidth: 50,
                        minHeight: 45,
                        maxHeight: 45,
                        borderRadius: 100,
                        padding: 0,
                        margin: 0,
                        backgroundColor: "white",
                        boxShadow: "none",
                      }}
                      href={"/actividad/" + codigoAct}
                    >
                      <ArrowBackIosNew style={{ color: "#6C3373" }} />
                    </Button>
                  </div>
                )}

                <div style={{ padding: 20, paddingTop: 0, marginTop: 0 }}>
                  {actividadIniciada === false ? (
                    <Grid
                      container
                      spacing={2}
                      columns={{ xs: 4, sm: 12, md: 20 }}
                    >
                      <Grid item xs={4} sm={4} md={7}>
                        <div
                          style={{
                            padding: 20,
                            overflowY: "auto",
                            height: "66vh",
                          }}
                        >
                          {data.data.infoClase.imagen === undefined ? (
                            <div
                              style={{
                                backgroundImage: `url(${iconDefault})`,
                                backgroundPosition: "center center",
                                backgroundRepeat: "no-repeat",
                                backgroundSize: "cover",
                                height: "220px",
                                width: "90%",
                                borderRadius: 18,
                              }}
                            />
                          ) : (
                            <div
                              style={{
                                backgroundImage:
                                  'url("' + data.data.infoClase.imagen + "",
                                backgroundPosition: "center center",
                                backgroundRepeat: "no-repeat",
                                backgroundSize: "cover",
                                height: "220px",
                                width: "90%",
                                borderRadius: 25,
                              }}
                            />
                          )}

                          <div
                            style={{
                              padding: 10,
                              textAlign: "start",
                            }}
                          >
                            <Typography variant="titulo_2">
                              {data.data.infoClase.nombreclase}
                            </Typography>
                            <div
                              style={{
                                paddingTop: 10,
                                display: "flex",
                                flexDirection: "row",
                              }}
                            >
                              <Button
                                onClick={(e) => {
                                  iniciarActividadConClase();
                                }}
                                variant={"contained"}
                              >
                                Comenzar
                              </Button>

                              {exists ? (
                                <Link
                                  to={{
                                    pathname: "/vistaclases",
                                    state: {
                                      nombreCurso:
                                        infoCurso.data.datosCurso.nombre,
                                      nombreClase:
                                        data.data.infoClase.nombreclase,
                                      idCurso: infoCurso.data.datosCurso.id,
                                      isSuccessful: true,
                                      idClase: idClase,
                                      tipoCurso: "privados",
                                      indexUnidad:
                                        props.location.state.indexUnidad,
                                      indexClase:
                                        props.location.state.indexClase,
                                      dataClase: props.location.state.dataClase,
                                    },
                                  }}
                                >
                                  <Button
                                    style={{
                                      marginLeft: 5,
                                      backgroundColor: "white",
                                    }}
                                    variant={"outlined"}
                                    disabled={false}
                                  >
                                    Editar
                                  </Button>
                                </Link>
                              ) : (
                                <Button
                                  style={{ marginLeft: 5 }}
                                  variant={"outlined"}
                                  disabled={true}
                                >
                                  Editar
                                </Button>
                              )}
                            </div>
                            <div>
                              {/* <h2>Una Clase del curso {"ADDCURSO"}</h2> */}
                              <h3
                                style={{
                                  marginLeft: 0,
                                  fontSize: "20px",
                                  color: "#707070",
                                  fontWeight: 600,
                                }}
                              >
                                Objetivo de clase
                              </h3>
                              <p
                                style={{
                                  textAlign: "justify",
                                  fontSize: "14px",
                                  fontWeight: 500,
                                  color: "#707070",
                                }}
                              >
                                {data.data.infoClase.objetivo}
                              </p>
                              <h3
                                style={{
                                  marginLeft: 0,
                                  fontSize: "20px",
                                  color: "#707070",
                                  fontWeight: 600,
                                }}
                              >
                                Temática de clase
                              </h3>
                              <p
                                style={{
                                  textAlign: "justify",
                                  fontSize: "14px",
                                  fontWeight: 500,
                                  color: "#707070",
                                }}
                              >
                                {data.data.infoClase.tematica}
                              </p>
                            </div>
                          </div>
                        </div>
                      </Grid>
                      <Grid item xs={4} sm={8} md={13}>
                        <div
                          style={{
                            padding: 10,
                            overflowY: "auto",
                            height: "66vh",
                          }}
                        >
                          {/* <Typography variant="h5" style={{ marginBottom: 20 }}>
                            Actividades ({dataActividades.length})
                          </Typography> */}
                          {dataActividades.map((data, index) => {
                            return (
                              <div
                                style={{
                                  marginBottom: 20,
                                  height: "auto",
                                }}
                                key={index}
                              >
                                <div
                                  style={{ textAlign: "start", padding: 10 }}
                                >
                                  <Typography
                                    variant="tituloActividadACTONLINE"
                                    style={{ marginTop: 0 }}
                                  >
                                    {data.dataActividad.nombreActividad}
                                  </Typography>
                                  {[
                                    { id: "profesor", name: "Profesor" },
                                    // { id: "alumnos", name: "Alumno" },
                                    // { id: "pizarra", name: "Pizarra" },
                                  ].map((vistasData, index) => {
                                    return (
                                      <DiapositivasActOnline
                                        key={index}
                                        codigo={codigoAct}
                                        idClase={idClase}
                                        idActividad={data.key}
                                        idVista={vistasData.id}
                                        nameVista={vistasData.name}
                                      />
                                    );
                                  })}
                                </div>
                              </div>
                            );
                          })}
                        </div>
                      </Grid>
                    </Grid>
                  ) : (
                    <div>
                      <MainProfesorActOnline
                        codigoAct={codigoAct}
                        data={data}
                        dataActividades={dataActividades}
                        idClase={idClase}
                        infoCurso={infoCurso}
                      ></MainProfesorActOnline>
                    </div>
                  )}
                </div>
              </div>
            </div>
          ) : (
            <Notfound></Notfound>
          )}
        </div>
      ) : (
        <div className="contenido">
          <Backdrop
            sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={isLoader.dataClase || isLoader.dataInfoCurso}
          >
            <CircularProgress color="inherit" />
          </Backdrop>
        </div>
      )}
    </div>
  );
}

export default MainVistaClasesActOn;
