import {
  // Alert,
  Box,
  Button,
  CircularProgress,
  Tab,
  Tabs,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import backImage from "../../../assets/images/login_background.png";
import { Link } from "react-router-dom";
import CrearActividadCursosPublicos from "./CrearActividadCursosPublicos";
import { getActividadesCursosPublicos } from "../FirebaseVistaClase";
import VistaActividadPublicaProfesor from "./Vistas/VistaActividadPublicaProfesor";
import VistaActividadPublicaAlumno from "./Vistas/VistaActividadPublicaAlumno";
import VistaActividadPublicaPizarra from "./Vistas/VistaActividadPublicaPizarra";


function RedBar() {
  return <Box sx={{ height: 5 }} />;
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0 }} style={{ marginTop: 15 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function MainCursoUnitarioPublico(props) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [nombreCurso, setNombreCurso] = useState("hola");
  const [nombreClase, setNombreClase] = useState("");
  const [idCurso, setIdCurso] = useState("");
  const [idClase, setIdClase] = useState("");
  const [banderaGetActividad, setBanderaGetActividad] = useState(true);
  const [tipoCurso, setTipoCurso] = useState("");
  const [estado, setestado] = useState(null);


  const [actividades, setActividades] = useState([]);

  useEffect(() => {
    try {
      if (props.location.state.isSuccessful === true) {
        setNombreCurso(props.location.state.nombreCurso);
        setNombreClase(props.location.state.nombreClase);
        setIdCurso(props.location.state.idCurso);
        setIdClase(props.location.state.idClase);
        setTipoCurso(props.location.state.tipoCurso);
        setestado(props.location.state.estado);

        getActividadesCursosPublicos(
          props.location.state.idCurso,
          props.location.state.idClase,
          (value) => {
            setActividades(value);
            setBanderaGetActividad(false);
          }
        );
      } else {
        window.location = "/";
      }
    } catch (error) {
      window.location = "/";
    }
    // eslint-disable-next-line
  }, []);
  return (
    <div
      style={{
        height: "100%",
        minWidth: "fit-content",
        width: "100%",
        backgroundColor: "rgba(234, 234, 234, 1)",
        backgroundImage: `url(${backImage})`,
        backgroundSize: "repeat",
        marginTop: 0,
        display: "flex",
        minHeight: "100vh",
      }}
    >
      <div
        style={{
          width: "100%",
        }}
      >
        <Typography
          variant="h4"
          style={{
            padding: 30,
            marginLeft: 25,
            color: "black",
          }}
        >
          {nombreCurso} - {nombreClase}
        </Typography>
        <Link
          style={{
            textDecoration: "none",
          }}
          to={{
            pathname: "/planificaciones",
            state: {
              idCurso: idCurso,
              nombreCurso: nombreCurso,
              isBack: true,
              tipoCurso: tipoCurso,
              estado: estado,
            },
          }}
        >
          <Button
            style={{
              marginLeft: 30,
              marginRight: 30,
              marginTop: 10,
              backgroundColor: "#82e082",
            }}
            color="primary"
            href="/planificaciones"
          >
            Volver a clases
          </Button>
        </Link>
        <RedBar></RedBar>
        {!estado && (
          <CrearActividadCursosPublicos
            idCurso={idCurso}
            idClase={idClase}
          ></CrearActividadCursosPublicos>
        )}

        {!banderaGetActividad ? (
          <div>
            {actividades.length > 0 ? (
              <div
                style={{
                  marginLeft: 30,
                  marginRight: 30,
                  marginTop: 10,
                  marginBottom: 10,
                }}
              >
                <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
                  <Tabs
                    value={value}
                    onChange={handleChange}
                    aria-label="basic tabs example"
                    style={{
                      backgroundColor: "white",
                      textAlign: "center",
                    }}
                  >
                    <Tab label="Profesor" {...a11yProps(0)} />
                    <Tab label="Alumnos" {...a11yProps(1)} />
                    <Tab label="Pizarra" {...a11yProps(2)} />
                  </Tabs>
                </Box>
                <TabPanel value={value} index={0}>
                  <VistaActividadPublicaProfesor
                    actividades={actividades}
                    idClase={idClase}
                    idCurso={idCurso}
                    estado={estado}
                  ></VistaActividadPublicaProfesor>
                </TabPanel>
                <TabPanel value={value} index={1}>
                  <VistaActividadPublicaAlumno
                    actividades={actividades}
                    idClase={idClase}
                    idCurso={idCurso}
                    estado={estado}
                  ></VistaActividadPublicaAlumno>
                </TabPanel>
                <TabPanel value={value} index={2}>
                  <VistaActividadPublicaPizarra
                    actividades={actividades}
                    idClase={idClase}
                    idCurso={idCurso}
                    estado={estado}
                  ></VistaActividadPublicaPizarra>
                </TabPanel>
              </div>
            ) : (
              <div
                style={{

                  marginBottom: 80,
                  textAlign: "center",
                  height: "100px",
                }}
              >
                <Box sx={{ width: "auto", padding: 2 }}>
                  <p>No registra actividad</p>
                </Box>
              </div>
            )}
          </div>
        ) : (
          <div
            style={{
              marginLeft: 30,
              marginRight: 30,
              marginTop: 80,
              marginBottom: 80,
              textAlign: "center",
              height: "100px",
            }}
          >
            <Box sx={{ width: "auto", padding: 2 }}>
              <CircularProgress color="inherit" />
            </Box>
          </div>
        )}
      </div>
    </div>
  );
}

export default MainCursoUnitarioPublico;
