import {
  Button,
  ButtonGroup,
  Card,
  CardContent,
  CardMedia,
  Grow,
  LinearProgress,
  Paper,
  Stack,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useState } from "react";
import {
  deleteActividadOnline,
  getActividadesOnline,
} from "./BDActividadesOnline";
import defaultImage from "../../assets/images/default_icon_curso_2.png";
import Swal from "sweetalert2";

import { Link } from "react-router-dom";

function ListarActividadesOnline() {
  const [actividadesOnline, setActividadesOnline] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(false);
  useEffect(() => {
    getActividadesOnline((data) => {
      setActividadesOnline(data);
      setbanderaCarga(true);
    });
  }, []);


  return (
    <div
      style={{
        paddingTop: 20,
      }}
    >
      {banderaCarga ? (
        <Paper
          style={{
            width: "auto",
            height: "auto",
            padding: 20,
            borderRadius: 30,
            marginBottom: 30,
            boxShadow: "none",
          }}
        >

          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
            }}
          >
            {actividadesOnline.map((actividad, index) => {

              return (
                <Grow
                  in={banderaCarga}
                  style={{ transformOrigin: "0 0 0" }}
                  timeout={400 * (index + 1)}
                  key={index}
                >
                  <Card
                    sx={{
                      display: "flex",
                      borderWidth: 1,
                      padding: 3,
                      marginRight: 5,
                      marginTop: 3,
                      marginBottom: 3,
                      minHeight: 250,
                      maxWidth: 340,
                      borderRadius: "30px",
                    }}
                    elevation={"4"}
                  >
                    <CardMedia
                      component="img"
                      sx={{ width: 120, borderRadius: "18px" }}
                      image={
                        actividad.data.datosCurso.imagen === undefined
                          ? defaultImage
                          : actividad.data.datosCurso.imagen
                      }
                      alt={"Miniatura " + actividad.data.datosCurso.nombre}
                    />
                    <Box sx={{ display: "flex", flexDirection: "column" }}>
                      <CardContent sx={{ flex: "1 0 auto", paddingRight: 0 }}>
                        <Typography variant="h5" style={{ fontSize: "18px" }}>
                          {actividad.data.datosCurso.nombre}
                        </Typography>

                        <Typography
                          variant="subtitle1"
                          color="text.secondary"
                          style={{
                            maxWidth: 200,
                            wordWrap: "break-word",
                            overflowWrap: "break-word",
                            hyphens: "auto",
                          }}
                        >
                          <p style={{fontSize: '14px', margin: 0, padding: 0}}>
                            <b>
                              {"Código: " +
                                actividad.data.datosActividad.codigoActividad}
                            </b>
                          </p>
                          <p style={{fontSize: '14px', margin: 0, padding: 0}}>
                            {actividad.data.datosActividad.identificadorCurso +
                              " - " +
                              actividad.data.datosActividad.establecimiento}
                          </p>
                        </Typography>
                      </CardContent>
                      <Box
                        sx={{
                          display: "flex",
                          flexDirection: "row",
                          flexWrap: "wrap",
                          justifyContent: "flex-end",
                          pl: 1,
                          pb: 1,
                          height: "-webkit-fill-available",
                          alignItems: "flex-end",
                        }}
                      >
                        <ButtonGroup
                          key={
                            "bttn" +
                            actividad.data.datosActividad.codigoActividad
                          }
                          disableElevation
                          variant="contained"
                          color="primary"
                        >
                          <Link
                            to={
                              "/actividad/" +
                              actividad.data.datosActividad.codigoActividad
                            }
                          >
                            <Button
                              variant="contained"
                              style={{
                                height: "35px",
                                width: "fit-content",
                                minWidth: 80,
                                marginRight: 10,
                              }}
                            >
                              Ver
                            </Button>
                          </Link>

                          <Button
                            style={{
                              height: "35px",
                              width: "fit-content",
                              minWidth: 80,
                            }}
                            onClick={() => {
                              Swal.fire({
                                title: "Eliminar Actividad Online",
                                text:
                                  "Eliminar actividad online del curso " +
                                  actividad.data.datosCurso.nombre +
                                  " con código Nº " +
                                  actividad.data.datosActividad.codigoActividad,
                                showCancelButton: true,
                                confirmButtonText: "Borrar Actividad",
                                cancelButtonText: "Cerrar",
                                confirmButtonColor: "red",
                              }).then((result) => {
                                /* Read more about isConfirmed, isDenied below */
                                if (result.isConfirmed) {
                                  deleteActividadOnline(
                                    actividad.key,
                                    actividad.data.datosActividad
                                      .codigoActividad,
                                    (stats) => {
                                      if (stats) {
                                        Swal.fire({
                                          title: "Actividad Online Borrada",
                                          icon: "success",
                                          showConfirmButton: true,
                                        });
                                      } else {
                                        Swal.fire({
                                          title:
                                            "Error al eliminar la actividad",
                                          icon: "error",
                                          showConfirmButton: true,
                                          text: "La actividad online no se ha borrado, vuelva a intentarlo...",
                                        });
                                      }
                                    }
                                  );
                                }
                              });
                            }}
                            variant="outlined"
                          >
                            Borrar
                          </Button>
                        </ButtonGroup>
                      </Box>
                    </Box>
                  </Card>
                </Grow>
              );
            })}
            {actividadesOnline.length === 0 && (
              <div
                style={{
                  minHeight: "200px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  width: "100%",
                }}
              >
                <Typography style={{ textAlign: "center" }} variant="h5">
                  No registra actividades creadas
                </Typography>
              </div>
            )}
          </div>
        </Paper>
      ) : (
        <Paper
          style={{
            width: "auto",
            height: "auto",
            padding: 20,
            borderRadius: 30,
            marginBottom: 30,
            boxShadow: "none",
          }}
        >
          <div
            style={{
              minHeight: "400px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Stack sx={{ width: "80%", color: "grey.500" }} spacing={2}>
              <LinearProgress />
            </Stack>
          </div>
        </Paper>
      )}
    </div>
  );
}

export default ListarActividadesOnline;
