import {
  Button,
  Checkbox,
  FormControlLabel,
  FormGroup,
  Input,
  Modal,
  Typography,
} from "@mui/material";
import { Box } from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import parse from "html-react-parser";
import { Editor } from "@tinymce/tinymce-react";
import tinymce from "tinymce";
import domtoimage from "dom-to-image";
import { setDisenoDiapo } from "./FirebaseDiseñoDiapositivas";
import {
  dataValueButton,
  dataValueCheckbox,
  dataValueLista,
  dataValueSinAccion,
  dataValueTextArea,
  dataValueUploadPhoto,
  editorDiseñoDiapositiva,
  renderButtonList,
  renderCheckBoxList,
  renderTextArea,
  renderUploadPhoto,
} from "../../Funcionalidades/AccionesDiapositivas";

function RedBar() {
  return <Box sx={{ height: 10 }} />;
}

function EditarDiseno({
  banderaEditarDiseno,
  nombrePlantilla,
  valorTemporalHTML,
  accionesEditar,
  tipoVista,
}) {
  const editorRef = useRef(null);

  const { setBanderaEditarDiseno } = React.useContext(UserContext);

  const handleClose = () => setBanderaEditarDiseno(false);

  const [isContinuar, setisContinuar] = useState(false);
  const [valueCheckbox, setvalueCheckbox] = useState(dataValueCheckbox());

  const [cantBotones, setcantBotones] = useState(1);
  const [cantBotonesCheckbox, setcantBotonesCheckbox] = useState(2);

  const [contenidoEditor, setcontenidoEditor] = useState(valorTemporalHTML);

  const [listBotones, setlistBotones] = useState([]);
  const [listCheckBox, setlistCheckBox] = useState([]);

  const ariaLabel = { "aria-label": "description" };

  useEffect(() => {
    setcontenidoEditor(valorTemporalHTML);
    if (accionesEditar === undefined) {
      setvalueCheckbox(dataValueSinAccion());
    } else {
      if (accionesEditar.tipoAccion === "Button") {
        setvalueCheckbox(dataValueButton());
        setcantBotones(accionesEditar.cantidadItem);
      }
      if (accionesEditar.tipoAccion === "CheckBox") {
        setvalueCheckbox(dataValueLista());
        setcantBotonesCheckbox(accionesEditar.cantidadItem);
      }
      if (accionesEditar.tipoAccion === "TextArea") {
        setvalueCheckbox(dataValueTextArea());
      }
      if (accionesEditar.tipoAccion === "Photo") {
        setvalueCheckbox(dataValueUploadPhoto());
      }
    }
  }, [valorTemporalHTML, accionesEditar]);

  const continuarVistaPrevia = () => {
    if (editorRef.current) {
      setcontenidoEditor(editorRef.current.getContent());
    }
    var listBotonestemp = [];
    for (let index = 0; index < cantBotones; index++) {
      listBotonestemp.push(index + 1);
    }
    setlistBotones(listBotonestemp);

    var listCheckBoxtemp = [];
    for (let index = 1; index <= cantBotonesCheckbox; index++) {
      listCheckBoxtemp.push(index);
    }
    setlistCheckBox(listCheckBoxtemp);
    setisContinuar(true);
  };

  const subirDatos = () => {
    var node = document.getElementById("my-node");

    domtoimage
      .toPng(node, { quality: 0.7 })
      .then(function (dataUrl) {
        var tipoAccion = null;
        var cantidadItem = null;
        if (valueCheckbox[0]) {
          tipoAccion = "Button";
          cantidadItem = cantBotones;
        }
        if (valueCheckbox[1]) {
          tipoAccion = "CheckBox";
          cantidadItem = cantBotonesCheckbox;
        }
        if (valueCheckbox[3]) {
          tipoAccion = "TextArea";
        }
        if (valueCheckbox[4]) {
          tipoAccion = "Photo";
        }
        setDisenoDiapo(
          tipoVista,
          nombrePlantilla,
          tipoAccion,
          cantidadItem,
          contenidoEditor,
          dataUrl
        );
      })
      .catch(function (error) {
        console.error("oops, something went wrong!", error);
      });
  };

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "75%",
    bgcolor: "white",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
    maxWidth: "80%",
    maxHeight: "80%",
    overflow: "auto",
  };

  return (
    <div>
      <Modal
        open={banderaEditarDiseno}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography
            style={{
              marginBottom: 20,
            }}
            id="modal-modal-title"
            variant="h6"
            component="h2"
          >
            Nombre de plantilla: {nombrePlantilla}
          </Typography>
          <div>
            <RedBar></RedBar>
            {!isContinuar ? (
              <div>
                <Editor
                  onInit={(evt, editor) => (editorRef.current = editor)}
                  apiKey="awyk0fee0yvyjg1nr6hk79jxp2h2ftmvuu7go89blr8pfawr"
                  initialValue={contenidoEditor}
                  init={{
                    height: 700,
                    max_height: 700,
                    resize: false,
                    menubar: true,
                    language: "es_MX",
                    fontsize_formats:
                      "8pt 9pt 10pt 11pt 12pt 14pt 15pt 16pt 17pt 18pt 20pt 21pt 22pt 24pt 26pt 28pt 30pt 32pt 36pt 40pt 48pt 60pt 72pt 96pt",
                    plugins: [
                      "advlist autolink lists link image",
                      "charmap print preview anchor help",
                      "searchreplace visualblocks code",
                      "insertdatetime media table paste wordcount",
                      "image code",
                    ],
                    toolbar:
                      "insertfile undo redo | styleselect | fontsizeselect | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link image",

                    image_title: true,
                    automatic_uploads: true,
                    file_picker_types: "image",

                    file_picker_callback: function (cb, value, meta) {
                      var input = document.createElement("input");
                      input.setAttribute("type", "file");
                      input.setAttribute("accept", "image/*");

                      input.onchange = function () {
                        var file = this.files[0];

                        var reader = new FileReader();

                        reader.onload = function () {
                          var id = "blobid" + new Date().getTime();
                          var blobCache =
                            tinymce.activeEditor.editorUpload.blobCache;
                          var base64 = reader.result.split(",")[1];
                          var blobInfo = blobCache.create(id, file, base64);
                          blobCache.add(blobInfo);
                          cb(blobInfo.blobUri(), { title: file.name });
                        };
                        reader.readAsDataURL(file);
                      };

                      input.click();
                    },

                    content_style:
                      "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",
                  }}
                />
                <h4
                  style={{
                    color: "black",
                    marginLeft: 0,
                    textAlign: "start",
                    marginBottom: 10,
                  }}
                >
                  Acciones para la diapositiva
                </h4>
                <FormGroup style={{ flexDirection: "row" }}>
                  <FormControlLabel
                    control={
                      <Checkbox
                        onClick={(e) => {
                          setvalueCheckbox(
                            editorDiseñoDiapositiva(0, e.target.checked)
                          );
                        }}
                        checked={valueCheckbox[0]}
                      />
                    }
                    label="Botón"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        onClick={(e) => {
                          setvalueCheckbox(
                            editorDiseñoDiapositiva(1, e.target.checked)
                          );
                        }}
                        checked={valueCheckbox[1]}
                      />
                    }
                    label="Alternativas"
                  />{" "}
                  {tipoVista === "alumnos" && (
                    <FormControlLabel
                      control={
                        <Checkbox
                          onClick={(e) => {
                            setvalueCheckbox(
                              editorDiseñoDiapositiva(3, e.target.checked)
                            );
                          }}
                          checked={valueCheckbox[3]}
                        />
                      }
                      label="Campo de texto"
                    />
                  )}
                  {tipoVista === "alumnos" && (
                    <FormControlLabel
                      control={
                        <Checkbox
                          onClick={(e) => {
                            setvalueCheckbox(
                              editorDiseñoDiapositiva(4, e.target.checked)
                            );
                          }}
                          checked={valueCheckbox[4]}
                        />
                      }
                      label="Fotografía"
                    />
                  )}
                  <FormControlLabel
                    control={
                      <Checkbox
                        defaultChecked
                        onClick={(e) => {
                          setvalueCheckbox(
                            editorDiseñoDiapositiva(2, e.target.checked)
                          );
                        }}
                        checked={valueCheckbox[2]}
                      />
                    }
                    label="Sin acciones"
                  />
                  {}
                </FormGroup>
                <RedBar></RedBar>

                {valueCheckbox[0] && (
                  <div
                    style={{
                      textAlign: "start",
                    }}
                  >
                    <h4
                      style={{
                        color: "black",
                        marginLeft: 0,
                        marginBottom: 10,
                      }}
                    >
                      Cantidad de botones
                    </h4>
                    <Input
                      style={{ textAlign: "start" }}
                      defaultValue={1}
                      value={cantBotones}
                      inputProps={ariaLabel}
                      type="number"
                      onChange={(e) => {
                        if (e.target.value > 3) {
                          setcantBotones(3);
                        } else {
                          if (e.target.value < 1) {
                            setcantBotones(1);
                          } else {
                            setcantBotones(e.target.value);
                          }
                        }
                      }}
                    />
                  </div>
                )}
                <RedBar></RedBar>

                {valueCheckbox[1] && (
                  <div
                    style={{
                      textAlign: "start",
                    }}
                  >
                    <h4
                      style={{
                        color: "black",
                        marginLeft: 0,
                        marginBottom: 10,
                      }}
                    >
                      Cantidad de alternativas
                    </h4>
                    <Input
                      style={{ textAlign: "start" }}
                      defaultValue={1}
                      value={cantBotonesCheckbox}
                      inputProps={ariaLabel}
                      type="number"
                      onChange={(e) => {
                        if (e.target.value > 5) {
                          setcantBotonesCheckbox(5);
                        } else {
                          if (e.target.value < 2) {
                            setcantBotonesCheckbox(2);
                          } else {
                            setcantBotonesCheckbox(e.target.value);
                          }
                        }
                      }}
                    />
                  </div>
                )}
                <RedBar></RedBar>
                <Button
                  onClick={(e) => {
                    continuarVistaPrevia();
                  }}
                >
                  Continuar
                </Button>
              </div>
            ) : (
              <div>
                {/**
                 * Vista previa
                 *
                 */}

                <div>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      justifyContent: "space-between",
                      alignContent: "center",
                      marginBottom: 0,
                    }}
                  >
                    <h4 style={{ color: "black", marginLeft: 0 }}>
                      Crear diseño de diapositiva en vista de {tipoVista}
                    </h4>
                    <Button
                      color="error"
                      style={{
                        backgroundColor: "red",
                        color: "white",
                        marginLeft: 40,
                        height: "fit-content",
                      }}
                      onClick={(e) => {
                        setisContinuar(false);
                      }}
                    >
                      Volver
                    </Button>
                  </div>

                  <div
                    style={{
                      borderStyle: "solid",
                      borderRadius: 25,
                      // marginTop: 0,
                      width: "90%",
                    }}
                  >
                    <div className="my-node" id="my-node">
                      <div
                        style={{
                          margin: 30,
                        }}
                      >
                        <div style={{}}>
                          {parse(contenidoEditor)}
                          {/**Vista previa antes de subir el diseño editado */}
                          {valueCheckbox[0] && renderButtonList(listBotones)}
                          {valueCheckbox[1] && renderCheckBoxList(listCheckBox)}
                          {valueCheckbox[3] && renderTextArea()}
                          {valueCheckbox[4] && renderUploadPhoto()}
                        </div>
                      </div>
                      <RedBar></RedBar>
                    </div>
                  </div>
                  <h2 style={{ color: "black", marginTop: 10 }}>
                    Nombre del diseño: {nombrePlantilla}{" "}
                  </h2>
                  <Button
                    style={{
                      backgroundColor: "blue",
                      color: "white",
                    }}
                    onClick={(e) => {
                      subirDatos();
                    }}
                  >
                    Modificar plantilla
                  </Button>
                </div>

                <Button
                  onClick={(e) => {
                    setisContinuar(false);
                  }}
                >
                  Volver
                </Button>
              </div>
            )}
          </div>
        </Box>
      </Modal>
    </div>
  );
}

export default EditarDiseno;
