import React from "react";
import parse from "html-react-parser";
import { renderContenidoAccionesButton, renderContenidoAccionesCheckBox, renderTextArea, renderUploadPhoto } from "../pages/Funcionalidades/AccionesDiapositivas";

function CaratulaDiapositiva({ htmlCode, acciones, zoomValue }) {
  return (
    <div
      style={{
        minWidth: 1300,
        minHeight: 800,
        maxWidth: 1300,
        maxHeight: 800,
        width: 1300,
        height: 800,
        borderRadius: "60px",
        borderWidth: 10,
        borderColor: "#9c5fa2",
        borderStyle: "solid",
        padding: 20,
        zoom: zoomValue,
        backgroundColor: "white",
      }}
    >
      <div>
        {parse(htmlCode)}
        {acciones.tipo === "Button" &&
          renderContenidoAccionesButton(acciones.item)}
        {acciones.tipo === "CheckBox" &&
          renderContenidoAccionesCheckBox(acciones.item)}
        {acciones.tipo === "TextArea" && renderTextArea()}
        {acciones.tipo === "Photo" && renderUploadPhoto()}
      </div>
    </div>
  );
}

export default CaratulaDiapositiva;
