import { Button } from "@mui/material";
import FolderOpenOutlinedIcon from "@mui/icons-material/FolderOpenOutlined";
import React from "react";

function ListarCarpetas() {
  return (
    <div
      style={{
        padding: 10,
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
      }}
    >
      {[0, 1, 2, 3, 4, 5, 6, 7].map(() => {
        return (
          <div style={{ margin: 10, marginLeft: 0, marginRight: 12 }}>
            <Button
              variant="outlined"
              startIcon={
                <FolderOpenOutlinedIcon
                  style={{ color: "#9c5fa2", fontSize: 30 }}
                />
              }
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "flex-start",
                backgroundColor: "white",
                borderStyle: "none",
                color: "#555",
                minWidth: "250px",
                fontSize: 18,
                padding: 30,
                paddingLeft: 15
              }}
            >
              Nombre Carpeta
            </Button>
          </div>
        );
      })}
    </div>
  );
}

export default ListarCarpetas;
