import { Button, Typography } from "@mui/material";
import React from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import { Popover } from "@mui/material";
import { Box } from "@mui/material";
import { deleteClasesPrivados } from "../../../database/firebase";
import { deleteClasesPublicos } from "../Publicos/firebaseCursosPublicos";


function DeleteClases({ id, nombre, state, idClase }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [msg, setMsg] = React.useState("¿Eliminar la clase - " + nombre);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const identificador = open ? "simple-popover" : undefined;

  return (
    <div>
      <Button
        aria-describedby={identificador}
        color="error"
        onClick={handleClick}
        style={{
          minHeight: "35px",
          textAlign: "center",
          maxWidth: "fit-content",
        }}
        title="Borrar Clase"
        variant="outlined"
      >
        <DeleteIcon />
      </Button>
      <Popover
        id={identificador}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <div style={{ padding: 20, paddingBottom: 5 }}>
          <Typography sx={{ p: 2 }}>{msg}</Typography>
          <Box textAlign="center" style={{ marginTop: 10, marginBottom: 10 }}>
            {state === "privado" && (
              <Button
                style={{
                  width: "fit-content",
                }}
                variant="contained"
                onClick={(e) => {
                  //deleteClasesPrivados(id, value.key, (data) => {})
                  deleteClasesPrivados(id, idClase, (callback) => {
                    if (!callback) {
                      setMsg(
                        "Error al eliminar la clase " +
                          nombre +
                          " de su registro"
                      );
                    } else {
                      handleClose();
                    }
                  });
                }}
              >
                Eliminar
              </Button>
            )}
            {state === "publico" && (
              <Button
                style={{
                  width: "fit-content",
                }}
                variant="contained"
                onClick={(e) => {
                  deleteClasesPublicos(id, idClase, (callback) => {
                    if (!callback) {
                      setMsg(
                        "Error al eliminar el curso " +
                          nombre +
                          " de su registro"
                      );
                    } else {
                      handleClose();
                    }
                  });
                }}
              >
                Eliminar
              </Button>
            )}

            <Button
              style={{
                width: "fit-content",
                marginLeft: 5
              }}
              variant="outlined"
              onClick={(e) => {
                setAnchorEl(null);
              }}
            >
              Cerrar
            </Button>
          </Box>
        </div>
      </Popover>
    </div>
  );
}

export default DeleteClases;
