import { Button } from "@mui/material";
import React, { useContext } from "react";
import { UserContext } from "../../../Funcionalidades/UseContext";
import parse from "html-react-parser";
import { Box } from "@mui/material";
import domtoimage from "dom-to-image";
import { updateDiapositiva } from "../../../Diapositivas/FirebaseDiapositivas";
import {
  renderContenidoAccionesButton,
  renderContenidoAccionesCheckBox,
  renderTextArea,
  renderUploadPhoto,
} from "../../../Funcionalidades/AccionesDiapositivas";
import Swal from "sweetalert2";
import "../../../../styles/CustomClassSweetAlert2.css";

function RedBar() {
  return <Box sx={{ height: 60 }} />;
}

function EditorVistaPrevia({
  uid,
  idClase,
  idCurso,
  keyActividad,
  idDiapositiva,
  tipoVista,
  nombreDiapositiva,
  tipoAcciones,
  orden,
  lugarAlmacenamiento,
}) {
  const { ContenidoHTMLEditado, accionesEditada, setsecciones } =
    useContext(UserContext);

  const subirDatos = () => {
    let timerInterval;
    var valueState = "Obteniendo la miniatura...";
    Swal.fire({
      customClass: {
        container: "my-swal",
      },
      title: "Editando diapositiva.",
      html: "<b></b>",
      timerProgressBar: false,
      didOpen: () => {
        Swal.showLoading();
        const b = Swal.getHtmlContainer().querySelector("b");
        timerInterval = setInterval(() => {
          b.textContent = valueState;
        }, 50);
      },
      willClose: () => {
        clearInterval(timerInterval);
      },
    });

    var node = document.getElementById("my-node");

    domtoimage
      .toPng(node, { quality: 0.7 })
      .then(function (dataUrl) {
        valueState = "Editando los datos ingresados...";

        updateDiapositiva(
          uid,
          idCurso,
          idClase,
          keyActividad,
          tipoVista,
          nombreDiapositiva,
          idDiapositiva,
          accionesEditada.item,
          tipoAcciones,
          ContenidoHTMLEditado,
          orden,
          dataUrl,
          lugarAlmacenamiento,
          (value) => {
            if (value) {
              Swal.fire({
                customClass: {
                  container: "my-swal",
                },
                icon: "success",
                title: "Diapositiva editada",
                text: "La diapositiva ha sido editada con exito.",
                showConfirmButton: true,
                allowOutsideClick: false,
                allowEscapeKey: false,
                confirmButtonText: "Continuar",
              }).then((result) => {
                /* Read more about isConfirmed, isDenied below */
                if (result.isConfirmed) {
                  window.location.reload();
                }
              });
            } else {
              Swal.fire({
                customClass: {
                  container: "my-swal",
                },
                icon: "error",
                title: "Diapositiva no fue editada",
                text: "Ha ocurrido un error al momento de editar la diapositiva, intente nuevamente. Si el problema perciste, contante con soporte de arguemntapp. ",
                showConfirmButton: true,
                allowOutsideClick: true,
                allowEscapeKey: true,
                confirmButtonText: "Cerrar",
              }).then((result) => {
                /* Read more about isConfirmed, isDenied below */
              });
            }
          },
          (state) => {
            valueState = state;
          }
        );
      })
      .catch(function (error) {
        console.error("oops, something went wrong!", error);
      });
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
      }}
    >
      <div>
        <div
          className="my-node"
          id="my-node"
          style={{
            borderStyle: "solid",
            borderRadius: 25,
            backgroundColor: "white",
            padding: 30,
            minWidth: 1200,
            maxWidth: 1200,
            width: 1200,
            minHeight: 700,
            maxHeight: 700,
            height: 700,
          }}
        >
            {/**Vista previa antes de editar una diapositiva creada */}
            {parse(ContenidoHTMLEditado)}
            {accionesEditada.tipo === "Button" &&
              renderContenidoAccionesButton(accionesEditada.item)}
            {accionesEditada.tipo === "CheckBox" &&
              renderContenidoAccionesCheckBox(accionesEditada.item)}
            {accionesEditada.tipo === "TextArea" && renderTextArea()}
            {accionesEditada.tipo === "Photo" && renderUploadPhoto()}
            <RedBar></RedBar>
        </div>
      </div>
      <div
        style={{
          marginTop: 20,
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <Button
          style={{
            borderStyle: "solid",
            borderWidth: 1,
          }}
          onClick={(e) => {
            setsecciones("Acciones");
          }}
        >
          Volver
        </Button>
        <Button
          style={{
            borderStyle: "solid",
            borderWidth: 1,
          }}
          variant="contained"
          onClick={(e) => {
            subirDatos();
          }}
        >
          Actualizar Datos
        </Button>
      </div>
    </div>
  );
}

export default EditorVistaPrevia;
