import React, { useState } from "react";
import BookmarkIcon from "@mui/icons-material/Bookmark";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  Typography,
} from "@mui/material";
import { getContenidoCursos } from "../../Publicos/firebaseCursosPublicos";
import LoadingArgumentapp from "../../../../components/LoadingArgumentapp";

function CopyContArgumentapp({ nombreCurso, idCurso, estadocurso }) {
  const [open, setOpen] = React.useState(false);
  const [banderaCopia, setbanderaCopia] = useState(false);
  const [isSuccessfully, setisSuccessfully] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <IconButton
        title="Copiar contenido Argumentapp"
        size="large"
        onClick={handleClickOpen}
      >
        <BookmarkIcon style={{ color: "GrayText" }} fontSize="inherit" />
      </IconButton>

      <Dialog
        open={open}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          <Typography variant="titulo_3">
            {" "}
            {"Copiar Contenido Argumentapp"}
          </Typography>
        </DialogTitle>
        {banderaCopia === true ? (
          <div
            style={{
              height: 100,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <LoadingArgumentapp></LoadingArgumentapp>
          </div>
        ) : (
          <DialogContent>
            {banderaCopia === "copiado" && (
              <DialogContentText
                id="alert-dialog-description"
                variant="textoClase"
                style={{ fontSize: 16, textAlign: "justify" }}
              >
                Contenido Argumentapp “{nombreCurso}” copiado en tu sección
                personal de planificaciones.
              </DialogContentText>
            )}
            {banderaCopia === "No_copiado" && (
              <DialogContentText
                id="alert-dialog-description"
                variant="textoClase"
                style={{ fontSize: 16, textAlign: "justify" }}
              >
                Contenido Argumentapp “{nombreCurso}” no logró ser copiado en tu
                sección personal de planificaciones, vuelva a intentarlo
                nuevamente.
              </DialogContentText>
            )}
            {banderaCopia === false && (
              <DialogContentText
                id="alert-dialog-description"
                variant="textoClase"
                style={{ fontSize: 14, textAlign: "justify" }}
              >
                Al aceptar copiarías el Contenido Argumentapp “{nombreCurso}” a
                la sección personal de planificaciones, con ello podrás crear,
                modificar y eliminar a gusto personal las clases y diapositivas
                que entrega Argumentapp a sus usuarios.
              </DialogContentText>
            )}
          </DialogContent>
        )}
        {isSuccessfully ? (
          <div>
            <DialogActions>
              <Button
                variant="contained"
                style={{ width: "fit-content" }}
                onClick={(e) => {
                  window.location.reload();
                }}
                autoFocus
              >
                Cerrar
              </Button>
            </DialogActions>
          </div>
        ) : (
          <DialogActions>
            <Button
              disabled={banderaCopia}
              variant="outlined"
              onClick={handleClose}
              style={{ width: "fit-content" }}
            >
              Cancelar
            </Button>
            <Button
              variant="contained"
              disabled={banderaCopia}
              style={{ width: "fit-content" }}
              onClick={(e) => {
                setbanderaCopia(true);
                setTimeout(() => {
                  getContenidoCursos(idCurso, (value) => {
                    setisSuccessfully(value);
                    if (value) {
                      setbanderaCopia("copiado");
                    } else {
                      setbanderaCopia("No_copiado");
                    }
                  });
                }, 1000);
              }}
              autoFocus
            >
              Aceptar
            </Button>
          </DialogActions>
        )}
      </Dialog>
    </div>
  );
}

export default CopyContArgumentapp;
