import firebaseConf from "../../database/firebase";

export const getDisenosDiapositivasXVista = async (tipoVista, callback) => {
  var arrayValueFromDatabase = [];

  var arrayValue = [];

  const dbDiapo = firebaseConf
    .database()
    .ref("disenos/diapositivas/")
    .child(tipoVista)
    .orderByChild("fechaCreacion");

  const firestoreDiapo = firebaseConf
    .firestore()
    .collection("contenido")
    .doc("data")
    .collection("diseno")
    .doc("plantilla")
    .collection(tipoVista);

  await dbDiapo.once("value", (snapshot) => {
    snapshot.forEach((childSnapshot) => {
      var childKey = childSnapshot.key;
      var childData = childSnapshot.val();

      arrayValueFromDatabase.push({ datos: childData, key: childKey });
    });

    arrayValueFromDatabase.forEach((valor) => {
      firestoreDiapo
        .doc(valor.key)
        .get()
        .then((value) => {
          arrayValue.push({
            datos: valor.datos,
            key: valor.key,
            contenidoHTML: value.data().contenidoHTML,
            miniatura: value.data().miniatura,
          });
        })
        .finally(() => {
          if (arrayValue.length === arrayValueFromDatabase.length) {
            callback(arrayValue);
          }
        });
    });

    if (arrayValueFromDatabase.length === 0) {
      callback(arrayValue);
    }
  });
};

export const setDiapositiva = (
  uid_in,
  idCurso,
  idClase,
  keyIdActividad,
  tipoVista,
  nombreDiapositiva,
  accionesUpload,
  tipoAcciones,
  contenidoHTMLLocal,
  orden,
  image,
  lugarAlmacenamiento,
  callback,
  stateUpload
) => {
  let uid = lugarAlmacenamiento === "privados" ? uid_in : "data";

  let idDiapositiva = firebaseConf
    .firestore()
    .collection("cursos")
    .doc(lugarAlmacenamiento)
    .collection(uid)
    .doc(idCurso)
    .collection("clases")
    .doc(idClase)
    .collection("actividad")
    .doc(keyIdActividad)
    .collection(tipoVista)
    .doc().id;

  let dataRealtime = {
    nombre: nombreDiapositiva,
    orden: orden,
    acciones: { tipo: tipoAcciones, item: accionesUpload },
  };
  let dataFirestore = { contenidoHTML: contenidoHTMLLocal, miniatura: image };

  let databaseDiapo = firebaseConf
    .database()
    .ref(
      "cursos/" +
        lugarAlmacenamiento +
        "/" +
        uid +
        "/" +
        idCurso +
        "/" +
        idClase +
        "/" +
        keyIdActividad +
        "/" +
        tipoVista +
        "/" +
        idDiapositiva
    );

  firebaseConf
    .firestore()
    .collection("cursos")
    .doc(lugarAlmacenamiento)
    .collection(uid)
    .doc(idCurso)
    .collection("clases")
    .doc(idClase)
    .collection("actividad")
    .doc(keyIdActividad)
    .set({ temp: "temp" });

  let firestoreDiapo = firebaseConf
    .firestore()
    .collection("cursos")
    .doc(lugarAlmacenamiento)
    .collection(uid)
    .doc(idCurso)
    .collection("clases")
    .doc(idClase)
    .collection("actividad")
    .doc(keyIdActividad)
    .collection(tipoVista)
    .doc(idDiapositiva);

  databaseDiapo
    .set(dataRealtime)
    .then(() => {
      stateUpload("Subiendo los datos ingresados (1/2)...");
      firestoreDiapo
        .set(dataFirestore)
        .then(() => {
          stateUpload("Subiendo los datos ingresados (2/2)...");

          callback(true);
        })
        .catch(() => {
          callback(false);
          console.log("error");
        });
    })
    .catch(() => {
      callback(false);
      console.log("error");
    });
};

