export const deleteDiapositiva = (
  uid,
  idCurso,
  idClase,
  keyIdActividad,
  tipoVista,
  lugarAlmacenamiento,
  idDiapositiva
) => {
  let databaseDiapo = firebaseConf
    .database()
    .ref(
      "cursos/" +
        lugarAlmacenamiento +
        "/" +
        uid +
        "/" +
        idCurso +
        "/" +
        idClase +
        "/" +
        keyIdActividad +
        "/" +
        tipoVista +
        "/" +
        idDiapositiva
    );

  let firestoreDiapo = firebaseConf
    .firestore()
    .collection("cursos")
    .doc(lugarAlmacenamiento)
    .collection(uid)
    .doc(idCurso)
    .collection("clases")
    .doc(idClase)
    .collection("actividad")
    .doc(keyIdActividad)
    .collection(tipoVista)
    .doc(idDiapositiva);

  var orden = firebaseConf
    .database()
    .ref(
      "cursos/" +
        lugarAlmacenamiento +
        "/" +
        uid +
        "/" +
        idCurso +
        "/" +
        idClase +
        "/" +
        keyIdActividad +
        "/" +
        tipoVista
    );

  // var arrauUpdate = 1;
  var arrayDiapo = [];

  databaseDiapo
    .remove()
    .then(() => {
      firestoreDiapo
        .delete()
        .then(() => {
          orden.orderByChild("orden").once("value", (diaposNuevas) => {
            diaposNuevas.forEach((diapo) => {
              arrayDiapo.push({ id: diapo.key });
              console.log(diapo.key);
            });
            arrayDiapo.forEach((value, index) => {
              orden
                .child(value.id)
                .update({
                  orden: index,
                })
                .then(() => {
                  if (arrayDiapo.length === index + 1) {
                    window.location.reload();
                  }
                });
            });
          });

          // arrayDiapo.forEach((value, index) => {
          //   orden
          //     .child(value.id)
          //     .update({
          //       orden: index,
          //     })
          //     .then(() => {
          //       if (arrayDiapo.length === index + 1) {
          //         window.location.reload();
          //       }
          //     });
          // });
        })
        .catch(() => {});
    })
    .catch(() => {});
};

