export const updateOrdenDiapositiva = (
  uid,
  tipoCurso,
  idCurso,
  idClase,
  keyIdActividad,
  tipoVista,
  items,
  callback
) => {
  var arrauUpdate = [];

  // eslint-disable-next-line array-callback-return
  items.map((value, index) => {
    let dataRealtime = {
      orden: index,
    };

    let databaseDiapo = firebaseConf
      .database()
      .ref(
        "cursos/" +
          tipoCurso +
          "/" +
          uid +
          "/" +
          idCurso +
          "/" +
          idClase +
          "/" +
          keyIdActividad +
          "/" +
          tipoVista +
          "/" +
          value.key
      );

    databaseDiapo
      .update(dataRealtime)
      .then(() => {
        arrauUpdate.push(true);
      })
      .catch(() => {
        console.log(false);
      })
      .finally(() => {
        // console.log(arrauUpdate.length);

        if (arrauUpdate.length === items.length) {
          callback(true);
        }
      });
  });
};

