import { ChonkyActions, FileHelper, FullFileBrowser } from "../../../components/chonky";
import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from "react";
import { subirFileMap, subirDocumento } from "./Firebase/Pedagogicos";
import { v4 as uuidv4 } from "uuid";
import AddContenido from "./AddContenido"

export const VFSBrowser = React.memo(({ baseFileMap, rootFolderId }) => {
    const [openUpload, setOpenUpload] = useState(false);

    const useCustomFileMap = () => {
        const _baseFileMap = baseFileMap;
        const _rootFolderId = rootFolderId;
        const [fileMap, setFileMap] = useState(_baseFileMap);
        const [currentFolderId, setCurrentFolderId] = useState(_rootFolderId);
        const currentFolderIdRef = useRef(currentFolderId);

        useEffect(() => {
            currentFolderIdRef.current = currentFolderId;
        }, [currentFolderId]);

        const deleteFiles = useCallback((files) => {
            setFileMap((currentFileMap) => {
                const newFileMap = { ...currentFileMap };

                files.forEach((file) => {
                    delete newFileMap[file.id];

                    if (file.parentId) {
                        const parent = newFileMap[file.parentId];
                        const newChildrenIds = parent.childrenIds.filter(
                            (id) => id !== file.id
                        );
                        newFileMap[file.parentId] = {
                            ...parent,
                            childrenIds: newChildrenIds,
                            childrenCount: newChildrenIds.length,
                        };
                    }
                });

                return newFileMap;
            });
        }, []);

        const moveFiles = useCallback((files, source, destination) => {
            setFileMap((currentFileMap) => {
                const newFileMap = { ...currentFileMap };
                const moveFileIds = new Set(files.map((f) => f.id));

                const newSourceChildrenIds = source.childrenIds.filter(
                    (id) => !moveFileIds.has(id)
                );
                newFileMap[source.id] = {
                    ...source,
                    childrenIds: newSourceChildrenIds,
                    childrenCount: newSourceChildrenIds.length,
                };

                let newDestinationChildrenIds = [
                    ...(destination.childrenIds === undefined ? [] : destination.childrenIds),
                    ...files.map((f) => f.id),
                ];

                newFileMap[destination.id] = {
                    ...destination,
                    childrenIds: newDestinationChildrenIds,
                    childrenCount: newDestinationChildrenIds.length,
                };

                files.forEach((file) => {
                    newFileMap[file.id] = {
                        ...file,
                        parentId: destination.id,
                    };
                });

                subirFileMap(newFileMap);

                return newFileMap;
            });
        }, []);

        const createFolder = useCallback((folderName) => {
            setFileMap((currentFileMap) => {
                const newFileMap = { ...currentFileMap };

                const newFolderId = uuidv4();
                newFileMap[newFolderId] = {
                    id: newFolderId,
                    name: folderName,
                    isDir: true,
                    modDate: new Date(),
                    parentId: currentFolderIdRef.current,
                    childrenIds: [],
                    childrenCount: 0,
                };

                const parent = newFileMap[currentFolderIdRef.current];
                newFileMap[currentFolderIdRef.current] = {
                    ...parent,
                    childrenIds: [
                        ...(parent.childrenIds === undefined
                            ? []
                            : parent.childrenIds),
                        newFolderId,
                    ],
                    childrenCount: parent.childrenIds === undefined ? 1 : parent.childrenIds.length + 1
                };

                subirFileMap(newFileMap);

                return newFileMap;
            });
        }, []);

        const uploadFile = useCallback((file, fileImagen) => {

            subirDocumento(file, fileImagen, ({urlDocumento, urlImagen}) => {

                setFileMap((currentFileMap) => {
                    const newFileMap = { ...currentFileMap };
    
                    const fileId = uuidv4();
                    newFileMap[fileId] = {
                        id: fileId,
                        name: file.name,
                        isDir: false,
                        modDate: new Date(),
                        url: urlDocumento,
                        thumbnailUrl: urlImagen,
                        parentId: currentFolderIdRef.current,
                        childrenIds: [],
                        childrenCount: 0
                    };
    
                    const parent = newFileMap[currentFolderIdRef.current];
                    newFileMap[currentFolderIdRef.current] = {
                        ...parent,
                        childrenIds: [
                            ...(parent.childrenIds === undefined
                                ? []
                                : parent.childrenIds),
                            fileId,
                        ],
                        childrenCount: parent.childrenIds === undefined ? 1 : parent.childrenIds.length + 1
                    };
    
                    subirFileMap(newFileMap);
    
                    return newFileMap;
                });

            })

            //console.log(file)
            //console.log(fileImagen)
            //console.log(currentFolderIdRef.current)
            
        }, []);

        return {
            fileMap,
            currentFolderId,
            setCurrentFolderId,
            deleteFiles,
            moveFiles,
            createFolder,
            uploadFile,
        };
    };

    const useFiles = (fileMap, currentFolderId) => {
        return useMemo(() => {
            const currentFolder = fileMap[currentFolderId];
            const childrenIds =
                currentFolder.childrenIds === undefined
                    ? []
                    : currentFolder.childrenIds;
            const files = childrenIds !== null ? childrenIds.map((fileId) => fileMap[fileId]) : []
            return files;
        }, [currentFolderId, fileMap]);
    };

    const useFolderChain = (fileMap, currentFolderId) => {
        return useMemo(() => {
            const currentFolder = fileMap[currentFolderId];

            const folderChain = [currentFolder];

            let parentId = currentFolder.parentId;
            while (parentId) {
                const parentFile = fileMap[parentId];
                if (parentFile) {
                    folderChain.unshift(parentFile);
                    parentId = parentFile.parentId;
                } else {
                    break;
                }
            }

            return folderChain;
        }, [currentFolderId, fileMap]);
    };

    const useFileActionHandler = (
        setCurrentFolderId,
        deleteFiles,
        moveFiles,
        createFolder,
        uploadFile,
        setOpen
    ) => {
        return useCallback(
            (data) => {
                if (data.id === ChonkyActions.OpenFiles.id) {
                    const { targetFile, files } = data.payload;
                    const fileToOpen = targetFile ?? files[0];
                    if (fileToOpen && FileHelper.isDirectory(fileToOpen)) {
                        setCurrentFolderId(fileToOpen.id);
                        return;
                    }

                    files.forEach((file) => {
                        window.open(file.url, "_blank")
                    })

                } else if (data.id === ChonkyActions.DeleteFiles.id) {
                    deleteFiles(data.state.selectedFilesForAction);
                } else if (data.id === ChonkyActions.MoveFiles.id) {
                    moveFiles(
                        data.payload.files,
                        data.payload.source,
                        data.payload.destination
                    );
                } else if (data.id === ChonkyActions.CreateFolder.id) {
                    const folderName = prompt("Nombre de carpeta:");
                    if (folderName) createFolder(folderName);
                } else if (data.id === ChonkyActions.UploadFiles.id) {
                    setOpenUpload(true);
                }
            },
            [createFolder, deleteFiles, moveFiles, setCurrentFolderId]
        );
    };

    const {
        fileMap,
        currentFolderId,
        setCurrentFolderId,
        deleteFiles,
        moveFiles,
        createFolder,
        uploadFile,
    } = useCustomFileMap();

    const files = useFiles(fileMap, currentFolderId);
    const folderChain = useFolderChain(fileMap, currentFolderId);

    const handleFileAction = useFileActionHandler(
        setCurrentFolderId,
        deleteFiles,
        moveFiles,
        createFolder,
        uploadFile,
        setOpenUpload
    );

    const fileActions = useMemo(
        () => [
            ChonkyActions.CreateFolder,
            ChonkyActions.DeleteFiles,
            ChonkyActions.UploadFiles
        ],
        []
    );

    return (
        <div style={{ height: 600 }}>
            <FullFileBrowser
                files={files}
                folderChain={folderChain}
                fileActions={fileActions}
                onFileAction={handleFileAction}
            />
            <AddContenido open={openUpload} setOpen={setOpenUpload} uploadFile={uploadFile} />
        </div>
    );
});
