import { Backdrop, CircularProgress } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import Notfound from "../../Page404/Notfound";
import {
  getDataActividadxCodigo,
  isActividadOnUser,
} from "../BDActividadesOnline";
import ContenidoActividadOnline from "./ContenidoActividadOnline";
import firebaseConf from "../../../database/firebase";

function MainVistaUnitariaActOn() {
  const [isOwner, setisOwner] = useState(false);
  const [isLoader, setisLoader] = useState(true);
  let { codigoAct } = useParams();

  const [data, setdata] = useState({});

  useEffect(() => {
    firebaseConf.auth().onAuthStateChanged((user) => {
      isActividadOnUser(user.uid, codigoAct, (resp) => {
        setTimeout(() => {
          setisOwner(resp);
          if (resp) {
            getDataActividadxCodigo(codigoAct, (resp) => {
              setdata(resp);
              setisLoader(false);
            });
          } else {
            setisLoader(false);
          }
        }, 300);
      });
    });
  }, [codigoAct]);

  return (
    <div className="main-container">
      {!isLoader ? (
        <div className="contenido">
          {isOwner ? (
            <div>
              <ContenidoActividadOnline
                actividad={data}
              ></ContenidoActividadOnline>
            </div>
          ) : (
            <Notfound></Notfound>
          )}
        </div>
      ) : (
        <div className="contenido">
          <Backdrop
            sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={isLoader}
          >
            <CircularProgress color="inherit" />
          </Backdrop>
        </div>
      )}
    </div>
  );
}

export default MainVistaUnitariaActOn;
