import {
  Button,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  TextField,
} from "@mui/material";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";
import { IconButton } from "@mui/material";

export const dataValueCheckbox = () => {
  return [false, false, false, false, false];
};
export const dataValueButton = () => {
  return [true, false, false, false, false];
};
export const dataValueLista = () => {
  return [false, true, false, false, false];
};
export const dataValueSinAccion = () => {
  return [false, false, true, false, false];
};
export const dataValueTextArea = () => {
  return [false, false, false, true, false];
};
export const dataValueUploadPhoto = () => {
  return [false, false, false, false, true];
};

export const renderCheckBoxList = (listCheckBox) => {
  return (
    <div
      style={{
        textAlign: "start",
      }}
    >
      <FormControl>
        <FormGroup>
          {listCheckBox.map((value) => {
            let name = "Item " + value;
            return <FormControlLabel control={<Checkbox />} label={name} />;
          })}
        </FormGroup>
      </FormControl>
    </div>
  );
};
export const renderButtonList = (listBotones) => {
  return (
    <div>
      {listBotones.map((value) => {
        return (
          <Button
            key={value}
            style={{
              backgroundColor: "green",
              color: "white",
              marginLeft: 10,
            }}
          >
            Botón acción {value}
          </Button>
        );
      })}
    </div>
  );
};

export const renderTextArea = () => {
  return (
    <div
      style={{
        textAlign: "center",
      }}
    >
      <TextField
        id="outlined-multiline-flexible"
        multiline
        style={{
          width: "80%",
        }}
        rows={7}
      />
    </div>
  );
};

export const renderUploadPhoto = () => {
  return (
    <div
      style={{
        textAlign: "center",
      }}
    >
      <IconButton aria-label="delete" size="large">
        <AddPhotoAlternateIcon sx={{ fontSize: 60 }} />
        <p style={{ fontSize: 20 }}>Agregar una fotografía</p>
      </IconButton>{" "}
    </div>
  );
};

export const editorDiseñoDiapositiva = (position, e) => {
  var editor = [];
  for (let index = 0; index < 5; index++) {
    if (position === index) {
      editor[index] = e;
    } else {
      editor[index] = false;
    }
  }
  return editor;
};

export const renderContenidoAccionesButton = (accionesUpload) => {
  return (
    
    <div>
      {accionesUpload.map((value, index) => {
        return (
          <Button
            style={{
              backgroundColor: "green",
              color: "white",
              marginLeft: 10,
            }}
            key={index}
            
          >
            {value.name}
          </Button>
        );
      })}
    </div>
  );
};

export const renderContenidoAccionesCheckBox = (accionesUpload) => {
  return (
    <div>
      <FormGroup
        style={{
          display: "flex",
          flexDirection: "row",
        }}
      >
        {accionesUpload.map((value, index) => {
          return (
            <FormControlLabel
              control={<Checkbox />}
              label={value.name}
              key={index}
            />
          );
        })}
      </FormGroup>
    </div>
  );
};
