import React, { useEffect, useMemo, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import EditorAccionesDiapositiva from "./Componentes/EditorAccionesDiapositiva";
import EditorHTMLDiapositiva from "./Componentes/EditorHTMLDiapositiva";
import EditorVistaPrevia from "./Componentes/EditorVistaPrevia";

function EditarDiapositiva({
  contenidoHTML,
  acciones,
  uid,
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  nombreDiapositiva,
  orden,
  lugarAlmacenamiento,
  idDiapositiva,
}) {
  const [secciones, setsecciones] = useState("EditordeTexto");

  const [ContenidoHTMLEditado, setContenidoHTMLEditado] =
    useState(contenidoHTML);
  const [accionesEditada, setAccionesEditadas] = useState(acciones);

  useEffect(() => {
    setContenidoHTMLEditado(contenidoHTML);
    setAccionesEditadas(acciones);
  }, [contenidoHTML, acciones]);

  const VariablesEdicion = useMemo(
    () => ({
      setContenidoHTMLEditado,
      setAccionesEditadas,
      setsecciones,
      ContenidoHTMLEditado,
      accionesEditada,
    }),
    [
      setsecciones,
      setContenidoHTMLEditado,
      setAccionesEditadas,
      ContenidoHTMLEditado,
      accionesEditada,
    ]
  );

  return (
    <div>
      <UserContext.Provider value={VariablesEdicion}>
        {secciones === "EditordeTexto" && (
          <EditorHTMLDiapositiva></EditorHTMLDiapositiva>
        )}
        {secciones === "Acciones" && (
          <div>
            <EditorAccionesDiapositiva
              idClase={idClase}
              idCurso={idCurso}
              uid={uid}
            ></EditorAccionesDiapositiva>
          </div>
        )}
        {secciones === "VistaPrevia" && (
          <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'center'}}>
            <EditorVistaPrevia
              uid={uid}
              idCurso={idCurso}
              idClase={idClase}
              keyActividad={keyActividad}
              tipoVista={tipoVista}
              idDiapositiva={idDiapositiva}
              nombreDiapositiva={nombreDiapositiva}
              tipoAcciones={acciones.tipo}
              orden={orden}
              lugarAlmacenamiento={lugarAlmacenamiento}
            ></EditorVistaPrevia>
          </div>
        )}
      </UserContext.Provider>
    </div>
  );
}

export default EditarDiapositiva;
