import  {
  getDiapositivas,
} from "../../Diapositivas/FirebaseDiapositivas";
import firebaseConf from "../../../database/firebase";

export const getActividadesGeneral = (
  uid,
  idCurso,
  idClase,
  lugarAlamacenamiento,
  callback
) => {
  var database = firebaseConf.database();
  var arrayValue = [];
  database
    .ref(
      "/cursos/" +
        lugarAlamacenamiento +
        "/" +
        uid +
        "/" +
        idCurso +
        "/" +
        idClase +
        "/lista_actividad"
    )
    .orderByChild("fechaCreacion")
    .once("value", (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        var childKey = childSnapshot.key;
        var childData = childSnapshot.val();
        arrayValue.push({ data: childData, key: childKey });
      });
    })
    .then(() => {
      callback(arrayValue);
    })
    .catch(() => {
      callback(false);
    });
};

export const generarEsquema = (
  uid,
  idCurso,
  idClase,
  idActividad,
  lugarAlamacenamiento,
  nombreCurso,
  nombreClase,
  nombreActividad,
  result
) => {
  var dataJson = [["Name", "Manager", "ToolTip"]];

  dataJson.push([
    {
      v: "Curso",
      f:
        "<div><p>" +
        nombreCurso +
        '</p><div style="color:red; font-style:italic">CURSO</div></div>',
    },
    "",
    "Curso: " + nombreCurso,
  ]);

  dataJson.push([
    {
      v: "Clase",
      f:
        "<div><p>" +
        nombreClase +
        '</p><div style="color:red; font-style:italic">CLASE</div></div>',
    },
    "Curso",
    "VP",
  ]);
  dataJson.push([
    {
      v: "Actividad",
      f:
        "<div><p>" +
        nombreActividad +
        '</p><div style="color:red; font-style:italic">Actividad</div></div>',
    },
    "Clase",
    "Actividad -" + nombreActividad,
  ]);

  getDiapositivas(
    uid,
    idCurso,
    idClase,
    idActividad,
    "profesor",
    lugarAlamacenamiento,
    (callback) => {
      callback.forEach((data) => {
        dataJson.push([
          {
            v: data.key,
            f:
              "<div><p>" +
              data.datos.nombre +
              '</p><div style="min-height: 120;max-height: 120;min-width: 210;max-width: 210px;border-radius: 25px;background-color: white;border-color: black;border-width: 2px;border-style: solid;"><img alt="miniatura diapositiva" style="width: 150px;height: 100px; padding: 0px; border-radius: 25px" src="' +
              data.miniatura +
              '"></img></div></div>',
          },
          "Actividad",
          data.datos.nombre,
        ]);
        if (data.datos.acciones.tipo === "Button") {

          data.datos.acciones.item.forEach((item, index) => {

            dataJson.push([
              {
                v: data.key + "_Button_" + index,
                f: "<div><p>" + item.name + "</p></div>",
              },
              data.key,
              "Acción: " + item.name,
            ]);

            dataJson.push([
              {
                v: "Actividad_" + item.href.keyActividad + data.key,
                f:
                  "<div><p>" +
                  item.href.nombreActividad +
                  '</p><div style="color:red; font-style:italic">Actividad</div></div>',
              },
              data.key + "_Button_" + index,
              "Actividad -" + nombreActividad,
            ]);

            dataJson.push([
              {
                v:
                  data.key +
                  "_Button" +
                  index +
                  "_" +
                  item.href.keyDiapositiva,
                f:
                  "<div><p>" +
                  item.href.nombreDiapositiva +
                  '</p><div style="min-height: 120;max-height: 120;min-width: 210;max-width: 210px;border-radius: 25px;background-color: white;border-color: black;border-width: 2px;border-style: solid;"><img alt="miniatura diapositiva" style="width: 150px;height: 100px; padding: 0px; border-radius: 25px" src="' +
                  "https://leadsfac.com/wp-content/uploads/2019/10/Lorem-Ipsum-alternatives.png" +
                  '"></img></div>' +
                  '<br><div style="color:red; font-style:italic">Vista ' +
                  String(item.href.tipoVista).toUpperCase() +
                  "</div></div>",
              },
              "Actividad_" + item.href.keyActividad + data.key,
              "Diapositiva: " + item.href.nombreDiapositiva,
            ]);

          });
        }
      });

      result(dataJson);
    }
  );
};
