import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  Typography,
} from "@mui/material";
import React, { useContext, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import VistaDiapositiva from "../VistaDiapositiva";

function ItemDisenoEnLista({ value, index }) {
  const { setbanderaDiapositivaDiseno, setContenidoHTML, setListadoAcciones } =
    useContext(UserContext);
  const [open, setOpen] = useState(false);

  const style = {
    minHeight: "280px",
    maxHeight: "280px",
    minWidth: "450px",
    maxWidth: "450px",
    borderRadius: 25,
    backgroundColor: "white",
    marginBottom: "10px ",
    display: "flex",
    flexDirection: "column",
    marginLeft: 10,
  };

  return (
    <div>
      <Grid key={index} item>
        <Button
          style={style}
          onClick={() => {
            setOpen(true);
          }}
          title={"Diapositiva " + value.datos.nombre}
        >
          <img
            alt="miniatura diapositiva"
            style={{
              width: "450px",
              height: "280px",
            }}
            src={value.miniatura}
          ></img>
        </Button>
        <h3
          style={{
            textAlign: "center",
            marginLeft: 0,
          }}
        >
          {value.datos.nombre}
        </h3>
      </Grid>
      <Dialog
        open={open}
        onClose={(e) => {
          setOpen(false);
        }}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        maxWidth={"lg"}
      >
        <DialogTitle>
          <Typography
            variant="tituloAlert"
            // style={{
            //   marginBottom: "30px",
            //   marginLeft: 30,
            // }}
          >
            {value.datos.nombre}
          </Typography>
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <VistaDiapositiva
              valorHtml={value.contenidoHTML}
              acciones={value.datos.accion}
            ></VistaDiapositiva>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            variant="outlined"
            onClick={(e) => {
              setOpen(false);
            }}
          >
            Cerrar
          </Button>
          <Button
            variant="contained"
            onClick={(e) => {
              setContenidoHTML(value.contenidoHTML);
              setListadoAcciones(value.datos.accion);
              setbanderaDiapositivaDiseno(false);
            }}
            autoFocus
          >
            Utilizar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ItemDisenoEnLista;
