import {
    Modal,

} from "@mui/material";
import React, { useCallback, useContext, useMemo, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import PublishIcon from "@mui/icons-material/Publish";
import DoNotDisturbOnIcon from "@mui/icons-material/DoNotDisturbOn";
import {
    actualizarEstado,
} from "./firebaseCursosPublicos";
import { CustomIconButton } from "../../../styles/ArgumentappTheme";
import AddIcon from "@mui/icons-material/Add";
import BoxCrearClase from "../Clases/BoxCrearClase";

function CrearClasesPublicas({ idCurso, estado }) {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleClose = useCallback(() => {
    setOpen(false);
  });



  const { setestadoCurso } = useContext(UserContext);

  const providerValue = useMemo(
    () => ({
      handleClose,
    }),
    [handleClose]
  );

  return (
    <div>
      <div
        style={{
          backgroundColor: "transparent",
        }}
      >
        {!estado && (
          <CustomIconButton
            onClick={handleOpen}
            style={{
              marginTop: 30,
              marginBottom: 20,
            }}
            title={"Agregar Clase"}
          >
            <AddIcon style={{ color: "#fff" }} />
          </CustomIconButton>
        )}

        <CustomIconButton
          title={
            !estado
              ? "Publicar Unidad Argumentapp"
              : "Despublicar Unidad Argumentapp"
          }
          onClick={(e) => {
            actualizarEstado(idCurso, !estado, (data) => {
              if (data.resultado === "Correcto") {
                setestadoCurso(data.estadoCurso);
              } else {
                setestadoCurso(data.estadoCurso);
              }
            });
          }}
          style={{
            marginTop: 30,
            marginBottom: 20,
          }}
        >
          {!estado ? <PublishIcon /> : <DoNotDisturbOnIcon />}
        </CustomIconButton>
      </div>
      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <UserContext.Provider value={providerValue}>
            <BoxCrearClase tipoCurso={"publicos"}></BoxCrearClase>
        </UserContext.Provider>
      </Modal>
    </div>
  );
}

export default CrearClasesPublicas;
