import {
  Alert,
  Box,
  Button,
  IconButton,
  TextareaAutosize,
  Typography,
} from "@mui/material";
import React, { useContext, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import CheckIcon from "@mui/icons-material/Check";
import { useHistory } from "react-router-dom";
import CloseIcon from "@mui/icons-material/Close";
import { CustomInput2 } from "../../../styles/ArgumentappTheme";
import BoxCrearActividad from "../../VistaUnitariaClase/BoxCrearActividad";

function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function BoxCrearClase({ tipoCurso }) {
  const [selectorBox, setSelectorBox] = useState("clases");
  const [selectorView, setselectorView] = useState("step1");
  const [nombreClase, setNombreClase] = useState("");
  const [duracion, setDuracion] = useState("");
  const [objetivo, setObjetivo] = useState("");
  const [banderaAlerta, setbanderaAlerta] = useState({
    state: false,
    msg: "",
  });

  const { handleClose } = useContext(UserContext);
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "auto",
    boxShadow: 24,
    p: 4,
    height: "auto",
    minWidth: "fit-content",
    backgroundColor: "#fff",
    borderRadius: "18px",
  };

  const history = useHistory();

  const isOkStep = () => {
    if (selectorView === "step1") {
      if (nombreClase !== "" && duracion !== "" && objetivo !== "") {
        setbanderaAlerta({
          state: false,
          msg: "",
        });

        //Subir datos

        setselectorView("finish");
      } else {
        setbanderaAlerta({
          state: true,
          msg: "Todos los campos son obligatorios para crear una clase. Vuelva a intentarlo.",
        });
        setTimeout(() => {
          setbanderaAlerta({
            state: false,
            msg: "",
          });
        }, 5000);
      }
    } else {
      if (selectorView === "finish") {
        setSelectorBox("actividades");
      }
    }
  };

  const resetValuesModal = () => {
    setSelectorBox("clases");
    setselectorView("step1");
    setNombreClase("");
    setDuracion("");
    setObjetivo("");
    setbanderaAlerta({
      state: false,
      msg: "",
    });
  };

  return (
    <div>
      {selectorBox === "clases" && (
        <Box sx={style}>
          {selectorView !== "finish" && (
            <div
              style={{
                marginBottom: "10px",
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                alignContent: "center",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <div></div>
              <Typography id="modal-modal-title" variant="titulo_4">
                Agregar una Clase
              </Typography>

              <IconButton
                onClick={(e) => {
                  resetValuesModal();
                  handleClose();
                }}
                aria-label="delete"
              >
                <CloseIcon />
              </IconButton>
            </div>
          )}

          {banderaAlerta.state === false ? (
            <div style={{ height: selectorView === "step1" ? 28 : 0 }}></div>
          ) : (
            <Alert style={{ marginBottom: 5, width: "469px" }} severity="error">
              {banderaAlerta.msg}
            </Alert>
          )}
          {selectorView === "step1" && (
            <div>
              <div style={{ display: "flex", flexDirection: "row" }}>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "space-between",
                    height: 350,
                  }}
                >
                  <div style={{ display: "flex", flexDirection: "column" }}>
                    <Typography variant="textoForm">
                      Nombre de la Clase*
                    </Typography>
                    <CustomInput2
                      required
                      value={nombreClase}
                      onChange={(e) => {
                        setNombreClase(e.target.value);
                      }}
                    />
                  </div>
                  <div style={{ display: "flex", flexDirection: "column" }}>
                    <Typography variant="textoForm">
                      Duración (módulos)*
                    </Typography>
                    <CustomInput2
                      required
                      value={duracion}
                      onChange={(e) => {
                        setDuracion(e.target.value);
                      }}
                      style={{
                        backgroundColor: "white",
                        minWidth: "300px",
                      }}
                    />
                  </div>
                  <div style={{ display: "flex", flexDirection: "column" }}>
                    <Typography variant="textoForm">Objetivo*</Typography>

                    <TextareaAutosize
                      style={{
                        width: 469,
                        height: 160,
                        resize: "none",
                        overflow: "auto",
                        overflowY: "auto",
                        borderRadius: 10,
                        color: "#9c5fa2",
                        padding: 10,
                        fontWeight: 600,
                        fontSize: 14,
                      }}
                      maxLength={400}
                      onChange={(e) => {
                        setObjetivo(e.target.value);
                      }}
                    />
                  </div>
                </div>
              </div>
              <RedBar></RedBar>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "center",
                  padding: 25,
                }}
              >
                <Button
                  variant="contained"
                  // onClick={(e) => subirDatos()}
                  onClick={(e) => isOkStep()}
                >
                  Crear clase
                </Button>
              </div>
            </div>
          )}

          {selectorView === "finish" && (
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "space-between",
                alignItems: "center",
                height: 300,
                width: 400,
                padding: 25,
              }}
            >
              <div>
                <CheckIcon
                  fontSize="large"
                  style={{
                    color: "#9c5fa2",
                    borderRadius: 50,
                    borderStyle: "solid",
                    borderWidth: 1,
                    height: 60,
                    width: 60,
                    padding: 10,
                  }}
                ></CheckIcon>
              </div>
              <div>
                <Typography variant="textoClase">
                  Clase fue creada correctamente
                </Typography>
              </div>
              <div style={{ width: "100%" }}>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-around",
                  }}
                >
                  <Button
                    variant="outlined"
                    style={{ width: 150 }}
                    onClick={(e) => {
                      history.go(0);
                    }}
                  >
                    Ir a la unidad
                  </Button>
                  <Button
                    onClick={(e) => {
                      setSelectorBox("actividades");
                    }}
                    variant="contained"
                    style={{ width: 150 }}
                  >
                    Crear actividad
                  </Button>
                </div>
              </div>
              <div style={{ textAlign: "center" }}>
                <Typography variant="infoTextoRosa">
                  * Si vas a la unidad y seleccionas la clase, vas a poder crear
                  actividades en el mismo panel.
                </Typography>
              </div>
            </div>
          )}
        </Box>
      )}
      {selectorBox === "actividades" && (
        <BoxCrearActividad tipoCurso={tipoCurso}></BoxCrearActividad>
      )}
    </div>
  );
}

export default BoxCrearClase;
