/* eslint-disable no-unused-vars */
import React, { useEffect, useMemo, useState } from "react";
import ContenidoMisCursos from "./ContenidoMisCursos";

import { UserContext } from "../Funcionalidades/UseContext";
import ListarClases from "./Clases/ListarClases";

import { Typography } from "@mui/material";
import MainVistaPlanificacionesCarpetas from "./MainVistaPlanificacionesCarpetas";

function MainCursos(props) {
  const [banderaClases, setBanderaClases] = useState(false);
  const [tipoSalida, setTipoSalida] = useState("right");
  const [idCursoVer, setIdCursoVer] = useState("");
  const [nombreCursoVer, setNombreCursoVer] = useState("");
  const [banderaMainPlanificaciones, setBanderaMainPlanificaciones] =
    useState(true);

  const [tipoCursos, settipoCursos] = useState("");
  const [estadoCurso, setEstadoCurso] = useState("");

  const [indexUnidad, setindexUnidad] = useState(0);

  const providerValue = useMemo(
    () => ({
      banderaClases,
      setBanderaClases,
      idCursoVer,
      setIdCursoVer,
      setNombreCursoVer,
      settipoCursos,
      setEstadoCurso,
      setindexUnidad,
      setTipoSalida,
      tipoCursos,
      estadoCurso,
      setBanderaMainPlanificaciones,
      banderaMainPlanificaciones,
    }),
    [
      banderaClases,
      setBanderaClases,
      idCursoVer,
      setIdCursoVer,
      setNombreCursoVer,
      settipoCursos,
      setEstadoCurso,
      setindexUnidad,
      setTipoSalida,
      tipoCursos,
      estadoCurso,
      setBanderaMainPlanificaciones,
      banderaMainPlanificaciones,
    ]
  );

  useEffect(() => {
    try {
      if (props.location.state.isBack === true) {
        setIdCursoVer(props.location.state.idCurso);
        setNombreCursoVer(props.location.state.nombreCurso);
        settipoCursos(props.location.state.tipoCurso);
        setEstadoCurso(props.location.state.estado);
        setTipoSalida(props.location.state.tipoSlide);
        setindexUnidad(props.location.state.indexUnidad);
        setBanderaClases(true);
      } else {
      }
    } catch (error) {
      // window.location = "/";
    }
    // eslint-disable-next-line
  }, []);

  return (
    <UserContext.Provider value={providerValue}>
      <div className="main-container">
        <div
          style={{
            width: "100%",
            overflowX: "hidden",
          }}
        >
          <Typography variant="titulo">Mis planificaciones</Typography>

          <div className="contenido">
            {banderaMainPlanificaciones ? (
              <MainVistaPlanificacionesCarpetas></MainVistaPlanificacionesCarpetas>
            ) : (
              <div>
                {!banderaClases ? (
                  <ContenidoMisCursos></ContenidoMisCursos>
                ) : (
                  <ListarClases
                    id={idCursoVer}
                    nombreCurso={nombreCursoVer}
                    indexUnidad={indexUnidad}
                    tipoSlide={tipoSalida}
                  ></ListarClases>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </UserContext.Provider>
  );
}

export default MainCursos;
