import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Modal,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext, useEffect } from "react";
import { UserContext } from "../../../Funcionalidades/UseContext";
import EditarDiapositiva from "../EditarDiapositiva";

function BotonEditarDiapositiva({
  nombreDiapositiva,
  contenidoHTML,
  acciones,
  uid,
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  orden,
  idDiapositiva,
  state,
}) {
  const { banderaDialogEdit, setBanderaDialogEdit } = useContext(UserContext);
const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "70%",
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  maxHeight: "90vh",
  overflow: "auto",
  borderRadius: 10,
};



  return (
    <div>
      {state && (
        <Button
          variant="contained"
          style={{
            textAlign: "center",
            margin: "5px",
            width: "-webkit-fill-available",
            borderStyle: "solid",
            borderWidth: 1,
          }}
          onClick={(e) => {
            setBanderaDialogEdit(true);
          }}
        >
          <i
            style={{
              fontSize: "30px",
              backgroundColor: "white",
            }}
            className="fas fa-pen-square"
          ></i>
          Editar Diapositiva
        </Button>
      )}
      <Modal
        open={banderaDialogEdit}
        onClose={(e) => {
          setBanderaDialogEdit(false);
        }}
        style={{
          overflow: "hidden",
        }}
      >
        <Box sx={style}>
          <Typography
            variant="titulo_2"
            style={{ marginBottom: 20 }}
            component="h2"
          >
            Editar diapositiva {nombreDiapositiva}
          </Typography>
          <EditarDiapositiva
            contenidoHTML={contenidoHTML}
            acciones={acciones}
            uid={uid}
            idCurso={idCurso}
            idClase={idClase}
            keyActividad={keyActividad}
            tipoVista={tipoVista}
            nombreDiapositiva={nombreDiapositiva}
            orden={orden}
            lugarAlmacenamiento={"privados"}
            idDiapositiva={idDiapositiva}
          ></EditarDiapositiva>
        </Box>
      </Modal>
    </div>
  );
}

export default BotonEditarDiapositiva;
