import { Button, Paper, Tab, Tabs, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useMemo, useState } from "react";
import Unidades from "./Unidades";
import ListarMaterial from "./ListarMaterial";
import { UserContext } from "../../Funcionalidades/UseContext";
import ListarClases from "./ListarClases";

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 0 }} style={{ marginTop: 15 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        "aria-controls": `simple-tabpanel-${index}`,
    };
}

function MainContenidoArgumentapp(props) {
    const [banderaClases, setBanderaClases] = useState(false);
    const [banderaMaterial, setBanderaMaterial] = useState(false);
    const [idCursoVer, setIdCursoVer] = useState("");
    const [idClaseVer, setIdClaseVer] = useState("");
    const [nombreCursoVer, setNombreCursoVer] = useState("");
    const [nombreClaseVer, setNombreClaseVer] = useState("hh");
    const [tipoSalida, setTipoSalida] = useState("right");
    const [tipoCursos, setTipoCursos] = useState("");
    const [estadoCurso, setEstadoCurso] = useState("");
    const [indexUnidad, setindexUnidad] = useState(0);

    useEffect(() => {
        try {
            if (props.location.state.isBack === true) {
                setIdCursoVer(props.location.state.idCurso);
                setNombreCursoVer(props.location.state.nombreCurso);
                setTipoCursos(props.location.state.tipoCurso);
                setEstadoCurso(props.location.state.estado);
                setTipoSalida(props.location.state.tipoSlide);
                setindexUnidad(props.location.state.indexUnidad);
                setBanderaClases(true);
            } else {
            }
        } catch (error) {
            // window.location = "/";
        }
        // eslint-disable-next-line
    }, []);

    const providerValue = useMemo(
        () => ({
            banderaClases,
            setBanderaClases,
            banderaMaterial,
            setBanderaMaterial,
            idCursoVer,
            setIdCursoVer,
            idClaseVer,
            setIdClaseVer,
            setNombreCursoVer,
            setNombreClaseVer, 
            nombreClaseVer,           
            setTipoCursos,
            setEstadoCurso,
            setindexUnidad,
            setTipoSalida,
            tipoCursos,
            estadoCurso,
        }),
        [
            banderaClases,
            setBanderaClases,
            banderaMaterial,
            setBanderaMaterial,
            idCursoVer,
            setIdCursoVer,
            idClaseVer,
            setIdClaseVer,
            setNombreCursoVer,
            setNombreClaseVer,
            nombreClaseVer,
            setTipoCursos,
            setEstadoCurso,
            setindexUnidad,
            setTipoSalida,
            tipoCursos,
            estadoCurso,
        ]
    );
    return (
        <UserContext.Provider value={providerValue}>
            <div className="main-container">
                <div
                    style={{
                        width: "100%",
                        overflowX: "hidden",
                    }}
                >
                    <Typography variant="titulo">
                        Material Argumentapp
                    </Typography>

                    <div className="contenido">
                        {!banderaClases && !banderaMaterial && (
                            <div>
                                <div>
                                    <Unidades />
                                </div>
                            </div>
                        )}

                        {banderaClases && (
                            <ListarClases
                                id={idCursoVer}
                                nombreCurso={nombreCursoVer}
                                estado={estadoCurso}
                                indexUnidad={indexUnidad}
                                tipoSlide={tipoSalida}                               
                            />
                        )}

                        {banderaMaterial && (
                            <ListarMaterial
                                id={idClaseVer}
                                nombreClase={nombreClaseVer}
                                tipoSlide={tipoSalida}
                            />
                        )}
                    </div>
                </div>
            </div>
        </UserContext.Provider>
    );
}

export default MainContenidoArgumentapp;
