import {
  Alert,
  Paper,
  Typography,
} from "@mui/material";
import { Box } from "@mui/material";
import React, { useEffect, useState } from "react";
import backImage from "../../assets/images/login_background.png";
import { getPaneles } from "./FirebasePanelAdmin";
import ItemModuloAdmin from "./ItemModuloAdmin";

function MainModuloAdminGeneral() {
  const style = {
    padding: 10,
    textAlign: "center",
    backgroundColor: "transparent",
    boxShadow:'none',
  };
  const [paneles, setPaneles] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(true);

  useEffect(() => {
    getPaneles((data) => {
      setPaneles(data);
      setbanderaCarga(false);
    });
  }, []);

  return (
    <div className="main-container">
      <div
        style={{
          width: "100%",
        }}
      >
        <Typography variant="titulo">
          <b>Modulo administrativo</b>
        </Typography>
        <div
          style={{
            marginTop: 50,
            marginBottom: 10,
          }}
        >
          {!banderaCarga ? (
            <Box display="grid" gridTemplateColumns="repeat(12, 1fr)" gap={2}>
              {paneles.map((value) => {
                return (
                  <Box key={value.key} gridColumn={value.data.gridColumn}>
                    <Paper style={style}>
                      <ItemModuloAdmin
                        description={value.data.description}
                        href={value.data.href}
                        image={value.data.image}
                        image_alt={value.data.image_alt}
                        title={value.data.title}
                      ></ItemModuloAdmin>
                    </Paper>
                  </Box>
                );
              })}
            </Box>
          ) : (
            <div></div>
          )}
          <div></div>
        </div>
      </div>
    </div>
  );
}

export default MainModuloAdminGeneral;
