import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import { UserContext } from "../../Funcionalidades/UseContext";
import CrearDiseñoDiapositiva from "./CrearDiseñoDiapositiva";
import CreateIcon from "@mui/icons-material/Create";
import {
  delDisenoPlantilla,
  getDisenosDiapositivas,
} from "./FirebaseDiseñoDiapositivas";
import { getColor } from "../../../styles/ArgumentappTheme";

function ListarDiseños({ tipoVista, keyVista }) {
  const [disenos, setdisenos] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(true);
  const [isBorrar, setIsBorrar] = useState(false);

  useEffect(() => {
    if (disenos.length === 0) {
      getDisenosDiapositivas(keyVista, (data) => {
        setdisenos(data);
        setbanderaCarga(false);
      });
    } // eslint-disable-next-line
  }, []);

  let alto = "230px ";
  let ancho = "330px";

  const {
    setvalorTemporalHTML,
    setbanderaVistaPrevia,
    setAcciones,
    setnombrePlantilla,
    setBanderaEditarDiseno,
    setTipoVista
  } = useContext(UserContext);

  return (
    <div>
      {!banderaCarga ? (
        <Grid container justifyContent="start" spacing={6}>
          {disenos.map((value, index) => {
            return (
              <Grid key={index} item>
                <div>
                  <Button
                    style={{
                      minHeight: alto,
                      maxHeight: alto,
                      minWidth: ancho,
                      maxWidth: ancho,
                      marginBottom: "10px ",
                      display: "flex",
                      flexDirection: "column",
                      backgroundImage: `url(${value.miniatura})`,
                      backgroundSize: "100%",
                      backgroundRepeat: "no-repeat",
                      borderStyle: "solid",
                      borderWidth: "2px",
                      borderColor: "black",
                      borderRadius: 25,
                      backgroundPosition: "center",
                      backgroundOrigin: "content-box",
                    }}
                    onClick={(e) => {
                      setvalorTemporalHTML(value.contenidoHTML);
                      setAcciones(value.datos.accion);
                      setnombrePlantilla(value.datos.nombre);
                      setbanderaVistaPrevia(true);
                    }}
                  ></Button>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      flexWrap: "nowrap",
                      alignContent: "center",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <h4
                      style={{
                        color: "black",
                        textAlign: "center",
                        marginLeft: 0,
                      }}
                    >
                      {value.datos.nombre}
                    </h4>
                    <Button
                      style={{
                        backgroundColor: "white",
                        marginLeft: "10px",
                        minWidth: 30,
                        minHeight: 30,
                        maxHeight: 30,
                        maxWidth: 30,
                        textAlign: "center",
                        borderColor: getColor().mainColor,
                        borderWidth: 1,
                        borderStyle: 'solid'
                      }}
                      onClick={(e) => {
                        setvalorTemporalHTML(value.contenidoHTML);
                        setAcciones(value.datos.accion);
                        setnombrePlantilla(value.datos.nombre);
                        setbanderaVistaPrevia(false);
                        setBanderaEditarDiseno(true);
                        setTipoVista(keyVista);
                      }}
                    >
                      <CreateIcon fontSize="30"></CreateIcon>
                    </Button>
                    <Button
                      onClick={() => {
                        setIsBorrar(true);
                      }}
                      style={{
                        backgroundColor: "red",
                        color: "white",
                        marginLeft: "10px",
                        minWidth: "30px",
                        maxWidth: "30px",
                        minHeight: "30px",
                        maxHeight: "30px",
                      }}
                    >
                      X
                    </Button>

                    <Dialog
                      open={isBorrar}
                      onClose={(e) => setIsBorrar(false)}
                      aria-labelledby="alert-dialog-title"
                      aria-describedby="alert-dialog-description"
                    >
                      <DialogTitle id="alert-dialog-title">
                        {"¿Eliminar el diseño de diapositiva?"}
                      </DialogTitle>
                      <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                          Estás seguro que quieres borrar el diseño{" "}
                          {value.datos.nombre} en el perfil de {keyVista}
                        </DialogContentText>
                      </DialogContent>
                      <DialogActions>
                        <Button onClick={(e) => setIsBorrar(false)}>
                          Cancelar
                        </Button>
                        <Button
                          onClick={(e) =>
                            delDisenoPlantilla(keyVista, value.key)
                          }
                          autoFocus
                        >
                          Borrar
                        </Button>
                      </DialogActions>
                    </Dialog>
                  </div>
                </div>
              </Grid>
            );
          })}

          <Grid
            key={"add"}
            style={{
              display: "flex",
              flexDirection: "column",
              flexWrap: "nowrap",
              justifyContent: "center",
              alignContent: "center",
              alignItems: "center",
              minHeight: alto,
              minWidth: ancho,
            }}
            item
          >
            <CrearDiseñoDiapositiva
              tipoVista={keyVista}
            ></CrearDiseñoDiapositiva>
          </Grid>
        </Grid>
      ) : (
        //ADD LOADER DE ESPERA
        <div>
          <p>cargando...</p>
        </div>
      )}
    </div>
  );
}

export default ListarDiseños;
