import { Box, Button, Modal,  Typography } from "@mui/material";
import React, { useState } from "react";
import FormControl from "@mui/material/FormControl";
import CheckIcon from "@mui/icons-material/Check";

function RedBar() {
    return <Box sx={{ height: 20 }} />;
}

export default function AddContenido({ open, setOpen, uploadFile }) {
    //const [open, setOpen] = useState(true);

    const [file, setFile] = useState(null);
    const [nameFile, setNameFile] = useState("");
    const fileInputRef = React.createRef();
    const [fileImagen, setFileImagen] = useState(null);
    const [nameFileImagen, setNameFileImagen] = useState("");
    const fileInputRefImagen = React.createRef();

    //const handleOpen = () => setOpen(true);

    const getFile = (e) => {
        setFile(e.target.files[0]);
        setNameFile(e.target.files[0].name);
    };

    const getFileImagen = (e) => {
        setFileImagen(e.target.files[0]);
        setNameFileImagen(e.target.files[0].name);
    };

    const style = {
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        width: "auto",
        border: "1px solid #000",
        boxShadow: 24,
        p: 4,
        height: "auto",
        minWidth: 400,
        borderRadius: "10px",
        backgroundColor: "#fff",
    };

    return (
        <div>
            <Modal
                open={open}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography
                        id="modal-modal-title"
                        variant="h6"
                        component="h2"
                        color="black"
                        style={{
                            marginBottom: "30px",
                        }}
                    >
                        Agregar documento
                    </Typography>
                    <div>
                        <FormControl sx={{ m: 0, minWidth: 550 }}>                            
                            <div
                                style={{
                                    display: "flex",
                                    padding: 5,
                                    alignItems: "center",
                                }}
                            >
                                <Button
                                    style={{
                                        borderStyle: "solid",
                                        borderColor: "white",
                                        borderWidth: 1,
                                    }}
                                    onClick={() => fileInputRef.current.click()}
                                >
                                    Subir archivo
                                </Button>
                                <input
                                    ref={fileInputRef}
                                    accept=".pdf,.doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                                    type="file"
                                    hidden
                                    onChange={getFile}
                                />
                                {file !== null && <CheckIcon />}
                                {nameFile !== "" && (
                                    <p
                                        style={{
                                            color: "white",
                                            marginLeft: 10,
                                        }}
                                    >
                                        {nameFile}
                                    </p>
                                )}
                            </div>
                            <RedBar />
                            <div
                                style={{
                                    display: "flex",
                                    padding: 5,
                                    alignItems: "center",
                                }}
                            >
                                <Button
                                    style={{
                                        borderStyle: "solid",
                                        borderColor: "white",
                                        borderWidth: 1,
                                    }}
                                    onClick={() =>
                                        fileInputRefImagen.current.click()
                                    }
                                >
                                    Subir imágen de portada
                                </Button>
                                <input
                                    ref={fileInputRefImagen}
                                    accept="image/*"
                                    type="file"
                                    hidden
                                    onChange={getFileImagen}
                                />
                                {fileImagen !== null && <CheckIcon />}
                                {nameFileImagen !== "" && (
                                    <p
                                        style={{
                                            color: "white",
                                            marginLeft: 10,
                                        }}
                                    >
                                        {nameFileImagen}
                                    </p>
                                )}
                            </div>
                            <RedBar />
                            <div>
                                <Button
                                    color="success"
                                    style={{
                                        backgroundColor: "green",
                                        color: "white",
                                        marginRight: 10,
                                    }}
                                    onClick={() => {

                                        const newFile = { file: file, name: nameFile }
                                        const newFileImagen = { file: fileImagen, name: nameFileImagen}

                                        uploadFile(newFile, newFileImagen)
                                    }}
                                >
                                    Subir
                                </Button>
                                <Button
                                    color="error"
                                    style={{
                                        backgroundColor: "red",
                                        color: "white",
                                        marginRight: 10,
                                    }}
                                    onClick={() => {
                                        setOpen(false);
                                    }}
                                >
                                    Cerrar
                                </Button>
                            </div>
                        </FormControl>
                    </div>
                </Box>
            </Modal>
        </div>
    );
}
