import firebaseConf from "../../../database/firebase";
import { generarKey } from "../../Funcionalidades/generarClaveFirebase";

export const getDisenosDiapositivas = async (tipoVista, callback) => {
  var arrayValueFromDatabase = [];

  var arrayValue = [];

  const dbDiapo = firebaseConf
    .database()
    .ref("disenos/diapositivas/")
    .child(tipoVista)
    .orderByChild("fechaCreacion");

  const firestoreDiapo = firebaseConf
    .firestore()
    .collection("contenido")
    .doc("data")
    .collection("diseno")
    .doc("plantilla")
    .collection(tipoVista);

  await dbDiapo.once("value", (snapshot) => {
    snapshot.forEach((childSnapshot) => {
      var childKey = childSnapshot.key;
      var childData = childSnapshot.val();

      arrayValueFromDatabase.push({ datos: childData, key: childKey });
    });

    arrayValueFromDatabase.forEach((valor) => {
      firestoreDiapo
        .doc(valor.key)
        .get()
        .then((value) => {
          arrayValue.push({
            datos: valor.datos,
            key: valor.key,
            contenidoHTML: value.data().contenidoHTML,
            miniatura: value.data().miniatura,
          });
        })
        .finally(() => {
          if (arrayValue.length === arrayValueFromDatabase.length) {
            callback(arrayValue);
          }
        });
    });

    if (arrayValueFromDatabase.length === 0) {
      callback(arrayValue);
    }
  });
};

export const setDisenoDiapo = (
  tipoVista,
  nombreDiseno,
  tipoAccion,
  cantidadItem,
  contenidoHTML,
  image
) => {
  let dbDiapo = firebaseConf
    .database()
    .ref("disenos/diapositivas/")
    .child(tipoVista)
    .child(generarKey(nombreDiseno));

  let firestoreDiapo = firebaseConf
    .firestore()
    .collection("contenido")
    .doc("data")
    .collection("diseno")
    .doc("plantilla")
    .collection(tipoVista);

  dbDiapo
    .set({
      nombre: nombreDiseno,
      fechaCreacion: Date.now(),
      // contenido: contenidoHTML,
      accion: { tipoAccion: tipoAccion, cantidadItem: cantidadItem },
      // image: image,
    })
    .then(() => {
      firestoreDiapo
        .doc(generarKey(nombreDiseno))
        .set({
          contenidoHTML: contenidoHTML,
          miniatura: image,
        })
        .then(() => {
          window.location.reload();
        });
    })
    .catch(() => console.log("error"));
};

export const delDisenoPlantilla = (tipoVista, keyDiseno) => {
  firebaseConf
    .database()
    .ref("disenos/diapositivas/")
    .child(tipoVista)
    .child(keyDiseno)
    .remove()
    .then(() => {
      firebaseConf
        .firestore()
        .collection("contenido")
        .doc("data")
        .collection("diseno")
        .doc("plantilla")
        .collection(tipoVista)
        .doc(keyDiseno)
        .delete()
        .then(() => {
          window.location.reload();
        });
    });
};
