import { Alert, Box, Button, IconButton, Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { UserContext } from "../Funcionalidades/UseContext";
import CloseIcon from "@mui/icons-material/Close";
import CheckIcon from "@mui/icons-material/Check";
import { CustomInput2 } from "../../styles/ArgumentappTheme";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import DoNotDisturbOnIcon from "@mui/icons-material/DoNotDisturbOn";

function RedBar() {
  return <Box sx={{ height: 20 }} />;
}

function BoxCrearActividad({tipoCurso}) {
  const [selectorView, setselectorView] = useState("step1");
  const [nombreActividad, setNombreActividad] = useState("");
  const [duracion, setDuracion] = useState("");
  const [materiales, setMateriales] = useState("");
  const [tipo, setTipo] = useState([{ tipo: "", minutos: "" }]);
  var tempTipo = tipo;
  const [banderaAlerta, setbanderaAlerta] = useState({
    state: false,
    msg: "",
  });

  const [refresh, setrefresh] = useState(true);

  useEffect(() => {
    setTipo(tempTipo);
  }, [tempTipo, refresh]);

  const { handleClose } = useContext(UserContext);
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    boxShadow: 24,
    p: 4,
    height: "auto",
    backgroundColor: "#fff",
    borderRadius: "18px",
    width: 532,
  };

  const history = useHistory();

  const resetValuesActividad = () => {
    setbanderaAlerta({
      state: false,
      msg: "",
    });
    setNombreActividad("");
    setDuracion("");
    setMateriales("");
    setTipo([{ tipo: "", minutos: "" }]);
    setselectorView("step1");
  };

  const isOkStep = () => {
    if (selectorView === "step1") {
      if (
        nombreActividad !== "" &&
        duracion !== "" &&
        materiales !== "" &&
        tipo !== ""
      ) {
        setbanderaAlerta({
          state: false,
          msg: "",
        });

        //Subir datos

        setselectorView("finish");
      } else {
        setbanderaAlerta({
          state: true,
          msg: "Todos los campos son obligatorios para crear una actividad. Vuelva a intentarlo.",
        });
        setTimeout(() => {
          setbanderaAlerta({
            state: false,
            msg: "",
          });
        }, 5000);
      }
    }
  };
  const resizeProgresiones = (state) => {
    let tempData = tempTipo;
    if (state === "less") {
      tempData.pop();
      tempTipo = tempData;
      setrefresh(!refresh);
    } else {
      tempData.push({ tipo: "", minutos: "" });
      tempTipo = tempData;
      setrefresh(!refresh);
    }
  };

  const renderTipo = () => {
    var render = []; // eslint-disable-next-line
    tipo.map((value, index) => {
      render.push(
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
          }}
          key={index}
        >
          <div style={{ display: "flex", flexDirection: "column" }}>
            <Typography style={{ fontSize: 12 }} variant="textoForm">
              Tipo
            </Typography>
            <CustomInput2
              required
              style={{ width: 220 }}
              onBlur={(e) => {
                let tempTipo = tipo;
                tempTipo[index].tipo = e.target.value;
                setTipo(tempTipo);
                console.log(e.target.value);
              }}
            />
          </div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <Typography style={{ fontSize: 12 }} variant="textoForm">
              Minutos
            </Typography>
            <CustomInput2
              required
              style={{ width: 220 }}
              type="number"
              onBlur={(e) => {
                let tempTipo = tipo;
                tempTipo[index].minutos = e.target.value;
                setTipo(tempTipo);
                console.log(e.target.value);
              }}
            />
          </div>
        </div>
      );
    });
    return render;
  };

  return (
    <Box sx={style}>
      {selectorView !== "finish" && (
        <div
          style={{
            marginBottom: "10px",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            alignContent: "center",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <div></div>
          <Typography id="modal-modal-title" variant="titulo_4">
            Agregar una Actividad
          </Typography>

          <IconButton
            onClick={(e) => {
              resetValuesActividad();
              handleClose();
            }}
            aria-label="delete"
          >
            <CloseIcon />
          </IconButton>
        </div>
      )}

      {banderaAlerta.state === false ? (
        <div style={{ height: selectorView === "step1" ? 10 : 0 }}></div>
      ) : (
        <Alert style={{ marginBottom: 5, width: "469px" }} severity="error">
          {banderaAlerta.msg}
        </Alert>
      )}
      {selectorView === "step1" && (
        <div>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "space-between",
                height: 460,
                width: "469px",
              }}
            >
              <div style={{ display: "flex", flexDirection: "column" }}>
                <Typography variant="textoForm">
                  Nombre de la actividad*
                </Typography>
                <CustomInput2
                  required
                  style={{ width: "100% !important" }}
                  value={nombreActividad}
                  onChange={(e) => {
                    setNombreActividad(e.target.value);
                  }}
                />
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                <Typography variant="textoForm">Duración (minutos)*</Typography>
                <CustomInput2
                  required
                  value={duracion}
                  type="number"
                  onChange={(e) => {
                    setDuracion(e.target.value);
                  }}
                  style={{
                    backgroundColor: "white",
                    maxWidth: "130px",
                  }}
                />
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                <Typography variant="textoForm">Materiales*</Typography>
                <CustomInput2
                  required
                  value={materiales}
                  onChange={(e) => {
                    setMateriales(e.target.value);
                  }}
                />
              </div>
              <div style={{ display: "flex", flexDirection: "column" }}>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    overflowY: "auto",
                    height: 180,
                    justifyContent: "center",
                  }}
                >
                  {renderTipo()}
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-between",
                  width: "100%",
                }}
              >
                {tipo.length > 1 ? (
                  <Button
                    variant="contained"
                    startIcon={
                      <DoNotDisturbOnIcon style={{ color: "#9c5fa2" }} />
                    }
                    style={{
                      width: "fit-content",
                      fontSize: 12,
                      padding: 7,
                      height: "fit-content",
                      color: "#9c5fa2",
                      backgroundColor: "transparent",
                      boxShadow: "none",
                    }}
                    onClick={(e) => {
                      resizeProgresiones("less");
                    }}
                  >
                    Quitar
                  </Button>
                ) : (
                  <div></div>
                )}
                <Button
                  variant="contained"
                  startIcon={<AddCircleIcon style={{ color: "#9c5fa2" }} />}
                  style={{
                    width: "fit-content",
                    fontSize: 12,
                    padding: 7,
                    height: "fit-content",
                    color: "#9c5fa2",
                    backgroundColor: "transparent",
                    boxShadow: "none",
                  }}
                  onClick={(e) => {
                    resizeProgresiones("more");
                  }}
                >
                  Crear más
                </Button>
              </div>
            </div>
          </div>
          <RedBar></RedBar>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              padding: 25,
              paddingTop: 5,
            }}
          >
            <Button
              variant="contained"
              // onClick={(e) => subirDatos()}
              onClick={(e) => isOkStep()}
            >
              Crear actividad
            </Button>
          </div>
        </div>
      )}

      {selectorView === "finish" && (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
            alignItems: "center",
            height: 300,
            width: "100%",
            padding: 25,
          }}
        >
          <div>
            <CheckIcon
              fontSize="large"
              style={{
                color: "#9c5fa2",
                borderRadius: 50,
                borderStyle: "solid",
                borderWidth: 1,
                height: 60,
                width: 60,
                padding: 10,
              }}
            ></CheckIcon>
          </div>
          <div>
            <Typography variant="textoClase">
              Actividad fue creada correctamente
            </Typography>
          </div>
          <div style={{ width: "100%" }}>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "space-around",
              }}
            >
              <Button
                variant="outlined"
                style={{ width: 150 }}
                onClick={(e) => {
                  history.go(0);
                }}
              >
                Ir a la unidad
              </Button>
            </div>
          </div>
          <div style={{ textAlign: "center" }}>
            <Typography variant="infoTextoRosa">
              * Si vas a la unidad y seleccionas la clase, vas a poder crear
              diapositivas para cada actividad que tengas en el mismo panel.
            </Typography>
          </div>
        </div>
      )}
    </Box>
  );
}

export default BoxCrearActividad;
