export const generarKey = (data) => {

    var data_out = data;
    data_out = data_out
      .toUpperCase()
      .replace(/ /g, "_")
      .replace(".", "")
      .replace(",", "")
      .replace(";", "")
      .replace(":", "")
      .replace("´", "")
      .replace("*", "")
      .replace("\"", "")
      .replace("!", "")
      .replace("$", "")
      .replace("#", "")
      .replace("[", "")
      .replace("]", "")
      .replace("/", "");


    return data_out

}