import React, { useEffect } from "react";
import Slide from "@mui/material/Slide";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";
import CachedIcon from "@mui/icons-material/Cached";
import { sortableContainer, sortableElement } from "react-sortable-hoc";
import {
  getDiapositivas,
  updateOrdenDiapositiva,
} from "./FirebaseDiapositivas";

import { arrayMoveImmutable } from "array-move";
import { Box } from "@mui/system";

const SortableItem = sortableElement(({ value }) => (
  <div
    style={{
      zIndex: 2000,
    }}
  >
    <div
      style={{
        minHeight: "200px",
        maxHeight: "200px",
        minWidth: "300px",
        maxWidth: "300px",
        borderRadius: 25,
        backgroundColor: "white",
        borderColor: "black",
        borderWidth: "2px",
        borderStyle: "solid",
        marginBottom: "10px ",
        display: "flex",
        flexDirection: "column",
        marginRight: 10,
      }}
    >
      <img
        alt="miniatura diapositiva"
        style={{
          width: "300px",
          height: "200px",
        }}
        src={value.miniatura}
      ></img>
    </div>
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        flexWrap: "nowrap",
        alignContent: "center",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <h4
        style={{
          color: "black",
          textAlign: "center",
          marginLeft: 0,
        }}
      >
        {value.datos.nombre}
      </h4>
    </div>
  </div>
));

const SortableContainer = sortableContainer(({ children }) => {
  return (
    <div
      style={{
        display: "flex",
        flexWrap: "wrap",
        flexDirection: "row",
        alignContent: "center",
        justifyContent: "center",
        border: 0,
        whiteSpace: "nowrap",
        backgroundColor: "transparent",
      }}
    >
      {children}
    </div>
  );
});

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function OrdernarDiapositivas({
  nombreActividad,
  actividad,
  uid,
  idCurso,
  idClase,
  tipoVista,
  tipoCurso,
}) {
  useEffect(() => {
    getDiapositivas(
      uid,
      idCurso,
      idClase,
      actividad,
      tipoVista,
      tipoCurso,
      (callback) => {
        setItem(callback);
      }
    );
  }, [uid, idCurso, idClase, actividad, tipoVista, tipoCurso]);

  const [open, setOpen] = React.useState(false);
  const [banderaSubida, setBanderaSubida] = React.useState(false);

  const [items, setItem] = React.useState([]);

  const onSortEnd = ({ oldIndex, newIndex }) => {
    setItem(arrayMoveImmutable(items, oldIndex, newIndex));
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const actualizarDatos = () => {
    setBanderaSubida(true);
    updateOrdenDiapositiva(
      uid,
      tipoCurso,
      idCurso,
      idClase,
      actividad,
      tipoVista,
      items,
      (value) => {
        if (value) {
          window.location.reload();
        }
      }
    );
  };

  return (
    <div>
      <Button
        variant="outlined"
        onClick={handleClickOpen}
        style={{
          marginLeft: "10px",
          minWidth: "30px",
          maxWidth: "30px",
          minHeight: "30px",
          maxHeight: "30px",
        }}
        title={'Ordenar diapositiva'}
      >
        <CachedIcon></CachedIcon>
      </Button>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        maxWidth={"xl"}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>
          {"Ordenar diapositivas en " + nombreActividad}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            {banderaSubida ? (
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "center",
                  justifyItems: "center",
                  padding: 10,
                }}
              >
                <CircularProgress />
              </Box>
            ) : (
              <SortableContainer
                onSortEnd={onSortEnd}
                shouldUseDragHandle={true}
                axis="xy"
              >
                {items.map((value, index) => (
                  <SortableItem
                    key={`item-${index}`}
                    index={index}
                    value={value}
                  />
                ))}
              </SortableContainer>
            )}
            {items.length === 0 && (
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "center",
                  justifyItems: "center",
                  padding: 5,
                }}
              >
                <p>Actividad no registra diapositivas</p>
              </Box>
            )}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cerrar</Button>
          {!(items.length === 0 || items.length === 1) && (
            <Button onClick={(e) => actualizarDatos()}>Actualizar</Button>
          )}
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default OrdernarDiapositivas;
