import { Button, Popover } from "@mui/material";
import React, { useContext } from "react";
import ClonarDiapositiva from "../../../Funcionalidades/ClonarDiapositiva";
import BotonBorrarDiapositiva from "./BotonBorrarDiapositiva";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import { UserContext } from "../../../Funcionalidades/UseContext";
import BotonBorrarDiapoPublic from "../../ModuloCursosPublicos/Componentes/BotonBorrarDiapoPublic";
function OpcionesDiapo({
  idClase,
  idCurso,
  keyActividad,
  tipoVista,
  nombreDiapositiva,
  acciones,
  contenidoHTML,
  orden,
  idDiapositiva,
  uid,
  imagen,
  isContenidoArgumentapp,
}) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const {
    setBanderaContenidoDiapositiva,
    settipoSlide,
    setbanderaSlide,
    setbanderaSlideContenidoDiapo,
    setContenidoHTML,
    setAcciones,
    setNombreDiapositiva,
    setorden,
    setIdDiapositiva,
    settipoVista,
    setkeyActividad,
  } = useContext(UserContext);

  return (
    <div>
      <Button
        style={{
          marginLeft: 5,
          minWidth: 20,
          maxWidth: 20,
          minHeight: 30,
          maxHeight: 30,
        }}
        aria-describedby={id}
        variant="contained"
        onClick={handleClick}
        title={"Opciones"}
      >
        <MoreHorizIcon sx={{ fontSize: 22 }} />
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Button
          style={{
            textAlign: "center",
            margin: "5px",
            width: "-webkit-fill-available",
            borderStyle: "solid",
            borderWidth: 1,
          }}
          onClick={(e) => {
            setContenidoHTML(contenidoHTML);
            setAcciones(acciones);
            setNombreDiapositiva(nombreDiapositiva);
            setorden(orden);
            setIdDiapositiva(idDiapositiva);
            settipoVista(tipoVista);
            setkeyActividad(keyActividad);
            settipoSlide("right");
            setbanderaSlide(false);
            setTimeout(() => {
              setBanderaContenidoDiapositiva(true);
              setbanderaSlideContenidoDiapo(true);
            }, 200);
          }}
        >
          <i
            style={{
              fontSize: "30px",
              backgroundColor: "white",
            }}
            className="fas fa-pen-square"
          ></i>
          Editar Diapositiva
        </Button>

        <ClonarDiapositiva
          idClase={idClase}
          idCurso={idCurso}
          keyActividad={keyActividad}
          tipoVista={tipoVista}
          uid={uid}
          nombreDiapositiva={nombreDiapositiva}
          acciones={acciones.item}
          contenidoHTML={contenidoHTML}
          tipoAcciones={acciones.tipo}
          imagen={imagen}
          lugarAlmacenamiento={isContenidoArgumentapp ? "publicos" : "privados"}
        ></ClonarDiapositiva>
        {isContenidoArgumentapp ? (
          <BotonBorrarDiapoPublic
            idClase={idClase}
            idCurso={idCurso}
            keyActividad={keyActividad}
            keyDiapositiva={idDiapositiva}
            tipoVista={tipoVista}
            nombreDiapositiva={nombreDiapositiva}
          ></BotonBorrarDiapoPublic>
        ) : (
          <BotonBorrarDiapositiva
            idClase={idClase}
            idCurso={idCurso}
            keyActividad={keyActividad}
            keyDiapositiva={idDiapositiva}
            tipoVista={tipoVista}
            uid={uid}
            nombreDiapositiva={nombreDiapositiva}
          ></BotonBorrarDiapositiva>
        )}
      </Popover>
    </div>
  );
}

export default OpcionesDiapo;
