import { Box, Button, Grid, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import LoadingArgumentapp from "../../../../components/LoadingArgumentapp";
import { getDiapositivas } from "../../../Diapositivas/FirebaseDiapositivas";

function ContenidoDiapoImportar({ idCurso, idClase, keyActividad, tipoVista }) {
      const [diapositivas, setDiapositivas] = useState([]);
      const [banderaCarga, setbanderaCarga] = useState(false);
      useEffect(() => {
        getDiapositivas(
          "data",
          idCurso,
          idClase,
          keyActividad,
          tipoVista,
          "publicos",
          (callback) => {
            setDiapositivas(callback);
            setbanderaCarga(true);
          }
        );
        // eslint-disable-next-line
      }, []);
  return (
    <div>
      {banderaCarga ? (
        <Grid
          container
          justifyContent="start"
          spacing={2}
          style={{
            width: "50vw",
            overflowX: "auto",
            whiteSpace: "nowrap",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
          }}
        >

          {diapositivas.map((value) => (
            <Grid key={value.key} item>
              <div>
                <Button
                  style={{
                    minHeight: "200px",
                    maxHeight: "200px",
                    minWidth: "300px",
                    maxWidth: "300px",
                    borderRadius: 25,
                    backgroundColor: "white",
                    borderColor: "black",
                    borderWidth: "2px",
                    borderStyle: "solid",
                    marginBottom: "0px ",
                    display: "flex",
                    flexDirection: "column",
                  }}
                //   onClick={(e) => {
                //     setContenidoHTML(value.contenidoHTML);
                //     setAcciones(value.datos.acciones);
                //     setNombreDiapositiva(value.datos.nombre);
                //     setorden(value.datos.orden);
                //     setIdDiapositiva(value.key);
                //     settipoVista(tipoVista);
                //     setkeyActividad(keyActividad);
                //     settipoSlide("right");
                //     setbanderaSlide(false);
                //     setTimeout(() => {
                //       setBanderaContenidoDiapositiva(true);
                //       setbanderaSlideContenidoDiapo(true);
                //     }, 200);
                //   }}
                >
                  <img
                    alt="miniatura diapositiva"
                    style={{
                      width: "300px",
                      height: "200px",
                    }}
                    src={value.miniatura}
                  ></img>
                </Button>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    flexWrap: "nowrap",
                    alignContent: "center",
                    justifyContent: "space-between",
                    alignItems: "center",
                    maxWidth: "300px",
                  }}
                >
                  <h4
                    style={{
                      color: "black",
                      textAlign: "start",
                      marginLeft: 0,
                    }}
                  >
                    {value.datos.nombre}
                  </h4>

                </div>
              </div>
            </Grid>
          ))}
          {diapositivas.length === 0 && (
            <div
              style={{
                whiteSpace: "nowrap",
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                justifyContent: "center",
                alignContent: "center",
                alignItems: "center",
                height: 150,
                width: "60vw",
              }}
            >
              <Typography
                style={{
                  display: "flex",
                  flexDirection: "row",
                  alignItems: "center",
                  padding: "30px",
                }}
              >
                No registra diapositivas
              </Typography>
            </div>
          )}
        </Grid>
      ) : (
        <Grid
          container
          justifyContent="start"
          spacing={6}
          style={{
            width: "60vw",
            whiteSpace: "nowrap",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            justifyContent: "center",
            alignContent: "center",
            alignItems: "center",
          }}
        >
          <div
            style={{
              textAlign: "center",
            }}
          >
            <Box sx={{ width: "auto", padding: 8 }}>
              <LoadingArgumentapp></LoadingArgumentapp>
            </Box>
          </div>
        </Grid>
      )}
    </div>
  );
}

export default ContenidoDiapoImportar;
