import { Typography } from "@mui/material";
import React from "react";
import ArgumentappAvatar from "../../Funcionalidades/ArgumentappAvatar";

export default function Perfil(){
    const username = window.localStorage.getItem("dataUserArgumentapp_nombre");
    const tipoUsuario = window.localStorage.getItem("tipoUsuario")

    return (
        <div>
            <div
                style={{
                    position: "absolute",
                    marginTop: 15,
                }}
            >
                <ArgumentappAvatar nombre={username} />
            </div>
            <div
                style={{
                    position: "relative",
                    left: 140,
                    top: 20
                }}
            >
                <Typography variant="h5">{username}</Typography>
            </div>
            <div
                style={{
                    position: "relative",
                    left: 140,
                    top: 30
                }}
            >
                <Typography variant="h6" style={{ textTransform: "capitalize" }}>{tipoUsuario}</Typography>
            </div>
        </div>
    )
}