import React, { useEffect, useState } from "react";
import Slide from "@mui/material/Slide";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Typography,
} from "@mui/material";
import TableChartIcon from "@mui/icons-material/TableChart";
import { getActividadesGeneral } from "./generarEsquema";
import RenderGrafico from "./RenderGrafico";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function OrgChart({
  nombreClase,
  uid,
  idCurso,
  idClase,
  nombreCurso,
  lugarAlamacenamiento,
}) {
  const [open, setOpen] = React.useState(false);
  const [actividades, setActividades] = useState([]);

  useEffect(() => {
    getActividadesGeneral(
      uid,
      idCurso,
      idClase,
      lugarAlamacenamiento,
      (result) => {
        setActividades(result);
      }
    );
  }, [uid, idCurso, idClase, lugarAlamacenamiento]);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
      <Button
        title="Arbol de la clase"
        variant="outlined"
        onClick={handleClickOpen}
        style={{ maxWidth: "fit-content" }}
      >
        <TableChartIcon></TableChartIcon>
      </Button>
      <Dialog
        open={open}
        maxWidth={"xl"}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>{"Arbol de la clase - " + nombreClase}</DialogTitle>
        <DialogContent
          style={{
            display: "flex",
            flexDirection: "column",
            flexWrap: "nowrap",
          }}
        >
          {actividades.map((data, index) => {
            return (
              <Accordion key={index}>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>{data.data.nombreActividad}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <RenderGrafico
                    uid={uid}
                    idCurso={idCurso}
                    idClase={idClase}
                    idActividad={data.key}
                    nombreActividad={data.data.nombreActividad}
                    lugarAlamacenamiento={lugarAlamacenamiento}
                    nombreCurso={nombreCurso}
                    nombreClase={nombreClase}
                  ></RenderGrafico>
                </AccordionDetails>
              </Accordion>
            );
          })}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cerrar</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default OrgChart;

