import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Modal, Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react'
import EditarDiapositiva from '../../ContenidoActividad/EditarDiapositiva';

function BotonEditarDiapoPublic({
  nombreDiapositiva,
  contenidoHTML,
  acciones,
  idCurso,
  idClase,
  keyActividad,
  tipoVista,
  orden,
  idDiapositiva,
  state,
}) {
      const [banderaDialogEdicion, setBanderaDialogEdicion] = useState(false);

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "70%",
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  maxHeight: "90vh",
  overflow: "auto",
  borderRadius: 10
};

  return (
    <div>
      {state && (
        <Button
          style={{
            textAlign: "center",
            margin: "5px",
            width: "-webkit-fill-available",
            borderStyle: "solid",
            borderWidth: 1,
          }}
          variant="contained"
          onClick={(e) => {
            setBanderaDialogEdicion(true);
          }}
        >
          <i
            style={{
              fontSize: "30px",
              backgroundColor: "white",
            }}
            className="fas fa-pen-square"
          ></i>
          Editar Diapositiva
        </Button>
      )}
      <Modal
        open={banderaDialogEdicion}
        onClose={(e) => {
          setBanderaDialogEdicion(false);
        }}
        style={{
          overflow: "hidden",
        }}
      >
        <Box sx={style}>
          <Typography variant="titulo_2" style={{marginBottom: 20}} component="h2">
            Editar diapositiva {nombreDiapositiva}
          </Typography>
          <EditarDiapositiva
            contenidoHTML={contenidoHTML}
            acciones={acciones}
            idCurso={idCurso}
            idClase={idClase}
            keyActividad={keyActividad}
            tipoVista={tipoVista}
            nombreDiapositiva={nombreDiapositiva}
            orden={orden}
            lugarAlmacenamiento={"publicos"}
            idDiapositiva={idDiapositiva}
            uid={'data'}
          ></EditarDiapositiva>
        </Box>
      </Modal>
    </div>
  );
}

export default BotonEditarDiapoPublic
