import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Divider,
  List,
  ListItem,
  Paper,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { getDiapositivasActOnline } from "../../../BDActividadesOnline";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import RenderRespuestas from "./RenderRespuestas";
// import { getColor } from "../../../../../styles/ArgumentappTheme";

function RenderDiapositivasRespuesta({
  codigo,
  idClase,
  idActividad,
  idVista,
}) {
  useEffect(() => {
    getDiapositivasActOnline(codigo, idClase, idActividad, idVista, (diapo) => {
      setDiapositivas(diapo);
      setbanderaCarga(false);
    });
  }, [codigo, idClase, idActividad, idVista]);
  const [diapositivas, setDiapositivas] = useState([]);
  const [banderaCarga, setbanderaCarga] = useState(true);
  // const color = getColor();
  return (
    <List>
      <div>
        {!banderaCarga && (
          <div style={{ textAlign: "center" }}>
            {diapositivas.length === 0 && (
              <h4 style={{ color: "black", marginBottom: 20, marginTop: 0 }}>
                Actividad no registra diapositivas
              </h4>
            )}
          </div>
        )}

        {diapositivas.map((value) => (
          <div key={value.key}>
            <ListItem>
              <Accordion
                style={{
                  width: "100%",
                  boxShadow: "none",
                  borderRadius: 30,
                }}
                disabled={
                  value.datos.acciones.tipo === "Sin acciones" ? true : false
                }
              >
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                  <Paper
                    style={{
                      boxShadow: "none",
                      display: "flex",
                      backgroundColor: "transparent",
                    }}
                  >
                    <div style={{ padding: 5 }}>
                      <img
                        alt="miniatura diapositiva"
                        style={{
                          width: "160px",
                          height: "100px",
                          borderColor: "black",
                          borderWidth: 1,
                          borderStyle: "solid",
                          borderRadius: 20,
                        }}
                        src={value.miniatura}
                      ></img>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        flexWrap: "wrap",
                        alignContent: "center",
                        justifyContent: "center",
                        alignItems: "center",
                        paddingLeft: 30,
                      }}
                    >
                      <Typography variant="h5">
                        Diapositiva: <b>{value.datos.nombre}</b>
                      </Typography>
                    </div>
                  </Paper>
                </AccordionSummary>
                {value.datos.acciones.tipo !== "Sin acciones" && (
                  <AccordionDetails>
                    <RenderRespuestas
                      codigo={codigo}
                      idClase={idClase}
                      idActividad={idActividad}
                      contenidoHTML={value.contenidoHTML}
                      keyDiapositiva={value.key}
                      nombreDiapositiva={value.datos.nombre}
                      acciones={value.datos.acciones}
                    ></RenderRespuestas>
                  </AccordionDetails>
                )}
              </Accordion>
            </ListItem>
            <Divider />
          </div>
        ))}
      </div>
    </List>
  );
}

export default RenderDiapositivasRespuesta;
